<template>
    <section>
        <div>
            <tg-list :IsShowAction="true" ModuleName="Company" SubModuleCode="" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="QUOTATION_ID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'QUOTATION_NUMBER'">
                        <div>
                            <a href="#" @click="handleClick(data.row)" :title="data.row[data.column.COLUMN_NAME]"  data-toggle-tooltip="tooltip" data-original-title="Click here to Update Account">{{ data.row[data.column.COLUMN_NAME] }}</a>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'START_DATE'">
                    <td class="text-center single-action tourguide_a">
                        {{ $options.filters.formatDate(data.row.START_DATE) }}
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'END_DATE'">
                    <td class="text-center single-action tourguide_a">
                        {{ $options.filters.formatDate(data.row.END_DATE) }}
                    </td>
                </template>
            </template>
        </tg-list>
        </div>
  
    </section>
</template>
<script>
import Schema from './QuotesListingSchema'
import DataService from "../../../services/DataService";
import moment from 'moment'
export default { 
    components: {  },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            TotalRecords: 0,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            ProjectName:'',
            taskId: '',
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            ListingData: [],
            searchCondition: '',
            Buttons: Schema.buttons,
            QuotesListingSchema: false,
            leftSearchFields: [{
                fieldName: 'QuotesTitle',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
        };
    },
    created: async function() {   
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.user_Id = this.GetUserInfo.ID;
        this.FetchData();
   
    },
    methods: {
        FetchData:async function(){
            var vm = this;
            var params = `projectId=0&codeName=Client&clientId=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            if (!vm.isBlank(this.searchCondition)) {
                params += `&search=${this.searchCondition}`;
            }
            await DataService.GetQuotationListByQuotationType(params).then(function (response) {            
                if (response.data) {
                    if (response.data.length > 0) {
                        vm.ListingData = response.data;
                        vm.noRecord = false;
                        vm.TotalRecords=response.data[0].TOTAL_RECORDS;

                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ListingData = [];
                    vm.TotalRecords = 0
                }
            });
        },
        handleClick(item) {
            var vm = this;
            vm.$router.push({path: `/CRM/ManageQuotation/${item.QUOTATION_ID}/CRM/CRM_QUOTES/0?clientId=${vm.UserId}`});
            //window.location.href = `/CRM/ManageQuotation/${item.QUOTATION_ID}/CRM/CRM_QUOTES/0`;
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if(value.searchCondition !=''){
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            } 
            else {
                this.searchCondition = "";
            }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    
                    case 'Edit':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditTeam':
                    this.handleClick(item);
                    break;  
                   
            }
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "AddQuotes":
                vm.AddQuotes();
                    break;
                case "BackToList":
                    this.BackToList();
                    break;
                case "DeleteClientData":
                vm.commonDeleteData();
                    break;
            }
        },
    
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
};
</script>