<template>
    <section class="col-md-12 p-0">
        <div class="container-fluid p-0">
            <div class="col-md-12 p-0">
                <div class="listing">
                    <div class="table-responsive table-fix-header float-left" style="max-height:630px;">
                        <table id="mainTable" class="table table-bordered mt-0" style="width: 1552px;">
                            <thead class="thead-bg">
                                <tr>
                                    <th><span>{{ $t("#") }}</span></th>
                                    <th><span>{{ $t("UtilityName") }}<span class="text-danger d-inline-block">*</span></span></th>
                                    <th v-if="isCheckExtraFeatureUtilityBilling"><span>{{ $t("UtilityType") }}<span class="text-danger d-inline-block">*</span></span></th>
                                    <th v-if="isCheckExtraFeatureUtilityBilling"><span>{{ $t("MeterInfo") }}</span></th>
                                    <th v-if="isCheckExtraFeatureUSICBILLING"><span>{{ $t("Description") }}</span></th>
                                    <th v-if="isCheckExtraFeatureUSICBILLING"><span>{{ $t("Utlityid") }}</span></th>
                                    <th v-if="isCheckExtraFeatureUSICBILLING"><span>{{ $t("DatabaseName") }}</span></th>
                                    <th><span>{{ $t("ProcessTime") }}</span></th>
                                    <th v-if="configNotes == 'yes'"><span>{{ $t("Notes") }}</span></th>
                                    <th class="text-center single-action"><span>{{ $t("Delete") }}</span></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in AdditionalInfoData" :key="item.clientAdditionalId">
                                    <td class="float-left">
                                        {{ index + 1 }}
                                    </td>
                                    <td>
                                        <input
                                            type="text"
                                            v-model="item.companyName"
                                            @input="removeError(item, 'companyName')"
                                            :class="['form-control', {
                                                'border-danger': item._validation && item._validation.companyName,
                                                'is-invalid': item._validation && item._validation.companyName
                                            }]"
                                        />
                                    </td>
                                    <td v-if="isCheckExtraFeatureUtilityBilling">
                                        <select
                                            v-model="item.utilityTypeId"
                                            @change="removeError(item, 'utilityTypeId')"
                                            :disabled="item.meterDescription !== null && item.meterDescription !== ''"
                                            :class="['form-control', {
                                                'border-danger': item._validation && item._validation.utilityTypeId,
                                                'is-invalid': item._validation && item._validation.utilityTypeId
                                            }]"
                                        >
                                            <option value="" disabled>{{ $t("Select") }}</option>
                                            <option v-for="itm in utilityTypeList" :key="itm.value" :value="itm.value">
                                                {{ itm.name }}
                                            </option>
                                        </select>
                                    </td>
                                    <td v-if="isCheckExtraFeatureUtilityBilling">
                                        <div class="description-hover float-left">
                                            <span class="description-text"
                                                :title="item.meterDescription"
                                                v-html="TruncateStr(item.meterDescription, 40)">
                                            </span>
                                        </div>
                                        <div class="float-right" v-if="item.clientAdditionalId != 0">
                                            <a href="javascript:;" title="View All"
                                            @click="ViewMeterList(item)"
                                            class="viewcdccodenew viewicon enable"
                                            data-toggle-tooltip="tooltip">
                                                <i class="fa fa-eye text-info action-icon"></i>
                                            </a>
                                        </div>
                                    </td>
                                    <td v-if="isCheckExtraFeatureUSICBILLING">
                                        <div class="description-hover">
                                            <span class="description-text"
                                                :title="item.description"
                                                v-html="TruncateStr(item.description, 30)">
                                            </span>
                                        </div>
                                        <div class="float-right" v-if="item.clientAdditionalId != 0">
                                            <a href="javascript:;" title="View All"
                                            @click="ViewCDCCodeList(item)"
                                            class="viewcdccodenew viewicon enable"
                                            data-toggle-tooltip="tooltip">
                                                <i class="fa fa-eye text-info action-icon"></i>
                                            </a>
                                        </div>
                                    </td>
                                    <td v-if="isCheckExtraFeatureUSICBILLING">
                                        <input type="text" :class="{ 'form-control': true }" v-model="item.notes"/>
                                    </td>
                                    <td v-if="isCheckExtraFeatureUSICBILLING">
                                            <select name="dbName" v-model="item.dbName" :class="{ 'form-control': true }">
                                                <option value="" disabled selected>{{ $t("Select") }}</option>
                                                    <option v-for="itm in databaseList" :key="itm.value"
                                                        :value="itm.value">
                                                        {{ itm.name }}
                                                </option>
                                            </select>
                                    </td>
                                    <td>
                                        <span class="float-left" v-if="item.processingBasedOn == 'EventBased'">{{ item.eventFor }}</span>
                                        <span class="float-left" v-else>{{ item.processingTime }}</span>
                                        <div class="float-right width_17 margintop3 single-action"  v-if="item.clientAdditionalId != 0">
                                            <a href="javascript:;" v-on:click="AddTime(item)" title="Add" class="addProcessTime">
                                                <i class="fa fa-plus-circle text-dark action-icon"></i>
                                            </a>
                                        </div>
                                    </td>
                                 
                                    <td class="text-center single-action" v-if="configNotes == 'yes'">    
                                        <a href="javascript:;" class="text-darks single-action" v-on:click="NoteDataByUtility(item)">
                                            <em aria-hidden="true" class="fa fa-file-text-o"></em><span class="noti-circle noti-orange">{{ item.noOfNotes }}</span></a>
                                    </td>
                                    <td class="text-center">
                                        <a class="blue recyclebin enable ancdeleteadditionalinfo" @click="RemoveInvoice(index,item.clientAdditionalId)" v-if="item.meterDescription =='' || item.meterDescription == null">
                                            <i class="fa fa-trash text-danger action-icon"></i>
                                        </a>
                                    </td>
                                </tr>
                                <tr v-if="IsAdditionalInfoDataShow">
                                    <td colspan="10" class="text-center text-danger no_records no-record">
                                        {{ $t('NoRecordfound') }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                       
                    </div>
                    <div class="col-lg-12 p-0 float-left text-right mt-3" v-if="!IsAdditionalInfoDataShow">
                        <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveUtilityData()" title="Save">
                            <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                        </a>
                        <a href="javascript:;" class="btn btn-danger" v-on:click="Reset()" title="Reset">
                            <i class="fa fa-repeat pr-2"></i>{{ $t('Reset') }}
                        </a>
                            <input type="hidden" id="hdnPageNum" value="1">
                    </div>
                    
                </div>
               
            </div>
        </div>
        <ModalAddUtilityTime v-if="isShowAddUtilityTime" :clientId="UserId" :ClientAdditionalId="ClientAdditionalId"></ModalAddUtilityTime>
        <AddNoteUtilityOrDivision v-if="isShowAddNoteDivision" :utilityId="utilityId" :divisionId="divisionId" viewType="Utility" :clientId="clientId" modulename="Billing" submodulecode="Billing_InvoiceGrouping" 
        :callbackfunction="GetDivisionMappingDetail"></AddNoteUtilityOrDivision>
        <CdcCodeList v-if="isCdcCodeList" :clientId="clientId" :utilityId="utilityId" :ClientAdditionalId="ClientAdditionalId"></CdcCodeList>
        <UtilityMeterMapping v-if="isMeterList" :utilityTypeId="utilityTypeId" :clientId="clientId" :ClientData="ClientData" :ClientAdditionalId="ClientAdditionalId"></UtilityMeterMapping>
    </section>
</template>
<script>

import ModalAddUtilityTime from "../../../components/Modals/modalAddUtilityTime.vue";
import AddNoteUtilityOrDivision from "../../../components/Modals/AddNoteUtilityOrDivision.vue";
import DataService from "../../../services/DataService";
import CdcCodeList from "./CdcCodeList.vue";
import UtilityMeterMapping from "./UtilityMeterMapping.vue";

export default {
    components: {
        ModalAddUtilityTime,
        CdcCodeList,
        AddNoteUtilityOrDivision,
        UtilityMeterMapping
    },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        },
        IsAdditionalInfoDataShow:{
            type: Boolean            
        },
        AdditionalInfoData:{
            type: Array
        }
    },
    data() {
        return {

            isLoading: false,
            CompanyId: null,
            UserId: null,
            Result: [],
            invoiceFrequencyCustomDate:[],
            invoiceTypeList:[],
            projectList:[],
            invoiceDetailTemplateList:[],
            fileFormatList:[],
            configNotes:'',
            isAssociateProjectWithDivision:false,
            isShowWorkdayInvoiceType : false,
            isShowAddNoteDivision:false,
            divisionId:0,
            clientId:0,
            utilityId:0,
            AdditionalInfoData:[],
            databaseList:[],
            districtList:[],
            isShowAddUtilityTime:false,
            ClientAdditionalId:0,
            isCdcCodeList:false,
            isCheckExtraFeatureUtilityBilling:false,
            isCheckExtraFeatureUSICBILLING:false,
            utilityTypeList:[],
            isMeterList: false,
            utilityTypeId:0
        };
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
        await vm.CheckFeaturesForCompany('USICBILLING'),
        await vm.CheckFeaturesForCompany('UTILITY_BILLING')

        if(vm.isCheckExtraFeatureUSICBILLING){
            await vm.getDatabaseOrDistrictList('DATABASE_LIST');
            await vm.getDatabaseOrDistrictList('BILLING_DISTRICT_LIST');
        }
        if(vm.isCheckExtraFeatureUtilityBilling){
            await vm.getDatabaseOrDistrictList('UTILITY_TYPE_FOR_BILLING');
        }
        await vm.GetUtilityMappingDetail();
    },

    methods: {
        removeError(item, field) {
            if (!item._validation) return;
            item._validation[field] = false;
        },
        Reset : function(){
            var vm = this;
            vm.$parent.LoadAdditionalInfo();
        },
        CheckFeaturesForCompany: async function (type) {
            var vm = this;
            var appendingUrl = `featureCode=${type}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if(type == 'USICBILLING'){
                    if (response.data == true) {
                         vm.isCheckExtraFeatureUSICBILLING = true;
                    } else {
                         vm.isCheckExtraFeatureUSICBILLING = false;
                    }
                }
                else if(type == 'UTILITY_BILLING'){
                    if (response.data == true) {
                         vm.isCheckExtraFeatureUtilityBilling = true;
                    } else {
                         vm.isCheckExtraFeatureUtilityBilling = false;
                    }
                }
               
            });
        },
        ViewMeterList: function(item){
            var vm = this;
            vm.utilityTypeId = item.utilityTypeId;
            debugger;
            vm.ClientAdditionalId = item.clientAdditionalId;
            vm.clientId = this.UserId;
            vm.isMeterList = true;
        },
        ViewCDCCodeList: function(item){
             var vm = this;
             vm.ClientAdditionalId = item.clientAdditionalId;
             vm.clientId = this.UserId;
             vm.utilityId = item.clientAdditionalId;
             vm.isCdcCodeList = true;
        },
        NoteDataByUtility: function(item){
             var vm = this;
             vm.utilityId = item.clientAdditionalId;
             vm.clientId = this.UserId;
             vm.isShowAddNoteDivision =true;
        },
        AddTime:function(item){
            var vm = this;
            vm.ClientAdditionalId = item.clientAdditionalId;
            vm.isShowAddUtilityTime = true;
        },
        getDatabaseOrDistrictList: async function(type) {
            var vm = this;
            let Url = `id=&moduleName=Billing&type=${type}&search=&refId=`
            await DataService.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    if(type == 'DATABASE_LIST'){
                        vm.databaseList = response.data.DATA; 
                    }
                    else if(type == 'UTILITY_TYPE_FOR_BILLING'){
                        vm.utilityTypeList = response.data.DATA
                    }
                    else{
                        vm.districtList = response.data.DATA
                    }
                }

            })
        },
        GetUtilityMappingDetail: async function () {
            var vm = this;
            await DataService.GetUtilityMappingDetail().then((response) => {
                if(response.data != null){
                    this.configNotes = response.data.getNotesByCompanyId;
                }
            });
        },
        ViewNotes: function(item){
             var vm = this;
             vm.divisionId = item.DIVISIONS_ID;
             vm.clientId = this.UserId;
             vm.isShowAddNoteDivision =true;
        },
        async CheckExtraFeatures(featureCode) {
            const params = `featureCode=${featureCode}`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    if(featureCode == 'AssociateProjectWithDivision'){
                        this.isAssociateProjectWithDivision = response.data === true;
                    }
                    else
                    {
                        this.isShowWorkdayInvoiceType = response.data === true;
                    }
                    
                }
            } catch (error) {
                console.error("Error checking extra features:", error);
            }
        },

        RemoveInvoice(index,clientAdditionalId) {
            var vm = this;
            const ReqFrom = vm.isCheckExtraFeatureUtilityBilling ? 'UTILITY_BILLING' : 'USIC_BILLING';
            var msg = vm.$t("ConfirmDelete");
            vm.confirmR(msg, true, null,function () {
                vm.AdditionalInfoData.splice(index, 1); 
                // vm.AdditionalInfoData.splice(index, 1);
                if(clientAdditionalId != 0){
                    var postJSON = {
                        ids: clientAdditionalId,
                        companyId: vm.CompanyId,
                        userId: vm.user_id,
                        moduleName: "Billing",
                        refCode: "TALYGEN_CLIENT_ADDITIONAL_INFO"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {   
                            vm.$parent.LoadAdditionalInfo();
                            if(ReqFrom == 'UTILITY_BILLING'){
                                vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("TalygenUtility")]), "success", true, vm.$t("Alert"));
                            }
                            else{
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('AdditionalInfoClient')]), "Success", true, vm.$t('Alert'));
                            }
                        }
                        else {
                            if(ReqFrom == 'UTILITY_BILLING'){
                                vm.ShowAlert(vm.$t("DeletedError", [vm.$t("TalygenUtility")]), "failure", true, vm.$t("Alert"));
                            }
                            else{
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('AdditionalInfoClient')]), "failure", true, vm.$t('Alert'));
                            }
                           
                        }
                    });
                }
            });
        },
        SaveUtilityData: function () {
            var vm = this;
            let hasError = false;
            debugger;
            vm.AdditionalInfoData.forEach((item) => {
                if (!item._validation) item._validation = {};
                if (!item.companyName || item.companyName.trim() === "") {
                    item._validation.companyName = true;
                    hasError = true;
                } else {
                    item._validation.companyName = false;
                }
                if (vm.isCheckExtraFeatureUtilityBilling) {
                    if (!item.utilityTypeId || item.utilityTypeId === "") {
                        item._validation.utilityTypeId = true;
                        hasError = true;
                    } else {
                        item._validation.utilityTypeId = false;
                    }
                }
            });
            if (hasError) return;
            const ReqFrom = vm.isCheckExtraFeatureUtilityBilling ? 'UTILITY_BILLING' : 'USIC_BILLING';
            var params = `ClientId=${vm.UserId}&ReqFrom=${ReqFrom}`;
            var obj = {
                MainData: vm.AdditionalInfoData
            }
            DataService.SaveUtilityData(params, obj).then(function (response) {
                if (response.data.status == "Success") {
                    if(ReqFrom == 'UTILITY_BILLING'){
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("TalygenUtility")]), "success", true, vm.$t("Alert"));
                    }
                    else{
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("AdditionalInfoClient")]), "success", true, vm.$t("Alert"));
                    }
                   
                    vm.$parent.LoadAdditionalInfo();
                    vm.GetUtilityMappingDetail();
                    vm.$parent.IsAdditionalInfoDataShow = false;

                }
                else {
                    vm.ShowAlert(vm.$t('UnknownErrorOccur'), "failure", true, vm.$t("Alert"));
                }
            });
        },


    }
};
</script>