<template>
  <section v-if="isInvoice">
    <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Billing" :listType="['List']"
      :RenderRowActionMethod="billRenderActions" SubModuleCode="Billing" IdentityColumn="id" HeaderText="Invoices"
      :ListData="BillListing" :HeaderData="Headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
      :TotalRecords="TotalRecords" SearchPosition="top" :SearchFields="topSearchFields" :LegendArray="LegendArray"
      :ListDataCallBackFunction="FetchData" :SortExp="sortExp" :SortBy="sortBy"
      v-on:ActionButtonClick="billActionButtonClick" :NorecordfoundText="$t('NoRecordfound')"
      v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
      v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
      <template v-slot:slotdata="{ data }">
        <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
          <span>
            {{ $options.filters.formatDate(data.row.CREATED_AT) }}
          </span>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'BILL_DUE_DATE'">
          <span>
            {{ $options.filters.formatDate(data.row.BILL_DUE_DATE) }}
          </span>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'BILL_NET_TOTAL'">
          <a href="javascript:;" @click="openPaymentHistory(data)"><span>
           {{ $n(data.row.BILL_NET_TOTAL, "currency", $getLocaleByCurrency(data.row.ISO_CODE))}}</span></a>
        </template>
        <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
          <span v-if="
            data.row.IS_RECURRING != 1 && data.row.RECURRING_STATUS != 'STOP'
          ">
            <span v-bind:class="getStatusClassList(data.row.STATUS_CODE, 'STATUS_NAME')">
              {{ data.row.STATUS_NAME }}
            </span>
            <!-- <span class="float-left mr-1 w-100 position-relative" v-else v-if="data.row.STATUS_CODE === 'CLBERROR'" >
              <span style="cursor:inherit;" class="btn btn-danger py-0 d-block font-14">{{ $t("Failed") }}</span>
              <a href="javascript:;" class="ExportId position-absolute info-mobile-view" @click="openExportErrorModel(data)">
                <i class="fa fa-info-circle text-info bg-white" style="border-radius:100%"></i>
              </a> </span> -->
          </span>
          <span v-else class="stop">{{ $t("Stopped") }} </span>
        </template> 
          <template v-if="data.column.COLUMN_NAME == 'BILL_NUMBER'">
          <a href="javascript:;" @click="BillViewDetail(data.row)"><span>
              {{ data.row.BILL_NUMBER }} </span></a>
        </template>
      </template>
    </tg-list>

    <transition name="modal">
      <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block dialog" v-if="isPaymentHistory">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header theme-primary partition-full">
                <h4 class="modal-title">{{ $t("PaymentHistory") }}</h4>
                <button type="button" class="close" v-on:click="Close">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                <div class="card-body float-left p-0 col-12 float-left">
                  <div class="row">
                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>{{ $t("BillNumber") }}:</b>
                          {{ paymentHistoryData.MainInvoiceNumber }}</label>
                      </div>
                    </div>

                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>{{ $t("IssueDate") }}:</b>
                          {{
                          formatDateMonth(
                          paymentHistoryData.MainInvoiceIssueDate
                          )
                          }}</label>
                      </div>
                    </div>
                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>{{ $t("DueDate") }}:</b>
                          {{
                          formatDateMonth(
                          paymentHistoryData.MainInvoiceDueDate
                          )
                          }}</label>
                      </div>
                    </div>
                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>Advance (PKR):</b>
                          <span v-if="
                              paymentHistoryData.MainInvocieAdvacncedRecived ==
                              0
                            ">{{ $n(0.00 , 'currency', $getLocaleByCurrency(CurrencyCode)) }}</span>
                          <span v-if="
                              paymentHistoryData.MainInvocieAdvacncedRecived !=
                              0
                            "> {{ $n(paymentHistoryData.MainInvocieAdvacncedRecived , 'currency', $getLocaleByCurrency(CurrencyCode)) }}</span>
                        </label>
                      </div>
                    </div>
                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>Discount Flat Rate (PKR):</b>
                          <span v-if="
                              paymentHistoryData.MainInvoiceFlatDiscount == 0
                            ">{{ $n(0.00 , 'currency', $getLocaleByCurrency(CurrencyCode)) }}</span>
                          <span v-if="
                              paymentHistoryData.MainInvoiceFlatDiscount != 0
                            ">{{ $n(paymentHistoryData.MainInvoiceFlatDiscount , 'currency', $getLocaleByCurrency(CurrencyCode)) }}</span>
                        </label>
                      </div>
                    </div>
                    <div class="col-md-6 col-lg-4">
                      <div class="form-group">
                        <label><b>Amount (PKR):</b> 
                          <span v-if="paymentHistoryData.MainInvocieNetAmount == 0"> 0.00</span>
                          <span v-if="paymentHistoryData.MainInvocieNetAmount != 0">{{ $n(paymentHistoryData.MainInvocieNetAmount , 'currency', $getLocaleByCurrency(CurrencyCode)) }} </span>
                        </label>
                      </div>
                    </div>
                  </div>

                  <div class="listing">
                    <div class="table-responsive table-fix-header">
                      <table id="maintable" class="table-sorting table table-bordered my-0">
                        <thead class="thead-bg">
                          <tr>
                            <th width="30%">
                              <span>{{ $t("Notes") }}</span>
                            </th>
                            <th width="20%">
                              <span>{{ $t("PaymentDate") }}</span>
                            </th>
                            <th width="20%" class="text-right">
                              <span>{{ $t("Amount") }} ({{
                                paymentHistoryData.MainInvoiceCompanyIsoCode
                                }})</span>
                            </th>
                            <th width="30%" class="text-center">
                              <span>{{ $t("Action") }}</span>
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-if="!paymentList || paymentList.length == 0">
                            <td colspan="4" class="text-center text-danger" style="border-bottom:1px solid #c6cace!important;">
                              {{ $t("NoPaymentFound") }}
                            </td>
                          </tr>
                          <!-- Check if payment list is available -->
                          <tr v-else-if="
                              paymentList &&
                              paymentList.length > 0 &&
                              totalPayments < MainInvocieNetAmount
                            " v-for="(childInvoice, index) in paymentList" :key="index" class="myrow">
                            <td>
                              <span>{{ childInvoice.MainNote }}</span>
                            </td>
                            <td>
                              <span>{{
                                formatDateMonth(childInvoice.PaymentDate)
                                }}</span>
                            </td>
                            <td class="text-right">
                              <span>{{
                                formatAmount(childInvoice.Amount)
                                }}</span>
                            </td>
                            <td class="text-center">
                              <!-- <a v-if="userType !== 'CL'" href="javascript:;" class="pay_hist_edit EditAnchor" title="Edit">
                                                                <i class="fa fa-pencil text-success action-icon"></i>
                                                                </a>
                                                                <a v-if="userType !== 'CL'" href="javascript:;" class="pay_hist_update UpdateAnchor" style="display: none" title="Update">
                                                                <i class="fa fa-clock-o text-secondary action-icon"></i>
                                                                </a>
                                                                <a v-if="userType !== 'CL'" href="javascript:;" class="pay_hist_cancel UpdateCancelAnchor" style="display: none" title="Cancel">
                                                                <i class="fa fa-trash text-danger action-icon"></i>
                                                                </a> -->
                            </td>
                          </tr>
                        </tbody>
                        <tfoot>
                          <tr>
                            <td></td>
                            <td class="text-right">
                              {{ $t("NetAmountPaid") }}:
                            </td>
                            <td class="text-right font-weight-bold">
                              {{ $n(formatAmount(totalPaid) , 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                            </td>
                            <td></td>
                          </tr>
                          <tr>
                            <td></td>
                            <td class="text-right">
                              {{ $t("OutStandingAmount") }}
                            </td>
                            <td class="text-right font-weight-bold">
                              <span :style="{
                                  color:
                                    outstandingAmount < 0 ? 'green' : 'red',
                                }">
                                   {{ $n(formatAmount(outstandingAmount) , 'currency', $getLocaleByCurrency(CurrencyCode)) }} 
                              </span>
                            </td>
                            <td></td>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <div class="col-lg-6 text-right p-0">
                  <a href="javascript:;" class="btn btn-danger formbtn text-white" v-on:click="Close"><em
                      class="fa fa-close pr-2"></em>{{ $t("Cancel") }}</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="modal">
      <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block dialog" v-if="isPayment">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header theme-primary partition-full">
                <h4 class="modal-title">{{ $t("Payment") }}</h4>
                <button type="button" class="close" v-on:click="ClosePayment">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                <div class="card-body float-left p-0 col-12 float-left">
                  <div class="row">
                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>{{ $t("BillNumber") }}:
                          {{ paymentDetailsData[0].BILL_NUMBER }}</label>
                      </div>
                    </div>

                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>{{ $t("IssueDate") }}:
                          {{
                          formatDateM(paymentDetailsData[0].BILL_ISSUE_DATE)
                          }}</label>
                      </div>
                    </div>
                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>{{ $t("DueDate") }}:
                          {{
                          formatDateM(paymentDetailsData[0].BILL_DUE_DATE)
                          }}</label>
                      </div>
                    </div>
                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>Total Amount ({{ paymentDetailsData[0].ISO_CODE }}):
                          <span v-if="paymentDetailsData[0].BILL_NET_TOTAL == 0">0.00</span>
                          <span v-if="paymentDetailsData[0].BILL_NET_TOTAL != 0"> {{ $n(paymentDetailsData[0].BILL_NET_TOTAL, "currency", $getLocaleByCurrency(paymentDetailsData[0].ISO_CODE))}}</span>
                        </label>
                      </div>
                    </div>
                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>Paid Amount Till Now ({{
                          paymentDetailsData[0].ISO_CODE
                          }}):
                          <span v-if="paymentDetailsData[0].PAID>0">{{ $n(paymentDetailsData[0].PAID, "currency", $getLocaleByCurrency(paymentDetailsData[0].ISO_CODE))}}</span>
                            <span v-else
                            >0.00</span>
                        </label>
                      </div>
                    </div>

                    <div class="col-md-4 col-lg-4">
                      <div class="form-group">
                        <label>Payment Date:</label>
                        <span> {{ paymentDate }} </span>
                      </div>
                    </div>

                    <!-- 
                                     <div class="clearfix"></div>
                                         <div class="col-lg-12">
                                        <label>Payment Date:<span class="mandatory">*</span></label>
                                       <input id="PaymentDate" class="form-control" v-model="paymentDate" readonly/>
                                     </div> -->

                    <div class="clearfix"></div>

                    <div class="listing mt-2 col-lg-12" v-if="paymentDetailsData[0].HAS_MULIPLE_TERMS">
                      <div class="table-responsive table-fix-header">
                        <table id="maintable" class="table-sorting table table-bordered my-0">
                          <thead class="thead-light">
                            <tr>
                              <th width="25%">
                                <span>{{ $t("Notes") }}</span>
                              </th>
                              <th width="20%" class="text-center">
                                <span>{{ $t("Due Date") }} </span>
                              </th>
                              <th width="20%" class="text-left">
                                <span>{{ $t("Due Amount") }} ({{
                                  paymentTermsData[0].ISO_CODE
                                  }})</span>
                              </th>
                              <th width="20%" class="text-left">
                                <span>{{ $t("Mark Payment") }}</span>
                              </th>
                              <th width="15%" class="text-center">
                                <span>{{ $t("Action") }}</span>
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-if="
                                !paymentTermsData ||
                                paymentTermsData.length == 0
                              ">
                              <td colspan="5" class="text-center text-danger">
                                {{ $t("NoPaymentFound") }}
                              </td>
                            </tr>
                            <!-- Check if payment list is available -->
                            <tr v-else-if="
                                paymentTermsData && paymentTermsData.length > 0
                              " v-for="(
                                termsPaymentRow, index
                              ) in paymentTermsData" :key="index" class="myrow">
                              <td>
                                <span>{{ termsPaymentRow.NOTES }} </span>
                              </td>
                              <td class="text-center">
                                <span>{{ formatDateM(termsPaymentRow.DUE_ON) }}
                                </span>
                              </td>
                              <td>
                                <input class="form-control" id="txtduedamount" v-model="termsPaymentRow.DUE_AMOUNT"
                                  maxlength="12" readonly />
                              </td>
                              <td>
                                <label class="mr-2" :class="{
                                    total_disable_class_bg:
                                      termsPaymentRow.DUE_AMOUNT === 0.0
                                  }">
                                  <input type="radio" value="1" v-model="termsPaymentRow.IS_FULL_PAYMENT" :checked="
                                      termsPaymentRow.IS_FULL_PAYMENT === '1'
                                    " />
                                  Full Payment
                                </label>
                                <label :class="{
                                    total_disable_class_bg:
                                      termsPaymentRow.DUE_AMOUNT === 0.0
                                  }">
                                  <input type="radio" value="0" v-model="termsPaymentRow.IS_FULL_PAYMENT" :checked="
                                      termsPaymentRow.IS_FULL_PAYMENT === '0'
                                    " />
                                  Partial
                                </label>

                                <div v-if="termsPaymentRow.IS_FULL_PAYMENT === '0'">
                                  <input type="number" class="form-control" id="txtrecivedamount"
                                    v-model="termsPaymentRow.AMOUNT_RECEIVED" maxlength="12"
                                    @blur="checkAmountReceived(termsPaymentRow)" :class="{
                                    total_disable_class_bg:
                                      termsPaymentRow.DUE_AMOUNT === 0.0
                                  }" />
                                </div>
                              </td>
                              <td class="text-center">
                                <div class="col-lg-12">
                                  <div v-if="shouldDisplay && paymentDetailsData[0].HAS_MULIPLE_TERMS">
                                    <span id="submitField">
                                      <a id="SaveMultipleTerms" title="Save" :class="{
                                          total_disable_class_bg:
                                            termsPaymentRow.DUE_AMOUNT === 0.0,
                                        }" @click="
                                          SaveMultipleTerms(termsPaymentRow)
                                        " class="submitbtn btn btn-success" href="javascript:;"><i
                                          class="fa fa-save pr-2"></i>Save</a>
                                    </span>
                                  </div>

                                  <!-- @*<a class="btn btn-danger text-white" id="PartialPopupCancel" title="@Resources.Cancel"><i class="fa fa-close pr-2"></i>@Resources.Cancel</a>*@ -->
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div class="listing mt-2 col-lg-12" v-if="!paymentDetailsData[0].HAS_MULIPLE_TERMS && IsAlreadyPaid==false">
                      <div class="table-responsive table-fix-header">
                        <table id="maintable" class="table-sorting table table-bordered my-0">
                          <thead class="thead-light">
                            <tr>
                              <th width="25%">
                               
                                 <label class="float-left">Notes:</label><span class="mandatory float-left">*</span>
                              </th>

                              <th width="15%" class="text-left">
                                <label class="float-left">Amount:</label><span class="mandatory float-left">*</span>
                              </th>

                              <th width="15%" class="text-left">
                                <label class="float-left">Refrence number:</label><span class="mandatory float-left">*</span>
                              </th>

                              <th width="15%" class="text-left">
                                <label class="float-left">Bill Date:</label><span class="mandatory float-left">*</span>
                              </th>
                               <th width="15%" class="text-left">
                                <label class="float-left">Payment Mode:</label><span class="mandatory float-left">*</span>
                              </th>

                              <th width="15%" class="text-center">
                                <span>{{ $t("Action") }}</span>
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <!-- Check if payment list is available -->
                            <tr>
                              <td>
                                <textarea name="Notes" id="txtNotes" v-model="Notes" rows="2" cols="100"
                                  class="form-control"/>
                              </td>

                              <td>
                                <span class="fullPaid"
                                  v-if="paymentDetailsData.length > 0 && (paymentDetailsData[0].BILL_NET_TOTAL || 0) - (paymentDetailsData[0].PAID || 0) <= 0">Paid</span>
                                <span v-else>
                                  <input type="number" class="form-control" id="txtamount" v-model="amount"
                                    maxlength="12"  onkeypress="return event.key !== '-' && this.value.length < 12" />
                                </span>
                              </td>
                              <td>
                               <span>
                                  <input type="text" class="form-control" id="txtamount" v-model="RefNo"
                                    maxlength="18" />
                                </span>
                              </td>
                               <td>
                              <span>{{BillDate}}</span>
                            </td>
                            <td>
                             <select class="form-control" v-model="BillMode">
                                  <option value="Cash">Cash</option>
                                  <option value="Cheque">Cheque</option>
                                   <option value="Online">Online</option>
                                   <option value="DD">DD</option>
                              </select>                   
                            </td>
                              <td class="text-center">
                                <div class="col-lg-12">
                                  <a v-if="shouldDisplay && !paymentDetailsData[0].HAS_MULIPLE_TERMS"
                                    id="MakePartialPay" title="Save" @click="SavePartialPayment"
                                    class="submitbtn btn btn-success mr-2" href="javascript:;"><i
                                      class="fa fa-save pr-2"></i>Save</a>
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div class="mt-2 col-lg-12" v-if="IsAlreadyPaid==true">
                    <div class="alert alert-danger text-center" role="alert">
                     {{ $t('InvoiceAlreadyPaid') }}
                    </div>
                  </div>

                    <div class="col-lg-12 text-right mt-3">
                      <a href="javascript:;" class="btn btn-danger formbtn text-white" v-on:click="ClosePayment"><em
                          class="fa fa-close pr-2"></em>{{ $t("Cancel") }}</a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <Attachment v-if="isAttachment" :BillId="billId"></Attachment>
    <ExportError v-if="IsLogError" :BillId="billId"></ExportError>
    <InvoiceHistory v-if="isInvoiceHistory" :BillId="billId" ></InvoiceHistory>
    <InvoicePaySlip v-if="isPaySlip" :BillId="billId"></InvoicePaySlip>
    <SendBill v-if="isSendBill" :BillId="billId"></SendBill>
    <InvoiceCancelModal v-if="isInvoiceCancel" :Billid="billId"></InvoiceCancelModal>

  </section>
  <section v-else>
    <InvoiceTemplate v-if="isTemplate" :details="templatedata" @backtoInvoiceList="backtoInvoiceList"
      :hitFrom="'Invoice'" />
  </section>
</template>
<script>
const defaultAppURL = process.env.VUE_APP_DEFAULT_APP_URL;
import DataService from "../../../services/DataService";
import BillListingSchema from "./BillListingSchema";
import InvoiceTemplate from "../ClientDetails/InvoiceTemplate.vue";
import Attachment from './Attachment.vue';
import ExportError from './ExportError.vue';
import InvoiceHistory from './InvoiceHistory.vue';
import InvoicePaySlip from './InvoicePaySlip.vue';
import SendBill from '../ClientDetails/SendBill.vue';
import InvoiceCancelModal from "./InvoiceCancelModal.vue";
export default {
  components: { InvoiceTemplate ,Attachment, ExportError,InvoiceHistory, InvoicePaySlip, SendBill, InvoiceCancelModal},
  props: {
    UserId: {
      type: Number,
    },
    ClientData: {
      type: Object,
    },
  },
  data() {
    return {
      isSaving: false,
      isInvoiceCancel: false,
      Headers: BillListingSchema.billColumnSchema,
      StatusListForListing: [
        {
          STATUS: "Active",
          STATUS_ID: 1001,
          STATUS_COLOR: "#ffffff",
          STATUS_BG_COLOR: "#28a745",
        },
        {
          STATUS: "Inactive",
          STATUS_ID: 1002,
          STATUS_COLOR: "#ffffff",
          STATUS_BG_COLOR: "#d72435",
        },
      ],
      listheaderbuttons: BillListingSchema.buttons,
      listActions: BillListingSchema.listActions,
      topSearchFields: BillListingSchema.topsearchSchema,
      list: [],
      TotalRecords: 0,
      pageSize: 10,
      pageNum: 1,
      sortBy: "",
      searchCondition: "",
      isPaymentHistory: false,
      GroupStart: 1,
      GroupEnd: 3,
      CurrentPage: 1,
      TotalPages: 0,
      sortExp: "",
      totalPayments: 0,
      BillListing: [],
      paymentHistoryData: [],
      MainInvocieNetAmount: 0,
      paymentList: [],
      LegendArray: [
        {
          class: "legend-Progress colorboxsquare",
          text: this.$t('BillGenerated'),
        },
        {
          class: "legend-ovrdue colorboxsquare",
          text: this.$t('Credit') + " " + this.$t('Note'),
        },
      ],
      noRecords: false,
      isInvoice: true,
      isTemplate: false,
      templatedata: {},

      /*Payment Details Dated:9-Jan-2024 */
      isPayment: false,
      paymentDetailsData: [],

      paymentTermsData: [],
      amount: 0,
      amountToPay: 0, // The amount to be paid

      Notes: "", // Notes for the payment
      paymentDate: "", // Payment date

      amountRecieved: 0,
      isAttachment:false,
      billId:'',
      IsLogError:false,
      isInvoiceHistory:false,
      isPaySlip: false,
      BillDate: '',
      BillMode: 'Cash',
      RefNo: 0,
      isSendBill: false,
      IsAlreadyPaid: false,
      BillId:0,
      additionalClass:'',
      isLoading:false,
    };
  },
  created: async function () {
    var vm = this;
    vm.topSearchFields[1].onChange = vm.getClickEvent;
    await vm.FetchData();
    vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    vm.BillDate= vm.$options.filters.formatDate(new Date());
  },
  computed: {
    totalPaid() {
      return this.paymentList.reduce((sum, payment) => sum + payment.Amount, 0);
    },
    outstandingAmount() {
      return this.MainInvocieNetAmount - this.totalPaid;
    },
  },
  methods: {
    formatAmount(amount) {
      return amount.toFixed(2);
    },
    Close: function () {
      var vm = this;
      vm.isPaymentHistory = false;
    },
    ClosePayment: function () {
      var vm = this;
      vm.isPayment = false;
      vm.paymentDetailsData=[];
      vm.paymentTermsData=[];
    },

    formatDateM(dateTimeString) {
       if (!dateTimeString) return "";
      // Parse the input ISO date string into a JavaScript Date object
      const date = new Date(dateTimeString);

      // Define month abbreviations
      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];

      // Get the month abbreviation, day, and year
      const formattedMonth = months[date.getMonth()]; // 0-based index
      const formattedDay = date.getDate(); // Day without leading zero
      const formattedYear = date.getFullYear(); // Full year

      // Return the formatted date
      return `${formattedMonth}-${formattedDay}-${formattedYear}`;
    },

    formatDateMonth(dateTimeString) {
      if (!dateTimeString) return "";
      //debugger;
      const [datePart, timePart] = dateTimeString.split(" "); // ['06-11-2024', '06:00:09']
      const [day, month, year] = datePart.split("-"); // ['06', '11', '2024']
      const date = new Date(`${year}-${month}-${day}T00:00:00`);
      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      const formattedDay = String(date.getDate()).padStart(2, "0");
      const formattedMonth = months[date.getMonth()];
      const formattedYear = date.getFullYear();
      return `${formattedMonth}-${formattedDay}-${formattedYear}`;
    },
