    MarkAsSent: async function name(params) {
      //debugger;
      var vm = this;
      var selectedBillIds = [];
      vm.BillListing.forEach((item) => {
        if (item.isSelected == true) {
          selectedBillIds.push(item.BILL_ID);
        }
      });

      //at least one invoice must selected
      if (selectedBillIds.length === 0) {
        vm.ShowAlert(vm.$t("AtLeastOneInvoice"), "failure");
        return;
      }

      // Convert the array of selectedBillIds to a comma-separated string
      var billIds = selectedBillIds.join(",");

      vm.confirmR(vm.$t("ConfirmRelease"),
        true,
        false,
        async function (result) {
          if (result === undefined) {
            try {
              var url = `billIds=${billIds}&statusCode=CLBSND`;
              await DataService.UpdateClientInvoiceStatus(url).then(
                (response) => {
                  //debugger;
                  if (response.data[0].ERROR_CODE == 1) {
                    vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success");
                    vm.FetchData();
                  } else {
                    vm.ShowAlert(
                      vm.$t("ErrorWhileProcessingRequest"),
                      "failure"
                    );
                  }
                }
              );
            } catch (error) {}
          }
        }
      );
    },
    DownloadBillList: async function () {
      var vm = this;
      var url = `pageSize=${vm.TotalRecords}&pageNum=${vm.pageNum}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&clientId=${vm.UserId}`;
      await DataService.GetInvoiceListing(url).then(function (response) {
        if (response.data != "" && response.data != null) {
          if (response.data.length > 0) {
            vm.ConvertJSONToCSV(response.data, "Invoice", true);
          }
        }
      });
