    ConvertJSONToCSV: function (data, filename, showHeader) {
      const excludeColumns = [
        "BILL_ID",
        "BILL_RECORD_AMOUNT",
        "COMPANY_ID",
        "COMPANY_NAME",
        "IS_AUTO_SEND",
        "ISO_CODE",
        "IS_RECURRING",
        "MILESTONE_ID",
        "RECURRING_FREQUENCY",
        "RECURRING_STATUS",
        "ROWNUMBER",
        "STATUS_CODE",
        "TEMPLATE_ID",
        "TIME_ROUNDING",
        "TOTAL_RECORDS",
      ];
      const headerMap = {
        BILL_NUMBER: "Invoice No",
        CLIENT_NAME: "Contract",
        PROJECT_NAME: "Project Name",
        PARENT_INVOICE: "Parent Invoice",
        CREATED_AT: "Issue Date",
        BILL_DUE_DATE: "Due Date",
        PAYMENT_CREATED_AT: "Payment Date",
        BILL_NET_TOTAL: "Amount (PKR)",
        STATUS_NAME: " Status",
      };
      if (data && Array.isArray(data) && data.length > 0) {
        const headers = Object.keys(data[0]);
        const filteredHeaders = headers.filter(
          (header) => !excludeColumns.includes(header)
        );
        const mappedHeaders = filteredHeaders.map(
          (header) => headerMap[header] || header
        );
        const rows = data.map((row) => {
          return mappedHeaders
            .map((mappedHeader) => {
              const originalHeader =
                filteredHeaders[mappedHeaders.indexOf(mappedHeader)];
              let cellValue =
                row[originalHeader] !== undefined &&
                row[originalHeader] !== null
                  ? String(row[originalHeader])
                  : "";
              if (
                cellValue.includes(",") ||
                cellValue.includes("\n") ||
                cellValue.includes('"')
              ) {
                cellValue = `"${cellValue.replace(/"/g, '""')}"`;
              }
              return cellValue;
            })
            .join(",");
        });
        const csvContent = (showHeader ? [mappedHeaders.join(",")] : [])
          .concat(rows)
          .join("\n");
        const blob = new Blob([csvContent], {
          type: "text/csv;charset=utf-8;",
        });
        const link = document.createElement("a");
        link.setAttribute("href", URL.createObjectURL(blob));
        link.setAttribute("download", `${filename}.csv`);
        link.click();
      } else {
        console.log("No valid data to convert.");
      }
    },
    BackToList: function () {
      var vm = this;
      window.location.href = vm.$router.resolve({ name: "ClientListing" }).href;
    },
    getStatusClassList: function (status) {
        var returnClass = '';
        switch (status) {
            case 'INVPAID':
                returnClass = 'btn btn-danger py-0 d-block font-14';
                break;
            case 'CLBSND':
                returnClass = 'btn btn-primary py-0 d-block font-14';
                break;
            case 'CLBPAD':
                returnClass = 'btn btn-info py-0 d-block font-14';
                break;
            case 'CLBPPAD':
                returnClass = 'btn btn-primary py-0 d-block font-14';
                break;
            case 'CLBGEN':
                returnClass = 'btn btn-warning py-0 d-block font-14';
                break;
            case 'RLSTOXERO':
                returnClass = 'btn btn-secondary py-0 d-block font-14';
                break;
            case 'CLBUPDATED':
                returnClass = 'btn btn-success py-0 d-block font-14';
                break;
            case 'RLSTOWORKDAY': 
                returnClass = 'btn btn-dark py-0 d-block font-14'; 
                break;
            case 'CLBERROR':  
                returnClass = 'btn btn-danger py-0 d-block font-14';
                break;
            default:
                returnClass = '';
        }
        return returnClass;
    },
    sortdata: function (sortBy) {
      this.sortBy = sortBy;
      var sortExp = "ASC";
      if (this.sortExp == "ASC") {
        sortExp = "DESC";
      }
      this.sortExp = sortExp;
      this.sortBy = sortBy;
      this.sortExp = sortExp;
      this.pageNum = 1;
      this.GroupStart = this.pageNum;
      this.GroupEnd = this.pageNum + 2;
      this.FetchData();
    },
    backtoInvoiceList() {
      this.isInvoice = true;
      this.isTemplate = false;
      this.FetchData();
    },
    shouldDisplay() {
      const billNetTotal = this.paymentDetailsData[0]?.BILL_NET_TOTAL ?? 0;
      const paid = this.paymentDetailsData[0]?.PAID ?? 0;
     

      return !(billNetTotal - paid <= 0);
    },
    handleAttachment(item){
      var vm = this;
      vm.isAttachment = true;
      vm.billId=item.BILL_ID
    },
    History(item){
      var vm=this;
      vm.isInvoiceHistory=true
      vm.billId=item.BILL_ID
    },
    async clickPaySlip(item){
      var vm=this;
      vm.isPaySlip=true;
      vm.billId=item.BILL_ID;
    }
  },
};
</script>
