<template>
    <section>
        <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle d-flex">
                            <h4 class="modal-title">{{ $t('AdditionalInfo') }}</h4>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <tg-list ModuleName="Billing" :HeaderText="$t('CDCCode')" :isShowHeaderText="true" SubModuleCode="Billing" IdentityColumn="CDC_ID" :listType="['List']" :showCheckBox="true"
                                :ListData="CdcCodeListing" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsShowAction="false"
                                :RenderRowActionMethod="() => { }" :ListDataCallBackFunction="FetchRecords"
                                :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                v-on:SortdataButtonClick="sortdata">
                                <template v-slot:slotdata="{ data }">
                                    <template v-if="data.column.COLUMN_NAME == 'Edit'">
                                        <span>
                                            <div class="text-center single-action">
                                                <a href="javascript:;" title="Edit" v-on:click="EditCdcCode(data.row)" class="EditCdc" id="EditCdc">
                                                    <i class="fa fa-pencil text-success action-icon"></i>
                                                </a>
                                            </div>
                                        </span>
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                                        <span v-if="data.row.STATUS_NAME == 'Active'">
                                            <div class="text-center single-action">
                                                <a class="UpdateCDCCodeStatus" href="javascript:;"  v-on:click="ChangeCdcStatus(data.row)">
                                                    <i class="fa fa-check text-info action-icon"></i>
                                                </a>
                                            </div>
                                        </span>
                                        <span v-else-if="data.row.STATUS_NAME == 'Inactive'">
                                            <div class="text-center single-action">
                                                <a class="UpdateCDCCodeStatus" href="javascript:;"  v-on:click="ChangeCdcStatus(data.row)">
                                                    <i class="fa fa-check text-secondary action-icon"></i>
                                                </a>
                                            </div>
                                        </span>
                                    </template>
                                </template>
                            </tg-list>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ModalAddCdcCode v-if="isAddCdcCode" :cdcId="cdcId" :clientId="clientId" :utilityId="utilityId" :ClientAdditionalId="ClientAdditionalId"></ModalAddCdcCode>
    </section>
</template>
<script>

import DataServices from "@/services/DataService";
import ModalAddCdcCode from "../../../components/Modals/modalAddCdcCode.vue";
export default {
    components: {
        ModalAddCdcCode
    },
    props: {
        utilityId: {
            type: Number,
            required: false
        },
        clientId: {
            type: Number,
            required: false
        },
        ClientAdditionalId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            isLoading:false,
            CurrentPage: 1,
            TotalPages: 0,
            Headers: [{
                "COLUMN_NAME": "CDC_CODE",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "CDC Code",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "SBA_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "SBA Id",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "STATE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "State",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ABBREVIATIONS",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Abbreviations",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "ISAH",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is After Hour",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "ISWIRECENTER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is Wire Center",
                "DISPLAY_ORDER": 6
            },
            {
                "COLUMN_NAME": "DIVISION_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Invoice Grouping",
                "DISPLAY_ORDER": 7
            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 8,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 9,
                settings: {
                    isInSlot: true
                }
            }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            CdcCodeListing: [],
            listheaderbuttons:[
                {
                    title: 'Add New',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'Add',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                },
                {
                    title: 'Delete',
                    iconClass: 'fa fa-trash',
                    callbackfunction: 'Delete',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    isEnabledOnSelectedRow: true
                },
            ],
            isAddCdcCode:false,
            cdcId:0

        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
    },
    methods: {
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Add":
                    this.AddNew();
                    break;
                case "Delete":
                    this.DeleteCdc();
                    break;
            }
        },
        AddNew: function(){
            var vm = this;
            vm.cdcId=0;
            vm.isAddCdcCode =true;
        },
        EditCdcCode : function(item){
            var vm = this;
            vm.cdcId=item.CDC_ID;
            vm.isAddCdcCode =true;
        },
        DeleteCdc: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("cdc_code")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "Billing",
                        refCode: "TALYGEN_BE_CDC_CODE_INFO"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {                           
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('cdc_code')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('cdc_code')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('cdc_code')]), "failure", true, vm.$t('Alert'));
            }
        },
        ChangeCdcStatus: function(item){
            var ths=this;
            ths.isLoading=true;
            let statusId=0;
            if(item.STATUS_NAME == 'Active'){
                statusId = 1002;
            }
            else{
                statusId = 1001;
            }
            var url=`id=${item.CDC_ID}&statusId=${statusId}`;
            DataServices.UpdateCDCCodeStatus(url).then((response)=>{
                if(response.data.msg=='success'){
                    ths.ShowAlert(ths.$t('CDCCodeStatusUpdated'),"success", true, ths.$t("Alert"));
                    ths.FetchRecords();
                }else{
                    ths.ShowAlert(ths.$t('ErrorWhileProcessingRequest'),"failure", true, ths.$t("Alert"));
                }
                ths.isLoading=false;
            })
        },
        FetchRecords: async function () {
            var vueObj = this;
            var url = `clientId=${vueObj.clientId}&clientAdditionalId=${vueObj.ClientAdditionalId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await DataServices.GetCdcCodeListing(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                   
                    if (response.data.cdcCodeListing != null) {
                        vueObj.CdcCodeListing = JSON.parse(response.data.cdcCodeListing);
                        vueObj.TotalRecords = JSON.parse(response.data.cdcCodeListing)[0].TOTAL;
                        vueObj.TotalPages = Math.ceil(vueObj.TOTAL / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.CdcCodeListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.CdcCodeListing = [];
                }               
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        Close:function(){
            this.$parent.isCdcCodeList =false;
            this.$parent.$parent.LoadAdditionalInfo();
        }

    },
}
</script>