<template>
    <section>
        <div>
            <div style="margin-top: -7px">
            <tg-list :IsShowAction="false" ModuleName="Company" SubModuleCode="" :LegendArray="[]" :isShowHeaderText="false" :HeaderText="$t('Contract')"
                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                IdentityColumn="CONTRACT_ID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData"
                :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'CONTRACT_END_DATE'">
                        <td class="text-center single-action tourguide_a">
                            {{ $options.filters.formatDate(data.row.CONTRACT_END_DATE) }}
                        </td>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'CONTRACT_NAME'">
                        <div v-if="data.row.STATUS_NAME != 'Expired'">
                            <a href="javascript:;" @click="handleClick(data.row)" :title="data.row[data.column.COLUMN_NAME]"  data-toggle-tooltip="tooltip" data-original-title="Click here to Update Account">{{ data.row[data.column.COLUMN_NAME] }}</a>
                    </div>
                    <div v-else> {{ data.row[data.column.COLUMN_NAME] }}</div>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                        <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'status_id')"
                        >
                        {{ data.row.STATUS_NAME }}
                    </span>
                </template>
                    </template>
                </template>
            </tg-list>
        </div>
        </div>

    </section>
</template>
<script>
import Schema from './ContractListingSchema'
import DataService from "../../../services/DataService";
import moment from 'moment'
export default { 
    components: {  },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            TotalRecords: 0,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            ProjectName:'',
            taskId: '',
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            ListingData: [],
            searchCondition: '',
            listheaderbuttons: Schema.buttons,
            QuotesListingSchema: false,
            leftSearchFields: [{
                fieldName: this.$t("CONTRACTNAME"),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
        };
    },
    created: async function() {   
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.user_Id = this.GetUserInfo.ID;
        this.FetchData();

    },
    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("Contract");

            divElement[0].appendChild(spanElement);
        }
    },
    methods: {
        handleClick(item) {
            var vm = this;
            var parentId= vm.$route.params.parentId;
            var clientId=vm.$route.params.id;
            vm.$router.push({path: `/CRM/ManageContract/${item.CONTRACT_ID}/CRM/CRM_CONTRACTS?clientId=${clientId}&parentId=${parentId}`});
            //window.location.href = `/CRM/ManageContract/${item.CONTRACT_ID}/CRM/CRM_CONTRACTS?clientId=${vm.UserId}&parentId=${parentId}`;
        },
        FetchData:async function(){
            var vm = this;
            var params = `projectId=0&productIds=&resellerIds=&contractId=&contractName=${vm.searchCondition}&companyName=&billtoClientName=&clientIds=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            await DataService.GetContractListing(params).then(function (response) {           
                if (response.data) {
                    if (response.data.length > 0) {
                        vm.ListingData = response.data;
                        vm.noRecord = false;
                        vm.TotalRecords=response.data[0].TOTALRECORDS;

                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ListingData = [];
                    vm.TotalRecords = 0
                }
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined' ) {
                if(value.searchCondition !=''){
                    let str = value.searchConditionJson;
                    let result = str.split('=')[1];
                    this.searchCondition = result;
                }else{
                    this.searchCondition = "";
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'Expired':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Cancelled':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 bg-yellow text-center text-white d-block';
                    break;
                case 'History':
                    returnClass = 'px-2 bg-dark text-center text-white d-block';
                    break;
                case 'Undefined':
                    returnClass = 'px-2 bg-info text-center text-white d-block';
                    break;
                default:
                    returnClass = 'px-2 bg-yellow text-center text-white d-block';
            }
            return returnClass;
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    
                    case 'Edit':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
              
                case 'openDelete':
                    this.DeleteMultiple(item);
                    break;  
                   
            }
        },
        DeleteMultiple: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ApplicationId")), function () {
                    let url = `ids=${val}&tableName=talygen_contracts&primaryKey=contract_id`;
                    DataService.Delete(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.listheaderbuttons[0].isdisabled = true;
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "openAssociate":
                vm.AddContract();
                    break;
                case "BackToList":
                    vm.BackToList();
                    break;
                case "openDelete":
                    vm.DeleteMultiple();
                        break;
            }
        },
        AddContract: function (item) {
            var vm =this;
            var parentId= vm.$route.params.parentId;
            var clientId=vm.$route.params.id;
            //window.location.href = `/CRM/ManageContract/0/CRM/CRM_CONTRACTS?clientId=${vm.UserId}&parentId=${parentId}`;
            vm.$router.push({path: `/CRM/ManageContract/0/CRM/CRM_CONTRACTS?clientId=${clientId}&parentId=${parentId}`});
        },
        BackToList: function () {
            var vm=this;
            vm.$router.push({ name: 'ClientListing' });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
};
</script>