<template>
  <section>
    <div class="ticketsmallfix theme-primary partition p-actions-expand " style="overflow: visible;"
      v-if="isPlanSelection">
      <span class="p-name text-white">{{ $t('AssociatePlan') }}</span>
      <span class="p-actions float-right" @click="backtoPlanList()">
        <a title="Back to list" href="javascript:;" class="p-action-btn text-white ">
          <em class="fa fa fa-angle-double-left"></em> <br>
          <span>{{ $t('BacktoList') }}</span>
        </a>
      </span>
    </div>

    <div class="btn-group mt-4 mb-2 mb-md-0 d-block text-center" v-if="isPlanSelection">
            <button
              type="button" style="border-radius: 30px 0px 0 30px !important; padding: 10px 15px 10px 25px;"
              class="btn btn-space font-16"
              :class="{ 'btn-info': planPaymentType === 'onetime', 'btn-outline-info': planPaymentType !== 'onetime' }"
              @click="settype('onetime')"
            >
              {{ $t('OneTime') }}
            </button>
            <button
              type="button" style="border-radius: 0px 30px 30px 0px !important; padding: 10px 25px 10px 15px;" class="btn btn-space font-16"
              :class="{ 'btn-info': planPaymentType === 'recurring', 'btn-outline-info': planPaymentType !== 'recurring' }"
              @click="settype('recurring')">

              {{ $t('Recurring') }}
              
            </button>
        </div>
    <!-- <div v-if="isShowbillingCycle" class="bg-light col-12 mt-2 float-left pb-2">
      <div class="btn-group mt-3 mb-2 mb-md-0 d-block text-center" v-if="isPlanSelection">
        <button type="button" style="border-radius: 30px 0px 0 30px !important; padding: 0px 15px 0px 25px;"
          class="btn btn-space font-16"
          :class="{ 'btn-success': selectedBilling === 'annually', 'btn-outline-success': selectedBilling !== 'annually' }"
          @click="setBilling('annually')">
          Annually
        </button>
        <button type="button" style="border-radius: 0px 30px 30px 0px !important; padding: 0px 25px 0px 15px;"
          class="btn btn-space font-16"
          :class="{ 'btn-success': selectedBilling === 'monthly', 'btn-outline-success': selectedBilling !== 'monthly' }"
          @click="setBilling('monthly')">
          Monthly
        </button>
      </div>

      <span class="showDiscountType d-block text-center font-16" v-if="isPlanSelection">

        <span style="color: #000;">Annual Billing</span>
        <span :class="[selectedBilling == 'annually' ? 'font-weight-600 text-success' : 'text-success']"> Save <span
            id="showExtraDiscount">an extra</span> {{ discount }}%</span>
      </span>
    </div> -->

    <div class="ticketsmallfix theme-primary partition p-actions-expand " style="overflow: visible;"
      v-if="isPlanDetails">
      <span class="p-name text-white">{{ $t('PlanDetails') }}</span>
      <span class="p-actions float-right" @click="backtoPlanList()">
        <a title="Back to list" href="javascript:;" class="p-action-btn text-white ">
          <em class="fa fa fa-angle-double-left"></em> <br>
          <span>{{ $t('BacktoList') }}</span>
        </a>
      </span>
    </div>

    <div class="stepwizard my-3 d-flex justify-content-center" v-if="isAddInvoice">
      <div class="stepwizard-row setup-panel d-flex">
        <!-- Plan Step -->
        <div class="stepwizard-step col-xs-3 d-flex align-items-center" @click="switchToPlan">
          <div :class="['step-1', 'rounded-circle', 'text-white', isPlanSelection ? 'bg-primary' : 'bg-secondary']"> 1
          </div>
          <p class="mb-0">
            <small class="pl-3 font-18 font-weight-600">{{ $t('Plan') }}</small>
          </p>
        </div>
        <div class="col-xs-3 stepwizard-d d-flex align-items-center">
          <span></span>
        </div>

        <!-- Checkout Step -->
        <div class="stepwizard-step col-xs-3 d-flex align-items-center">
          <div :class="['step-2 ', 'rounded-circle', 'text-white', isPlanDetails ? 'bg-primary  ml-2' : 'bg-secondary  ml-2']"> 2
          </div>
          <p class="mb-0">
            <small class="pl-3 font-18">{{ $t('Checkout') }}</small>
          </p>
        </div>
      </div>
    </div>


    <Plan v-if="isPlanSelection" :ClientData="ClientData" :UserId="UserId" :discount="discount" :paymentType="planPaymentType"
       @closeCurrentPage="handlePlanDetails" />
    <PlanDetails v-if="isPlanDetails" :plandetails="plandetails" :UserId="UserId" 
      @backtoPlanList="backtoPlanList" />

  </section>
</template>

<script>
import Plan from "../ClientDetails/AssociatePlan.vue";
import PlanDetails from "../ClientDetails/PlanDetails.vue";
import DataService from '../../../services/DataService';
export default {
  components: { Plan, PlanDetails },
  props: ["ClientData", "UserId"],
  data() {
    return {
      isPlanSelection: true,
      isPlanDetails: false,
      plandetails: {},
      //selectedBilling: "monthly",
      discount: 0,
      isAddInvoice: false,
      planPaymentType:'onetime',
      isShowbillingCycle:false
    };
  },
  async created() {
    //await this.GetsubscriptionDiscount();
  },
  methods: {
    handlePlanDetails(details) {
      this.isPlanSelection = false;
      this.isPlanDetails = true;
      this.isAddInvoice = true;
      this.plandetails = details;
    },
    setBilling(type) {
      this.selectedBilling = type;
      this.GetsubscriptionDiscount()
    },
    settype(type){
        if(type=='recurring'){
          this.selectedBilling= "annually",
          this.isShowbillingCycle=true
        }
        else{
          this.selectedBilling= type,
          this.isShowbillingCycle=false
        }
        this.planPaymentType=type
      },
    handleInvoiceTemplate(data) {
      console.log("Received invoice template data:", data);
      this.template(data);
    },
    template(data) {
      this.isPlanDetails = false;
      this.isTemplate = true;
      this.templateDetails = data;
      console.log(" invoice templateDetails data:", data);
    },
    async GetsubscriptionDiscount() {
      const params = `ruleType=${4}&externalAppCode=TALYGEN`;
      const response = await DataService.GetAllSubscriptionDiscount(params);
      if (response.data[0].DiscountAmount) {
        this.discount = response.data[0].DiscountAmount
      }
      else {
        this.discount = 0
      }
    },
    switchToPlan() {
      this.isPlanSelection = true;
      this.isPlanDetails = false;
      this.isAddInvoice = false;

    },
    backtoPlanList() {
      this.$emit('backToPlanListing');
    }

  },
};
</script>

