<template>
    <section v-if=" IsSubscriptionBilling">
        <tg-list :IsShowAction="true" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="client_plan_mapping_id"
            :ListData="RateScheduledeListing" :HeaderData="Headers" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
            :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod" :isShowHeaderText="true"
            v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('RateSchedule')"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $options.filters.formatDate(data.row.created_at) }}
                    </span>
                </template>
            </template>
        </tg-list>
        <ServiceRatesList v-if="isServices" :Id="clientplanmappingid" :PlanId="PlanId" :ClientId="ClientId"></ServiceRatesList>
        <ViewHistory v-if="isViewHistory" :Id="clientplanmappingid"></ViewHistory>
    </section>

     <section v-else>

            <SubscriptionRateSchedule :ClientId="ClientId"></SubscriptionRateSchedule>

        </section> 
</template>
<script>
import DataService from "../../../services/DataService";
import ServiceRatesList from "../../Client/ClientDetails/ServiceRatesList.vue"
import ViewHistory from "../../Client/ClientDetails/ViewHistoryRateSchedule.vue"
import SubscriptionRateSchedule from "./SubscriptionRateSchedule.vue";
export default {
    components: { ServiceRatesList, ViewHistory, SubscriptionRateSchedule },
    props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            RateScheduledeListing: [],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            noRecords: false,
            Headers: [

                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "payment_type",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Payment Type",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "PlanType",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Type",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "IsProrate",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Pro Rate",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created At",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "modified_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified At",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "modified_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified By",
                    DISPLAY_ORDER: 3,
                },
            ],
            listActions:
                [
                    {
                        title: 'Edit',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-pencil text-info action-icon',
                        callbackfunction: 'edit',
                        isDisabled: false,
                        additionalSpan: ''
                    },
                    {
                        title: 'View History',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-eye',
                        isDisabled: false,
                        callbackfunction: 'viewHistory',
                        additionalSpan: ''
                    },
                ],
            isServices: false,
            clientplanmappingid: 0,
            PlanId:0,
            isViewHistory: false,
            IsSubscriptionBilling: false,
            IsUtilityBilling: false
        }
    },
    async created() {
        var vm = this;

        await vm.CheckExtraFeatures();
        await vm.FetchData();
    },
    methods: {
        async CheckExtraFeatures() {
            debugger
            var vm = this;
            const params = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    const features = JSON.parse(response.data);
                    features.forEach(feature => {
                        if (feature.FEATURE_CODE === 'SUBSCRIPTION_BILLING') {
                            vm.IsSubscriptionBilling = true;
                        }
                        if (feature.FEATURE_CODE === 'UTILITY_BILLING') {
                            vm.IsUtilityBilling = true;
                        }
                    });
                }
            } catch (error) {

                console.log("Error checking extra features:", error);
            }
        },

        async FetchData() {
            var vueObj = this;
            vueObj.PlanListing = [];
            var url = `clientId=${this.ClientId}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&search=${vueObj.searchCondition}`;
            await DataService.GetRateScheduleListForSubscription(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'created_at') {
                        column.settings = {
                            isInSlot: true
                        };
                    }
                });
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.RateScheduledeListing = response.data.result.data;

                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.RateScheduledeListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.PlanListing = [];
                }
            })
        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'edit':
                    vm.edit(item);
                    break;
                case 'viewHistory':
                    vm.viewHistory(item);
                    break;

            }
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                        case 'Edit':
                        action.isVisible = true;
                        action.isDisabled = row.payment_type == "One Time" && row.client_plan_mapping_id>0 ? true : false;
                        break
                    case 'View History':
                        action.isVisible = true;
                        break;


                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        edit(data) {

            this.clientplanmappingid = data.client_plan_mapping_id
            this.isServices = true
        },
        async viewHistory(data) {
            this.clientplanmappingid = data.client_plan_mapping_id;
            this.isViewHistory = true
        },
        close() {
            this.isServices = false
        },
        commonHeaderButtonClick(event) { },
        sortdata(sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
    }
}
</script>