<template>
    <section>
        <div class="my-popups gd_mainbase">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('Notes') }}
                            </h5>
                            <span class="float-right pt-2">
                                <a href="javascript:;" data-toggle-tooltip="tooltip" class="text-white" @click="AddNewNote"><em class="fa fa-plus mr-2"></em>{{$t('AddNote')}} </a>
                            </span>
                            <button class="close" v-on:click="onCancel"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <Form ref="LeadForm" v-if="IsNote">
                                <dynamic-form lang="en" ref="refCommunication" :mandatorytext="MandatoryString"
                                    :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                    <template v-slot:tgslot-Content="{ data }">
                                        <ckeditor :editor="editor" @ready="onReady" v-model="editorData" ref="textarea"
                                            :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                                        <span class="invalid-feedback">{{ data.error }}</span>
                                    </template>
                                </dynamic-form>
                            </Form>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-4 p-0 float-left" v-if="!IsNote">
                                <div class="col-lg-12 mb-3 p-0" v-for="(item, index1) in NoteData" :key="index1">
                                    <div class="customer-headersmall notespanel" style="border-bottom: 1px solid #eee;">
                                        <div class="profileicon"> <em class="fa fa-user"></em></div>
                                        <div class="customer-info w-100">

                                            <div class="d-flex justify-content-between">
                                                <h1 class="mb-0">{{ item.notes_created_by }}</h1>
                                                <span class="sender-department">
                                                    <small class="text-secondary font-14 mr-4 font-weight-medium"
                                                        v-bind:title="item.note_date | formatDateTime">
                                                        {{ item.note_date | formatDateTime }}
                                                    </small>


                                                    <span class="ml-4"><a href="javascript:;"
                                                            v-if="(LogginUserId == item.created_by)" class="float-right"
                                                            id="DeleteNote" v-on:click="deleteNote(item.crm_note_id)">
                                                            <em class="fa fa-trash-o text-danger font-18"
                                                                aria-hidden="true"></em>
                                                        </a>
                                                        <a href="javascript:;" v-if="(LogginUserId == item.created_by)"
                                                            class="float-right mr-3" v-on:click="editNote(item)">
                                                            <em class="fa fa-pencil text-success font-18"
                                                                aria-hidden="true"></em>
                                                        </a>
                                                    </span>
                                                </span>

                                            </div>

                                            <div class="customer-meta w-90">
                                                <p class="mb-0" v-html="item.crm_notes"></p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div v-if="NoteData.length == 0" class="text-center text-danger border p-2 norecord">
                                    {{ $t("NorecordfoundText") }}
                                </div> 
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {   
    data() {
        return {
            MandatoryString: this.$t('MandatoryString'),
            isLoading: false,
            editor: this.CLASSIC_EDITOR,
            LogginUserId: null,
            editorData: '',
            crmNoteId: null,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [{
                        astype: "RadioField",
                        label: "Visibility",
                        name: "Visibility ",
                        value: "1",
                        placeholder: "",
                        disabled: false,
                        config: {
                            options: [
                                { value: "1", name: "Public" },
                                { value: "0", name: "Private" }
                            ]
                        },
                        validationRules: "required"
                    },
                    {
                        astype: "SlotField",
                        label: this.$t('NotesDescription'),
                        name: "Content",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            required: true,
                            max: 1000,
                        },
                        config: {
                            options: [],
                        },
                    }]
                }
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.AddNewNote,
            }],
            NoteData: [],
            IsNote:false
        }
    },
    created: function () {
        var ths = this;
        ths.LogginUserId = ths.GetUserInfo.ID;
        ths.NoteListing();      
    },
    methods: {
        NoteListing() {
            var ths = this;
            ths.NoteData = [];
            var ApiURL = `id=${this.$route.params.id}&refId=${this.$route.params.id}&moduleName=Company&subModuleCode=Client`;
            DataService.NoteListing(ApiURL).then(response => {
                if (response.data.data.length > 0) {
                    response.data.data.forEach(item => {
                        item.note_date = new Date(item.note_date);
                        ths.NoteData.push(item);
                    })

                } else {
                    ths.NoteData = [];
                }
            })
        },     
        onSubmit() {
            var vm = this;
            var data = {
                sourceId: vm.$route.params.id,
                moduleName: 'Company',
                subModuleCode: 'Client',
                note: vm.editorData,
                isPrivate: vm.FormSchema[0].Data[0].value,
                crmNotesId: vm.crmNoteId
            };
            var objectPostString = JSON.stringify(data);
            DataService.SaveNoteData(objectPostString).then(function (response) {
                if (response.data[0].Status == "Success") {
                    if (vm.crmNoteId != null) {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Note')]), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Note')]), "success", true, vm.$t("Alert"));
                    }
                    vm.NoteListing();
                    vm.editorData='';
                    vm.IsNote = false;  

                }
            })
        },
        onCancel() {
            var ths = this;
            ths.$emit('CloseNotePopup');
        },
        editNote(itm){
            var ths=this;
            ths.IsNote = true;
            ths.crmNoteId = itm.crm_note_id;
            ths.FormSchema[0].Data[0].value = itm.IS_PRIVATE ? "1" : "0";
            ths.editorData = itm.crm_notes;                   
        },
        deleteNote: function (id) {
            var vm = this;
            if(id != null){
                 vm.confirmR(this.$t("ConfirmDelete"), this.$t("Delete"), this.$t("Note"), function () {
                    var postJSON = {
                        ids: id,
                        moduleName: "Client",
                        subModuleCode: "Company",
                        refCode: 'DELETE_VIEW_CRM_NOTE'
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonDelete(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {                      
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t("NOTE")]), "success", true, vm.$t("Alert"));
                            vm.NoteListing();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t("NOTE")]), "failurfe", true, vm.$t("Alert"));
                        }
                    });
                })
            }   
        },
        AddNewNote(){
            var ths=this;
            ths.IsNote = !ths.IsNote;
        }
    }
}
</script>
<style scoped>
.gd_mainbase .profileicon {   
    margin-top: -12px;
}
</style>