<template>
    <div class="SubscriptionListing">
        <section v-show="isPlanListing">
            <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Billing" :listType="['List']"
                :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="plan_mapping_id"
                :ListData="PlanListing" :HeaderData="Headers" :callbackfunction="FetchData"
                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
                :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod" :isShowHeaderText="true"
                v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('Plans')"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'status_name'">
                        <span v-bind:class="getStatusClassList(data.row.status_name, 'status_name')">
                            {{ data.row.status_name }}
                        </span>
                    </template>

                    <template v-if="data.column.COLUMN_NAME == 'plan_image_url'">
                        <div class="form-control p-0 border-0" v-if="data.row.plan_image_url != null" alt="" title="">
                            <img :src='data.row.plan_image_url' class="border p-2" height="50" width="100" title="" />
                        </div>
                        <div class="form-control p-0 border-0" v-if="data.row.plan_image_url == null">
                            <img src='/Content/images/imagenotavailable.jpg' alt="No Image Available" height="50"
                                width="100" title="" />
                        </div>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'plan_name'">
                        <a href="javascript:;" v-on:click="OpenPlanDetail(data.row)"><i aria-hidden="true"></i>{{
                            data.row.plan_name }}
                        </a>
                        <!-- <span v-else>{{ data.row.plan_name  }}</span> -->
                    </template>

                    <template v-if="data.column.COLUMN_NAME == 'BasePriceTotal'">
                        <span>
                            {{ $n(data.row.BasePriceTotal, "currency", $getLocaleByCurrency(CurrencyCode)) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'one_time_charge'">
                        <span>
                            {{ $n(data.row.one_time_charge, "currency", $getLocaleByCurrency(CurrencyCode)) }}
                        </span>
                    </template>

                    <template v-if="data.column.COLUMN_NAME == 'total_units'">
                        <span>
                            {{ $n(data.row.total_units) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'effective_date'">
                        <span>
                            {{ $options.filters.formatDate(data.row.effective_date) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'expiration_date'">
                        <span>
                            {{ $options.filters.formatDate(data.row.expiration_date) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'created_at'">
                        <span>
                            {{ $options.filters.formatDate(data.row.created_at) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'next_invoice_date'">
                        <span>
                            {{ $options.filters.formatDate(data.row.next_invoice_date) }}
                        </span>
                    </template>
                </template>
            </tg-list>
        </section>
        <PlanContainer v-if="isPlanContainer" :ClientData="ClientData" :UserId="UserId"
            @backToPlanListing="backToPlanListing">
        </PlanContainer>

        <PlanDetails v-if="isPlanDetails" :plandetails="plandetails" :UserId="UserId" :flag="flag"
            :Discounts="selectedDiscounts" :Taxes="selectedTaxes" />
        <PlanHistory v-if="isPlanHistory" :PlanMappingId="PlanMappingId" @close="isPlanHistory = false" />
        <modalViewPlan v-if="isViewPlan" :PlanId="PlanId" :ClientId="UserId" :PlanMappingId="PlanMappingId"
            @close="isViewPlan = false" />
        <viewReceipt v-if="isInvoice" :templateDetails="templateDetails" @close="isInvoice = false" />
        <AddComment v-if="IsComment" @ChangeStatus="ChangeStatus" :MappingId="MappingId" :type="type"
            :planNamesStr="planNamesStr"></AddComment>
        <PlanRequest v-if="isPlanRequest" :PlanMappingId="PlanMappingId" @close="isPlanRequest = false" />

        <ModalViewPlanDetails v-if="isViewPlanDetailsUtilityCase" :PlanId="PlanId" :ClientId="UserId"
            :PlanMappingId="PlanMappingId" @close="isViewPlanDetailsUtilityCase = false"></ModalViewPlanDetails>

    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import SubScriptionListingSchema from './SubScriptionListingSchema';
import PlanContainer from "../ClientDetails/PlanContainer.vue";
import PlanDetails from "../ClientDetails/PlanDetails.vue";
import PlanHistory from './PlanHistory.vue';
import modalViewPlan from '../../../components/Modals/modalViewPlan.vue';
import viewReceipt from '../../../components/Modals/viewReceipt.vue';
import AddComment from '../../Client/Subscription/AddCommentModal.vue';
import PlanRequest from './PlanRequest.vue';
import ModalViewPlanDetails from '../../../components/Modals/ModalViewPlanDetails.vue';

export default ({
    name: 'PlanListing',
    components: { PlanContainer, PlanDetails, PlanHistory, modalViewPlan, viewReceipt, AddComment, PlanRequest, ModalViewPlanDetails },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            Headers: [
                {
                    COLUMN_NAME: "plan_image_url",
                    DATA_TYPE: "image",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Image",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Name",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "plan_type",
                    DATA_TYPE: "image",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Type",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "billing_frequency",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Frequency",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "plan_description",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Description",
                    DISPLAY_ORDER: 5,
                },
                 {
                    COLUMN_NAME: "IsProrated",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Pro Rate",
                    DISPLAY_ORDER: 5,
                },
                // {
                //     COLUMN_NAME: "base_price",
                //     DATA_TYPE: "text",
                //     SORTABLE: true,
                //     VISIBLE: true,
                //     DISPLAY_NAME: "Amount",
                //     DISPLAY_ORDER: 5,
                // },
                // {
                //     COLUMN_NAME: "discount_value",
                //     DATA_TYPE: "text",
                //     SORTABLE: true,
                //     VISIBLE: true,
                //     DISPLAY_NAME: "Discount",
                //     DISPLAY_ORDER: 6,
                // },

                {
                    COLUMN_NAME: "total_units",
                    DATA_TYPE: "int",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Total Number Of Quantity",
                    DISPLAY_ORDER: 6,
                },
                {
                    COLUMN_NAME: "BasePriceTotal",
                    DATA_TYPE: "currency",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Charges",
                    DISPLAY_ORDER: 7,
                },
                {
                    COLUMN_NAME: "one_time_charge",
                    DATA_TYPE: "currency",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "One Time Charges",
                    DISPLAY_ORDER: 7,
                },
                {
                    COLUMN_NAME: "status_name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 8,

                },
                {
                    COLUMN_NAME: "effective_date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Effective Date",
                    DISPLAY_ORDER: 9,

                },
                {
                    COLUMN_NAME: "next_invoice_date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Next Invoice Date",
                    DISPLAY_ORDER: 10,

                }
                // ,
                // {
                //     COLUMN_NAME: "expiration_date",
                //     DATA_TYPE: "text",
                //     SORTABLE: false,
                //     VISIBLE: true,
                //     DISPLAY_NAME: "Expiration Date",
                //     DISPLAY_ORDER: 8,

                // }
            ],
            // SubHeaders: [{
            //     "COLUMN_NAME": "Service Name",
            //     "DATA_TYPE": "string",
            //     "SORTABLE": true,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": "Client Name",
            //     "DISPLAY_ORDER": 1,
            //     settings: {
            //         isInSlot: true,
            //     }
            // },
            // {
            //     "COLUMN_NAME": "Unit Rate",
            //     "DATA_TYPE": "string",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": "#Projects",
            //     "DISPLAY_ORDER": 2,
            //     settings: {
            //         isInSlot: true,
            //     }
            // },
            // {
            //     "COLUMN_NAME": "Quantity",
            //     "DATA_TYPE": "string",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": "Phone",
            //     "DISPLAY_ORDER": 3,
            // },
            // {
            //     "COLUMN_NAME": "Total Rate",
            //     "DATA_TYPE": "string",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": "Email",
            //     "DISPLAY_ORDER": 4,
            // }

            // ],

            listheaderbuttons: SubScriptionListingSchema.buttons,
            listActions:
                [
                    {
                        title: 'Edit',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-pencil text-info action-icon',
                        callbackfunction: 'Edit',
                        isDisabled: false,
                        additionalSpan: ''
                    },
                    {
                        title: 'View Details',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-eye',
                        isDisabled: false,
                        callbackfunction: 'viewDetails',
                        additionalSpan: ''
                    },
                    {
                        title: 'View History',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-eye',
                        isDisabled: false,
                        callbackfunction: 'viewHistory',
                        additionalSpan: ''
                    },
                    {
                        title: 'Manage discount',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-pencil text-success action-icon',
                        isDisabled: false,
                        callbackfunction: 'manageDiscount',
                        additionalSpan: ''
                    },
                    {
                        title: 'De-Activated',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-times text-danger action-icon',
                        isDisabled: false,
                        callbackfunction: 'deAssociate',
                        additionalSpan: ''
                    },
                    {
                        title: 'Print Invoice',
                        href: 'javascript:void(0)',
                        icon: 'fas fa-file-invoice text-info action-icon',
                        // isDisabled: true,
                        callbackfunction: 'printInvoice',
                        additionalSpan: ''
                    },
                    {
                        title: 'Generate Invoice',
                        href: 'javascript:void(0)',
                        icon: 'fas fa-file-invoice text-info action-icon',
                        isDisabled: false,
                        callbackfunction: 'Generatenvoice',
                        additionalSpan: ''
                    },
                    {
                        title: 'Receipt',
                        href: 'javascript:void(0)',
                        icon: 'fas fa-file-invoice text-info action-icon',
                        isDisabled: false,
                        callbackfunction: 'Receipt',
                        additionalSpan: ''
                    },
                    {
                        title: 'Frequency Change History',
                        href: 'javascript:void(0)',
                        icon: 'fas fa-exchange-alt text-primary action-icon',
                        isDisabled: false,
                        callbackfunction: 'PlanChangeRequest',
                        additionalSpan: ''
                    }
                ],
            list: [],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            GroupStart: 1,
            GroupEnd: 3,
            PlanId: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            PlanListing: [],
            noRecords: false,
            isPlanContainer: false,
            isPlanListing: true,
            isPlanDetails: false,
            PlanList: [],
            plandetails: [],
            isPlanHistory: false,
            PlanMappingId: 0,
            isViewPlan: false,
            selectedDiscounts: [],
            selectedTaxes: [],
            templateDetails: {},
            isInvoice: false,
            IsComment: false,
            MappingId: '',
            type: 'Client',
            CurrencyCode: 'PKR',
            planNamesStr: '',
            CompanyId: '',
            SelectedDate: '',
            isPlanRequest: false,
            IsSubscriptionBilling: false,
            IsUtilityBilling: false,
            FeatureCode: null,
            isViewPlanDetailsUtilityCase: false
        }
    },
    async created() {
        var vm = this;
        vm.CompanyId = vm.GetUserInfo.CompanyId
        await vm.CheckExtraFeatures();
        await vm.FetchData();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
        async CheckExtraFeatures() {
            var vm = this;
            const params = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    const features = JSON.parse(response.data);
                    features.forEach(feature => {
                        if (feature.FEATURE_CODE === 'SUBSCRIPTION_BILLING') {
                            vm.IsSubscriptionBilling = true;
                            vm.FeatureCode = feature.FEATURE_CODE;
                        }
                        if (feature.FEATURE_CODE === 'UTILITY_BILLING') {
                            vm.IsUtilityBilling = true;
                            vm.FeatureCode = feature.FEATURE_CODE;
                            vm.listheaderbuttons[0].isvisible= false;
                        }
                    });
                }
            } catch (error) {

                console.log("Error checking extra features:", error);
            }
        },

        async FetchData() {
            var vueObj = this;
            vueObj.PlanListing = [];
            var url = `clientId=${this.UserId}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&search=${vueObj.searchCondition}`;
            await DataService.GetPlanMappingListForSubscriptionPlan(url).then(function (response) {

                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.PlanListing = response.data.result.data;
                        vueObj.PlanListing.forEach((element) => {
                            console.log(element)
                            if (element.frequency == 'One Time') {
                                element.next_invoice_date = ''
                            }
                            if (element.plan_code == 'UTILITY_BILLING') {
                                vueObj.Headers.forEach((column) => {
                                    column.settings = null;

                                    if (column.COLUMN_NAME == 'total_units') {
                                        column.VISIBLE = false
                                    }
                                    if (column.COLUMN_NAME == 'BasePriceTotal') {
                                        column.VISIBLE = false
                                    }
                                    if (column.COLUMN_NAME == 'IsProrated') {
                                        column.VISIBLE = false
                                    }
                                    if (column.COLUMN_NAME == 'one_time_charge') {
                                        column.VISIBLE = false
                                    }
                                });
                                vueObj.listheaderbuttons[0].isvisible = false;
                            } else {
                                //vueObj.listheaderbuttons[1].isvisible=false;
                            }
                        });
                        for (var row in vueObj.PlanListing) {
                            vueObj.PlanListing[row].isCheckBoxDisabled = false;
                            if (vueObj.PlanListing[row].status_name == 'Inactive' || vueObj.PlanListing[row].status_name == 'Cancelled') {
                                vueObj.PlanListing[row].isCheckBoxDisabled = true;
                            } else {
                                vueObj.PlanListing[row].isCheckBoxDisabled = false;
                            }
                        }
                        vueObj.Headers.forEach((column) => {
                            column.settings = null;
                            // if (column.COLUMN_NAME == 'expiration_date') {
                            //     column.settings = {
                            //         isInSlot: true
                            //     }
                            // }
                            if (column.COLUMN_NAME == 'effective_date') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'status_name') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'plan_name') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'BasePriceTotal') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'one_time_charge') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'total_units') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'plan_image_url') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'next_invoice_date') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                        });
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.PlanListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.PlanListing = [];
                }
            })
        },
        getStatusClassList: function (status) {
            let returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success'; // Green
                    break;
                case 'Pending Activation':
                    returnClass = 'px-2 text-center text-dark d-block bg-warning'; // Yellow
                    break;
                case 'Past Due':
                    returnClass = 'px-2 text-center bg-warning text-white d-block'; // Orange (custom)
                    break;
                case 'Paused':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'Grace-Period':
                    returnClass = 'px-2 text-center text-white d-block bg-secondary'; // Teal (custom)
                    break;
                case 'Suspended':
                    returnClass = 'px-2 bg-primary text-center text-white d-block'; // Custom orange
                    break;
                case 'Cancelled':
                    returnClass = 'px-2 text-center text-white d-block bg-danger'; // Red
                    break;
                case 'Churned':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block'; // Dark Gray
                    break;
                default:
                    returnClass = 'px-2 text-center text-white d-block bg-danger'; // Fallback gray
            }

            return returnClass;
        },
        OpenPlanDetail(item) {
            var vm = this;
            vm.viewDetails(item);
            // if (code == "SUBSCRIPTION_BILLING") {
            //     window.open(vm.$router.resolve({ name: 'ManageSubscriptionPlanDetail', params: { id: vm.PlanId } }).href, '_blank');
            // } else {
            //     window.open(vm.$router.resolve({ name: 'ManagePlan', params: { id: vm.PlanId } }).href, '_blank');
            // }
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AssociatePlan":
                    this.AssociatePlan();
                    break;
                case "ChangeStatus":
                    this.ChangeStatusModal();
                    break;
                case "BackToList":
                    this.BackToList();
                    break;
            }
        },
        ChangeStatusModal: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.MappingId = val;
                var ids = vm.MappingId.split(',').map(id => parseInt(id.trim()));

                vm.planNamesStr = vm.PlanListing
                    .filter(plan => ids.includes(plan.CLIENT_PLAN_MAPPING_ID))
                    .map(plan => plan.plan_name)
                    .join(', ');

                vm.IsComment = true;
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        renderActions(row) {
            var vm = this;
            let rtnActionArray = [];
            vm.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        if (row.status_id == 1002 || row.frequency == 'One Time' || row.status_name == 'Cancelled') {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = true;
                        } else {
                            action.isVisible = true;
                        }

                        break;
                    case 'View Details':
                        if (row.status_id == 1002) {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = false;
                        } else {
                            action.isVisible = false;
                        }

                        break;
                    case 'View History':
                        if (row.status_id == 1002) {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = true;
                        } else {
                            action.isVisible = true;
                        }

                        break;
                    case 'Manage discount':
                        if (row.status_id == 1002 || row.status_name == 'Cancelled') {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = false;
                        } else {
                            action.isVisible = false;
                        }

                        break;
                    case 'De-Activated':
                        if (row.status_id == 1002 || row.status_name == 'Cancelled') {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = true;
                        } else {
                            action.isVisible = true;
                        }

                        break;
                    case 'Print Invoice':
                        action.isVisible = false;
                        break;
                    case 'Generate Invoice':
                        if (vm.FeatureCode !== 'UTILITY_BILLING') {
                            if (row.status_id == 1001 && (vm.CompanyId == 158446 || vm.CompanyId == 122872 || vm.CompanyId == 158770 || vm.CompanyId == 125897)) {
                                action.isVisible = true;
                                action.isDisabled = false;
                            } else {
                                action.isVisible = false;
                            }
                        }
                        else {
                            action.isVisible = true;
                            action.isDisabled = false;
                        }

                        break;
                    case 'Receipt':
                        if (row.status_id == 1002) {
                            action.isVisible = false;
                            action.isDisabled = true
                            action.isVisible = true;
                        }
                        else {
                            action.isVisible = true;
                        }
                        break;
                    case 'Frequency Change History':
                        if (vm.FeatureCode !== 'UTILITY_BILLING') {
                            if (row.status_id == 1002) {
                                action.isVisible = false;
                                action.isDisabled = true
                                action.isVisible = true;
                            }
                            else {
                                action.isVisible = true;
                                action.isDisabled = false
                            }
                        }
                        else {
                            action.isVisible = false;
                            action.isDisabled = true
                        }

                        break;

                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'Edit':
                    vm.edit(item);
                    break;
                case 'viewDetails':
                    vm.viewDetails(item);
                    break;
                case 'viewHistory':
                    vm.viewHistory(item);
                    break;
                case 'Manage discount':
                    vm.manageDiscount(item);
                    break;
                case 'deAssociate':
                    vm.deAssociate(item);
                    break;
                case 'printInvoice':
                    vm.printInvoice(item);
                    break;
                case 'Generatenvoice':
                    vm.Generatenvoice(item);
                    break;
                case 'Receipt':
                    vm.GetReceipt(item);
                    break;
                case 'PlanChangeRequest':
                    vm.PlanChangeRequest(item);
                    break;

            }
        },

        BackToList() {
            this.$router.push('/Client/Index');
        },
        AssociatePlan() {
            this.isPlanContainer = true;
            this.isPlanListing = false;
        },
        sortdata(sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        async edit(data) {
            this.isPlanContainer = false;
            this.isPlanListing = false;
            this.isPlanDetails = true;
            this.flag = 'isEditingFromPlanListing';
            await this.LoadPlansMappingSubscription(data.plan_mapping_id);
        },
        async deAssociate(item) {
            var vm = this;
            vm.confirmR(vm.$t('DeAsscoiateKey'), true, false, async function (result) {
                if (result === undefined) {
                    try {
                        var obj = {
                            planIds: item.id,
                            clientId: vm.UserId,
                            type: "DEASSOCIATE",
                            PlanName: item.plan_name
                        };
                        var postString = JSON.stringify(obj);
                        const response = await DataService.DeAsscoiate(postString);
                        if (response.status === 200) {
                            await vm.FetchData();
                            //alert('Plan de-associated successfully');
                        }
                    } catch (error) {
                        console.error('Failed to de-associate plans:', error);
                        alert('Error occurred while de-associating plans');
                    }
                }
            });
        },

        async LoadPlansMappingSubscription(plan_mapping_id) {
            try {
                this.selectedDiscounts = [];
                this.selectedTaxes = [];
                var params = `plan_mapping_id=${plan_mapping_id}`
                const response = await DataService.LoadPlansMappingSubscriptionById(params);
                if (response.data.status === "Success" && response.data.code === "1") {
                    const plans = response.data.result;
                    plans.forEach(plan => {
                        plan.services = plan.services
                            ? JSON.parse(`[${plan.services}]`)
                            : [];

                        plan.planrate = plan.planrate ? JSON.parse(`[${plan.planrate}]`) : [];
                        // Apply discount only for annual billing
                        // if (plan.selectedBilling === 'annually') {  //&& this.discount > 0
                        //     const discountValue = plan.BaseRate * plan.discount / 100;
                        //     plan.discountedRate = (plan.BaseRate - discountValue).toFixed(2);
                        //     plan.finalRate = (plan.discountedRate * 12).toFixed(2);

                        // } else {
                        //     plan.discountedRate = plan.BaseRate.toFixed(2);
                        //     plan.finalRate = plan.BaseRate.toFixed(2)
                        // }
                        plan.timebased_discount = plan.discount;
                        plan.BaseRate = plan.BaseRate.toFixed(2);
                        //display Saved Amount
                        // const discountedValue = plan.BaseRate * plan.discount / 100;
                        // plan.DisplaydiscountedRate = (plan.BaseRate - discountedValue).toFixed(2);
                        // //---------------------------------------------
                        // plan.savedAmount = this.calculateSavedAmount(plan.BaseRate, plan.DisplaydiscountedRate);

                    });





                    // this.PlanList = plans;
                    this.plandetails = plans;
                    // this.plandetails = this.PlanList.filter(x => x.client_id == this.UserId) //840735
                    // this.plandetails = this.PlanList.filter(x => x.plan_mapping_id == plan_mapping_id) //840735
                    console.log("this.plandetails", this.plandetails);
                    if (plans[0]?.tax_discount != null) {
                        plans[0]?.tax_discount.map(element => {
                            let obj = {}
                            if (element.type == 'Discounts') {
                                obj = {
                                    ADJUSTMENT_ID: element.ADJUSTMENT_ID,
                                    ADJUSTMENT_NAME: element.ADJUSTMENT_NAME,
                                    ADJUSTMENT_PERCENTAGE: element.ADJUSTMENT_PERCENTAGE,
                                    DISCOUNT_TYPE: element.DISCOUNT_TYPE
                                }

                                this.selectedDiscounts.push(obj)
                            }
                            else if (element.type == 'Tax') {
                                obj = {
                                    ADJUSTMENT_ID: element.ADJUSTMENT_ID,
                                    ADJUSTMENT_NAME: element.ADJUSTMENT_NAME,
                                    ADJUSTMENT_PERCENTAGE: element.ADJUSTMENT_PERCENTAGE,
                                    DISCOUNT_TYPE: element.DISCOUNT_TYPE
                                }
                                this.selectedTaxes.push(obj)
                            }

                        })
                    }


                }
            } catch (error) {
                console.error("Error loading plans:", error);
            }
        },

        calculateSavedAmount(baserate, discountRate) {
            let discountprice = baserate - discountRate;
            discountprice = discountprice * 12;
            return discountprice.toFixed(2);
        },

        backToPlanListing() {
            this.isPlanContainer = false;
            this.isPlanListing = true;
            this.FetchData();
        },
        async viewDetails(item) {
            var vm = this;
            vm.PlanId = item.id;
            vm.PlanMappingId = item.plan_mapping_id;
            if (vm.FeatureCode !== 'UTILITY_BILLING') {
                vm.isViewPlan = true;
            }
            else {
                vm.isViewPlanDetailsUtilityCase = true;
            }

        },
