            var vm = this;
            vm.PlanMappingId = item.plan_mapping_id;
            vm.isPlanHistory = true;
        },
        async PlanChangeRequest(item) {
            var vm = this;
            vm.PlanMappingId = item.plan_mapping_id;
            vm.isPlanRequest = true;
        },
