        async Generatenvoice(item) {
            
            var vm = this;
            this.selectedBilling = item.billing_frequency;
            const params = {
                json: '',
                clientId: this.UserId,
                type: 'Generate',
                billingCycle: this.selectedBilling,
                isForAddOrEdit: 'no',
                SelectedDate: vm.$options.filters.formatDateYear(item.next_invoice_date),
                PlanMappingId: item.CLIENT_PLAN_MAPPING_ID,
                Type: vm.FeatureCode

            };
            try {
                const response = await DataService.GenrateInvoice(params);
                if (response.status == 200) {
                    const result = response.data.result.data;
                    if (response.data.status == 'Success') {
                        if (result && result[0].status_id == 1) {
                            vm.ShowAlert(result[0].status_message, 'success', false, this.$t('Alert'));
                            vm.FetchData();
                            vm.LoadPlansMappingSubscription();
                        }
                        else {
                            this.ShowAlert(result[0].status_message, 'failure', true, this.$t('Alert'));
                        }
                    } else {
                        this.ShowAlert(this.$t('SomethingWentWrong'), 'failure', true, this.$t('Alert'));
                    }
                }
            } catch (error) {
                console.error('Error fetching invoice template:', error);
            }

        },



        async GetReceipt(item) {
            var vm = this;
            let param = `clientPlanMappingId=${item.CLIENT_PLAN_MAPPING_ID}`
            const response = await DataService.getSubscribePlanDetails(param);
            const invoicedata = response.data.result.data;
            if (invoicedata != undefined && invoicedata[0].status_id == 1) {
                vm.isInvoice = true;
                vm.templateDetails = invoicedata;
            }
            else {

                vm.isInvoice = false;
                vm.ShowAlert(invoicedata[0].status_message, 'failure', true, this.$t('Alert'));
            }

        }

