<template>
  <div class="col-md-12 p-0">
   
   <div class="container-fluid py-3 border " id="divAddressfields" >

    <div class="row">
      <div class="col-md-6">
        <dynamic-form
          lang="en"
          ref="AdditionalOwner"
          :buttons="false"
          :schema="FormSchema"
        ></dynamic-form>
      </div>
    </div>
    <div class="row flex-row-reverse mt-2">
      <div class="col-lg-6 text-right">
        <a
          href="javascript:;"
          @click="onSubmit"
          class="btn btn-success mr-2"
          title=""
          id="addAddress"
          data-toggle-tooltip="tooltip"
          data-original-title="Save"
        >
          <i class="fa fa-save mr-2"></i>
          <span id="btnAddressText"> {{ $t("Save") }}</span></a
        >

        <a
          href="javascript:;"
          class="btn btn-danger"
          title=""
          id="CancelAddress"
          @click="onCancel"
          data-toggle-tooltip="tooltip"
          data-original-title="Cancel"
          ><i class="fa fa-times mr-2"></i>{{ $t("Cancel") }}</a
        >
      </div>
   

    <div class="col-lg-6">
      <div class="py-2">
        <span class="mandatory">{{ $t("Mandatory") }}</span>
      </div>
    </div>
     </div>
</div>

     <tg-list ModuleName="User" SubModuleCode="BILLING"
                                    IdentityColumn="ADDITIONAL_OWNER_ID" :listType="['List']" :ListData="AdditionalOwnerListing"
                                    :HeaderData="Headers" 
                                    :TotalRecords="TotalRecords"
                                    :IsShowAction="false" 
                                    :RenderRowActionMethod="renderActions"
                                    v-on:ActionButtonClick="actionButtonClick"
                                    :showCheckBox="true"
                                    :ListDataCallBackFunction="FetchData"
                                    :NorecordfoundText="$t('NoRecordfound')"
                                     v-on:PagerButtonClick="pagerMethod" 
                                      v-on:SortdataButtonClick="sortdata" v-on:HeaderButtonClick="commonHeaderButtonClick">
                                    <template v-slot:slotdata="{ data }">
                                       
                                           <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
      
            <span  v-bind:class="getStatusClassList(data.row.STATUS_NAME)"> 
           {{ data.row.STATUS_NAME }}
            </span>
            
           
         
        </template>

                                        <template v-if="data.column.COLUMN_NAME == 'Edit'">
                                            <td class="text-center single-action">
                                                        <div class="cstm-drop-btn">
                                                            <span><a @click="EditAddiotionOwner(data.row)"><em
                                                                        class="text-success" aria-hidden="true"><i
                                                                            class="fa fa-edit   action-icon"></i></em></a></span>
                                                        </div>
                                                    </td>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'Delete'">
                                          <td class="text-center single-action">
                                            <div class="cstm-drop-btn">
                                                            <span><a  @click="DeleteAddiotionOwner(data.row.ADDITIONAL_OWNER_ID)" ><em
                                                                        class="text-primary" aria-hidden="true"><i
                                                                            class="fa fa-trash text-danger action-icon"></i></em></a></span>
                                                        </div>
                                                         </td>
                                        </template>
                                      
                                    </template>
        </tg-list> 
 
 
  </div>
</template>

<script>
const defaultAppURL = process.env.VUE_APP_DEFAULT_APP_URL;
import DataService from "../../../services/DataService";

export default {
  props: {
    ClientId: {
      type: Number,
      required: true,
    },
  },

  data() {
    return {
      config: {
        isShowDrawButton: true,
        isShowSearchBox: true,
      },
      isUpdateForm: false,
      AdditionalOwnerId: 0,

      isdeleteButtonVisible: false,

      allSelected: false,

      FormSchema: [
        {
          layoutType: "double",
          Data: [
            {
              astype: "TextField",
              label: this.$t("name"),
              name: "Name:",
              value: "",
              placeholder: "",
              validationRules: {
                max: 200,
                required: true,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("User"),
              mode: "tag",
              name: "User",
              value: "",
              config: {
                options: [],
              },
              placeholder: this.$t("Select User"),
              validationRules: { required: true, max: 100 },
            },
          ],
        },
      ],
         StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
      TotalRecords: 0,
      PageSize: 10,
      PageNumber: 1,
      SortExp: '',
      SortBy: '',
      AdditionalOwnerListing: [],
      GroupStart: 1,
      GroupEnd: 3,
      CurrentPage: 1,
      TotalPages: 0,
      Headers: [
        {
          COLUMN_NAME: "NAME",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "name",
          DISPLAY_ORDER: 1,
        },
        
        {
          COLUMN_NAME: "USER_NAME",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "UserName",
          DISPLAY_ORDER: 2,
        },
        {
          COLUMN_NAME: "CREATED_BY",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "CreatedBy",
          DISPLAY_ORDER: 3,
        },
        {
          COLUMN_NAME: "CREATED_AT",
          DATA_TYPE: "string",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "CreatedDate",
          DISPLAY_ORDER: 4,
        },
        {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            },
         {
          COLUMN_NAME: "Edit",
          DATA_TYPE: "string",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: "Edit",
          DISPLAY_ORDER: 6,
          settings: {    isInSlot: true,  },
        },
         {
          COLUMN_NAME: "Delete",
          DATA_TYPE: "string",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: "Delete",
          DISPLAY_ORDER: 7,
          settings: {    isInSlot: true,  },
        },
      ],
      
      
    };
  },

  created() {
    //debugger;
    var vm = this;
    vm.GetUser();
    vm.FetchData();
   
  },

  methods: {

    
    
    GetUser() {
      var vm = this;
      var url = `id=&moduleName=&type=USERS&search=&refId=`;
      //debugger;
      DataService.GetDDLData(url).then((response) => {
        //debugger;
        console.log("response : ", response.data.DATA);
        if (response.data.DATA != undefined || response.data.DATA != null) {
          if (response.data.DATA.length > 0) {
            response.data.DATA.forEach((item) => {
              let obj = {
                value: item.value,
                name: item.name,
              };
              this.FormSchema[0].Data[1].config.options.push(obj);
            });
            vm.$refs.AdditionalOwner.UpdateKeyValue();
          }
        }
      });
    },

    onSubmit() {

      var vm = this;
      vm.FormSchema[0].Data[0].validationRules.required = true;
      vm.FormSchema[0].Data[1].validationRules.required = true;
      vm.$refs.AdditionalOwner.validateForm
        .validate()
        .then(async (valid) => {
          if (valid) {
            vm.isLoading = true;
            let obj = {
              AdditionalOwnerId: vm.AdditionalOwnerId,
              Name: vm.FormSchema[0].Data[0].value,

              SUserId: parseInt(vm.FormSchema[0].Data[1].value),
              ClientId: vm.ClientId,
              RefSource: "",
              RefSourceId: "",
            };

            DataService.SaveAdditionalOwner(obj).then((response) => {
                debugger;
              vm.isLoading = false;
              if (response.data != null) {
                if (response.data.Result == "0") {
                  vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('AdditionalOwner')]),"success", true, vm.$t("Alert"));
                  // vm.ShowAlert(vm.$t("Additional Owner successfully added"),"success",vm.$t("Alert"));
                  vm.clearForm();
                  // let obj = {
                  //     location_nw: "0,0"
                  // }
                  // vm.latLngValues = obj;
                  vm.FormSchema[0].Data[0].validationRules.required = false;
                  vm.FormSchema[0].Data[1].validationRules.required = false;
                  
                  vm.FetchData();
                } else if (response.data.Result == "1") {
                  vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('AdditionalOwner')]),"success", true, vm.$t("Alert"));
                  // vm.ShowAlert(vm.$t("Additional Owner has been successfully updated."),"success",vm.$t("Alert"));
                  vm.clearForm();                 
                  vm.FormSchema[0].Data[0].validationRules.required = false;
                  vm.FormSchema[0].Data[1].validationRules.required = false;
                  vm.isUpdateForm = false;
                  vm.FetchData();
                  //vm.getAdditionalAddressList();
                }
                 else if (response.data.Result === "-2") {
                     this.ShowAlert(this.$t('RecordAlreadyExists'), "danger", false, this.$t("Alert"));
                  }
                  else {
                     vm.ShowAlert(vm.$t("SomethingWentWrong"), "danger", vm.$t("Alert"));
                  }
              } else {
                 vm.ShowAlert(vm.$t("SomethingWentWrong"),"success",vm.$t("Alert"));
              }
            });
          }
        })
        .catch((error) => {
          console.error("Error validating form:", error);
        });
    },
    EditAddiotionOwner(item) {
            var vm = this;
            
            vm.isUpdateForm = true;
            vm.FormSchema[0].Data[0].value = item.NAME
            vm.FormSchema[0].Data[1].value = item.USER_ID
            
            
            vm.AdditionalOwnerId = item.ADDITIONAL_OWNER_ID;
            
        },
    FetchData: async function () {
      //debugger;
      var vueObj = this;
      //vueObj.AdditionalOwnerListing = [];
      //debugger;
    
      var url = `clientId=${vueObj.ClientId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}`;
      await DataService.GetGetAdditionalOwnerListing(url).then(function (
        response
      ) {
        debugger;
        if (response.data != "" && response.data != null) {
          if (response.data.length > 0) {
            debugger;
            vueObj.AdditionalOwnerListing = response.data;

            vueObj.TotalRecords = response.data[0].TOTAL_RECORDS;
            vueObj.TotalPages = Math.ceil(
              vueObj.TotalRecords / vueObj.PageSize
            );
            vueObj.CurrentPage = vueObj.PageNumber;
            vueObj.noRecord = false;
            
          } else {
            vueObj.noRecord = true;
            vueObj.TotalRecords = 0;
            vueObj.AdditionalOwnerListing = [];
          }
        } else {
          vueObj.noRecord = true;
          vueObj.TotalRecords = 0;
          vueObj.AdditionalOwnerListing = [];
        }
      });
    },
    DeleteAddiotionOwner: function (addiotionOwnerId) {            
            var vm = this;
            var val = addiotionOwnerId;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("AddiotionOwner")), function () {
                    var postJSON = {
                        Ids: val,
                        ClientId: vm.ClientId,

                        
                    };
                    
                    DataService.DeleteAdditionalOwner(postJSON).then(response => {
                        if (response.data.Result == 0) {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('AddiotionOwner')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('AddiotionOwner')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },    
clearForm() {
            var vm = this;
           
            vm.FormSchema[0].Data[0].value = ''
            vm.FormSchema[0].Data[1].value = ''
             vm.AdditionalOwnerId=0
          
        },
    onCancel() {
        var vm = this;
        vm.clearForm();
      // Handle cancel action
    },
    pagerMethod: function (value) {
      //debugger;
      this.PageSize = value.PageSize;
      this.PageNumber = value.PageNumber;
      this.FetchData();
    },
    getStatusClassList(status) {
      debugger;
      let returnClass = '';
      switch (status) {
        case 'Inactive':
          returnClass = 'btn btn-danger py-0 d-block font-14';
          break;
        case 'Active':
          returnClass = 'btn btn-success py-0 d-block font-14';
          break;
        default:
          returnClass = '';
      }
      return returnClass;
    },
    sortdata: function (sortBy) {
      //debugger;
      this.SortBy = sortBy;
      var sortExp = "ASC";
      if (this.SortExp == "ASC") {
        sortExp = "DESC";
      }
      else
      {
         sortExp = "ASC";
      }
      this.SortExp = sortExp;
      this.SortBy = sortBy;
      this.SortExp = sortExp;
      this.PageNumber = 1;
      this.GroupStart = this.PageNumber;
      this.GroupEnd = this.PageNumber + 2;
      this.FetchData();
    },
    renderActions: function (row) {},
    actionButtonClick(callbackfunction, item, index, event, isChild) {},
    commonHeaderButtonClick: function (event) {},
  },
};
</script>

<style>
.switch.dynamic-form {
  width: 74px;
}
.slider.round.toggle-no .slider-no {
  padding-left: 6px;
}
</style>