<template>
    <div class="my-popups">
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle d-flex">
                        <h4 class="modal-title">{{ $t('AdditionalInfo') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="AdditionalInfoForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-Description="{ data }">
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        clientId: {
            type: Number,
            required: false
        },
        ClientAdditionalId: {
            type: Number,
            required: false
        },
        cdcId:{
            type:Number,
            required:false
        },
        utilityId:{
            type: Number,
            required: false
        }
    },
    data() {
        return {
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Division'),
                            name: "Division",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "CustomSelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [],
                                onChange: this.onChangeState
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('sba_id'),
                            name: "sba_id",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        }
                    ],
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('cdc_code'),
                            name: "cdc_code",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('IsWireCenter'),
                            name: "IsWireCenter",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ],
                                onChange: this.onChangeWireCenter
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Abbreviations'),
                            name: "Abbreviations",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": false, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: this.$t('IsCountyDependent'),
                            name: "IsCountyDependent",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ],
                                onChange: this.onChangeCountyDependent
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "CustomSelectField",
                            label: this.$t('County'),
                            name: "County",
                            mode: "tag",
                            value: "",
                            disabled: true,
                            config: {
                                options: []
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('IsAH'),
                            name: "IsAH",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: { "required": false },
                        }
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: this.$t('UseDistrictMapping'),
                            name: "UseDistrictMapping",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: { "required": false },
                        }
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            stateList:[],
            sbaList:[],
            countyList:[],
            countydData:[]
        }
    },
    created: async function () {
        var vm = this;
        await vm.getDropdownList('CDC_DIVISION_LIST_BY_CLIENT_ID');
        await vm.getDropdownList('SBA_LIST_BY_CLIENT_ID');
        await vm.getDropdownList('UTILITY_STATE_LIST_BY_COUNTRY_ID');
        if(vm.cdcId != 0){
            await vm.GetSelectedWorkCode();
        }
      
    },
    methods: {
        GetDDLCounty: function(isoCode){
            var vm = this;
            var stateListByIsoCode = vm.stateList.find(x => x.value == isoCode);
            var stateId = stateListByIsoCode ? stateListByIsoCode.first_value : null;
            let Url = `id=&moduleName=Billing&type=UTILITY_COUNTY_BY_STATE_ID&search=&refId=${stateId}`
            DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                   if(response.data.DATA.length > 0){
                    vm.countyList = response.data.DATA;
                   }
                    vm.$refs.AdditionalInfoForm.UpdateKeyValue();
                }
            })
        },
        onChangeState :function(x,y,z){
            var vm = this;
            var isoCode = z.value;
            var stateListByIsoCode = vm.stateList.find(x => x.value == isoCode);
            var stateId = stateListByIsoCode ? stateListByIsoCode.first_value : null;
            let Url = `id=&moduleName=Billing&type=UTILITY_COUNTY_BY_STATE_ID&search=&refId=${stateId}`
             DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                   if(response.data.DATA.length > 0){
                    vm.$refs.AdditionalInfoForm.updateProperty("County", "value",  '');
                    vm.countyList = response.data.DATA;
                    vm.$refs.AdditionalInfoForm.updateProperty("County", "config.options",  response.data.DATA);
                   }
                    vm.$refs.AdditionalInfoForm.UpdateKeyValue();
                }

            })
        },
        GetSelectedWorkCode: async function () {
            var vm = this;
            var url = `CDC_Code_Id=${vm.cdcId}`;
            await DataServices.GetCdcCodeListById(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                   if(response.data.length > 0){
                    vm.GetDDLCounty(response.data[0].state);
                    vm.$refs.AdditionalInfoForm.updateProperty("Division", "value",  response.data[0].division_id);
                    vm.$refs.AdditionalInfoForm.updateProperty("State", "value",  response.data[0].state);
                    vm.$refs.AdditionalInfoForm.updateProperty("sba_id", "value",  response.data[0].be_sba_id);
                    vm.$refs.AdditionalInfoForm.updateProperty("cdc_code", "value",  response.data[0].cdc_code);
                    vm.$refs.AdditionalInfoForm.updateProperty("IsWireCenter", "value",  response.data[0].iswirecenter);
                    vm.$refs.AdditionalInfoForm.updateProperty("Abbreviations", "value",  response.data[0].abbreviations);
                    vm.$refs.AdditionalInfoForm.updateProperty("IsCountyDependent", "value",  response.data[0].is_county_dependent);
                    vm.$refs.AdditionalInfoForm.updateProperty("IsAH", "value",  response.data[0].isah);
                    vm.$refs.AdditionalInfoForm.updateProperty("UseDistrictMapping", "value",  response.data[0].use_district_mapping);
                    if(response.data[0].is_county_dependent == true){
                        vm.$refs.AdditionalInfoForm.updateProperty("County", "disabled",  false);
                    }
                    else{
                        vm.$refs.AdditionalInfoForm.updateProperty("County", "disabled",  true);
                    }
                    if(response.data[0].iswirecenter == true){
                        vm.$refs.AdditionalInfoForm.updateProperty("Abbreviations", "disabled",  true);
                    }
                    else{
                        vm.$refs.AdditionalInfoForm.updateProperty("Abbreviations", "disabled",  false);
                    }
                    setTimeout(function () {
                        var county = response.data[0].county_id;
                        if (!vm.isBlank(county)) {
                            var bindCounty = [];
                            var countyArray = county.split(',').map(c => c.trim()); // Convert to array and trim whitespace
                            vm.countydData = vm.countyList.filter(f => countyArray.includes(f.value.toString()));
                            vm.countydData.forEach((item) => {
                                bindCounty.push({
                                    value: item.value,
                                    label: item.name
                                });
                            });
                            vm.$refs.AdditionalInfoForm.updateProperty("County", "value", bindCounty);
                        }
                    }, 500); 
                    vm.$refs.AdditionalInfoForm.UpdateKeyValue();
                   }
                }           
            })
        },
        getDropdownList: async function(type) {
            var vm = this;
            let Url='';
            if(type == 'CDC_DIVISION_LIST_BY_CLIENT_ID' || type == 'SBA_LIST_BY_CLIENT_ID'){
                    Url = `id=&moduleName=Billing&type=${type}&search=&refId=${vm.clientId}`
            }
            else{
                    Url = `id=&moduleName=Billing&type=${type}&search=&refId=223`
            }
            await DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    if(type == 'CDC_DIVISION_LIST_BY_CLIENT_ID'){
                        vm.$refs.AdditionalInfoForm.updateProperty("Division", "config.options",  response.data.DATA);
                    }
                    else if(type == 'SBA_LIST_BY_CLIENT_ID'){
                        vm.sbaList = response.data.DATA;
                        vm.$refs.AdditionalInfoForm.updateProperty("sba_id", "config.options",  response.data.DATA);
                    }
                    else{
                        vm.stateList= response.data.DATA;
                        vm.$refs.AdditionalInfoForm.updateProperty("State", "config.options",  response.data.DATA);
                    }
                    vm.$refs.AdditionalInfoForm.UpdateKeyValue();
                }

            })
        },
        onChangeCountyDependent:function(x,y,z){
                var vm = this;
                var isCountyDependent = y.value[0];
                if(isCountyDependent == 'true'){
                    vm.$refs.AdditionalInfoForm.updateProperty("County", "disabled",  false);
                }
                else{
                    vm.$refs.AdditionalInfoForm.updateProperty("County", "disabled",  true);
                }
                vm.$refs.AdditionalInfoForm.UpdateKeyValue();
        },
        onChangeWireCenter:function(x,y,z){
                var vm = this;
                var isWireCenter = y.value[0];
                if(isWireCenter == 'true'){
                    vm.$refs.AdditionalInfoForm.updateProperty("Abbreviations", "disabled",  true);
                }
                else{
                    vm.$refs.AdditionalInfoForm.updateProperty("Abbreviations", "disabled",  false);
                }
                vm.$refs.AdditionalInfoForm.UpdateKeyValue();
        },
        Close: function () {
            this.$parent.isAddCdcCode = false;
            this.$parent.FetchRecords();
        },
        onSubmit: function (formfields, val) {            
            var vm = this;
            let countyValues = '';
            let countyNames = '';
            let selectedValues = Array.isArray(formfields.County) 
                ? formfields.County 
                : [formfields.County];

                const selectedValueIds = selectedValues.map(item => item.value);

            let matchedCounties = vm.countyList.filter(c => selectedValueIds.includes(c.value));
             countyValues = matchedCounties.map(c => c.value).join(',');
             countyNames = matchedCounties.map(c => c.name).join(',');

            var sbaName = vm.sbaList.find(x => x.value == formfields.sba_id);
            var sba = sbaName ? sbaName.name : null;
            
            var postJSON = {
                cdcId: vm.cdcId,
                cdcCode :formfields.cdc_code,
                sbaId : sba,
                state : formfields.State.value,
                additionalId: vm.ClientAdditionalId,
                clientId: vm.clientId,
                utilityId : vm.utilityId,
                abbreviations : formfields.Abbreviations,
                isah : formfields.IsAH[0] ? true : false,
                isWireCenter : formfields.IsWireCenter[0] ? true : false,
                divisionId: formfields.Division,
                beSbaId: formfields.sba_id,
                isCountyDependent : formfields.IsCountyDependent[0] ? true : false,
                countyId : countyValues,
                useDistrictMapping : formfields.UseDistrictMapping[0] ? true : false,
                districtId : 0,
                countyName : countyNames
            };
            var objectPostString = JSON.stringify(postJSON);
            DataServices.SaveCDCCodeInfo(objectPostString).then((response) => {
                    if (response.data != null && response.data != "" && response.data.status == "Success") {
                        if(response.data.result == 'INSERT'){
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("cdc_code")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else if(response.data.result == 'UPDATE'){
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("cdc_code")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else if(response.data.result == 'ALREADY EXIST'){
                                vm.ShowAlert(vm.$t('AlreadyExists',[vm.$t('cdc_code')]), "failure", true,  vm.$t("Alert"));
                        }
                    }
                    else{
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
            });
        }
    }
}

</script>