<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('View Plan History') }}</h4>

                            <button type="button" class="close" @click="$emit('close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing"
                                :listType="['List']" :RenderRowActionMethod="renderActions" SubModuleCode="Billing"
                                IdentityColumn="id" :ListData="PlanHistory" :HeaderData="Headers"
                                :callbackfunction="FetchData" :HeaderButtons="''" :TotalRecords="TotalRecords"
                                :ListDataCallBackFunction="FetchData"  :SortExp="sortExp"
                                :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod"
                                :isShowHeaderText="false" v-on:ActionButtonClick="actionButtonClick"
                                :HeaderText="$t('ViewPlanHistory')" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                v-on:SortdataButtonClick="sortdata">

                                <template v-slot:slotdata="{ data }">

                                    <template v-if="data.column.COLUMN_NAME === 'Variation'">
                                        <span v-if="data.row.VariationStatus === 'UP'">
                                            {{ data.row.Variation }} <i class="fa fa-arrow-up text-success"></i>
                                        </span>
                                        <span v-if="data.row.VariationStatus === 'DOWN'">
                                            {{ data.row.Variation }} <i class="fa fa-arrow-down text-danger"></i>
                                        </span>
                                    </template>


                                </template>

                            </tg-list>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        PlanMappingId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            PlanHistory: [],
            TotalRecords: 0,
            sortBy: '',
            sortExp: '',
            Headers: [
                {
                    COLUMN_NAME: "Change_Date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Change Date",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "Previous_Quantity",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Previous Quantity",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "Updated_Quantity",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Updated Quantity",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "Variation",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Variation",
                    DISPLAY_ORDER: 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "Updated_By",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Updated By",
                    DISPLAY_ORDER: 6,
                },
            ],
            // LegendArray: [
            //     {
            //         class: 'legend-Progress colorboxsquare',
            //         text: 'Currently active'
            //     },

            // ]
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        async FetchData() {
            var vm = this;
            try {
                var params = `planMappingId=${vm.PlanMappingId}`;
                const response = await DataService.GetPlanUnitHistoryForSubscription(params);
                if (response.data.status === "Success" && response.data.code === "1") {
                    if (response.data.result.data.length > 0) {
                        vm.TotalRecords = response.data.result.data[0]?.TotalRecords;
                        vm.isRateApplicableOnPlan = response.data.result.data[0]?.is_rate_applicable_on_plan;

                        if (vm.isRateApplicableOnPlan == null) {
                            //
                            let obj = {

                                COLUMN_NAME: "Service_name",
                                DATA_TYPE: "text",
                                SORTABLE: false,
                                VISIBLE: true,
                                DISPLAY_NAME: "Service name",
                                DISPLAY_ORDER: 1,
                            };

                            vm.Headers.unshift(obj);
                        }
                    }
                    vm.PlanHistory = response.data.result.data;
                    vm.PlanHistory.forEach(element => {
                        element.additionalClass = '';
                        if (element.unit_status == 1001) {
                            //
                            element.additionalClass = 'low-bar odd';
                        }
                    });

                }
                else {
                    vm.PlanHistory = [];
                    vm.TotalRecords = 0;
                }
            } catch (error) {
                console.log("Error loading plan history:", error);
            }
        },
        renderActions() {

        },
        pagerMethod() {

        },
        actionButtonClick() {

        },
        commonHeaderButtonClick() {

        },
        sortdata() {

        }
    }
}
</script>