<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add') }} {{ $t('Property') }}</h4>
                        <button type="button" class="close" v-on:click="CloseAddProperty">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :schema="FormSchema" ref="AddTaxCategoryForm"
                            :mandatorytext='MandatoryString'>
                        </dynamic-form>
                        <div class="row">
                            <div class="col-lg-12 mb-3">
                                <div class="heading-border mt-2">
                                    <h5 class="h5"><span>{{ $t('LocationDetails') }}</span></h5>
                                </div>
                            </div>
                            <div class="col-12 float-left mb-3">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" class="custom-control-input"
                                        style="z-index: 1; width: 19px; margin-top: 6px;"
                                        @change="GetPrimaryAddress($event)">
                                    <label class="custom-control-label universal-custom-control-label pt-1">
                                        <span class="float-left">{{$t('SameAsPrimaryAddress')}}</span>
                                    </label>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <dynamic-form lang="en" ref="refAddress" :buttons="false"
                                    :schema="FormSchemaa"></dynamic-form>
                            </div>
                            <div class="col-md-6">
                                <div class="row">
                                    <div class="col-sm-12">
                                        <MapComponent class="row" ref="mapComponnet" mapType="MapBox"
                                            mapContainer="addAddress" :config="config" mapHeight="355px" :Data="mapObj"
                                            :latLng="latLngValues" @GetAddress="GetAddress">
                                        </MapComponent>
                                        <div class="row flex-row-reverse mt-2 pt-2">
                                            <div class="col-lg-6 text-right">
                                                <a href="javascript:;" @click="OnSubmit" class="btn btn-success mr-2 "
                                                    title="" id="addAddress" data-toggle-tooltip="tooltip"
                                                    data-original-title="Save">
                                                    <i class="fa fa-save mr-2"></i>
                                                    <span id="btnAddressText"> {{ $t("Save") }}</span></a>

                                                <a href="javascript:;" class="btn btn-danger" title=""
                                                    id="CancelAddress" @click="CloseAddProperty"
                                                    data-toggle-tooltip="tooltip" data-original-title="Cancel"><i
                                                        class="fa fa-times mr-2"></i>{{ $t("Cancel")
                                                        }}</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row col-lg-6">
                            <div class="py-2">
                                <span class="mandatory">{{ MandatoryString }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import MapComponent from '../../components/Modals/MapComponent.vue';
import DataService from '../../services/DataService';
export default {
    components: { MapComponent },
    props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        },
        AddAddProperty: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            PropertyId: 0,
            mapObj: [],
            MandatoryString: this.$t('MandatoryString'),
            FormSchema: [
                {
                    layoutType: "four",
                    group_name: 'Property Details',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('PropertyTitle'),
                            name: "PropertyTitle",
                            value: "",
                            disabled: false,
                            visibility: true,
                            placeholder: this.$t('PropertyTitle'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PropertyType'),
                            name: "PropertyType",
                            value: "",
                            disabled: false,
                            visibility: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('PropertyType'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ConstructionStatus'),
                            name: "ConstructionStatus",
                            value: "",
                            disabled: false,
                            visibility: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('ConstructionStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Block'),
                            name: "BlockName",
                            value: "",
                            disabled: false,
                            visibility: true,
                            placeholder: this.$t('BlockName'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Floor'),
                            name: "Floor",
                            value: "",
                            disabled: false,
                            visibility: true,
                            placeholder: this.$t('Floor'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('PropertySize'),
                            name: "PropertySize",
                            value: "",                                                       
                            disabled: false,
                            visibility: true,                            
                            placeholder: this.$t('PropertySize'),
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Unit'),
                            name: "Unit",
                            value: "",
                            disabled: false,
                            visibility: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Unit'),
                            validationRules: { "required": false, max: 100 },
                        }                       
                                              
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseAddProperty,
                },
            ],
            FormSchemaa: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t("AddressLine1"),
                            name: "Address Line 1:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": true,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("AddressLine2"),
                            name: "Address Line 2:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": false,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("COUNTRY"),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                "required": true,
                                max: 250,
                            },
                            config: {
                                options: [],
                                onChange: this.ChangeState
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("StateProvince"),
                            name: "State",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                "required": false,
                            },
                            config: {
                                options: []
                            },

                        },
                        {
                            astype: "TextField",
                            label: this.$t("City"),
                            name: "City:",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                "required": false,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("ZIPCODE"),
                            name: "Zip Code:",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                "required": false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("NotesDescriptionAbtAddress"),
                            name: "Note/Description About Address",
                            userGuide: "Example: office Address",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": false,
                            },
                        }
                    ],
                },
            ],
            AddressList: [],
            countryList: [],
            Longitude: null,
            Latitude: null,
            latLngValues: '',
            config: {
                isShowDrawButton: true,
                isShowEtaOrLocator: true,
                isShowSearchBox: true,
                showPolygonButton: true
            },
            StateDDl: []
        }
    },
    created: function () {
        var ths = this;
         if (ths.AddAddProperty == null) {
            ths.GetCompanyAddress();
         }
        ths.GetPropertyType();
        ths.GetPropertyUnit()
        ths.GetPropertConstructioStauts();
        ths.GetCountryDDL();
        if (ths.AddAddProperty != null) {
            ths.BindPropertyDetails();
        }
    },
    methods: {
        BindPropertyDetails() {
            var vm = this;
            vm.PropertyId = vm.AddAddProperty.id
            vm.FormSchema[0].Data[0].value = vm.AddAddProperty.property_title;
            vm.FormSchema[0].Data[1].value = vm.AddAddProperty.property_type_id;
            vm.FormSchema[0].Data[2].value = vm.AddAddProperty.construction_status_id;
            vm.FormSchema[0].Data[3].value = vm.AddAddProperty.block_name;
            vm.FormSchema[0].Data[4].value = vm.AddAddProperty.floor_name;
            vm.FormSchemaa[0].Data[0].value = vm.AddAddProperty.user_address_1;
            vm.FormSchemaa[0].Data[1].value = vm.AddAddProperty.user_address_2;
            vm.FormSchemaa[0].Data[2].value = vm.AddAddProperty.country_id;
            vm.FormSchemaa[0].Data[3].value = vm.AddAddProperty.user_state;
            vm.FormSchemaa[0].Data[4].value = vm.AddAddProperty.user_state;
            vm.FormSchemaa[0].Data[5].value = vm.AddAddProperty.user_zipcode;
            vm.FormSchemaa[0].Data[6].value = vm.AddAddProperty.user_address_desc;
            vm.Latitude = vm.AddAddProperty.latitude;
            vm.Longitude = vm.AddAddProperty.longitude;
            let objnew = {
                location_nw: `${vm.Latitude},${vm.Longitude}`
            }
            vm.latLngValues = objnew;
        },
         async GetCompanyAddress() {
            var vm = this;
            var Latitude='';
             var Longitude='';
            await DataService.GetCompanyAddress().then((result) => {
                Latitude=result.data[0].latitude;
                Longitude=result.data[0].longitude;

            });
            vm.Latitude = Latitude;
            vm.Longitude = Longitude;
            let objnew = {
                location_nw: `${vm.Latitude},${vm.Longitude}`
            }
            vm.latLngValues = objnew;
        },
        GetAddress(data) {
            var vm = this;
            vm.Latitude = '';
            vm.Longitude = '';
            var indexValue = data.length - 1;
            vm.$refs.mapComponnet.fullAddress = [];
            vm.$refs.mapComponnet.lngLatData = [];
            let filteredCountry = vm.countryList.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            vm.FormSchemaa[0].Data[2].value = filterCountry;
            vm.FormSchemaa[0].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            vm.FormSchemaa[0].Data[1].value = data[indexValue].CompleteAddress.FullAddress;
            vm.ChangeState(null, filterCountry);
            setTimeout(function () {
                let filteredState = vm.StateDDl.filter((item) => {
                    return item.name === data[indexValue].CompleteAddress.AdministrativeAreaLevel1.LongName;
                });
                var filterState = filteredState == '' ? '' : filteredState[0].name;
                vm.FormSchemaa[0].Data[3].value = filterState;
                vm.$refs.refAddress.updateProperty("State", "value", vm.FormSchemaa[0].Data[3].value);
            }, 2000);
            vm.FormSchemaa[0].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            vm.FormSchemaa[0].Data[5].value = data[indexValue].CompleteAddress.PinCode;
            vm.FormSchemaa[0].Data[6].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel2.LongName;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }                
            });
            vm.Latitude = dataLng1 + ',' + dataLat2;
            vm.Longitude = dataLat1 + ',' + dataLng2;
            let objnew = {                
                location_nw:vm.Longitude.replace(",", '') + vm.Latitude.replace(",", ',')
            }
            vm.latLngValues = objnew;

        },
        GetCountryDDL() {
            var url = `moduleName=CRM&type=COUNTRIES`;
            var vm = this;
            DataService.GetCountryDDL(url).then((result) => {
                vm.countryList = result.data.DATA;
                vm.countryList.forEach((item) => {
                    vm.FormSchemaa[0].Data[2].config.options.push({
                        name: `${item.name}`,
                        value: `${item.value}`,
                    });
                });
                this.$refs.refAddress.UpdateKeyValue();
            });
        },
        ChangeState: async function (event, id) {
            var vm = this;
            var CountryId = 0;
            if (event == null) {
                CountryId = id;
            } else {
                CountryId = event.target.value.toString();
            }
            vm.FormSchemaa[0].Data[3].config.options = [];
            vm.StateDDl = '';
            var appendingUrl = `id=&&moduleName=CRM&type=${'STATE'}&search=&refId=` + CountryId;
            await DataService.GetCountryDDL(appendingUrl).then(function (response) {
                if (response.data) {
                    if (response.data.DATA.length > 0) {
                        vm.StateDDl = response.data.DATA;
                    }
                }
            });
        },        
        GetPropertyType() {
            var vm = this;
            var url = `id=&moduleName=&type=PROPERTY_TYPE&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[0].Data[1].config.options.push(obj);
                                vm.$refs.AddTaxCategoryForm.UpdateKeyValue();
                            })

                        }
                    }
                }
            })
        },
        GetPropertyUnit() {
            var vm = this;
            var url = `id=&moduleName=&type=PROPERTY_UNIT&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[0].Data[6].config.options.push(obj);
                                vm.$refs.AddTaxCategoryForm.UpdateKeyValue();
                            })

                        }
                    }
                }
            })
        },
        GetPropertConstructioStauts() {
            var vm = this;
            var url = `id=&moduleName=&type=PROPERTY_CONSTRUCTION_STATUS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[0].Data[2].config.options.push(obj);
                                vm.$refs.AddTaxCategoryForm.UpdateKeyValue();
                            })

                        }
                    }
                }
            })
        },
        GetPrimaryAddress(evt) {
            var vm = this;
            var ischecked = evt.target.checked;
            if (ischecked) {
                var url = `userId=${vm.ClientId}&userAddressId=0`;
                DataService.GetAdditionalAddressList(url).then((response) => {
                    if (response.data != null) {
                        vm.AddressList = [];
                        if (response.data.result.length > 0) {
                            vm.AddressList = response.data.result;
                            vm.FormSchemaa[0].Data[0].value = vm.AddressList[0].user_address_1;
                            vm.FormSchemaa[0].Data[1].value = vm.AddressList[0].user_address_2;
                            vm.FormSchemaa[0].Data[2].value = vm.AddressList[0].country_id;
                            vm.FormSchemaa[0].Data[3].value = vm.AddressList[0].user_state;
                            vm.FormSchemaa[0].Data[4].value = vm.AddressList[0].user_state;
                            vm.FormSchemaa[0].Data[5].value = vm.AddressList[0].user_zipcode;
                            vm.FormSchemaa[0].Data[6].value = vm.AddressList[0].user_address_desc;
                            vm.AddressList.forEach(element => {
                                element.isSelected = false;
                                if (element.is_primary_address == true) {
                                    vm.Latitude = element.latitude.replace(',', '');
                                    vm.Longitude = element.longitude.replace(",", '');
                                    let objnew = {
                                        location_nw: element.longitude.replace(",", '') + element.latitude.replace(",", ',')
                                    }
                                    vm.latLngValues = objnew;
                                }
                            })
                        }

                    }

                });
            }else{
                vm.FormSchemaa[0].Data[0].value = '';
                vm.FormSchemaa[0].Data[1].value = '';
                vm.FormSchemaa[0].Data[2].value = '';
                vm.FormSchemaa[0].Data[3].value = '';
                vm.FormSchemaa[0].Data[4].value = '';
                vm.FormSchemaa[0].Data[5].value = '';
                vm.FormSchemaa[0].Data[6].value = '';
                vm.latLngValues='';
            }

        },
        CloseAddProperty() {
            var vm = this;
            vm.$emit('CloseAddProperty');
        },
        OnSubmit() {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddTaxCategoryForm.validateForm.validate().then(async valid => {
                if (valid) {
                    vm.$refs.refAddress.validateForm.validate().then(async valid => {
                        if (valid) {                            
                            var Obj = {
                                id: vm.PropertyId,
                                property_title: vm.FormSchema[0].Data[0].value,
                                property_type_id: vm.FormSchema[0].Data[1].value,
                                construction_status_id: vm.FormSchema[0].Data[2].value,
                                block_name: vm.FormSchema[0].Data[3].value,
                                floor_name: vm.FormSchema[0].Data[4].value,
                                property_size:vm.FormSchema[0].Data[5].value,
                                property_unit:vm.FormSchema[0].Data[6].value,
                                client_id: vm.ClientId,
                                user_address_1: vm.FormSchemaa[0].Data[0].value,
                                user_address_2: vm.FormSchemaa[0].Data[1].value,
                                user_city: vm.FormSchemaa[0].Data[4].value,
                                user_state: vm.FormSchemaa[0].Data[3].value,
                                country_id: vm.FormSchemaa[0].Data[2].value,
                                user_zipcode: vm.FormSchemaa[0].Data[5].value,
                                user_address_desc: vm.FormSchemaa[0].Data[6].value,                              
                                latitude: vm.Latitude,
                                longitude: vm.Longitude
                            }
                            var JsonObj = JSON.stringify(Obj);
                            DataService.SaveClientProperty(JsonObj).then(function (response) {
                                vm.isLoading = false;
                                if (response.data.result.DATA.length > 0) {
                                    if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Property')]), "success", true, vm.$t("Alert"));
                                        vm.CloseAddProperty();
                                    }
                                    else if (response.data.result.DATA[0].VALUE == 'EXISTS') {
                                        vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Property')]), "warning", true, vm.$t("Alert"));
                                    }
                                    else {
                                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Property')]), "success", true, vm.$t("Alert"));
                                        vm.CloseAddProperty();
                                    }

                                } else {
                                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                                }
                            });
                        }else{
                             vm.isLoading = false;
                        }
                    })
                }else{
                     vm.isLoading = false;
                }
            })
        }
    }

}
</script>