<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Reason for Cancelling') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="defaultStatus" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
         Billid: { type: Number }
    
    },
    data(){
        return{
            isLoading:false,
            FormSchema:[
            {
                    layoutType: "single",
                    Data: [
                         
                        {
                            astype: "TextAreaField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            placeholder: this.$t('Enter Reason'),
                            validationRules: {
                                "required": true
                            },                         
                            disabled: false,
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,                    
                   
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId:null,
            userId:null
        }
    },
    created:function(){   
        debugger     
        var vm=this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetStatusList();
    },
    methods:{
         
        async onSubmit(formfields, item) {
                debugger;
                const vm = this;
                const reason = formfields.Reason;
                const url = `billIds=${vm.Billid}&statusCode=CLBCNCL&Reason=${reason}`;

                const response = await DataService.UpdateClientInvoiceStatus(url);

                if (response.data[0].ERROR_CODE == 1) {
                    vm.Close();
                    vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success");
                    vm.$parent.FetchData();
                } else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure");
                }
        },

        Close:function(){
            this.$parent.isInvoiceCancel=false;
        }
        
    }
}
</script>