<template>
    <section>  
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle d-flex">
                        <h4 class="modal-title" v-if="meterMappingId>0">{{ $t('UpdateMeter') }}</h4>
                        <h4 class="modal-title" v-else>{{ $t('AddMeter') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <AddPropertymodal v-if="IsAddProperty" @CloseAddProperty="CloseAddProperty" :ClientData="ClientData" :ClientId="ClientId" :AddAddProperty="AddAddProperty"></AddPropertymodal>
</section>
</template>
<script>
import DataServices from "@/services/DataService";
import AddPropertymodal from '../Modals/AddPropertymodal.vue';
export default {
    components: {AddPropertymodal},
    props: {
        reqFrom:{
            type: String,
            required : false
        },
        ClientAdditionalId: {
            type: Number,
            required: false
        },
        utilityTypeId:{
            type: Number,
            required: false
        },
        editMeterDetail: {
            type : Array,
            required :false
        },
        meterMappingId:{
            type:Number,
            required:false
        },
         ClientId: {
            type: Number
        },
        ClientData: {
            type: Object
        },
    },
    data() {
        return {
            FormSchema: [{
                layoutType: "four",
                Data: [

                    {
                        astype: "SelectField",
                        label: this.$t('MeterType'),
                        name: "Meter_type",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.GetMeterNameDDlData
                        },
                        placeholder: this.$t('Meter'),
                        validationRules: { "required": true, max: 100 },
                        disabled: true,
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('MeterName'),
                        name: "MeterName",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [],
                            onChange: this.getSerialNumber
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "CustomSelectField",
                        label: this.$t('MeterSerialNumber'),
                        name: "SerialNumber",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [],
                            onChange: this.getMeterMappingDetails
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "TextField",
                        label: this.$t('ModelName'),
                        name: "ModelName",
                        value: "",
                        visibility: true,
                        validationRules: [{ "required": false }],
                        disabled: true,
                    },
                ]
            },
            {
                layoutType: "four",
                Data: [
                    {
                        astype: "TextField",
                        label: this.$t('MeterService'),
                        name: "MeterService",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: true,
                        visibility: true
                    },
                    {
                        astype: "TextField",
                        label: this.$t('MeterUnit'),
                        name: "MeterUnit",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: true,
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Plan'),
                        name: "Plan",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [],
                            // onChange: this.getMeterMappingDetails
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('InstallationDate'),
                        name: "InstallationDate",
                        value: "",
                        placeholder: this.$t('Select Date'),
                        validationRules: {
                            "required": true,
                            max: 100
                        },
                        config: {
                            // minDate: new Date()
                        },
                        visibility: true,
                    },
                ]
            },
            {
                layoutType: "four",
                Data: [
                    {
                        astype: "TextField",
                        label: this.$t('SanctionedLoad'),
                        name: "Sanctioned_Load",
                        value: "",
                        validationRules: [{ "required": true }],
                        disabled: false,
                        visibility: false
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('MultiplyFactor'),
                        name: "Multiply_Factor",
                        value: "",
                        validationRules: [{ "required": true }],
                        disabled: false,
                        visibility: false
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('PowerFactor'),
                        name: "Power_factor",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: false,
                        visibility: false
                    },
                    {
                        astype: "TextField",
                        label: this.$t('VoltageRating'),
                        name: "Voltage_Rating",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: false,
                        visibility: false
                    },

                    {
                        astype: "TextField",
                        label: this.$t('CurrentRating'),
                        name: "Current_Rating",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: false,
                        visibility: false
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('PhaseType'),
                        name: "Phase_Type",
                        value: "",
                        placeholder: "",
                        visibility: false,
                        config: {
                            options: [{
                                value: '1',
                                name: 'Single Phase'
                            }, {
                                value: '2',
                                name: 'Triple Phase'
                            }],
                            // onChange: this.GetMeterNameDDlData
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "TextField",
                        label: this.$t('RegisterConstant'),
                        name: "RegisterConstant",
                        value: "",
                        validationRules: [{ "required": false }],
                        disabled: false,
                        visibility: false
                    },
                    {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('ConnectionType'),
                        name: "ConnectionType",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [{
                                value: 'Permanent',
                                name: 'Permanent'
                            }, {
                                value: 'Temporary',
                                name: 'Temporary'
                            }],
                            onChange: this.showTantativeField
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('TentativeEndDate'),
                        name: "TentativeEndDate",
                        value: "",
                        placeholder: this.$t('Select Date'),
                        validationRules: {
                            "required": false,
                            max: 100
                        },
                        config: {
                            // minDate: new Date()
                        },
                        visibility: false,
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Property'),
                        name: "Property",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [],
                            showAddIcon: true,
                            onAddButtonClick: this.OpenAddProperty,
                        },
                        validationRules: {"required": true },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Division'),
                        name: "Division",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: []
                        },
                        validationRules: {"required": false },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Contract'),
                        name: "Contract",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: []
                        },
                        validationRules: {"required": false },
                    }

                ]
            },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close,
                },
            ],
            MeterNameData: [],
            MeterId: null,
            SerialNumberData: [],
            SerialId: null,
            PlanData: [],
            PlanId: null,
            refreshKey: 0,
            isLoading: false,
            ClientId: 0,
            fileError: null,
            IsAddProperty:false,
            AddAddProperty:null,
            
        }
    },
    created: async function () {
        var vm = this;
        vm.isLoading=true;
        vm.ClientId = vm.$route.params.id;
        await vm.getMeterTypes();
        await vm.getPropertyType();
        await vm.getContractList();
        await vm.getDivisionList();
        setTimeout(function () {
            vm.BindMeterType();
        }, 200);  
        setTimeout(function () {
            if(vm.reqFrom == 'Edit'){
                vm.BindMeterDetailData();
           }
        vm.isLoading=false;
        }, 400);  
    },
    methods: {
         OpenAddProperty(val){
            var ths =this;
            ths.AddAddProperty=val;
            ths.IsAddProperty=true;
        }, 
         CloseAddProperty(){
            var ths =this;
            ths.IsAddProperty=false;
            ths.getPropertyType();
        },  
        BindMeterDetailData: async function(){
            var vm = this;
            vm.$refs.dynamicform.updateProperty("MeterName", "value", vm.editMeterDetail.asset_catalogue_id);
            vm.$refs.dynamicform.updateProperty("MeterName", "disabled", true);
            let ListData= [];
            ListData.push({
                value: vm.editMeterDetail.related_info_id,
                label: vm.editMeterDetail.serial_number
            })
            vm.$refs.dynamicform.updateProperty("SerialNumber", "value", ListData);
            vm.$refs.dynamicform.updateProperty("SerialNumber", "disabled", true);
            vm.$refs.dynamicform.updateProperty("ModelName", "value", vm.editMeterDetail.model_name);
            vm.$refs.dynamicform.updateProperty("ModelName", "disabled", true);
            vm.$refs.dynamicform.updateProperty("MeterService", "value", vm.editMeterDetail.meter_service);
            vm.$refs.dynamicform.updateProperty("MeterService", "disabled", true);
            vm.$refs.dynamicform.updateProperty("MeterUnit", "value", vm.editMeterDetail.meter_unit);
            vm.$refs.dynamicform.updateProperty("MeterUnit", "disabled", true);
            vm.$refs.dynamicform.updateProperty("Plan", "value", vm.editMeterDetail.plan_id);
            //vm.$refs.dynamicform.updateProperty("Plan", "disabled", true);
            vm.$refs.dynamicform.updateProperty("Status", "value", vm.editMeterDetail.status_id);
            vm.$refs.dynamicform.updateProperty("InstallationDate", "value", vm.editMeterDetail.date_Of_installation);
            //vm.$refs.dynamicform.updateProperty("InstallationDate", "disabled", true);
            vm.$refs.dynamicform.updateProperty("RegisterConstant", "value", vm.editMeterDetail.register_constant);
            vm.$refs.dynamicform.updateProperty("ConnectionType", "value", vm.editMeterDetail.connection_type);
            if(vm.editMeterDetail.connection_type == 'Temporary'){
                vm.$refs.dynamicform.updateProperty("TentativeEndDate", "value", vm.editMeterDetail.tentative_end_date);
                vm.$refs.dynamicform.updateProperty("TentativeEndDate", "visibility", true);
            }
            else{
                vm.$refs.dynamicform.updateProperty("TentativeEndDate", "visibility", false);
            }
            vm.$refs.dynamicform.updateProperty("Property", "value", vm.editMeterDetail.property_id);
            //vm.$refs.dynamicform.updateProperty("Property", "disabled", true);
            vm.$refs.dynamicform.updateProperty("Division", "value", vm.editMeterDetail.invoice_grouping_id);
            vm.$refs.dynamicform.updateProperty("Contract", "value", vm.editMeterDetail.contract_id);
            vm.$refs.dynamicform.updateProperty("Phase_Type", "value", vm.editMeterDetail.phase_type);
            vm.$refs.dynamicform.updateProperty("Sanctioned_Load", "value", vm.editMeterDetail.sanctioned_load);
            vm.$refs.dynamicform.updateProperty("Multiply_Factor", "value", vm.editMeterDetail.multiply_factor);
            vm.$refs.dynamicform.updateProperty("Power_factor", "value", vm.editMeterDetail.power_factor);
            vm.$refs.dynamicform.updateProperty("Voltage_Rating", "value", vm.editMeterDetail.volate_rating);
            vm.$refs.dynamicform.updateProperty("Current_Rating", "value", vm.editMeterDetail.current_rating);
            
            vm.$refs.dynamicform.UpdateKeyValue();

        },
        showTantativeField: function(e){
          var vm = this;
          var event = e.target.value;
          if(event == "Temporary"){
            vm.$refs.dynamicform.updateProperty("TentativeEndDate", "visibility", true);
            vm.$refs.dynamicform.UpdateKeyValue();
          }
          else{
            vm.$refs.dynamicform.updateProperty("TentativeEndDate", "visibility", false);
            vm.$refs.dynamicform.UpdateKeyValue();
          }
        },
        getDivisionList: async function(type) {
            var vm = this;
            let Url = `id=&moduleName=Billing&type=CDC_DIVISION_LIST_BY_CLIENT_ID&search=&refId=${vm.ClientId}`;
            await DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    var selctedValue=[];
                    if(response.data.DATA.length > 0){
                        response.data.DATA.forEach((item, index) => {
                            selctedValue.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    vm.$refs.dynamicform.updateProperty("Division", "config.options", selctedValue);
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            })
        },
        getContractList: async function(type) {
            var vm = this;
            let Url = `id=&moduleName=Billing&type=CONTRACT_LIST_BY_CLIENT_ID&search=&refId=${vm.ClientId}`;
            await DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    if(response.data.DATA.length > 0){
                        vm.$refs.dynamicform.updateProperty("Contract", "config.options", response.data.DATA);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            })
        },
        getPropertyType: async function(type) {
            var vm = this;
            let Url = `id=${vm.meterMappingId}&moduleName=Billing&type=PROPERTY_BY_CLIENT_ID&search=${vm.reqFrom}&refId=${vm.ClientId}`;
            await DataServices.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    if(response.data.DATA.length > 0){
                        vm.$refs.dynamicform.updateProperty("Property", "config.options", response.data.DATA);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            })
        },
        BindMeterType : async function(){
            var vm = this;
            vm.FormSchema[0].Data[0].value = vm.utilityTypeId;
            await vm.BindMeterNameData(null,vm.FormSchema[0].Data[0]);
        },
        async BindMeterNameData(event, data) {
            var vm = this;
            vm.FormSchema[0].Data[1].value = '';
            vm.FormSchema[0].Data[2].value = '';
            vm.FormSchema[0].Data[3].value = '';
            vm.FormSchema[1].Data[0].value = '';
            vm.FormSchema[1].Data[1].value = '';
            vm.FormSchema[1].Data[2].value = '';
            vm.FormSchema[0].Data[1].config.options = [];
            vm.FormSchema[0].Data[2].config.options = [];
            vm.FormSchema[1].Data[2].config.options = [];


            let obj = data.config.options.find(x => x.value == data.value);


            if (obj.name == 'Gas') {

                vm.FormSchema[2].Data[0].visibility = false;
                vm.FormSchema[2].Data[1].visibility = false;
                vm.FormSchema[2].Data[2].visibility = false;
                vm.FormSchema[2].Data[3].visibility = false;
                vm.FormSchema[2].Data[4].visibility = false;
                vm.FormSchema[2].Data[5].visibility = false;
                vm.FormSchema[2].Data[6].visibility = true;
            }
            if (obj.name == 'Electric') {

                vm.FormSchema[2].Data[0].visibility = true;
                vm.FormSchema[2].Data[1].visibility = true;
                vm.FormSchema[2].Data[2].visibility = true;
                vm.FormSchema[2].Data[3].visibility = true;
                vm.FormSchema[2].Data[4].visibility = true;
                vm.FormSchema[2].Data[5].visibility = true;
                vm.FormSchema[2].Data[6].visibility = false;
            }

            if (obj.name == 'Water') {
                vm.FormSchema[2].Data[0].visibility = false;
                vm.FormSchema[2].Data[1].visibility = false;
                vm.FormSchema[2].Data[2].visibility = false;
                vm.FormSchema[2].Data[3].visibility = false;
                vm.FormSchema[2].Data[4].visibility = false;
                vm.FormSchema[2].Data[5].visibility = false;
                vm.FormSchema[2].Data[6].visibility = false;
            }

            try {
                let Url = `type=is_meter&TypeValue=1&type2=utility_type_id&TypeValue2=${data.value}`;
                const response = await DataServices.GetMeterNameDDlData(Url);
                const responseData = response.data;
                if (Array.isArray(responseData) && responseData.length > 0) {
                    vm.$refs.dynamicform.updateProperty("MeterName", "config.options", responseData);
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.GetPlanDDlData(data.value);
                } else {
                    vm.$refs.dynamicform.updateProperty("MeterName", "config.options", []);
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            } catch (error) {
                console.error('Error fetching meter name data:', error);
            }
        },

        async GetPlanDDlData(meter_id) {
            var vm = this;
            // let Url = `clientId=${vm.ClientId}&meter_type_id=${meter_id}`;
            let Url = `clientId=0&meter_type_id=${meter_id}`;
            try {
                const response = await DataServices.GetCompanyPlans(Url);
                let PlanData = response.data;
                if (Array.isArray(PlanData) && PlanData.length > 0) {
                    vm.$refs.dynamicform.updateProperty("Plan", "config.options", PlanData);
                    vm.$refs.dynamicform.UpdateKeyValue();

                } else {
                    vm.$refs.dynamicform.updateProperty("Plan", "config.options", []);
                    vm.$refs.dynamicform.UpdateKeyValue();
                }
            } catch (error) {
                console.error('Error fetching plan data:', error);
            }
        },
        async getMeterTypes() {
            const vm = this;
            let url = `is_mapped=1`
            const response = await DataServices.GetMeterTypes(url);
            if (response?.data) {
                const responseData = JSON.parse(response.data.result).data;
                    if (Array.isArray(responseData) && responseData.length > 0) {
                        var selctedValue=[];
                        responseData.forEach((item, index) => {
                            selctedValue.push({
                                value: item.meter_id,
                                name: item.meter_name
                            })
                        })
                       vm.$refs.dynamicform.updateProperty("Meter_type", "config.options", selctedValue);
                       vm.$refs.dynamicform.UpdateKeyValue();
                    } 
                    else 
                    {
                        console.warn('No valid data found in the response');
                    }
            } 
            else 
            {
                console.error('Invalid response structure:', response);
            }
        },
        async GetMeterNameDDlData(event, data) {
            var vm = this;
            vm.FormSchema[0].Data[1].value = '';
            vm.FormSchema[0].Data[2].value = '';
            vm.FormSchema[0].Data[3].value = '';
            vm.FormSchema[1].Data[0].value = '';
            vm.FormSchema[1].Data[1].value = '';
            vm.FormSchema[1].Data[2].value = '';
            vm.FormSchema[0].Data[1].config.options = [];
            vm.FormSchema[0].Data[2].config.options = [];
            vm.FormSchema[1].Data[2].config.options = [];


            let obj = data.config.options.find(x => x.value == data.value);


            if (obj.name == 'Gas') {

                vm.FormSchema[2].Data[0].visibility = false;
                vm.FormSchema[2].Data[1].visibility = false;
                vm.FormSchema[2].Data[2].visibility = false;
                vm.FormSchema[2].Data[3].visibility = false;
                vm.FormSchema[2].Data[4].visibility = false;
                vm.FormSchema[2].Data[5].visibility = false;
                vm.FormSchema[2].Data[6].visibility = true;
            }
            if (obj.name == 'Electric') {

                vm.FormSchema[2].Data[0].visibility = true;
                vm.FormSchema[2].Data[1].visibility = true;
                vm.FormSchema[2].Data[2].visibility = true;
                vm.FormSchema[2].Data[3].visibility = true;
                vm.FormSchema[2].Data[4].visibility = true;
                vm.FormSchema[2].Data[5].visibility = true;
                vm.FormSchema[2].Data[6].visibility = false;
            }

            if (obj.name == 'Water') {
                vm.FormSchema[2].Data[0].visibility = false;
                vm.FormSchema[2].Data[1].visibility = false;
                vm.FormSchema[2].Data[2].visibility = false;
                vm.FormSchema[2].Data[3].visibility = false;
                vm.FormSchema[2].Data[4].visibility = false;
                vm.FormSchema[2].Data[5].visibility = false;
                vm.FormSchema[2].Data[6].visibility = false;
            }

            try {
                let Url = `type=is_meter&TypeValue=1&type2=utility_type_id&TypeValue2=${data.value}`;
                const response = await DataServices.GetMeterNameDDlData(Url);
                const responseData = response.data;

                if (Array.isArray(responseData) && responseData.length > 0) {
                    responseData.forEach(item => {

                        vm.FormSchema[0].Data[1].config.options.push({
                            value: item.value,
                            name: item.name
                        });
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.GetPlanDDlData(data.value);
                } else {
                    vm.FormSchema[0].Data[1].config.options = [];
                    console.warn('No data found or data is not an array.');
                }
            } catch (error) {
                console.error('Error fetching meter name data:', error);
            }
        },

        // Function to handle 'MeterName' type
        getSerialNumber(event, item) {
            var vm = this;
            vm.FormSchema[0].Data[2].config.options = [];
            vm.FormSchema[0].Data[3].value = '';
            vm.FormSchema[1].Data[0].value = '';
            vm.FormSchema[1].Data[1].value = '';
            if (item) {
                var appendingUrl = `id=${item.value}`;
                DataServices.GetSerialnumberByMeterId(appendingUrl).then(function (response) {
                    if (response?.data) {

                        if (Array.isArray(response.data) && response.data.length > 0) {
                            vm.FormSchema[0].Data[2].config.options = [];

                            let SerialNumberData = response.data;

                            SerialNumberData.forEach(item => {

                                vm.FormSchema[0].Data[2].config.options.push({
                                    value: item.value,
                                    name: item.name
                                });
                            })

                            vm.$refs.dynamicform.UpdateKeyValue();


                        } else {

                            console.warn('No data found or data is not an array.');
                        }
                    }
                });
            }
        },
        async getMeterMappingDetails(x,y,z) {
            try {
                var vm = this;
                vm.FormSchema[0].Data[3].value = '';
                vm.FormSchema[1].Data[0].value = '';
                vm.FormSchema[1].Data[1].value = '';
                if (z?.value != null && z?.label != null) {
                    var appendingUrl = `id=${z.value}`;
                    let response = await DataServices.GetMeterMappingDetails(appendingUrl);
                    if (response.data && Array.isArray(response.data) && response.data.length > 0) {
                        vm.FormSchema[0].Data[3].value = response.data[0].model_name || '';
                        vm.FormSchema[1].Data[0].value = response.data[0].meter_service || '';
                        vm.FormSchema[1].Data[1].value = response.data[0].meter_unit || '';
                    } else {
                        console.warn('No data found or data is empty');
                    }
                }
                // if (item) {
                   
                // }
            } catch (error) {
                console.error('Error fetching meter mapping details:', error);

            }
        },

        Close() {
            var vm = this;
            vm.FormSchema[0].Data[0].value = '';
            vm.FormSchema[0].Data[1].value = '';
            vm.FormSchema[0].Data[2].value = '';
            vm.FormSchema[0].Data[3].value = '';
            vm.FormSchema[1].Data[0].value = '';
            vm.FormSchema[1].Data[1].value = '';
            vm.FormSchema[1].Data[2].value = '';
            vm.FormSchema[1].Data[3].value = '';
            vm.FormSchema[2].Data[0].value = '';
            vm.FormSchema[2].Data[1].value = '';
            vm.FormSchema[2].Data[2].value = '';
            vm.FormSchema[2].Data[3].value = '';
            vm.FormSchema[2].Data[4].value = '';
            vm.FormSchema[2].Data[5].value = '';
            vm.FormSchema[2].Data[6].value = '';
            vm.FormSchema[2].Data[7].value = '';
            vm.FormSchema[2].Data[8].value = '';
            vm.$refs.dynamicform.validateForm.reset();
            vm.$parent.isAddMeter = false;
        },
        async OnSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                MeterMappingId: vm.meterMappingId,
                ClientId: vm.ClientId,
                ClientAdditionalId:vm.ClientAdditionalId,
                SerialNumber: vm.reqFrom == "Edit" ? formfields.SerialNumber[0].value : formfields.SerialNumber.value,
                PlanId: formfields.Plan,
                InstallationDate: formfields.InstallationDate,
                RegisterConstant: formfields.RegisterConstant,
                SanctionedLoad: formfields.Sanctioned_Load,
                MultiplyFactor: formfields.Multiply_Factor || 1,
                PowerFactor: formfields.Power_factor || 0,
                VoltageRating: formfields.Voltage_Rating,
                Current_Rating: formfields.Current_Rating,
                MeterTypeID: formfields.Meter_type,
                MeterNameID: formfields.MeterName,
                PhaseType: formfields.Phase_Type,
                ConnectionType: formfields.ConnectionType,
                Property: formfields.Property,
                InvoiceGroupingId: formfields.Division,
                ContractId: formfields.Contract,
                TentativeEndDate: formfields.TentativeEndDate,
                Status: formfields.Status
            };
            var params = JSON.stringify(jsonobj);
            DataServices.MeterMapping(params).then((response) => {
                if (response.data.Status == "Success") {
                    if(response.data.Result == '1'){
                        vm.refreshKey += 1;
                        vm.$parent.FetchData();
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Meter")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else if(response.data.Result == '0'){
                        vm.refreshKey += 1;
                        vm.$parent.FetchData();
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Meter")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                }
            });
        }
    }
}

</script>