<template>
  <section v-if="isInvoice == false && isPlanSubscription==false" class="listing">
    <!-- <span>Plan Details</span> -->
     <!-- <h5 class="text-center">Billing Frequency  {{selectedBilling }}</h5> -->
              <loader :is-visible="isLoading"></loader>
     
    <div class="theme-primary partition d-flex justify-content-between align-items-center p-2">
  <span class="p-name text-white">Billing Frequency: {{ selectedBilling }}</span> 
  <div class="d-flex align-items-center">
    <span class="p-actions" v-if="PlanMappingId == 0 || PlanMappingId == null">
      <a title="Update Plan Rate" href="javascript:;" @click="UpdatePlanRates()" class="p-action-btn text-white">
        <em class="fa fa-pencil"></em> <br>
        <span>{{ $t('Update Plan Rate') }}</span>
      </a>
    </span>
    <div class="p-actions ml-3" v-if="selectedBilling!='One Time'">
      <select v-model="selectedBilling" @change="handleBillingChange" class="form-control">
        <option v-for="option in FrequencyOptions" :key="option.value" :value="option.value">
          {{ option.name }}  
        </option>
      </select>
    </div>
  </div>
</div>

    <div class="table-responsive table-fix-header pt_maxheight700 mt-2">
      <table class="table table-bordered mt-0 planstable" v-if="plandetails.length">
        <thead class="thead-bg">
          <tr>
            <th>Plan</th>
            <th class="text-center" style="max-width:150px; min-width:150px; width:150px;">{{$t("Quantity")}}</th>
            <th class="text-center" style="max-width:250px; min-width:250px; width:250px;" v-if="PlanMappingId>0">{{$t("ChangedQuantity")}}</th>
            <th class="text-center" style="max-width:150px; min-width:150px; width:150px;">{{$t("UnitPrice")}}</th>
            <th class="text-center" style="max-width:150px; min-width:150px; width:150px;">{{$t("ItemTotal")}}</th>
          </tr>
        </thead>
        <tbody>
          <tr class="parentrows" v-for="(plan, planIndex) in plandetails" :key="planIndex">
            <td class="font-weight-medium">
              <a @click="toggleServices(planIndex)" id="aParentID"
                class="parentwithchildPlan d-inline-block parent-childrow">
                <em :class="showIcon ? 'fa fa-chevron-right' : 'fa fa-chevron-down'"> </em>
              </a>
              {{ plan.plan_name }}
            </td>
             <td>
              <div class="form-group mb-0" style="float: initial;" v-if="plan.is_rate_applicable_on_plan">
                <div class="input-group">
                  <div class="input-group-btn">
                    <button class="btn btn-default licdown" @click="PlandecrementQuantity(planIndex)">
                      <em class="fa fa-minus"></em>
                    </button>
                  </div>
                  <input type="number" class="form-control input-number noofpackage numberonly  text-right"
                    maxlength="6" v-model="plan.PlanUnit" @input="updateQuantity(planIndex, serviceIndex,'plan')"   min="1"/>
                  <div class="input-group-btn">
                    <button class="btn btn-default licup" @click="PlanincrementQuantity(planIndex,Rate(plan.planrate, plan.PlanUnit))">
                      <em class="fa fa-plus"></em>
                    </button>
                  </div>
                </div>
              </div>
            </td>
            <td class="text-center" v-if="PlanMappingId > 0">
              <div class="form-group mb-0" style="float: initial;" v-if="plan.is_rate_applicable_on_plan">
                
                <!-- Conditional label showing change status -->
                <div class="mb-1">
                  <span v-if="changed_unit === 0">0</span>
                  <span v-else-if="changed_unit > 0" class="text-success">
                    + {{ changed_unit }} <em class="fa fa-arrow-up"></em>
                  </span>
                  <span v-else class="text-danger">
                    {{ changed_unit }} <em class="fa fa-arrow-down"></em>
                  </span>
                </div>

              </div>
            </td>
            <td class="text-right" v-if="plan.is_rate_applicable_on_plan">
               <!-- {{ $n((Rate(plan.planrate, plan.PlanUnit,'plan')).toFixed(2), "currency")}}  -->
               {{ $n((Rate(plan.planrate, plan.PlanUnit, 'plan')).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode)) }}

            </td>
            <td class="text-right" v-if="!plan.is_rate_applicable_on_plan"></td>
             <td class="text-right" v-if="!plan.is_rate_applicable_on_plan">  {{ $n(calculateTotalItem(planIndex,'service'), "currency", $getLocaleByCurrency(CurrencyCode))}} </td> 
             <td class="text-right" v-if="plan.is_rate_applicable_on_plan">  {{ $n(calculateTotalItem(planIndex,'plan'), "currency", $getLocaleByCurrency(CurrencyCode))}}</td> 
             <!-- <td class="text-right" v-if="plan.is_rate_applicable_on_plan">{{ (Rate(plan.planrate, plan.PlanUnit,'plan') * plan.PlanUnit).toFixed(2) }}</td> -->
            <!-- <td class="text-right" v-if="!plan.is_rate_applicable_on_plan">{{ plan.finalRate }}</td>
            <td class="text-right" v-if="plan.is_rate_applicable_on_plan">{{ (plan.finalRate * plan.PlanUnit).toFixed(2) }}</td> -->
            
          </tr>

          <template v-for="(plan, planIndex) in plandetails">
            <tr class="childrows" v-for="(service, serviceIndex) in plan.services.filter(s => s.service_type_code !== 'Support')" :key="'service-' + service.service_id">
              <template v-if="isServicesVisible(planIndex)">
                <td class="childtext">{{ service.service_name }} ({{ service.service_type_name }})</td>
                <td>
                  <div class="form-group mb-0" style="float: initial;" v-if="!plan.is_rate_applicable_on_plan">
                    <div class="input-group">
                      <div class="input-group-btn">
                        <button class="btn btn-default licdown" @click="decrementQuantity(planIndex, serviceIndex)" :disabled="service.service_type_code === 'OneTime' || service.service_type_code === 'Activation'">
                          <em class="fa fa-minus"></em>
                        </button>
                      </div>
                      <input type="number" class="form-control input-number noofpackage numberonly  text-right"
                      min="1" maxlength="6" v-model="service.Unit" @input="updateQuantity(planIndex, serviceIndex,'service')" :disabled="service.service_type_code === 'OneTime' || service.service_type_code === 'Activation'" />
                      <div class="input-group-btn">
                        <button class="btn btn-default licup"
                          @click="incrementQuantity(planIndex, serviceIndex, Rate(service.ServiceRate, service.Unit,'services'))" :disabled="service.service_type_code === 'OneTime' || service.service_type_code === 'Activation'">
                          <em class="fa fa-plus"></em>
                        </button>
                      </div>
                    </div>
                  </div>
                </td>
              <td class="text-center"  v-if="PlanMappingId > 0">
                <div class="form-group mb-0" style="float: initial;" v-if="!plan.is_rate_applicable_on_plan">
                  
                  <!-- Conditional label showing change status -->
                  <div class="mb-1">
                    <span v-if="service.changed_unit === 0">0</span>
                    <span v-else-if="service.changed_unit > 0" class="text-success">
                      + {{ service.changed_unit }} <em class="fa fa-arrow-up"></em>
                    </span>
                    <span v-else class="text-danger">
                      {{ service.changed_unit }} <em class="fa fa-arrow-down"></em>
                    </span>
                  </div>

                </div>
              </td>
                <!-- <td class="text-right" v-if="service.ServiceRate != null">
                   <span v-if="getRateForUnit(service.Unit, service.ServiceRate) != null">
                    {{ $n(getRateForUnit(service.Unit, service.ServiceRate), "currency", $getLocaleByCurrency(CurrencyCode)) }}
                  </span>
                </td>
                 <td class="text-right" v-if="!plan.is_rate_applicable_on_plan">
                <span v-if="getRateForUnit(service.Unit, service.ServiceRate) != null">
                  {{ $n((getRateForUnit(service.Unit, service.ServiceRate) * service.Unit).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode)) }}
                </span>
              </td> -->
               <td class="text-right" v-if="service.ServiceRate != null && service.service_type_code !='UsageBased'">
                {{ $n(service.ServiceRate[0].rate, "currency", $getLocaleByCurrency(CurrencyCode)) }} 
                </td>
                  <td class="text-right" v-else>
                  <span v-if="getRateForUnit(service.Unit, service.ServiceRate) != null">
                    {{ $n(getRateForUnit(service.Unit, service.ServiceRate), "currency", $getLocaleByCurrency(CurrencyCode)) }}
                  </span>
                </td>
                <!-- <td class="text-right" v-if="!plan.is_rate_applicable_on_plan && PlanMappingId>0">{{ $n((Rate(service.ServiceRate, service.changed_unit,'services')*service.changed_unit).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}} </td> -->
                 <td class="text-right" v-if="!plan.is_rate_applicable_on_plan && service.service_type_code !='UsageBased'">{{ $n((service.ServiceRate[0].rate * service.Unit).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}}</td>
                  <td class="text-right" v-else-if="!plan.is_rate_applicable_on_plan && service.service_type_code === 'UsageBased'">
                    <span v-if="getRateForUnit(service.Unit, service.ServiceRate) != null">
                      {{ formatUsageBasedRate(service) }}
                    </span>
                  </td>

                <td colspan="2" v-if="plan.is_rate_applicable_on_plan"></td>
              </template>
            </tr>
          </template>

          <!-- <tr>
            <td>
                <span>{{discountType==0?'Discounts':'Flat Discount'}}</span><br>
              <a class="text-info" @click="toggleDiscountDropdown">
                <i class="fa fa-plus mr-1"></i>Add Discount
              </a>
              Discount Dropdown 
              <div v-if="discountDropdownVisible">
                <select v-model="selectedDiscount"  class="form-control" @change="updateTaxDiscount('discount')">
                  <option v-for="(discount, index) in discountList" :key="index" :value="discount.ADJUSTMENT_ID">
                    <p>{{ discount.ADJUSTMENT_NAME }} ,</p> <p>{{ discount.ADJUSTMENT_PERCENTAGE }}, </p> <div>{{ discount.DISCOUNT_TYPE==1?'Flat Discount':'Percentage' }}</div>
                  </option>
                </select>
              </div>
            </td>
            <td></td>
            <td></td>
            <td class="text-right">- {{ discountPrice }}({{discountType==0?discountPercentage+'%':discountType}})</td>
          </tr> -->
          <!-- Support Services -->
          <tr v-for="(service, index) in supportServices" :key="'Support-' + index">
            <td class="childtext">{{ service.service_name }} ({{ service.service_type_name }}<span v-if="service.ServiceRate && service.ServiceRate[0] && service.ServiceRate[0].billing_type === 'PERCENTAGE'"> - {{ service.ServiceRate[0].rate }}%</span>)</td>
            <td v-if="PlanMappingId>0"></td>
            <td></td>
            <td></td>
            <td class="text-right" v-if="!RateApplicableOnPlan">{{ $n(calculateSupportTotal(service), "currency", $getLocaleByCurrency(CurrencyCode)) }}</td>
            <td v-else></td>
          </tr>
          <tr class="subtotal" v-if="supportServices.length > 0 && !RateApplicableOnPlan">
             <td class="font-weight-medium">Subtotal with Support:</td>
             <td v-if="PlanMappingId>0"></td>
             <td></td>
             <td></td>
             <td class="text-right font-weight-medium">{{ $n((parseFloat(baseSubtotal) + parseFloat(supportTotal)).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode)) }}</td>
           </tr>
         <tr class="adddiscount">
            <td style="overflow: visible!important;">
              <a class="text-primary float-left font-weight-medium" @click="toggleDiscountDropdown">
                <em class=" mr-1 mt-2"></em>{{$t("SelectDiscount")}}
              </a>
              <div class="float-left ml-3" v-if="discountDropdownVisible">
                <multiselect v-model="selectedDiscounts" :options="discountList" :multiple="true"
                  :close-on-select="false" @change="updateMulitpleTaxDiscount('discount')" track-by="ADJUSTMENT_ID"
                  label="ADJUSTMENT_NAME" placeholder="Select Discounts"></multiselect>
              </div>
            </td>
            <td colspan="4" v-if="PlanMappingId>0"></td>
            <td colspan="3" v-else></td>

          </tr>
          <!-- Display Selected Discounts  -->
      <tr v-for="(discount, index) in selectedDiscounts" :key="index">
        <td class="childtext">{{ discount.ADJUSTMENT_NAME }} ({{ discount.ADJUSTMENT_PERCENTAGE}}{{discount.DISCOUNT_TYPE==null||discount.DISCOUNT_TYPE==0?'%':''}})</td>
        <td v-if="PlanMappingId>0"></td>
           <td></td>
        <td></td>
        <td class="text-right">
          - {{ $n(discount.calculatedDiscount, "currency", $getLocaleByCurrency(CurrencyCode))}} 
         
        </td>
      </tr> 
          <tr class="subtotal">
            <td class="font-weight-medium">{{$t("SubTotal")}}:</td>
            <td v-if="PlanMappingId>0"></td>
               <td></td>
            <td></td>
            <td class="text-right font-weight-medium">{{ $n(subtotal, "currency", $getLocaleByCurrency(CurrencyCode))}} </td>
          </tr>
          <!-- <tr>
            <td>
              Tax Exclusive<br />
              <a class="text-info" @click="toggleTaxDropdown" ><i class="fa fa-plus mr-1"></i>Add Tax</a>
              <div v-if="taxDropdownVisible">
                <select v-model="selectedTax"   class="form-control" @change="updateTaxDiscount('tax')">
                  <option v-for="(tax, index) in taxList" :key="index" :value="tax.ADJUSTMENT_ID">
                    {{ tax.ADJUSTMENT_NAME }} {{ tax.ADJUSTMENT_PERCENTAGE }}
                  </option>
                </select>
              </div>
            </td>
            <td></td>
            <td></td>
            <td class="text-right">+ {{ taxPrice }} ({{taxPercentage}}%)</td>
          </tr> -->

          <tr class="adddiscount">
            <td style="overflow: visible!important;"> 
              Tax Exclusive<br />
              <a class="text-primary float-left font-weight-medium" @click="toggleTaxDropdown">
                <em class=" mr-1 mt-2"></em>{{$t("SelectTax")}}
              </a>
              <div class="float-left ml-3" v-if="taxDropdownVisible">
                <multiselect v-model="selectedTaxes" :options="taxList" :multiple="true" :close-on-select="false"
                  @change="updateMulitpleTaxDiscount('tax')" track-by="ADJUSTMENT_ID" label="ADJUSTMENT_NAME"
                  placeholder="Select Taxes"></multiselect>
              </div>
            </td>
            <td colspan="4"></td>

          </tr>
          <!-- Display Selected Discounts  -->
      <tr v-for="(tax, taxindex) in selectedTaxes" :key="taxindex">
        <td class="childtext">{{ tax.ADJUSTMENT_NAME }} ({{ tax.ADJUSTMENT_PERCENTAGE  }}%)</td>
        <td v-if="PlanMappingId>0"></td>
         <td></td>
        <td></td>
        <td class="text-right">
           +  {{ $n(tax.CalculatedTax, "currency", $getLocaleByCurrency(CurrencyCode))}} 
         
        </td>
      </tr> 
          <tr class="grandtotal">
            <td class="font-weight-medium">{{$t("Total")}}</td>
            <td v-if="PlanMappingId>0"></td>
             <td></td>
            <td></td>
            <td class="text-right font-weight-medium">{{ $n(finalTotal, "currency", $getLocaleByCurrency(CurrencyCode))}} </td>
          </tr>
        </tbody>
      </table>
      <p v-else>{{ $t('NoPlansAvailable') }}</p>
    </div>
    <div class="row flex-row-reverse mb-2 mt-2">
                        <div class="col-lg-12 text-right">
                             <a href="javascript:;"  class=" btn btn-success mr-2" @click="generateInvoice">
         {{ $t('Next') }} <em class="fa fa-angle-double-right ml-2"></em></a>
        <!-- <a href="javascript:;" class=" btn btn-success mr-2" @click="generateInvoice" v-else><em class="fa fa-plus pr-2"></em>
         {{ $t('Subscribe') }}  </a> -->
        <!-- <a href="javascript:;" class="btn btn-info mr-2" title=""><i class="fa fa-send pr-2"></i>Send Invoice</a> -->
        <a href="javascript:;" class="btn btn-danger " title="" @click="backtoPlanList()">
          <em class="fa fa-close pr-2"></em> {{ $t('Cancel') }}</a>
                        </div>
     </div>
      <p style="font-size: 14px; color: #555; margin-top: 10px;" v-if="FrequencyNote">
        <strong> {{$t("BillingCycleNotes")}}</strong>
      </p>

      <div v-if="PlanMappingId>0 && (CompanyId==158446  || CompanyId==122872|| CompanyId==158770 || CompanyId==125897 || CompanyId== 125948)"><dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form></div>  
     <ServiceRatesList v-if="isServices" :Id="clientplanmappingid" :PlanId="PlanId" :ClientId="UserId" @refresh-plans="refreshPlans"></ServiceRatesList> 
  </section>
  <section v-else>  
    <InvoiceTemplate v-if="isTemplate" :details="templateDetails"  @backtoPlanDetails="backtoPlanDetails" :hitFrom="'Plans'"/>
    <ConfirmSubscription v-if="isPlanSubscription" :invoiceData="InvoiceSavedData" :descriptionData="descriptionData" :plandetails="plandetails" :UserId="UserId" :EstimatedAmount="EstimatedAmount" :EstimatePayableAmount="EstimatePayableAmount" :EstimatedAdjustmentAmount="EstimatedAdjustmentAmount" :PreviousCreditAmount="PreviousCreditAmount" :IsProrate="IsProrate" :PlanType="PlanType" :SelectedDate="SelectedDate" :selectedBilling="selectedBilling" :PreselectedBilling="PreselectedBilling" @backtoPlanList="backtoPlanList" :MultiplyFrequencyFactor="MultiplyFrequencyFactor" />
     <ConfirmAddSubscription v-if="isAddSubscription" :invoiceData="InvoiceSavedData" :descriptionData="descriptionData" :UserId="UserId" :estimated_total_amount="estimated_total_amount" :EstimatePayableAmount="EstimatePayableAmount" :IsProrate="IsProrate" :PlanType="PlanType" :selectedBilling="selectedBilling" @backtoPlanList="backtoPlanList" />
    
  </section>
</template>

<style>
.Planlisting .step-1,
.Planlisting .step-2 {
  width: 50px;
  height: 50px;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
}
</style>

<script>
import DataService from '../../../services/DataService';
import Multiselect from 'vue-multiselect';
import InvoiceTemplate from "../ClientDetails/InvoiceTemplate.vue";
import 'vue-multiselect/dist/vue-multiselect.min.css';
import ConfirmSubscription from "../ClientDetails/ConfirmPlanSubscription.vue";
import ServiceRatesList from "../ClientDetails/ServiceRatesList.vue";
import ConfirmAddSubscription from "../ClientDetails/ConfirmAddSubscription.vue";

export default {
  components: {
    Multiselect,
    InvoiceTemplate,
    ConfirmSubscription,
    ServiceRatesList,
    ConfirmAddSubscription
  },
  props: {
    plandetails: {
      type: Array,
      required: true,
    },
    UserId: {
      type: Number
    },
    flag: {
      type: String,
      required: false
    },
    Taxes:{
      type:Array,
      required: false
    },
    Discounts:{
      type:Array,
      required: false
    }

  },
  watch: {
    Taxes(newValue, oldValue) {
      this.taxDropdownVisible = true
      this.selectedTaxes = newValue;

    },
    Discounts(newValue, oldValue) {
      this.discountDropdownVisible = true
      this.selectedDiscounts = newValue;

    },
    plandetails(newVal, oldVal) {
      this.visibleServices = newVal.map((_, index) => index);
    }
  },
  async created() {
    var vm=this;
    vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    vm.isLoading = true;
    this.visibleServices = this.plandetails.map((_, index) => index);
    await this.GetddlDiscounts('Discounts');
    await this.GetddlTaxes('Tax');
    setTimeout(() => {
  (async () => {
    this.supportServices = [];
    this.plandetails.forEach(plan => {
      plan.services.forEach(service => {
         if (!Array.isArray(service.ServiceRate) || service.ServiceRate.length === 0) {
          service.ServiceRate = [{
            Freqrate: "0.00",
            Pre_rate: "0.00",
            billing_type: null,
            effective_end_date: null,
            effective_start_date: null,
            from: null,
            id: null,
            rate: "0.00",
            to: null
          }];
        }
        if (service.service_type_code === 'Support') {
          this.supportServices.push({...service});
          // copy to plandetails[0] if not already there
          const exists = this.plandetails[0].services.some(s => s.service_id === service.service_id);
          if (!exists) {
            this.plandetails[0].services.push({...service});
          }
        }
      });
    });
    vm.PlanMappingId = vm.plandetails[0].client_plan_mapping_id === undefined ? '' : vm.plandetails[0].client_plan_mapping_id;
    vm.PlanId= vm.plandetails[0].Plan_id;
    vm.RateApplicableOnPlan= vm.plandetails[0].is_rate_applicable_on_plan;
    if(vm.PlanMappingId >0  && (vm.CompanyId==158446 || vm.CompanyId==122872|| vm.CompanyId==158770 || vm.CompanyId==125897|| vm.CompanyId== 158850 || vm.CompanyId== 125948)){ 
      vm.SelectedDate = vm.$options.filters.formatDateYear(vm.plandetails[0].subscription_date);
      vm.BillDate= new Date(vm.plandetails[0].subscription_date);
      vm.FormSchema[0].Data[0].config.minDate=new Date(vm.plandetails[0].subscription_date);
      vm.FormSchema[0].Data[0].value = new Date(vm.plandetails[0].subscription_date);
    }else{
       vm.SelectedDate = '';
       vm.FormSchema[0].Data[0].value = '';
    }
      vm.PaymentType= vm.plandetails[0].payment_type;
      if (vm.PlanMappingId > 0) {
        vm.selectedBilling = vm.plandetails[0]?.selectedBilling || 'Monthly';
        vm.PreselectedBilling = vm.selectedBilling;
      } else {
        vm.selectedBilling = vm.plandetails[0]?.selectedFrequency || 'Monthly';
        vm.PreselectedBilling = vm.selectedBilling;
      }
      if(vm.PaymentType=='One Time'){
        vm.selectedBilling = 'One Time';
        vm.PreselectedBilling = 'One Time';
      }

      await vm.GetFrequencyDDlData();
      vm.handleBillingChange('OnLoad');

      //  Precompute rate for each service
      vm.plandetails.forEach(plan => {
        plan.services?.forEach(service => {
          const ratePerUnit = vm.getRateForUnit(service.Unit, service.ServiceRate);
          if (ratePerUnit != null) {
            service.calculatedRate = (ratePerUnit * service.Unit).toFixed(2); // store numeric value
          } else {
            service.calculatedRate = null;
          }
        });
      });
    })();
  }, 500);

  vm.CompanyId = vm.GetUserInfo.CompanyId;
  vm.isLoading = false;
},

  data() {
    return {
      visibleServices: [],
      discountList: [],
      taxList: [],
      discountDropdownVisible: false,
      selectedDiscount: null,
      taxDropdownVisible: false,
      selectedTax: null,
      discountPercentage: 0,
      taxPercentage: 0,
      discountType: 0,
      showIcon: false,
      invoiceTemplate: null,
      taxPrice: 0,
      discountPrice: 0,
      selectedDiscounts: [],
      selectedTaxes: [],
      isTemplate: false,
      templateDetails: {},
      isInvoice: false,
      selectedBilling:null,
      totalAmount: 0,
      changed_unit:0,
      PlanMappingId: '',
      isLoading: false,
      isPlanSubscription: false,
      InvoiceSavedData: [],
      EstimatedAmount:'',
      estimated_total_amount: '',
      EstimatedAdjustmentAmount: '',
      EstimatePayableAmount: '',
      CurrencyCode: 'PKR',
      PreviousCreditAmount: '',
       buttons: [],
       SelectedDate: '',
       CompanyId: '',
       FrequencyNote:'',
       FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('Selected Date'),
                            name: "SelectedDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                onChange: this.InvoiceComponentFieldsValues,
                               
                                // maxDate: false
                            },
                            validationRules: { "required": true },
                            disabled: false,
                        },
                    ]
                },
                
            ],
            FrequencyOptions:[],
            MultiplyFrequencyFactor: 1,
            Discount: 0,
            PreselectedBilling: '',
            descriptionData:[],
            PaymentType: '',
            isServices:false,
            clientplanmappingid:0,
            PlanId:0,
            BillDate:'',
            supportServices: [],
            RateApplicableOnPlan: '',
            IsProrate: false,
            PlanType: 'Prepaid',
            isAddSubscription: false
    };
  },
  computed: {
    baseSubtotal() {
      let total = 0;
      if (!this.plandetails[0].is_rate_applicable_on_plan ||this.plandetails[0].is_rate_applicable_on_plan === null) {
            total = this.plandetails.reduce((total, plan) => {
                return total + this.calculatePlanTotal(plan,'service');
            }, 0).toFixed(2);
        } else {
          total = this.plandetails.reduce((total, plan) => {
                return total + this.calculatePlanTotal(plan,'plan');
            }, 0).toFixed(2);
        }
      return parseFloat(total).toFixed(2);
    },
    subtotal() {
      let total = parseFloat(this.baseSubtotal);
      if (this.supportServices.length > 0) {
        total += parseFloat(this.supportTotal);
      }
      this.totalAmount = total.toFixed(2);
      //if (!this.plandetails[0].is_rate_applicable_on_plan || this.plandetails[0].is_rate_applicable_on_plan === null) {

      //   this.plandetails.forEach((plan) => {
      //     if (plan.services && plan.services.length > 0) {
      //       plan.services.forEach((service) => {
      //         if (service.ServiceRate != null) {
      //           const annualPrice = this.calculateAnnualPrice(service.Unit, service.ServiceRate[0]?.rate || 0);
      //           total += annualPrice;
      //           plan.BaseRate = total.toFixed(2);
      //         }
      //         else {
      //           total += 0;
      //           plan.BaseRate = total.toFixed(2);
      //         }
      //       });
      //     } else {
      //       total += this.calculateAnnualPrice(plan.PlanUnit, plan.BaseRate || 0);
      //     }
      //   })
      // }
      // else {
      //   total = this.plandetails[0].BaseRate * this.plandetails[0].PlanUnit;
      // }
      // Apply single discounts if applicable
      // if (this.discountPercentage) {
      //   if (this.discountType === 0) {
      //     const discountValue = (total * this.discountPercentage) / 100;
      //     this.discountPrice = discountValue.toFixed(2);
      //     total -= discountValue;
      //   } else {
      //     total -= this.discountPercentage;
      //   }
      // }

      // Apply multiple discounts if applicable
      if (this.selectedDiscounts && Array.isArray(this.selectedDiscounts)) {
        this.discountPrice = 0;

        this.selectedDiscounts = this.selectedDiscounts.map((discount) => {
          let discountValue = 0;
          if ((discount.DISCOUNT_TYPE ?? 0) === 0) {
            discountValue = (total * discount.ADJUSTMENT_PERCENTAGE) / 100;
          } else {
            discountValue = discount.ADJUSTMENT_PERCENTAGE;
          }

          total -= discountValue;
          this.discountPrice += discountValue;
          return {
            ...discount,
            calculatedDiscount: discountValue.toFixed(2),
          };
        });

        this.discountPrice = this.discountPrice.toFixed(2);
      }


      return parseFloat(total).toFixed(2);
    },

   supportTotal() {
     return this.supportServices.reduce((total, service) => {
       return total + parseFloat(this.calculateSupportTotal(service));
     }, 0);
   },

   finalTotal() {
      let total = parseFloat(this.subtotal) || 0;

      this.taxPrice = 0;

      if (this.selectedTaxes && Array.isArray(this.selectedTaxes)) {
        this.selectedTaxes = this.selectedTaxes.map((tax) => {
          let taxValue = (total * tax.ADJUSTMENT_PERCENTAGE) / 100;
          this.taxPrice += taxValue;

          return {
            ...tax,
            CalculatedTax: taxValue.toFixed(2)
          };
        });

        total += this.taxPrice;
      }

      return total.toFixed(2);
    },
  },

  methods: {
    toggleServices(planIndex) {
      if (this.visibleServices.includes(planIndex)) {
        this.visibleServices = this.visibleServices.filter((index) => index !== planIndex);
        this.showIcon = true;
      } else {
        this.visibleServices.push(planIndex);
        this.showIcon = false;
      }
    },
    isServicesVisible(planIndex) {
      return this.visibleServices.includes(planIndex);
