   updateQuantity(planIndex, serviceIndex,type) {
  var vm = this;
  if (type == 'service') {
    let quantity = String(this.plandetails[planIndex].services[serviceIndex].Unit || "");

    //  limit to 5 digits
    if (quantity.length > 5) {
      quantity = quantity.slice(0, 5);
      this.plandetails[planIndex].services[serviceIndex].Unit = parseInt(quantity, 10);
    }

    const ExistingQuanity = this.plandetails[planIndex].services[serviceIndex].existing_units;
    if (!isNaN(quantity) && parseInt(quantity, 10) > 0) {
      this.plandetails[planIndex].services[serviceIndex].Unit = parseInt(quantity, 10);
      this.plandetails[planIndex].services[serviceIndex].changed_unit = parseInt(quantity - ExistingQuanity);
    } else {
      this.plandetails[planIndex].services[serviceIndex].Unit = 0;
      this.plandetails[planIndex].services[serviceIndex].changed_unit = parseInt(quantity - ExistingQuanity);
    }
  }
  else {
    let quantity = String(this.plandetails[planIndex].PlanUnit || "");

    //  limit to 5 digits
    if (quantity.length > 5) {
      quantity = quantity.slice(0, 5);
      this.plandetails[planIndex].PlanUnit = parseInt(quantity, 10);
    }

    const ExistingQuanity = this.plandetails[planIndex].existing_units;
    if (!isNaN(quantity) && parseInt(quantity, 10) > 0) {
      this.plandetails[planIndex].PlanUnit = parseInt(quantity, 10);
      this.plandetails[planIndex].planrate[0].Unit = parseInt(quantity, 10);
      vm.changed_unit = parseInt(quantity - ExistingQuanity);
    } else {
      this.plandetails[planIndex].PlanUnit = 0;
      this.plandetails[planIndex].planrate[0].Unit = parseInt(quantity, 10);
      vm.changed_unit = parseInt(quantity - ExistingQuanity);
    }
  }
},
