    },
    calculateAnnualPrice(units, rate) {
      let annualRate;
      const timeBasedDiscount = this.plandetails[0].timebased_discount;
      if (this.selectedBilling == 'annually') {
        const subscriptionDiscount = rate - (rate * timeBasedDiscount / 100);
        annualRate = (units * subscriptionDiscount * 12);
      }
      else {
        annualRate = (units * rate) ;
      }


      return annualRate;
    },

    decrementQuantity(planIndex, serviceIndex) {
      const service = this.plandetails[planIndex].services[serviceIndex];
      if (service.Unit > 0) {
        service.Unit -= 1;
        service.changed_unit -=1;
