calculateSupportTotal(service) {
  if (service.ServiceRate != null && service.ServiceRate[0]) {
    const rateObj = service.ServiceRate[0];
    if (rateObj.billing_type === 'PERCENTAGE') {
      return (parseFloat(this.calculateTotalItem(0,'service')) * rateObj.rate / 100 * service.Unit).toFixed(2);
    } else {
      return (rateObj.rate * service.Unit).toFixed(2);
    }
  } else if (service.service_type_code === 'UsageBased') {
    const rate = this.getRateForUnit(service.Unit, service.ServiceRate);
    return rate ? (rate * service.Unit).toFixed(2) : 0;
  }
  return 0;
