 formatUsageBasedRate(service) {
    const rate = this.getRateForUnit(service.Unit, service.ServiceRate);
    if (rate != null) {
      service.newCaluclateRate = rate * service.Unit; // store the calculated value
      return this.$n(service.newCaluclateRate.toFixed(2), "currency", this.$getLocaleByCurrency(this.CurrencyCode));
    }
    return null;
  },

 getRateForUnit(unit, rateList) {
  var vm =this;
        if (!rateList || !Array.isArray(rateList)) return null;

        // find correct slab based on unit
        const slab = rateList.find(r => {
          return unit >= r.from && (r.to === null || unit <= r.to);
        });

        return slab ? (slab.Pre_rate - slab.Pre_rate * vm.Discount/100)* vm.MultiplyFrequencyFactor  : null;
      },

   Rate(rates, units, type) {
    var vm =this;
        let rate = 0;
        if (rates != null) {
          const absUnits = Math.abs(units);

          if (type === 'services') {
            for (const data of rates) {
              if ((absUnits >= data.from && (data.to === null || absUnits <= data.to))) {
                rate = data.Pre_rate;
                break;
              }
            }
          } else {
            if (rates[0].Rates) {
              for (const data of rates[0].Rates) {
                if ((absUnits >= data.from && (data.to === null || absUnits <= data.to))) {
                  rate = data.Pre_rate;
                  break;
                }
              }
            }
          }
        }
        const adjustedRate =(rate - rate * vm.Discount/100)*vm.MultiplyFrequencyFactor  //rate * vm.MultiplyFrequencyFactor * (1 - (vm.Discount || 0) / 100);
        return adjustedRate;
      },
      

    PlanincrementQuantity(planIndex) {
      var vm=this;
      const plan = this.plandetails[planIndex];
      // if (plan.PlanUnit < 999) { // Ensure a reasonable maximum limit
        plan.PlanUnit = parseInt(plan.PlanUnit, 10) + 1;
        plan.planrate[0].Unit= parseInt(plan.planrate[0].Unit, 10) + 1;
        vm.changed_unit =parseInt(vm.changed_unit, 10) + 1;
      // }
    },

    PlandecrementQuantity(planIndex) {
      var vm= this;
      const plan = this.plandetails[planIndex];
      if (plan.PlanUnit > 0) { 
        plan.PlanUnit -= 1;
        plan.planrate[0].Unit-= 1;
        vm.changed_unit -=1;
      }
     
    },
    async previewInvoice() {
      const invoiceData = {
        plans: this.plandetails.map(plan => ({

          Plan_id: plan.Plan_id,
          timebased_discount: plan.timebased_discount,
          discount_id: this.selectedDiscount,
          tax_id: this.selectedTax,
          services: plan.services.map(service => ({
            service_id: service.service_id,
            Unit: service.Unit
          }))
        })),

      };
      const jsonString = JSON.stringify(invoiceData);
      const params = `json=${jsonString}&clientId=${this.UserId}&type=Preview&billingCycle=${this.selectedBilling}`;
      try {
        const response = await DataService.GenrateInvoice(params);
        const data = response.data.result.data
        this.$emit('invoiceTemplatedata', data);

      } catch (error) {
        console.error('Error fetching invoice template:', error);
      }
    },
    backtoPlanList() {
      if (this.flag == 'isEditingFromPlanListing') {

        this.$parent.isPlanListing = true;
        this.$parent.isPlanDetails = false;
        this.taxDropdownVisible = false;
        this.discountDropdownVisible = false;
        this.$parent.FetchData();

      } else {
        this.$emit('backtoPlanList');
      }

    },
      async GetFrequencyDDlData() {
            var vm = this;
            let url = `moduleName=Company&type=Plan_Frequency&refId=${vm.plandetails[0].Plan_id}`;
            await DataService.GetDDLData(url).then(response => {
                if (response.data != null && response.data.DATA.length > 0) {
                    vm.FrequencyOptions = response.data.DATA;
                }
            })
        },
       handleBillingChange(type) {
  const vm = this;
  const billing = vm.selectedBilling.toLowerCase();
  const matched = vm.FrequencyOptions.find(option =>
    option.value && option.value.toLowerCase() === billing
  );
  // Set the discount from frequency option
  vm.Discount = matched ? parseFloat(matched.first_value) || 0 : 0;
  // Determine frequency factor
  let factor = 1;
  switch (billing) {
    case 'monthly':
      factor = 1;
      break;
    case 'weekly':
      factor = 1 / 4;
      break;
    case 'bi-weekly':
      factor = 1 / 2;
      break;
    case 'quarterly':
      factor = 3;
      break;
    case 'semi-annually':
      factor = 6;
      break;
    case 'annualy':
      factor = 12;
      break;
    case 'yearly':
      factor = 12;
      break;
    case '4 month basis':
      factor = 4;
      break;
    default:
      factor = 1;
  }
  // Set the frequency factor
  vm.MultiplyFrequencyFactor = factor;
  //  Recalculate and assign ServiceRate for each service
  if (!this.plandetails[0].is_rate_applicable_on_plan ||this.plandetails[0].is_rate_applicable_on_plan === null) {
  vm.plandetails.forEach(plan => {
    plan.services.forEach((service, Index) => {
      // Optional: use service.RateSlabs if you store rates separately
      const rateSource = Array.isArray(service.ServiceRate) ? service.ServiceRate : (service.RateSlabs || []);
     rateSource.forEach(function (rateSlab, i) {
      if (service.service_type_code === 'Activation' || service.service_type_code === 'OneTime') {
        // Assign numeric value with 2 decimal precision
        rateSlab.rate = parseFloat(service.ServiceRate[i].Pre_rate).toFixed(2);
      } else if (service.service_type_code !== 'Support' || (rateSlab.billing_type == 'FLAT' ||rateSlab.billing_type=='')) {
        if ((service.Unit >= rateSlab.from && (rateSlab.to === null || service.Unit <= rateSlab.to)) || vm.PlanMappingId > 0) {
          rateSlab.rate = parseFloat(vm.Rate(rateSource, service.Unit, 'services')).toFixed(2);
        }
      }

      if ((service.service_type_code !== 'Support' || (rateSlab.billing_type == 'FLAT' ||rateSlab.billing_type=='')) && type === 'OnLoad') {
        if (vm.PlanMappingId > 0) {
          let discounted = rateSlab.Pre_rate - (rateSlab.Pre_rate * vm.Discount / 100);
          rateSlab.Freqrate = (discounted * vm.MultiplyFrequencyFactor).toFixed(2);
          rateSlab.rate = rateSlab.Freqrate;
        } else {
          rateSlab.Freqrate = parseFloat(vm.Rate(rateSource, service.Unit, 'services')).toFixed(2);
        }
      }
    });
 
    });
  });
}else{
  vm.plandetails.forEach(plan => {
  if (
    plan.is_rate_applicable_on_plan &&
    Array.isArray(plan.planrate) &&
    plan.planrate.length > 0 &&
    Array.isArray(plan.planrate[0].Rates)
  ) {
    const rateSource = plan.planrate; //  use full planrate array, not planrate[0].Rates

    const newRate = vm.Rate(rateSource, plan.PlanUnit, 'plan');
    plan.BaseRate=Number(newRate.toFixed(2));
    plan.planrate[0].Rates[0].rate = Number(newRate.toFixed(2));
     if(type== 'OnLoad'){
        plan.Freqrate = Number(newRate.toFixed(2));
      }
  }
});

}
if(vm.PlanMappingId &&vm.PreselectedBilling != vm.selectedBilling){
  vm.FrequencyNote=true;
}else{
 vm.FrequencyNote=false;
    this.supportServices = [];
    this.plandetails.forEach(plan => {
      plan.services.forEach(service => {
        if (service.service_type_code === 'Support') {
          this.supportServices.push({...service});
          // copy to plandetails[0] if not already there
          const exists = this.plandetails[0].services.some(s => s.service_id === service.service_id);
          if (!exists) {
            this.plandetails[0].services.push({...service});
          }
        }
      });
    });
}
},

    backtoPlanDetails(){
      this.backtoPlanList();
    //this.$emit('backtoPlanList');
    },
     async refreshPlans() {
            try {
              var vm =this;
                this.isLoading = true;
                const selectedPlans = [];
                var type='recurring';
                if(vm.selectedBilling=='One Time'){
                  type='One Time'
                }else{
                  type='recurring';
                }
                const param = `client_id=${this.UserId}&type=${type}`
                const response = await DataService.LoadPlansMappingSubscription(param);
                if (response.data.status === "Success" && response.data.code === "1") {
                  vm.plandetails.splice(0, vm.plandetails.length); // Clear array reactively
                   const plans = response.data.result;
                    plans.forEach(plan => {
                        plan.services = plan.services ? JSON.parse(`[${plan.services}]`) : [];
                        plan.planrate=plan.planrate ? JSON.parse(`[${plan.planrate}]`) : [];
                        plan.BaseRate = plan.BaseRate? plan.BaseRate.toFixed(2) :0
                         plan.timebased_discount = this.discount;
                         plan.is_applicable_on_plan=plan.is_rate_applicable_on_plan==1?this.$t("PlanBased"):this.$t("ServiceBased");
                         plan.plan_type= plan.plan_type == 1 ? this.$t("Prepaid") : this.$t("Postpaid");
                         plan.selectedFrequency = 'Monthly';
                        plan.calculatedRate = plan.BaseRate;
                        });
                    this.PlanList = plans;
                    const selectedPlan = this.PlanList.find(item => item.Plan_id === vm.PlanId);
                    if (selectedPlan) {
                        selectedPlans.push(selectedPlan);
                        vm.plandetails.splice(0, vm.plandetails.length, ...selectedPlans); // Assign reactively
                        debugger
                        this.plandetails.forEach(plan => {
                        plan.services.forEach(service => {
                          if (!Array.isArray(service.ServiceRate) || service.ServiceRate.length === 0) {
                            service.ServiceRate = [{
                              Freqrate: "0.00",
                              Pre_rate: "0.00",
                              billing_type: null,
                              effective_end_date: null,
                              effective_start_date: null,
                              from: null,
                              id: null,
                              rate: "0.00",
                              to: null
                            }];
                          }
                        });
                      });
                        vm.handleBillingChange('OnLoad');
                        // Emit the updated plandetails to parent
                      this.$emit('update-plandetails', vm.plandetails);
                    }
                    this.isLoading = false;
                }
                else{
                    this.PlanList=[]
                    this.isLoading = false;
                }
            } catch (error) {
                console.error("Error loading plans:", error);
            }
        },
    onSubmit(){},
   InvoiceComponentFieldsValues(x,y,z){
    var vm =this;
    vm.SelectedDate= vm.$options.filters.formatDateYear(x);
   },
   UpdatePlanRates(){
    var vm =this; 
   this.isTemplate = false;
  this.isPlanSubscription = false;
  this.isAddSubscription=false;
  this.isInvoice = false;
  this.isServices = true;
   }
  }
}
  </script>
