<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <button type="button" class="close" @click=closepopup()>
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 name="header" class="modal-title">
                                View Rate Schedule History 
                            </h4>
                        </div>
                        <div class="modal-body">
                            <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
                :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="id"
                :ListData="historyList" :HeaderData="Headers" :callbackfunction="FetchData"
                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
                :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod" :isShowHeaderText="true"
                v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('RateSchedulede')"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                </tg-list>


                            <!-- <div class="listing">
                                <div class="table-responsive">
                                    <table class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                            <th class="text-center">Service Name</th>
                                            <th class="text-center">Billing Type</th>
                                            <th class="text-center">Billing Interval</th>
                                            <th class="text-center">From</th>
                                            <th class="text-center">To</th>
                                            <th class="text-center">Rate</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="historyList.length === 0">
                                                <td class="text-center text-danger no-record" colspan="8">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="item in historyList" :key="item.id">
                                                <td class="text-center">{{ item.service_name}}</td>
                                            <td class="text-center" >{{ item.billing_type }}</td>
                                            <td class="text-center" >{{ item.billing_interval }}</td>
                                            <td class="text-right">{{ item.from }}</td>
                                            <td class="text-right">{{ item.to ||'Infinity'}}</td>
                                            <td class="text-right">{{ item.rate }}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
     
    </section>
  </template>
  <script>
  import DataServices from "../../../services/DataService";
  export default {
    props: {
        Id:{
            type:Number,
            required:true
        }
    },
    data() {
      return {
        historyList:[],
        TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            noRecords: false,
            Headers: [
           
                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Plan Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "service_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Service Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "billing_type",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Billing Type",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "billing_interval",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Billing Interval",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "from",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "From",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "to",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "To",
                    DISPLAY_ORDER: 5,
                },
                {
                    COLUMN_NAME: "rate",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Rate",
                    DISPLAY_ORDER: 6,
                },
                {
                    COLUMN_NAME: "modified_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified At",
                    DISPLAY_ORDER: 7,
                },
                {
                    COLUMN_NAME: "modified_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified By",
                    DISPLAY_ORDER: 8,
                },
            ],
            listActions: [],
             LegendArray: [
                {
                class: "legend-Progress colorboxsquare",
                text: this.$t('Active'),
                },
                {
                class: "legend-ovrdue colorboxsquare",
                  text: this.$t('Inactive'),
                },
            ],
      }
    },
    created: async function () {
      var vm = this;
       vm.FetchData();
    },
    methods: {
         async FetchData() {
            try {
                var params = `clientPlanMappingId=${this.Id}&pageSize=${this.pageSize}&pageNum=${this.pageNum}&sortBy=&sortExp=`;
                await DataServices.ViewRateSchedulePlanServicesHistory(params).then((response) => {
                    if(response.data.Result.DATA==null){
                        if(response.data.Result.DATA[0].is_rate_applicable_on_plan){
                            this.Headers.forEach((column) => {
                                column.settings = null;
                                if (column.COLUMN_NAME == 'service_name') {
                                    column.VISIBLE=false
                                
                                } 
                                if(column.COLUMN_NAME=='billing_type'){
                                    column.VISIBLE=false
                                }
                            })

                        }
                    }
                    if (response.data.Result.DATA.length > 0) {
                    this.historyList = response?.data?.Result?.DATA || [];
                     this.historyList.forEach((row)=>{
                          // debugger;
                            row.additionalClass = '';
                            if (row.status_id ==1002) {
                               row.additionalClass='danger-bar odd';
                             }else{
                                row.additionalClass='low-bar';
                             }
                            
                        });      
                    this.TotalRecords = response.data.Result.DATA[0].TOTAL_RECORDS;
                    this.TotalPages = Math.ceil(this.TotalRecords / this.pageSize);
                    this.CurrentPage = this.pageNum;
                    this.noRecord = false;
                    }else {
                        this.noRecord = true;
                        this.TotalRecords = 0
                        this.historyList = [];
                    }
                });
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },
        closepopup(){
            this.$parent.isViewHistory=false
        },
        actionButtonClick(callbackfunction, item) {
        },
        renderActions(row) {},
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
    }
    
  };
  </script>