<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title" v-if="IsEdit==false">{{ $t('AddMeterReading') }}</h4>
                        <h4 class="modal-title" v-else>{{ $t('UpdateMeterReading') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="OnSubmit">

                            <!-- <template v-slot:tgslot-Meter="{ data }">
                                <div class="input-group">
                                    <MutliSelectDropDown :options="filteredOptions" :value="selectedProjects"
                                        placeholder="Select meter" :searchable="true" searchPlaceholder="Search meter"
                                        :maxItems="3" :selectAllEnabled="true" allText="Select All"
                                        @input="updateSelectedProjects" :Formtype="FormType" />
                                </div>
                            </template> -->

                        </dynamic-form>

                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    props: {
        ClientId: {
            type: Number
        },
        EditDetails: {
            type: Object,
            required: true
        }
    },

    data() {
        return {
            isLoading: false,
            ListingData: [],
            noRecord: false,
            IsEdit: false,
            FormSchema: [{
                layoutType: "four",
                Data: [
                    {
                        astype: "SelectField",
                        label: this.$t('MeterType'),
                        name: "Meter_type",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.getMeters
                        },
                        placeholder: this.$t('Meter'),
                        validationRules: { "required": true, max: 100 },
                        disabled: false,
                        visibility: true
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('MeterSerialNumber'),
                        name: "Meter",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.getPreviousReadingDate
                        },
                        placeholder: this.$t('Meter'),
                        validationRules: { "required": true, max: 100 },
                        disabled: false,
                    },
                    {
                        astype: "DateField",
                        label: this.$t('PreviousReadingDate'),
                        name: "Previous_Reading_Date",
                        value: '',
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            minDate: '',
                            onChange: this.setReadDate
                        },
                        validationRules: { "required": false },
                        disabled: false,
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('PreviousReading'),
                        name: "Pervious_Reading",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.calculateConsumption

                        },
                        placeholder: '',
                        validationRules: { "required": false, max: 100 },
                        disabled: false,
                    },



                ]
            },
            {

                layoutType: "four",
                Data: [

                    {
                        astype: "DateField",
                        label: this.$t('ReadDate'),
                        name: "Read_Date",
                        value: '',
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            // onChange: this.InvoiceComponentFieldsValues,
                            // maxDate: false
                            minDate: ''
                        },
                        validationRules: { "required": true },
                        disabled: false,
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Reading'),
                        name: "Reading",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.calculateConsumption
                        },
                        placeholder: '',
                        validationRules: { "required": true, max: 100 },
                        disabled: false,
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Consumption'),
                        name: "Consumption",
                        value: "0",
                        config: {
                            options: [],
                            // onChange: ,
                        },
                        placeholder: '',
                        validationRules: { "required": false, max: 100 },
                        disabled: true,
                        visibility: true,
                    },

                    {
                        astype: "NumericField",
                        label: this.$t('PressureValue'),
                        name: "Pressure_Value",
                        value: '',
                        placeholder: "",
                        config: {
                            // onChange:
                        },
                        validationRules: { "required": true },
                        visibility: false,
                        disabled: false,
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('GCV'),
                        name: "GCV",
                        value: '',
                        placeholder: "",
                        userGuide: "gross calorific value",
                        config: {

                        },
                        visibility: false,
                        validationRules: { "required": true },
                        disabled: false,
                    }
                ]
            },
            {
                layoutType: "single",
                Data: [
                    {
                        astype: "FileField",
                        label: this.$t('Image'),
                        name: "Attachment",
                        placeholder: "",
                        validationRules: { "required": false },
                        FieldIndex: 0,
                        config: {
                            maxSize: 5,
                            showSelectedFiles: [],
                            accept: [
                                {
                                    fileType: 'jpg',
                                    iconClass: 'fa-file-image-o'
                                },
                                {
                                    fileType: 'jpeg',
                                    iconClass: 'fa-file-image-o'
                                },
                                {
                                    fileType: 'png',
                                    iconClass: 'fa-file-image-o'
                                }
                            ],
                            onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                            onChange: this.GetBase64String,
                            showAddIcon: false,
                            onAddButtonClick: '',
                            multiple: false,
                            showSelectedFiles: []
                        },
                    }
                ]
            }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh pr-2'></i>Reset",
                    onClick: this.resetForm
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
            ]
        };
    },
    async created() {
        var vm = this;
        vm.isLoading=true;
        await vm.getMeterTypes();
        // Now continue with logic that depends on getMeterTypes being done
        if (vm.EditDetails != undefined && vm.EditDetails != null) {
            vm.BindMeterDetail();
            vm.getMeters(null, vm.FormSchema[0].Data[0]);
            vm.$refs.dynamicform.updateProperty("Meter", "value", vm.EditDetails.related_info_id);
            vm.FormSchema[0].Data[2].disabled = true;
            vm.FormSchema[0].Data[3].disabled = true;
            vm.IsEdit=true;
        }else{
            vm.IsEdit=false;
        }
        vm.isLoading=false;
    },
    methods: {
        BindMeterDetail: function () {
            var vm = this;
            if (vm.EditDetails.file_names != null && vm.EditDetails.file_names != undefined) {
                const url = vm.EditDetails.file_names;
                const parts = url.split('/');
                const fileName = parts.pop();
                var OBJ = {
                    attachmentName: fileName,
                    attachmentPath: vm.EditDetails.file_names
                };
                vm.FormSchema[2].Data[0].config.showSelectedFiles.push(OBJ);
            }
            vm.$refs.dynamicform.updateProperty("Previous_Reading_Date", "value", vm.EditDetails.PreviousReadingDate);
            vm.$refs.dynamicform.updateProperty("Pervious_Reading", "value", vm.EditDetails.OldReading);
            vm.$refs.dynamicform.updateProperty("Read_Date", "value", vm.EditDetails.ReadingDate);
            vm.$refs.dynamicform.updateProperty("Reading", "value", vm.EditDetails.CurrentReading);
            vm.$refs.dynamicform.updateProperty("Consumption", "value", vm.EditDetails.Consumption);
            vm.$refs.dynamicform.updateProperty("Meter_type", "value", vm.EditDetails.meter_id);
        },

        Close() {
            this.$parent.IsAddMeterReading = false;
        },

        setReadDate(date, data) {
            var vm = this;
            const previousDate = new Date(date);
            vm.FormSchema[1].Data[0].config.minDate = previousDate;
        },

        resetForm() {
            this.FormSchema[0].Data[0].value = ''
            this.FormSchema[0].Data[1].value = ''
            this.FormSchema[0].Data[2].value = ''
            this.FormSchema[0].Data[3].value = ''
            this.FormSchema[1].Data[0].value = ''
            this.FormSchema[1].Data[1].value = ''
            this.FormSchema[1].Data[2].value = '0'
            this.FormSchema[1].Data[3].value = ''
            this.$refs.dynamicform.validateForm.reset();
        },
        onDeleteSelectedFiles: function (item) {
            this.$delete(this.FormSchema[4].Data[0].config, 'showSelectedFiles');
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },

        async getMeterTypes() {
            debugger
            const vm = this;
            try {
                let url = `is_mapped=2`

                const response = await DataService.GetMeterTypes(url);
                if (response?.data) {
                    const responseData = JSON.parse(response.data.result).data;

                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {

                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.meter_id,
                                name: item.meter_name
                            });

                        });
                        vm.$refs.dynamicform.UpdateKeyValue();

                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Meter types:', error);
            } finally {
                //
            }
        },

        async getMeters(event, data) {
            debugger
            var vm = this;
            vm.FormSchema[0].Data[1].value = '';
            vm.FormSchema[0].Data[1].config.options = [];
            let obj = data.config.options.find(x => x.value == data.value);

            if (obj.name == 'Gas') {

                vm.FormSchema[1].Data[3].visibility = true;
                vm.FormSchema[1].Data[4].visibility = true;

            }
            if (obj.name == 'Electric') {

                vm.FormSchema[1].Data[3].visibility = false;
                vm.FormSchema[1].Data[4].visibility = false;

            }

            if (obj.name == 'Water') {
                vm.FormSchema[1].Data[3].visibility = false;
                vm.FormSchema[1].Data[4].visibility = false;
            }

            try {
                // let Url = `type=is_meter&TypeValue=1&type2=utility_type_id&TypeValue2=${data.value}`;
                // const response = await DataService.GetMeterNameDDlData(Url);
                let Url = `clientId=${vm.ClientId}&meterTypeId=${data.value}`
                const response = await DataService.GetMeters(Url);

                const responseData = JSON.parse(response.data.result).data;
                if (Array.isArray(responseData) && responseData.length > 0) {

                    responseData.forEach(item => {

                        vm.FormSchema[0].Data[1].config.options.push({
                            value: item.value,
                            name: item.name
                        });
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                } else {
                    vm.FormSchema[0].Data[1].config.options = [];
                    console.warn('No data found or data is not an array.');
                }
            } catch (error) {
                console.error('Error fetching meter name data:', error);
            }
        },

        // async getMeters() {
        //     debugger;
        //     const vm = this;
        //     let url = `clientId=${this.ClientId}`
        //     try {
        //         const response = await DataService.GetMeters(url);
        //         if (response?.data) {
        //             const responseData = JSON.parse(response.data.result).data;
        //             if (Array.isArray(responseData) && responseData.length > 0) {
        //                 responseData.map(item => {
        //                     (vm.FormSchema)[0].Data[1].config.options.push({
        //                         value: item.value,
        //                         name: item.name
        //                     });
        //                     vm.$refs.dynamicform.UpdateKeyValue();
        //                 });
        //             } else {
        //                 console.warn('No valid data found in the response');
        //             }
        //         } else {
        //             console.error('Invalid response structure:', response);
        //         }
        //     } catch (error) {
        //         console.error('Error fetching Customer type:', error);
        //     } finally {
        //         //
        //     }
        // },

        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        async getPreviousReadingDate(event, item) {
            const vm = this;
            let url = `meterId=${item.value}`
            try {
                const response = await DataService.GetPreviousReadingDate(url);
                if (response?.data) {
                    if (response.data.result != '') {
                        vm.FormSchema[0].Data[2].value = response.data.result.PreviousReadingDate
                        vm.FormSchema[0].Data[3].value = response.data.result.PreviousReading
                        vm.FormSchema[1].Data[1].config.minDate = response.data.result.PreviousReadingDate;
                    }
                    else {
                        vm.FormSchema[0].Data[2].value = '';
                        vm.FormSchema[0].Data[3].value = '';
                    }
                    //

                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Customer type:', error);
            } finally {
                //
            }
        },

        calculateConsumption() {
            debugger;
            const currentReading = parseFloat(this.FormSchema[1].Data[1].value);
            const previousReading = parseFloat(this.FormSchema[0].Data[3].value);
            // Check if current reading is not empty
            if (this.FormSchema[1].Data[1].value !== '') {

                if (currentReading >= previousReading) {
                    let consumption = currentReading - previousReading >= 0 ? currentReading - previousReading : 0;
                    this.FormSchema[1].Data[2].value = consumption;
                } else {
                    this.ShowAlert(this.$t("MeterReadingValidation"), "warning", this.$t('Alert'));
                }
            } else {
                // Handle the case where current reading is empty (if necessary)
                // this.ShowAlert(this.$t("Please enter the current reading"), "warning", this.$t('Alert'));
            }
        },

        async OnSubmit(fields, Data) {
            debugger
            const vm = this;

            // Construct object matching backend MeterReadingAddUpdate structure
            let obj = {
                MeterReadingId: vm.EditDetails ? vm.EditDetails.MeterReadingId : 0,
                MeterTypeId: fields.Meter_type,
                MeterId: fields.Meter,
                PreviousReadingDate: fields.Previous_Reading_Date,
                PerviousReading: fields.Pervious_Reading,
                ReadDate: fields.Read_Date,
                CurrentReading: fields.Reading,
                Consumption: fields.Consumption,
                PressureValue: fields.Pressure_Value || 0,
                GCV: fields.GCV || 0,
                Temperature: 0, // Or bind from form if needed
                ClientId: this.ClientId,
                attachment: vm.fileData || []  // Pass as array, not stringified
            };

            try {
                const response = await DataService.SaveMeterReading(obj);

                if (response?.data) {
                    if (response.data.result.Status_code == '0') {
                        this.ShowAlert(this.$t('AddedSuccess', [this.$t('MeterReading')]), "success", true, this.$t("Alert"));
                        // this.ShowAlert(this.$t("save meter reading"), "success", this.$t('Alert'));
                        this.$emit("refreshList");
                    } else {
                        this.ShowAlert(this.$t("Error while save meter data"), "failure", this.$t('Alert'));
                    }
                }
            } catch (error) {
                console.error("SaveMeterReading error:", error);
                this.ShowAlert(this.$t("Unexpected error occurred"), "failure", this.$t('Alert'));
            }
        }
    },
};
</script>

