
<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Property Log') }} - ({{ PropertyName}})</h4>
                        <button type="button" class="close" v-on:click="ClosePropertyHistory">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <tg-list ref="tgList" :listType="['List']" :IsShowAction="false" :showCheckBox="false" ModuleName="Property"
                            SubModuleCode="TaxCategory" IdentityColumn="id" :HeaderText="$t('Property')"
                            :ListData="ListingData" :HeaderData="Headers" :widgets="widgets"
                            :TotalRecords="TotalRecords" 
                            :ListDataCallBackFunction="GetPropertyHistoryList" :SortExp="SortExp" :SortBy="SortBy"
                            :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                          v-on:SortdataButtonClick="sortdata"
                             :LegendArray="LegendArray" 
                          > 
                          <template v-slot:slotdata="{ data }">                
                            <template v-if="data.column.COLUMN_NAME == 'next_modified_date'">
                                <td class="text-center single-action tourguide_a">
                                    {{ $options.filters.formatDateTime(data.row.next_modified_date) }}
                                </td>
                            </template>                
                        </template> 
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    data(){
        return{
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,           
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            ListingData: [],
            TotalRecords: 0,          
            SortBy: '', 
            SortExp: '',
            Headers: [
                {
                    "COLUMN_NAME": "property_field",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "property Field",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "old_value",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Old Value",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "new_value",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "New Value",
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "full_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Changed By",
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "next_modified_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Changed On",
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }
                }              
            ],
            Buttons: [], 
            listActions: [],         
            leftSearchFields: null,
        }
    },
    props: {
        PropertyId: {
            type: Number,
            required: true,
        },
        PropertyName:{
            type: String,
            required: true,
        }       
    },
    created:function(){
        var ths = this;        
        ths.GetPropertyHistoryList();    
    },
    methods:{
        ClosePropertyHistory(){
            var vm = this;
            vm.$emit('ClosePropertyHistory');
        },
        GetPropertyHistoryList(){
            var vm = this;
            vm.isLoading = true;            
            var url = `Propertyid=${vm.PropertyId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;            
            DataService.GetPropertyHistoryList(url).then((response) => {                
                vm.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.PageNumber = vm.PageNumber; 
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            })
        },
        leftCommonSearch: function (value) {},
        renderActions: function (row) {},
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetPropertyHistoryList();
        },
        actionButtonClick: function (callbackfunction, item) {},
        commonHeaderButtonClick: async function (event) {},
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetPropertyHistoryList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        } 
    }
}
</script>