<template>
    <section>
        <div style="margin-top: -7px">
            <tg-list :IsShowAction="false" ModuleName="Company" SubModuleCode="" :LegendArray="[]"  :isShowHeaderText="true" :HeaderText="$t('AdditionalContact')"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="CONTACT_ID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <AssociateContact v-if="OpenAssociateContact" :UserId="UserId"></AssociateContact>
        </div>
    </section>
</template>
<script>
import Schema from './ContactListingSchema'
import DataService from "../../../services/DataService";
import AssociateContact from "../../Client/ClientDetails/AssociateContacts.vue"
import moment from 'moment'
export default { 
    components: { AssociateContact },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            TotalRecords: 0,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            ProjectName:'',
            taskId: '',
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            listheaderbuttons: Schema.buttons,
            ListingData: [],
            searchCondition: '',
            Buttons: Schema.buttons,
            QuotesListingSchema: false,
            leftSearchFields: [{
                fieldName: this.$t('ContactName'),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            OpenAssociateContact:false
        };
    },
    created: async function() {   
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.user_Id = this.GetUserInfo.ID;
        this.FetchData();
    },
    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("AdditionalContact");

            divElement[0].appendChild(spanElement);
        }
    },
    methods: {
        FetchData:async function(){
            var vm = this;
            var params = `clientId=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            if (!vm.isBlank(this.searchCondition)) {
                params += `&search=${this.searchCondition}`;
            }
            await DataService.ListAdditionalNewContact(params).then(function (response) {           
                if (response.data) {
                    if (response.data.length > 0) {
                        vm.ListingData = response.data;
                        vm.TotalRecords = response.data[0].TOTALRECORDS;
                        vm.noRecord = false;

                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ListingData = [];
                    vm.TotalRecords = 0
                }
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    
                    case 'Edit':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "openAssociate":
                vm.AssociateContact();
                    break;
                case "openDeAssociate":
                    this.DeAssociateContact(); 
                    break;
                case "BackToList":
                    this.BackToList();
                    break;
            }
        },
        BackToList: function () {
            var vm=this;
            vm.$router.push({ name: 'ClientListing' });
        },
        AssociateContact: function (item) {
            var vm =this;
            vm.OpenAssociateContact = !vm.OpenAssociateContact;
        },
        DeAssociateContact: async function (event) {
            var vm = this;
            var data = [];
            vm.ListingData.forEach(item => {
                if (item.isSelected == true) {
                    var statusCodeId = item.CONTACT_ID;
                    var dataObject = {
                        id: statusCodeId,
                        userId: vm.UserId,
                        isfor: "DeAssociate"
                    };
                    data.push(dataObject);
                }
            });
            if (data.length > 0) {
                await vm.confirmR(vm.$t("ContactConfirmDelete"), true, (vm.$t("Delete")), async function () {
                var params = JSON.stringify(data);
                DataService.SubmitAdditionalNewContacts(params).then((response) => {
                    if (response.data.Status == 'Success') {
                        vm.ShowAlert(vm.$t("ContactsDeletedSuccessfully"), "success", true, vm.$t("Alert"));
                        vm.$parent.refreshKey += 1;
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("Contacts")]), "failure", true, vm.$t("Alert"));
                    }
                });
            });
            } else {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
};
</script>