<template>
  <section>
    <div class="container-fluid">
      <loader :is-visible="isLoading"></loader>
      <div class="col-lg-12 mt-3 mb-3 p-0">
        <div class="heading-border">
          <h5 class="h5">
            <span>
              {{ $t("TotalAvailableCredit") }}: <b class="text-success font-weight-medium">{{ $n(totalAmount, "currency", $getLocaleByCurrency(CurrencyCode))}}</b>
            </span>
          </h5>
        </div>
      </div>
      <div class="py-2 d-inline-block"><span class="legend-ovrdue colorboxsquare" style="border: 1px solid rgb(238, 238, 238);"></span>{{ $t("Expired") }} </div>
      <div class="listing">
        <div class="table-responsive">
          <table id="JobListingData" class="table table-bordered mt-0" style="width: 100%;">
            <thead class="thead-bg">
              <tr>
                <th><span>{{ $t("TransactionHistory") }}</span></th>
                <th><span>{{ $t("Remarks") }}</span></th>
                <th><span>{{ $t("Plan") }}</span></th>
                <th><span>{{ $t("SubscriptionStatus") }}</span></th>
                <th><span>{{ $t("CreatedBy") }}</span></th>
                <th><span>{{ $t("Status") }}</span></th>  
                <th><span>{{ $t("ExpiryDate") }}</span></th>
                <th class="text-right"><span>{{ $t("Amount") }} </span></th>
                <th class="text-right"><span>{{ $t("RunningBalance") }}</span></th>
              </tr>
            </thead>
            <tbody>
              <tr v-if="noRecord">
                <td colspan="5" class="text-danger text-center no-record"> {{ $t("NoRecordfound") }}</td>
              </tr>
              <tr v-for="(cInfo, index) in ListingData" :key="index" :class="['odd', cInfo.Expiry_Date && new Date(cInfo.Expiry_Date) < new Date() ? 'danger-bar' : '']">
                <td>{{ formatDateTime(cInfo.TRANSACTION_HISTORY) }}</td>
                <td>{{ cInfo.REMARKS }}</td>
                <td>{{ cInfo.plan_name }}</td>
                <td>{{ cInfo.Sub_status }}</td>
                <td>{{ cInfo.CREATED_BY }}</td>
                <td>{{ cInfo.STATUS }}</td>
                <td><span v-if="cInfo.Expiry_Date">{{ formatDateTime(cInfo.Expiry_Date) }}</span></td>
                <td class="text-right">
                  <span :class="{ 'text-success': cInfo.STATUS === 'Credit Added in Account',  'text-danger': cInfo.STATUS !== 'Credit Added in Account'}">
                    <span v-if="cInfo.STATUS === 'Credit Added in Account'">+</span>
                    <span v-else>-</span>
                    <span class="paddingright6">
                      {{ $n(cInfo.AMOUNT, "currency", $getLocaleByCurrency(CurrencyCode)) }}
                    </span>
                  </span>
                </td>
                <td class="text-right">
                  <span class="paddingright6">{{ $n(cInfo.RUNNING_BALANCE, "currency", $getLocaleByCurrency(CurrencyCode))}}</span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import DataService from "../../../services/DataService";
export default {
  components: {},
  props: {
    UserId: {
      type: Number
    },
    ClientData: {
      type: Object
    }
  },
  data() {
    return {
      noRecord: false,
      isLoading: false,
      CompanyId: null,
      ListingData: [],
      SortBy: '',
      SortExp: '',
      totalAmount: 0, 
      currency: 'USD',
      CurrencyCode: 'USD'
    };
  },
  created: async function() {
    var vm=this;
    this.CompanyId = this.GetUserInfo.CompanyId;
    this.user_Id = this.GetUserInfo.ID;
    this.currency = this.GetUserInfo.Currency;
     vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    await this.FetchData();
  },
  methods: {
    async FetchData() {
      var vm =this;
      vm.isLoading = true;
      try {
        const params = `clientId=${this.UserId}&sortBy=${this.SortBy}&sortExp=${this.SortExp}`;
        const response = await DataService.ListCreditHistory(params);
        if (response.data && response.data.length > 0) {
          vm.ListingData = response.data;
          vm.noRecord = false;
        } else {
          vm.ListingData = [];
          vm.noRecord = true;
        }
        vm.totalAmount = response.data.length > 0 ? response.data[0].RUNNING_BALANCE : 0;
      } catch (error) {
        console.error('Error fetching data:', error);
        this.noRecord = true;
        this.ListingData = [];
      } finally {
        vm.isLoading = false; 
      }
    },

    formatDateTime(dateTime) {
      return new Date(dateTime).toLocaleString(); 
    }
  }
};
</script>