<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('Manage Address') }}
                            </h5>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-md-6">
                                    <dynamic-form lang="en" ref="refAddress" :buttons="false"
                                        :schema="FormSchema"></dynamic-form>
                                </div>
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-sm-12">
                                            <MapComponent class="row" ref="mapComponnet" mapType="MapBox"
                                                mapContainer="addAddress" :config="config" mapHeight="355px"
                                                @GetAddress="GetAddress" :latLng="latLngValues">
                                            </MapComponent>
                                            <div class="row flex-row-reverse mt-2">
                                                <div class="col-lg-6 text-right">
                                                    <a v-if="isUpdateForm == true" href="javascript:;" @click="onSubmit"
                                                        class="btn btn-success  mr-2" title="" id="addAddress"
                                                        data-toggle-tooltip="tooltip" data-original-title="Save">
                                                        <i class="fa fa-refresh"></i>
                                                        <span id="btnAddressText"> {{ $t("Update") }}</span></a>

                                                    <a v-else href="javascript:;" @click="onSubmit"
                                                        class="btn btn-success mr-2 " title="" id="addAddress"
                                                        data-toggle-tooltip="tooltip" data-original-title="Save">
                                                        <i class="fa fa-save mr-2"></i>
                                                        <span id="btnAddressText"> {{ $t("Save") }}</span></a>

                                                    <a href="javascript:;" class="btn btn-danger" title=""
                                                        id="CancelAddress" @click="cancelAddress()"
                                                        data-toggle-tooltip="tooltip" data-original-title="Cancel"><i
                                                            class="fa fa-times mr-2"></i>{{ $t("Cancel")
                                                            }}</a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="row col-lg-6">
                                <div class="py-2">
                                    <span class="mandatory">{{ $t("Mandatory") }}</span>
                                </div>
                            </div>

                            <div class="mt-3 mb-2" v-if="AddressList.length > 1">
                                <div class="heading-border">
                                    <h5 class="h5">
                                        <span>{{ $t('AdditionalAddresses') }}</span>
                                        <small class="float-right">
                                            <a title="" id="DeleteMultipleAddress" v-on:click="removeaddress('MultipleDelete')"
                                                :class="{ 'table-head-btn': true, 'disabled': !isdeleteButtonVisible }"
                                                data-toggle-tooltip="tooltip"
                                                data-original-title="Please select one/multiple check box(s) to remove additional addresses(s)">
                                                <i class="fa fa-trash pr-2"></i>{{ $t("Delete") }}
                                            </a>
                                        </small>

                                    </h5>
                                </div>
                            </div>
                            <div class="row p-2">
                                <div class="col-12">
                                    <div class="d-inline-block">
                                        <span class="legend-pending colorboxsquare"></span>{{ $t("AddressIsVisibleToclient") }}
                                    </div>
                                </div>
                            </div>
                            <div class="listing">
                                <div class="table-responsive">
                                    <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>
                                                    <div class="custom-control custom-checkbox">
                                                        <input type="checkbox" class="AddressChkAll custom-control-input" id="chkAll_0"
                                                            v-model='allSelected' @click="selectAll($event)" name="checkbox" />
                                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                                            for="chkAll_0"></label>
                                                    </div>
                                                </th>
                                                <th>#</th>
                                                <th id="th-ADDRESS"> {{ $t('Address') }}</th>
                                                <th>{{$t('NotesDescription')}}</th>
                                                <th>{{$t('IsinCompanyDirectory')}}</th>
                                                <th>{{$t('IsPrimary')}}  {{$t('Address')}}</th>
                                                <th>{{ $t('Action') }}</th>
                                            </tr>
                                        </thead>

                                        <tbody>                                           
                                            <tr :class="item.is_client_address == true ? 'high-bar' : ''"
                                                v-for="(item, index) in AddressList" v-bind:Key="index">
                                                <td class="textaligncenter">
                                                    <div class="custom-control custom-checkbox">
                                                        <input :id='item.user_address_id' class="custom-control-input" type="checkbox"
                                                            name="checkbox" v-model="item.isSelected" :value="item.user_address_id"
                                                            @change="select($event, item)">
                                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                                            :for="item.user_address_id"></label>
                                                    </div>
                                                </td>
                                                <td>
                                                    <strong>{{ index + 1 }}</strong>
                                                </td>
                                                <td>
                                                      <!-- {{ item.user_address_1 }},{{ item.user_address_2 }},{{ item.user_state }},{{ item.country_name }},{{ item.user_zipcode }} -->
                                                        {{ formatFullAddress(item) }}
                                                </td>
                                                <td>
                                                    {{ TruncateStr(item.user_address_desc,20) }}
                                                </td>

                                                <td v-if="item.add_to_directory == true">
                                                    {{ $t('Yes') }}
                                                </td>
                                                <td v-else>
                                                    {{ $t('No') }}
                                                </td>
                                                <td>
                                                    <div class="custom-control custom-checkbox"><input
                                                            v-on:change="setPrimaryAddress($event, item)"
                                                            :checked="item.is_primary_address" type="checkbox"
                                                            class="custom-control-input"
                                                            style="z-index: 1; width: 19px; margin-top: 6px;"><label
                                                            class="custom-control-label universal-custom-control-label pt-1"></label>
                                                    </div>
                                                </td>
                                                <td class="text-center single-action">
                                                    <a class="enable" data-placement="left" v-on:click="editAddress(item)" title=""
                                                        href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Edit"><em
                                                            class="fa fa-pencil text-success action-icon mr-2"></em></a>
                                                    <a class="enable" data-placement="left"
                                                        v-on:click="removeaddress('singleDelete', item)" title=""
                                                        href="javascript:;" data-toggle-tooltip="tooltip"
                                                        data-original-title="Delete"><em
                                                            class="fa fa-trash text-danger action-icon"></em></a>
                                                </td>
                                            </tr>
                                            <tr v-if="AddressList.length == 0">
                                                <td colspan="8" class="text-danger text-center no-record">{{ $t('NoRecordfound') }}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import MapComponent from '../../../components/Modals/MapComponent.vue';
import DataService from '../../../services/DataService';
export default {
    components: {
        MapComponent
    },
    props: {
        ClientId: {
            type: Number
        },
        IsClient: {
            type: String
        }
    },
    data() {
        return {
            config: {
                isShowDrawButton: true,
                isShowSearchBox: true
            },
            isUpdateForm: false,
            userAddressId: null,
            countryList: [],
            isdeleteButtonVisible: false,
            AddressList: [],
            Longitude: null,
            Latitude: null,
            latLngValues: '',
            allSelected: false,
            addressIds: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t("AddressLine1"),
                            name: "Address Line 1:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": true,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("AddressLine2"),
                            name: "Address Line 2:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": false,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("COUNTRY"),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                "required": true,
                                max: 250,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("StateProvince"),
                            name: "State/Province",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                "required": false,
                            },

                        },
                        {
                            astype: "TextField",
                            label: this.$t("City"),
                            name: "City:",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                "required": false,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("ZIPCODE"),
                            name: "Zip Code:",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                "required": false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("NotesDescriptionAbtAddress"),
                            name: "Note/Description About Address",
                            userGuide: "Example: office Address",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                "required": false,
                            },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t("AddToCompanyDirectory"),
                            name: "Show in Company Directory",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "toggleField",
                            label:  this.$t("isVisibleToClient"),
                            name: "Visible To Client",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.visibleToClient
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("addresstype"),
                            name: "addresstype",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                "required": true                                
                            },
                            config: {
                                options: [],
                            },
                        }
                    ],
                },
            ],
            AddressTypeList:[]
        }
    },
    created:function(){
        var vm = this;
        vm.GetCountryDDL();
        vm.GetAddressDDL();
        vm.getAdditionalAddressList();
        if (vm.IsClient == 'Client') {
            vm.FormSchema[0].Data[8].visibility = false;
        } else {
            vm.FormSchema[0].Data[8].visibility = true;
        }
    },
    methods:{
        GetCountryDDL() {
            var url = `moduleName=CRM&type=COUNTRIES`;
            var vm = this;
            DataService.GetCountryDDL(url).then((result) => {                
                vm.countryList = result.data.DATA;
                vm.countryList.forEach((item) => {
                    vm.FormSchema[0].Data[2].config.options.push({
                        name: `${item.name}`,
                        value: `${item.value}`,
                    });
                });
                this.$refs.refAddress.UpdateKeyValue();
            });
        },
        GetAddressDDL() {
            var url = `moduleName=CRM&type=ADDRESS_TYPE`;
            var vm = this;
            DataService.GetCountryDDL(url).then((result) => { 
                vm.AddressTypeList = result.data.DATA;
                vm.AddressTypeList.forEach((item) => {
                    vm.FormSchema[0].Data[9].config.options.push({
                        name: `${item.name}`,
                        value: `${item.value}`,
                    });
                });
                this.$refs.refAddress.UpdateKeyValue();
            });
        },
        async getAdditionalAddressList() {
            var vm = this;
            var url = `userId=${vm.ClientId}&userAddressId=0`;
            await DataService.GetAdditionalAddressList(url).then((response) => {                
                if (response.data != null) {
                    vm.AddressList = [];
                    if (response.data.result.length > 0) {
                        vm.AddressList = response.data.result;
                        vm.AddressList.forEach(element => {
                            element.isSelected = false;
                             if (element.is_primary_address == true) {
                                let objnew = {
                                    location_nw: element.longitude.replace(",", '') + element.latitude.replace(",", ',')
                                }
                                vm.latLngValues = objnew;            
                             }
                        })
                    }
                }
            });
        },
        ClosePopUp(){
            this.$emit('ClosePopUp');
        },
        visibleToClient(event, item) {
            var vm = this;
            if (event == true) {
                vm.confirmR(vm.$t('VisibleToClientValidation'), true, false, function (result) {
                    if (result == undefined)
                        return vm.FormSchema[0].Data[8].value = true;
                });
                vm.FormSchema[0].Data[8].value = false
            }
            else {
                vm.FormSchema[0].Data[8].value = event
            }

        },
        selectAll() {
            var vm = this;
            let addressDetails = []
            let addressIds = '';
            let obj = {};
            vm.allSelected = !vm.allSelected;
            if (vm.allSelected) {
                vm.AddressList.forEach(ele => {
                    ele.isSelected = true;
                })
                vm.isdeleteButtonVisible = true;
            } else {
                vm.AddressList.forEach(element => {
                    element.isSelected = false;
                })
                vm.isdeleteButtonVisible = false;
            }
            vm.AddressList.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        addressId: ele.user_address_id,
                        userId: ele.user_id
                    }
                    addressDetails.push(obj);
                }
            })
            addressIds = addressDetails.map(item => item.addressId).join(',');
            vm.getAddressValue(addressIds);
        },
        select(event, item) {
            var vm = this;
            let addressDetails = []
            let addressIds = '';
            let obj = {};
            if (event.target.checked == true) {
                vm.AddressList.forEach(element => {
                    if (element.user_address_id == item.user_address_id) {
                        element.isSelected = true;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })
                vm.isdeleteButtonVisible = true;
            }
            else {
                vm.AddressList.forEach(element => {
                    if (element.user_address_id == item.user_address_id) {
                        element.isSelected = false;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })
            }
            vm.AddressList.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        addressId: ele.user_address_id,
                        userId: ele.user_id
                    }
                    addressDetails.push(obj);
                }
            })

            if (vm.AddressList.length == addressDetails.length) {
                vm.allSelected = true;
            }
            else {
                vm.allSelected = false;
            }
            addressIds = addressDetails.map(item => item.addressId).join(',');
            if (addressIds == "") {
                vm.isdeleteButtonVisible = false;
            }
            vm.getAddressValue(addressIds);
        },
        getAddressValue(addressIds) {
            var vm = this;
            vm.addressIds = '';
            vm.addressIds = addressIds;
        },
        clearForm() {
            var vm = this;
            vm.FormSchema[0].Data[0].value = ''
            vm.FormSchema[0].Data[1].value = ''
            vm.FormSchema[0].Data[2].value = ''
            vm.FormSchema[0].Data[3].value = ''
            vm.FormSchema[0].Data[4].value = ''
            vm.FormSchema[0].Data[5].value = ''
            vm.FormSchema[0].Data[6].value = ''
            vm.FormSchema[0].Data[7].value = ''
            vm.FormSchema[0].Data[8].value = ''
            vm.FormSchema[0].Data[9].value = ''
            vm.$refs.refAddress.validateForm.reset();
        },
        async setPrimaryAddress(event, item) {
            var vm = this;
            try {
                let obj = {
                    Num: event.target.checked ? 1 : 0,
                    userAddressID: item.user_address_id
                }
                var Jsondata = `postString=${JSON.stringify(obj)}`;
                await DataService.SetPrimaryAddress(Jsondata).then(response => {
                    if (response.data.status == 'Success') {
                        vm.ShowAlert(vm.$t("LeadOwnerChanged", [vm.$t("PrimaryAddress")]), "success", true, vm.$t("Alert"));
                        vm.getAdditionalAddressList();
                    }
                })
            } catch (error) {
                console.log("error message", error);
            }
        },
        editAddress(item) {
            var vm = this;
            vm.latLngValues = ''
            vm.isUpdateForm = true;
            vm.FormSchema[0].Data[0].value = item.user_address_1;
            vm.FormSchema[0].Data[1].value = item.user_address_2.toLowerCase() == 'null' ? '' : item.user_address_2 || '';
            vm.FormSchema[0].Data[2].value = item.country_id;
            vm.FormSchema[0].Data[3].value = item.user_city;
            vm.FormSchema[0].Data[4].value = item.user_state;
            vm.FormSchema[0].Data[5].value = item.user_zipcode;
            vm.FormSchema[0].Data[6].value = item.user_address_desc;
            vm.FormSchema[0].Data[7].value = item.add_to_directory;
            vm.FormSchema[0].Data[8].value = item.is_client_address;
            vm.FormSchema[0].Data[9].value = item.address_type;
            vm.longitude = item.longitude;
            vm.latitude = item.latitude;
            vm.userAddressId = item.user_address_id;
            let obj = {
                location_nw: item.longitude.replace(",", '') + item.latitude.replace(",", ',')
            }
            vm.latLngValues = obj;
        },
        removeaddress(event, item) {
            var vm = this;
            if(item.is_primary_address){
                vm.ShowAlert(vm.$t("CheckRemovePrimaryAddress"), "warning", vm.$t('Alert'));
                return
            }
            vm.confirmR(vm.$t('ConfirmDelete'), true, false, function (result) {
                let url = ''
                try {
                    if (event == 'MultipleDelete') {
                        url = `ids=${vm.addressIds}&userId=${vm.ClientId}`;
                    }
                    else {
                        url = `ids=${item.user_address_id}&userId=${vm.ClientId}`;
                    }                    
                    DataService.DeleteUserAddress(url).then(response => {
                        if (JSON.parse(response.data).statuscode == 'Deleted') {
                            vm.allSelected = false;
                            vm.$refs.mapComponnet.removeMarker();
                            vm.clearForm();
                            vm.isUpdateForm = false;
                            vm.getAdditionalAddressList();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Address')]), "Success", true, vm.$t('Alert'));
                        }
                    })
                } catch (error) {
                    console.log("error message", error);
                }
            });
        },
        GetAddress(data) {
            debugger
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            vueObj.$refs.mapComponnet.fullAddress = [];
            vueObj.$refs.mapComponnet.lngLatData = [];
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.countryList.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });

            let fullAddress=data[indexValue].CompleteAddress.FullAddress;           
            let city=data[indexValue].CompleteAddress.City.LongName;
            let state=data[indexValue].CompleteAddress.State.LongName;
            let pin =data[indexValue].CompleteAddress.PinCode;
            let country=data[indexValue].CompleteAddress.Country.LongName;
            const isPlusCode = /^[23456789CFGHJMPQRVWX]{4}\+[23456789CFGHJMPQRVWX]{2}/i.test(fullAddress);         
            if (!isPlusCode) {
                fullAddress = fullAddress.replace(country, '').replace(state, '').replace(city, '').replace(pin, '');
                vueObj.FormSchema[0].Data[0].value = fullAddress.split(',').map(part => part.trim()).filter(part => part.length).join(', ');  
                var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
                vueObj.FormSchema[0].Data[2].value = filterCountry;
                //vueObj.FormSchema[0].Data[3].value = vueObj.FormSchema[0].Data[4].value = data[indexValue].CompleteAddress.State.LongName;
                vueObj.FormSchema[0].Data[3].value = state;
                vueObj.FormSchema[0].Data[4].value = city;
                vueObj.FormSchema[0].Data[5].value =pin;
                var dataLat1 = ''; var dataLat2 = ''; 
                var dataLng1 = ''; var dataLng2 = '';
                data[indexValue].LatLng.forEach((item, index) => {
                    if (index == 0) {
                        dataLat1 = item.Lat;
                        dataLat2 = item.Lng;
                    }
                });
                vueObj.Latitude = dataLng1 + ',' + dataLat2;
                vueObj.Longitude = dataLat1 + ',' + dataLng2;

                let obj = {
                    location_nw: vueObj.Longitude.replace(",", '') + vueObj.Latitude.replace(",", ',')
                }
                vueObj.latLngValues = obj;
            }
            else{
                const geocoder = new google.maps.Geocoder();
                geocoder.geocode({ address: fullAddress}, function(results, status) {
                if (status === "OK") {
                    let address_components=results[0].address_components
                    if(address_components.length>0){                        
                        var address_componentss = address_components.filter(item => item.types[0] !== 'plus_code');
                        vueObj.FormSchema[0].Data[0].value=address_componentss[0].long_name;
                        var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
                        vueObj.FormSchema[0].Data[2].value = filterCountry;
                        vueObj.FormSchema[0].Data[3].value = address_componentss[3].long_name;
                        vueObj.FormSchema[0].Data[4].value = address_componentss[1].long_name;
                        vueObj.FormSchema[0].Data[5].value = pin;
                        // Find postal code component properly - check for various postal code types
                        var postalCodeComponent = address_components.find(component =>
                            component.types.includes('postal_code') ||
                            component.types.includes('postal_code_prefix') ||
                            component.types.includes('postal_code_suffix') ||
                            component.types.includes('plus_code') ||
                            component.types.includes('zip_code') ||
                            component.types.includes('zipcode')
                        );
                        // if (postalCodeComponent) {
                        //     console.log('Found postal code component:', postalCodeComponent);
                        //     vueObj.FormSchema[0].Data[5].value = postalCodeComponent.long_name;
                        // }
                    }
                    var dataLat1 = ''; var dataLat2 = ''; 
                    var dataLng1 = ''; var dataLng2 = '';
                    data[indexValue].LatLng.forEach((item, index) => {
                        if (index == 0) {
                            dataLat1 = item.Lat;
                            dataLat2 = item.Lng;
                        }
                    });
                    vueObj.Latitude = dataLng1 + ',' + dataLat2;
                    vueObj.Longitude = dataLat1 + ',' + dataLng2;
                    let obj = {
                        location_nw: vueObj.Longitude.replace(",", '') + vueObj.Latitude.replace(",", ',')
                    }
                    vueObj.latLngValues = obj;

                } else {
                    console.warn("Geocode failed:", status);
                }
                });
            }            
        },
        onSubmit() {
            var vm = this;
            vm.$refs.refAddress.validateForm.validate().then(async valid => {
                if (valid) {
                    vm.isLoading = true;
                    let obj = {
                        user_id: vm.ClientId,
                        user_address_id: vm.userAddressId === null ? 0 : vm.userAddressId,
                        user_address_1: vm.FormSchema[0].Data[0].value,
                        user_address_2: vm.FormSchema[0].Data[1].value,
                        country_id: parseInt(vm.FormSchema[0].Data[2].value),
                        user_city: vm.FormSchema[0].Data[3].value,
                        user_state: vm.FormSchema[0].Data[4].value,
                        user_zipcode: vm.FormSchema[0].Data[5].value,
                        user_address_desc: vm.FormSchema[0].Data[6].value,
                        is_client_address: vm.FormSchema[0].Data[8].value,
                        add_to_directory: vm.FormSchema[0].Data[7].value,
                        latitude: vm.Latitude,
                        longitude: vm.Longitude,
                        Address_type:vm.FormSchema[0].Data[9].value
                    }
                    var formData = new FormData();
                    formData.append("postString", JSON.stringify(obj));
                    DataService.AddupdateAdditionalAddress(formData).then(response => {
                        vm.isLoading = false;
                        if (response.data != null) {
                            if (response.data == 1) {
                                vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('Address')]),"success", true, vm.$t("Alert"));
                                vm.clearForm();
                                vm.$refs.mapComponnet.removeMarker();
                                vm.$refs.mapComponnet.clearSearchField();
                                vm.$refs.mapComponnet.resetMap();
                                vm.getAdditionalAddressList();
                            }
                            else if (response.data == 2) {
                                vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('Address')]),"success", true, vm.$t("Alert"));
                                vm.clearForm();
                                vm.isUpdateForm = false;
                                vm.userAddressId = null;
                                vm.$refs.mapComponnet.removeMarker();
                                vm.$refs.mapComponnet.clearSearchField();
                                vm.$refs.mapComponnet.resetMap();
                                vm.getAdditionalAddressList();
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t("SomethingWentWrong"), "success", vm.$t('Alert'));
                        }
                    });
                }

            }).catch(error => {
                console.error('Error validating form:', error);
            });
        },
        cancelAddress() {
            var vm = this;
            vm.clearForm();
            vm.isUpdateForm = false;
            vm.userAddressId = null;
            vm.$refs.mapComponnet.removeMarker();

        },
        formatFullAddress(item) {
            return [
            item.user_address_1,
            item.user_address_2,
            item.user_state,
            item.country_name,
            item.user_zipcode
            ]
            .filter(part => part && part.trim() !== '')
            .join(', ');
        }
    }
}
</script>