<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('ViewPlanRequestedHistory') }}</h4>

                            <button type="button" class="close" @click="$emit('close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing"
                                :listType="['List']" :RenderRowActionMethod="renderActions" SubModuleCode="Billing"
                                IdentityColumn="id" :ListData="PlanHistory" :HeaderData="Headers"
                                :callbackfunction="FetchData" :HeaderButtons="''" :TotalRecords="TotalRecords"
                                :ListDataCallBackFunction="FetchData"  :SortExp="sortExp"
                                :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod"
                                :isShowHeaderText="false" v-on:ActionButtonClick="actionButtonClick"
                                :HeaderText="$t('ViewPlanRequestedHistory')" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                v-on:SortdataButtonClick="sortdata">

                                <template v-slot:slotdata="{ data }">
                                    <template v-if="data.column.COLUMN_NAME === 'request_date'">
                                                <span>
                                                {{$options.filters.formatDate(data.row.request_date) }}
                                            </span>
                                    </template>
                                     <template v-if="data.column.COLUMN_NAME === 'applicable_from'">
                                                <span>
                                                {{$options.filters.formatDate(data.row.applicable_from) }}
                                            </span>
                                    </template>
                                </template>

                            </tg-list>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        PlanMappingId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            PlanHistory: [],
            TotalRecords: 0,
            sortBy: '',
            sortExp: '',
            Headers: [
                {
                    COLUMN_NAME: "request_date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Request Date",
                    DISPLAY_ORDER: 2,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "upcoming_frequency",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Upcoming Frequency",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "billing_amount",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Billing Amount",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "applicable_from",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Applicable From",
                    DISPLAY_ORDER: 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "requested_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Requested By",
                    DISPLAY_ORDER: 6,
                },
            ],
            // LegendArray: [
            //     {
            //         class: 'legend-Progress colorboxsquare',
            //         text: 'Currently active'
            //     },

            // ]
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        async FetchData() {
            var vm = this;
            try {
                var params = `planMappingId=${vm.PlanMappingId}`;
                const response = await DataService.GetPlanReuestedSubscription(params);
                if (response.data.status === "Success" && response.data.code === "1") {
                    if (response.data.result.length > 0) {
                        vm.TotalRecords = response.data.result?.TotalRecords;
                        vm.isRateApplicableOnPlan = response.data.result?.is_rate_applicable_on_plan;
                    }
                    vm.PlanHistory = response.data.result;
                    // vm.PlanHistory.forEach(element => {
                    //     element.additionalClass = '';
                    //     if (element.unit_status == 1001) {
                    //         element.additionalClass = 'low-bar odd';
                    //     }
                    // });
                }
                else {
                    vm.PlanHistory = [];
                    vm.TotalRecords = 0;
                }
            } catch (error) {
                console.log("Error loading plan history:", error);
            }
        },
        renderActions() {

        },
        pagerMethod() {

        },
        actionButtonClick() {

        },
        commonHeaderButtonClick() {

        },
        sortdata() {

        }
    }
}
</script>