<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width: 950px;">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Override') }} {{ $t('Tax') }}</h4>
                        <button type="button" class="close" v-on:click="CloseTaxClient">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="alert alert-primary" role="alert">
                            {{$t('ModifyTaxDetails')}}
                        </div>

                        <div class="col-12 p-0">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-12 text-right">
                                    
                                        <a href="javascript:;" class="btn btn-success mr-2" id="btnSave" @click="TaxClientOverrideNo"
                                        title="Add Tax"><i class="fa fa-plus pr-2"></i>{{$t('AddTax')}}</a>
                                        <a href="javascript:;" class="btn btn-primary" id="btnSave" @click="TaxClientOverrideYes"
                                        title="Apply Override"><i class="fa fa-save pr-2"></i>{{$t('ApplyOverride')}}</a>
                                </div>
                               
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            isLoading:false
        }
    },
    created: function () { },
    methods: {
        CloseTaxClient() {
            var vm = this;
            vm.$emit('CloseTaxClient');
        },
        TaxClientOverrideYes() {
            debugger
            var vm = this;
            vm.$emit('IsOverrideTaxClient', 'Yes');
        },
        TaxClientOverrideNo() {
            var vm = this;
            vm.$emit('IsOverrideTaxClient', 'No');
        }

    }
}
</script>