<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t("Attachment") }}</h5>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <dynamic-form ref="fileForm" :mandatorytext='MandatoryString' :buttons="buttons"
                                :schema="FormSchema" v-on:OnSubmit="uploadFile">

                            </dynamic-form>

                            <AttachmentList ref="AttachmentList" :BillId="BillId"></AttachmentList>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
import DataService from '../../../services/DataService';
import AttachmentList from './AttachmentList.vue';
export default {

    components: {
        AttachmentList
    },
    props: {
        BillId: {
            type: Number,
        },

    },
    data() {

        return {
            MandatoryString: this.$t('MandatoryString'),
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FileName'),
                            name: "FileName",
                            value: "",
                            placeholder: "",
                            config: {
                                //
                            },
                            validationRules: { "required": true, max: 2000 },
                        },
                        {
                            astype: "FileField",
                            showLabel: false,
                            label: this.$t('AttachItemImage'),
                            name: "AttachItemImage",
                            value: "",
                            placeholder: "",
                            FieldIndex: 0,
                            validationRules: { "required": true },
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc',
                                        iconClass: 'fa-file-word-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-pdf-o'
                                    },
                                    {
                                        fileType: 'docx',
                                        iconClass: 'fa-file-word-o'
                                    },
                                    {
                                        fileType: 'txt',
                                        iconClass: 'fa-file-text-o'
                                    },
                                    {
                                        fileType: 'xls',
                                        iconClass: 'fa-file-excel-o'
                                    },
                                    {
                                        fileType: 'xlsx',
                                        iconClass: 'fa-file-excel-o'
                                    }
                                ],

                                // onClickSelectedFiles: this.onDeleteSelectedFiles,
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                onChange: this.GetBase64String,
                                showSelectedFiles: [],
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple: false
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 3
                            },
                            validationRules: { "required": false, max: 2000 },
                        },
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    text: "<i class='fa fa-upload pr-2 pr-2'></i>Upload",
                    // onClick: this.uploadfile
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            isLoading: false
        }
    },

    created() {

        //
        console.log("FormSchema", this.FormSchema);

    },
    methods: {

        Close() {
            var vm = this;
            vm.$parent.isAttachment = false;


        },
        onDeleteSelectedFiles(item) {
            debugger;
            this.$delete(this.FormSchema[0].Data[1].config, 'showSelectedFiles');
        },
        GetBase64String(evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
                //
            }
        },

        async uploadFile(data, item) {
          
            try {
                if (this.fileData == undefined)
                    return
                this.isLoading = true;
                let obj = {
                    file_Name: data.FileName,
                    file_Description: data.Description,
                    invoice_id: this.BillId,
                    attachment: JSON.stringify(this.fileData)
                };
                let formData = new FormData();
                formData.append("postString", JSON.stringify(obj));
                const response = await DataService.SaveBillAttachment(formData);
                if (response.status === 200) {
                    this.isLoading = false;
                    if (response.data.Status == 'Success') {
                        this.FormSchema[0].Data[0].value = '';
                        this.FormSchema[0].Data[1].value = [];
                        this.FormSchema[1].Data[0].value = '';
                        this.$refs.fileForm.validateForm.reset();
                        this.onDeleteSelectedFiles();
                        this.$refs.AttachmentList.getBillAttachMentList();
                        this.ShowAlert(this.$t('SaveBillAttachment'), "success");
                    }
                    else if (response.data.status == 'Exceeds') {
                        this.ShowAlert(this.$t("ExceedBillAttachmentLimit"), "Warning", this.$t('Alert'));
                    }
                    else {
                        this.ShowAlert(this.$t("UnknownError"), "failure", this.$t('Alert'));
                    }
                } else {
                    this.isLoading = false;
                    console.error("Upload failed:", response);
                }
            } catch (error) {
                console.error("Error uploading file:", error);
            }
        }

    },

}

</script>
<style></style>