<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Plan"
            SubModuleCode="Plan" IdentityColumn="rate_schedule_id" :HeaderText="$t('RateSchedule')" :ListData="ListingData"
            :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="GetWorkCodeMappingList" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
            :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_name'">
                    <a class="inactive-disabled">
                        <span class="text-center text-white d-block"
                            :style="{ backgroundColor: data.row.status_bg_color }"> {{ $t(data.row.status_name) }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'rate'">
                    <a class="task-dec" @click="EditRateCode(data.row)">{{ data.row.rate != null ?
                        parseFloat(data.row.rate).toFixed(2) : '--' }} </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $d(data.row.created_at) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'start_date'">
                    <span>
                        {{ (data.row.start_date != null ? $d(data.row.start_date): "--") }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'end_date'">
                    <span>
                        {{ (data.row.end_date != null ? $d(data.row.end_date): "--") }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'modified_at'">
                    <span class="text-center">
                        {{ data.row.modified_at != null ? $d(data.row.modified_at) : '--' }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'modified_by'">
                    <span class="text-center">
                        {{ data.row.modified_by != null ? data.row.modified_by : '--' }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'billing_code'">
                    <span class="text-center">
                        {{ data.row.billing_code != '' ? data.row.billing_code : '--' }}
                    </span>
                </template>
            </template>
        </tg-list>
         <AddRate v-if="IsAddRate" :RateObj="RateObj" :ClientId="ClientId" @CloseRateCode="CloseRateCode"></AddRate> 
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import AddRate from './AddRate.vue';
export default {
   components: { AddRate },
   props: {
        ClientId: {
            type: Number,
            required: true,
        }
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            ListingData: [],
            TotalRecords: 0,
            SortBy: '',
            listActions: [
                {
                    title: 'Edit',
                    href: 'javascript:void(0)',
                    callbackfunction: 'EditRateCode',
                    icon: 'fa fa-pencil action-icon text-success',
                    additionalSpan: ''
                },
                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    callbackfunction: 'DeleteRateCode',
                    icon: 'fa fa-trash-o action-icon text-danger',
                    additionalSpan: ''
                }],
            Headers: [
                {
                    "COLUMN_NAME": "work_code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RateCode'),
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "comment",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Comment'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "billing_code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BillingCode'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "rate",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Rate'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('HBStatus'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "start_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EventStartDate'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "end_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EndDate'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "created_by",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SACreatedBy'),
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "created_at",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SACreatedAt'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "modified_by",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('modified_by'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "modified_at",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ModifiedAt'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            Buttons: [
                // {
                //     title: 'addrate',
                //     iconClass: 'fa fa-plus pr-2 ',
                //     callbackfunction: 'EditRateCode',
                //     additionalClass: '',
                //     isdisabled: false,
                //     isvisible: true,
                // },
                  {
                        title: 'BacktoList',
                        iconClass: 'fa fa-angle-double-left',
                        callbackfunction: 'BacktoList',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true
                    }   
            ],
            IsAddRate: false,
            RateObj: null,
            PlainId: 0
        }
    },
    created: function () {
        debugger
        var ths = this;
        ths.PlainId = this.$route.params.id;
        ths.Headers.forEach((itm => {
            itm.DISPLAY_NAME = ths.$t(itm.DISPLAY_NAME);
        }))
        ths.GetWorkCodeMappingList();
    },
    methods: {
        leftCommonSearch() { },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetWorkCodeMappingList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditRateCode':
                    this.EditRateCode(item);
                    break;
                case 'DeleteRateCode':
                    this.commonDeleteData(item.rate_schedule_id);
                    break;
            }
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
            switch (event.callbackfunction) {
                case "EditRateCode":
                    vm.EditRateCode(null);
                    break;
                     case "BacktoList":
                    vm.BacktoList();
                    break;
            }
        },
         BacktoList() {
            this.$router.push({ name: 'ClientListing' });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetWorkCodeMappingList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        GetWorkCodeMappingList() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var url = `Planid=&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&Clientid=${vm.ClientId}`;
            DataService.GetWorkCodeMappingList(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.ListingData.forEach((element) => {
                        ['start_date', 'end_date', 'created_at', 'modified_at'].forEach((key) => {
                            if (element[key]) {
                                element[key] = new Date(element[key]);
                            }
                        });
                    });

                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                    vm.TotalPages = 0;
                }
            }).catch(() => {
                vm.isLoading = false;
            });
        },
        EditRateCode(itm) {
            var ths = this;
            ths.RateObj = itm;
            ths.IsAddRate = true;
            ths.GetWorkCodeMappingList();
        },
        CloseRateCode() {
            var ths = this;
            ths.IsAddRate = false;
            ths.GetWorkCodeMappingList();
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = event;
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("RateCode")), async function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "RateCode",
                        subModuleCode: "RateCode",
                        refCode: "PLAN_RATE_CODE"
                    };
                    var objectPostString = JSON.stringify(postJSON)
                    DataService.CommonCommonDelete(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RateCode')]), "success", true, vm.$t("Alert"));
                            vm.GetWorkCodeMappingList();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RateCode')]), "failure", true, vm.$t("Alert"));
                        }

                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }

        },
    }
}
</script>
<style scoped>
.task-dec {
    color: #007bff !important;
    text-decoration: none;
    background-color: transparent;
}
</style>