<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ReplaceMeter') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        SerialNo: {
            type: String,
            required: true
        },
        MeterId: {
            type: Number,
            required: true
        },
        MeterMappingId:{
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "four",
                    group_name: this.$t('CurrentMeterInformation'),
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('SerialNumber'),
                            name: "SerialNumber",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('MeterFinalReading'),
                            name: "MeterFinalReading",
                            value: "",
                            validationRules: { "required": true},
                            disabled: false,
                        },
                        {
                        astype: "SelectField",
                        label: this.$t('MeterStatus'),
                        name: "MeterStatus",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [{ name: 'Faulty', value: 'faulty' }, { name: 'Damaged', value: 'damaged' }, { name: 'Stuck', value: 'stuck' }],
                        }, 
                    },
                    {
                    astype: "DateField",                                                
                    label: this.$t('DateOfMeterReplacement'),
                    name: "DateOfMeterReplacement",
                    value: "",                                                         
                    placeholder: this.$t('Select Date'),
                    validationRules: { 
                        "required": true, 
                        max: 100 
                    },
                    config: {}
                 },
                 ],
                },
                {
                    layoutType: "four",
                    group_name: this.$t('NewMeterAssignment'),
                    Data: [
                        {
                        astype: "SelectField",
                        label: this.$t('SerialNumber'),
                        name: "NewSerialNumber",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],
                        }, 
                        validationRules: { "required": true},
                         },
                        {
                            astype: "TextField",
                            label: this.$t('NewMeterInitialReading'),
                            name: "NewMeterInitialReading",
                            value: "",
                            validationRules: { "required": true},
                            disabled: false,
                        },
                    {
                    astype: "DateField",                                                
                    label: this.$t('DateOfInstallation'),
                    name: "DateOfInstallation",
                    value: "",                                                         
                    placeholder: this.$t('Select Date'),
                    validationRules: { 
                        "required": true, 
                        max: 100 
                    },
                    config: {
                    }
                 },
                 {
                        astype: "SelectField",
                        label: this.$t('ReasonforReplacement'),
                        name: "ReasonforReplacement",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [{ name: 'Faulty', value: 'faulty' }, { name: 'Routine Maintenance', value: 'maintenance' }, { name: 'Upgrade', value: 'upgrade' }],
                        }, 
                    },
                ],
                },
                {
                    layoutType: "four",
                    group_name: this.$t('AddtionalInformation'),
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comments'),
                            name: "Comments",
                            value: "",
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Carryforwardbillingmeternew'),
                            name: "Carryforwardbillingmeter",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: "",
                        }
                    ],
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
        await this.GetSerialNumberData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
            vm.FormSchema[0].Data[0].value= vm.SerialNo;
        },
        Close: function () {
            this.$parent.IsReplace = false;
        },
        GetSerialNumberData: async function () {
            var vm = this;
            let Url = `id=${vm.MeterId}`
            await DataServices.GetSerialnumberByMeterId(Url).then(function (response) {
                if (response.data) {
                  vm.FormSchema[1].Data[0].config.options= response.data;
                }
                vm.$refs.dynamicform.UpdateKeyValue();
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                CurrentMeterId: vm.MeterMappingId,
                MeterFinalReading: formfields.MeterFinalReading,
                MeterStatus: formfields.MeterStatus,
                DateOfReplacement: formfields.DateOfMeterReplacement,
                NewMeterId: formfields.NewSerialNumber,
                NewMeterInitialReading: formfields.NewMeterInitialReading,
                DateOfInstallation: formfields.DateOfInstallation,
                ReplacementReason: formfields.ReasonforReplacement,
                CarryForwardBilling: formfields.Carryforwardbillingmeter[0]==undefined?false:true,
                Comment: formfields.Comments,
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveMeterReplacement(params).then((response) => {
                if (response.data.Status == 'Success') {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Meter")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Meter")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>