                <div class="package-form-data">
                    <!-- <p class="price-sub" style="margin-top: 10px; margin-bottom: 10px;">
                            <span class="spnPackageCost"><font style="vertical-align: inherit;">
                                <font v-if="selectedBilling=='annually'" style="vertical-align: inherit;" class="">{{ item.symbol }} {{ item.discountedRate }}</font>
                                <font v-else style="vertical-align: inherit;" class="">{{ item.symbol }} {{ item.BaseRate }}</font>
                            </font>
                            </span>
                            <span>/User/Month</span>
                        </p>
                        <p class="bill-type" style="color: #fff; line-height: 24px; font-size: 16px; font-weight: 400;">Billed
                            <span class="spnbillingtype">
                                <font style="vertical-align: inherit;">
                                    <font v-if="selectedBilling=='annually'" style="vertical-align: inherit;">Annually</font>
                                    <font v-else style="vertical-align: inherit;">Monthly</font>
                                </font>
                            </span>
                            <span v-if="selectedBilling=='annually'" class="annualcost" style="float:right;">{{ item.symbol }} {{ item.finalRate }}</span>
                            <span v-else class="annualcost" style="float:right;">{{ item.symbol }} {{ item.BaseRate }}</span>
                        </p>
                        <p class="bill-type" style="text-spnbillingtype: none; color: #fff;  margin-bottom: 0px; line-height: 24px; font-size: 16px;font-weight:600; ">
                            <a v-if="selectedBilling=='annually'" href="javascript:;" style="color: #fff; " class="spntotalamountsaved">You Save </a>
                            <a v-else href="javascript:;" style="color: #fff; " class="spntotalamountsaved">You Could Save </a>
                            <span class="totalamountsaved">{{item.symbol}} {{ item.savedAmount }}</span>
                        </p> -->
                    <p class="price-sub" style="margin-top: 10px; margin-bottom: 10px;" v-if="item.is_applicable_on_plan =='PlanBased'|| item.is_applicable_on_plan =='Plan Based'">
                        <span :class="{spnPackageCost:true, discounted: item.discountBaseRate && item.discountBaseRate < item.BaseRate}">
                            <!-- <font v-if="selectedBilling == 'annually'" style="vertical-align: inherit;">
                                {{ item.symbol }} {{ item.discountedRate }}
                            </font> -->
                            <font style="vertical-align: inherit;">
                                {{ item.symbol }} {{ item.BaseRate }}
                            </font>
                        </span>
                        <span v-if="paymentType !== 'onetime'">/{{$t("User")}}/{{$t("Monthly")}}</span>
                        <span class="w-100 float-left font-16" v-if="item.discountBaseRate!=item.BaseRate">{{ item.discountBaseRate ? `${item.symbol} ${item.discountBaseRate}` : '' }}</span>
                    </p>
                      <p class="price-sub" style="margin-top: 10px; margin-bottom: 10px;" v-else>
                        <span :class="{spnPackageCost:true, discounted: item.discountBaseRate && item.discountBaseRate < item.recuring_charges}">
                            <!-- <font v-if="selectedBilling == 'annually'" style="vertical-align: inherit;">
                                {{ item.symbol }} {{ item.discountedRate }}
                            </font> -->
                            <font style="vertical-align: inherit;">
                                    {{ item.symbol }} {{ item.recuring_charges}}
                            </font>
                        </span>
                        <span v-if="paymentType !== 'onetime'">/{{$t("User")}}/{{$t("Monthly")}}</span>
                        <span class="w-40 float-right text-right font-16" v-if="item.discountBaseRate!=item.recuring_charges">{{ item.discountBaseRate ? `${item.symbol} ${item.discountBaseRate}` : '' }}</span>
