                            {{ item.symbol }} {{ item.calculatedRate || item.BaseRate }}
                        </span>

                    </p>


                     <p class="bill-type" style="margin-top: 10px; margin-bottom: 10px;" v-if="(item.is_applicable_on_plan =='ServiceBased'|| item.is_applicable_on_plan =='Service Based') && item.ontime_charges!=0 ">
                              <span v-if="paymentType !== 'onetime'">{{$t("OneTimeCharges")}}</span>
                            <span class="float-right" v-if="paymentType !== 'onetime'">
                                {{ item.symbol }} {{ item.ontime_charges }}
                            </span>
                    </p>
                    <p style="margin-top: 10px; margin-bottom: 10px;" v-else>&nbsp;</p>


                    <!-- <p v-if="paymentType !== 'onetime'" class="bill-type"
                        style="text-spnbillingtype: none; color: #fff;  margin-bottom: 0px; line-height: 24px; font-size: 16px;font-weight:600;">
                        <a v-if="selectedBilling == 'annually'" href="javascript:;" style="color: #fff;"
                            class="spntotalamountsaved">
                            You Save
                        </a>
                        <a v-else href="javascript:;" style="color: #fff;" class="spntotalamountsaved">
                            You Could Save
                        </a>
                        <span class="totalamountsaved">{{ item.symbol }} {{ item.savedAmount }}</span>
                    </p> -->
                </div>
            </div>
            <div class="pro-footer px-3 py-2 pro-footer-mt">
                <p>{{$t("ServiceIncludes")}}:</p>
                <div class="pckg_desc">
                    <ul>
                        <li v-for="(service, sIndex) in item.services" :key="sIndex">
                            {{ service.service_name }}
                        </li>
                    </ul>
                </div>
            </div>
            <div class="pack-add-count">
                <div class="buynowbtn text-center">
                    <a class="btn" @click="BuyNow(item.Plan_id)">Buy Now</a>
