<script>
import { select } from '@syncfusion/ej2-base';
import DataService from '../../../services/DataService';

export default {
    props: {
        UserId: {
            type: Number,
            required: true
        },
        ClientData: {
            type: Object,
            default: () => ({})
        },
        discount: {
            type: Number,
            required: true
        },
        paymentType: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            showbillbar: false,
            PlanList: [],
            totalLicense: 0,
            discountValue: 0,
            isLoading: false,
            frequencyMultipliers: {
                Monthly: 1,
                Quarterly: 3,
                SemiAnnually: 6,
                Yearly: 12
            }
        };
    },
    async created() {
        await this.CheckExtraFeatures();
        this.discountValue = this.discount;
    },
    watch: {
        // Watch for changes in selectedBilling and reload plans accordingly
        // selectedBilling() {
        //     this.LoadPlansMappingSubscription();
        // },
        paymentType(){
            this.LoadPlansMappingSubscription();

        }
    },
    methods: {
        async CheckExtraFeatures() {
            const params = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    const features = JSON.parse(response.data);
                    features.forEach(feature => {
                        if (feature.FEATURE_CODE === 'SUBSCRIPTION_BILLING') {
                            this.LoadPlansMappingSubscription();
                        }
                    });
                }
            } catch (error) {
                console.error("Error checking extra features:", error);
            }
        },
        async LoadPlansMappingSubscription() {
            try {
                this.isLoading = true;
                const param = `client_id=${this.UserId}&type=${this.paymentType}`
                const response = await DataService.LoadPlansMappingSubscription(param);
                if (response.data.status === "Success" && response.data.code === "1") {
                    const plans = response.data.result;
                    plans.forEach(plan => {
                        plan.services = plan.services ? JSON.parse(`[${plan.services}]`) : [];
                        plan.planrate=plan.planrate ? JSON.parse(`[${plan.planrate}]`) : [];
                        plan.BaseRate = plan.BaseRate? plan.BaseRate.toFixed(2) :0
                        // Apply discount only for annual billing
                        // if (this.selectedBilling === 'annually') {  //&& this.discount > 0
                        //     const discountValue = plan.BaseRate * this.discount / 100;
                        //     plan.discountedRate = (plan.BaseRate - discountValue).toFixed(2);
                        //     plan.finalRate = (plan.discountedRate * 12).toFixed(2);

                        // } else {
                        //     plan.discountedRate = plan.BaseRate.toFixed(2);
                        //     plan.finalRate = plan.BaseRate.toFixed(2)
                        // }
                         plan.timebased_discount = this.discount;
                         plan.is_applicable_on_plan=plan.is_rate_applicable_on_plan==1?this.$t("PlanBased"):this.$t("ServiceBased");
                         plan.plan_type= plan.plan_type == 1 ? this.$t("Prepaid") : this.$t("Postpaid");
                         plan.selectedFrequency = 'Monthly';
                        plan.calculatedRate = plan.BaseRate;
                        // plan.BaseRate = plan.BaseRate.toFixed(2);
                        // //display Saved Amount
                        // const discountedValue = plan.BaseRate * this.discount / 100;
                        // plan.DisplaydiscountedRate = (plan.BaseRate - discountedValue).toFixed(2);
                        // //---------------------------------------------
                        // plan.savedAmount = this.calculateSavedAmount(plan.BaseRate, plan.DisplaydiscountedRate);
                    });
                    // Assign updated plans to PlanList
                    this.PlanList = plans;
                    this.isLoading = false;
                }
                else{
                    this.PlanList=[]
                    this.isLoading = false;
                }
            } catch (error) {
                console.error("Error loading plans:", error);
            }
        },
        async BuyNow(id) {
            const selectedPlans = [];
            const selectedPlan = this.PlanList.find(item => item.Plan_id === id);
            if (selectedPlan) {
                selectedPlans.push(selectedPlan);
            }
            if (selectedPlans.length > 0) {
                console.log("Selected Plans:", selectedPlans);
                this.$emit('closeCurrentPage', selectedPlans);
            } else {
                console.log("No plans selected");
            }
        },
        calculateSavedAmount(baserate, discountRate) {
            let discountprice = baserate - discountRate;
            discountprice = discountprice * 12;
            return discountprice.toFixed(2);
        },
        // async GetFrequencyDDlData(item) {
        // const url = `moduleName=Company&type=Plan_Frequency&refId=${item.Plan_id}`;
        // try {
        //     const response = await DataService.GetDDLData(url);
        //     if (response.data && response.data.DATA) {
        //     this.$set(item, 'FrequencyOptions', response.data.DATA);
        //     } else {
        //     this.$set(item, 'FrequencyOptions', []);
        //     }
        // } catch (error) {
        //     console.error(`Error fetching frequency options for Plan ID ${item.Plan_id}`, error);
        //     this.$set(item, 'FrequencyOptions', []);
        // }
        // },
        onFrequencyChange(plan) {
            const multiplierMap = {
                Monthly: 1,
                Quarterly: 3,
                'Semi-Annually': 6,
                Yearly: 12,
                '4 Month Basis': 4
            };

            var selectedFreqObj = plan.additional_frequency.find(f => f.value === plan.selectedFrequency);

            var multiplier = multiplierMap[plan.selectedFrequency] || 1;

            var discountPercent = selectedFreqObj && selectedFreqObj.first_value
                ? parseFloat(selectedFreqObj.first_value)
                : 0;

                var baseAmount=0;  var baseAmount1=0;var discountAmount=0;var finalAmount =0;
            if (plan.is_rate_applicable_on_plan) {
             baseAmount = parseFloat(plan.BaseRate) * multiplier;
             discountAmount = baseAmount * (discountPercent / 100);
             finalAmount = baseAmount - discountAmount;
             plan.calculatedRate = finalAmount.toFixed(2);
            // BaseRate - Discount
            const discount = plan.BaseRate * (discountPercent / 100);
            plan.discountBaseRate = plan.BaseRate - discount;
            }else{
                 baseAmount = parseFloat(plan.recuring_charges) * multiplier;
                 discountAmount = baseAmount * (discountPercent / 100);
                 finalAmount = baseAmount - discountAmount;
                const discount = plan.recuring_charges * (discountPercent / 100);
                 const discount1 = plan.ontime_charges * (discountPercent / 100);
                plan.discountBaseRate = plan.recuring_charges - discount;
                plan.discountOnetimeRate = plan.ontime_charges ;
                plan.calculatedRate = (plan.discountBaseRate * multiplier).toFixed(2);
            }

        }

    }
};
</script>
<style>
.discounted{text-decoration: line-through;color: #ffd580;}
.package-form-data p{padding: 0 8px;}
.package-form-data p label{font-size: 14px;}
.package-form-data p span{font-size: 14px;}

.package-form-data p.plantype label{font-weight: 500;}



</style>