                                                                        {{ $t("Amount") }}
                                                                    </template>
                                                                </th>
                                                                <th style="min-width:100px;" class="text-center" v-if="service.service_type_name == 'Usage Based'">{{ $t("Action") }}</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(rate, ri) in newRateForms[service.planServiceId].rates" :key="ri">
                                                                <td v-if="service.service_type_name== 'Usage Based'"><input class="form-control" type="number" v-model.number="rate.from" min="0" /></td>
                                                                 <td v-if="service.service_type_name == 'Usage Based'">
                                                                    <input class="form-control" :class="{ 'is-invalid': serviceUnitToError(service.planServiceId, ri) }" :value="toDisplay(rate.to)" @input="rate.to = fromDisplay($event.target.value)" @blur="validateServiceUnitRange(service.planServiceId, ri)" placeholder="" type="number" />
                                                                </td>
                                                               <td><input :class="['form-control', { 'is-invalid': rateError(service.planServiceId, ri) }]" type="number" v-model.number="rate.rate" step="1" min="0" :max="newRateForms[service.planServiceId].billingType === 'PERCENTAGE' ? 100 : null" @input="enforceRateLimit(service.planServiceId, ri)" />
                                                                <span v-if="rateError(service.planServiceId, ri)" class="text-danger small">
                                                                    {{ $t("RateTypeValidation") }}
                                                                </span>
                                                                </td>
                                                                <td class="text-center" v-if="service.service_type_name == 'Usage Based'">
                                                                    <a href="javascript:;" @click="ri === 0 ? addRowData(service.planServiceId,rate) : removeRowData(service.planServiceId, ri)" class="mr-2">
                                                                        <em :class="ri === 0 ? 'round-icon-small fa fa-plus bg-success float-none' : 'round-icon-small fa fa-trash bg-danger float-none'"></em>
                                                                    </a>
                                                                </td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                            <div class="text-right mt-2">
                                                
                                                <button class="btn btn-success btn-sm mt-2 mr-2" @click="saveRate(service.planServiceId)" :disabled="!isFormValid(service.planServiceId)" v-if="IsSave">{{ $t("SaveRate") }}</button>
                                                                <button class="btn btn-success btn-sm mt-2 mr-2" @click="saveRate(service.planServiceId)" :disabled="!isFormValid(service.planServiceId)" v-if="IsUpdate">{{ $t("UpdateRate") }}</button>
                                                <button class="btn btn-danger btn-sm ms-1 mt-2" @click="toggleCollapse(service.planServiceId)">{{ $t("Cancel") }}</button>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            
                            </div>
                            </div>
                        </div>
                     </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-2 mt-2">
                                <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">
                                   <a href="javascript:;" title="" class="btn btn-success formbtn widthhalf" @click="SaveServiceBased"
                                    data-toggle-tooltip="tooltip" data-original-title="Save"><i class="fa fa-save pr-2"></i>Save</a>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
            </div>
        </div>
    </section>
  </template>
  <script>
  import DataServices from "../../../services/DataService";
  export default {
    props: {
        Id:{
            type:Number,
            required:true
        },
        PlanId:{
            type:Number,
            required:true
        },
        ClientId:{
            type:Number,
            required:true
        }
    },
    computed: {
        reactiveGroupedRates() {
            return this.PlanRateListing.map(item => {
                item.serviceTypes.forEach(st => {
                    st.serviceList.forEach(service => {
                        service.grouped = this.groupedRates(service.rateDetails);
                    });
                });
                return item;
            });
        }
    },
    data() {
      return {
        billingInterval: "monthly",
        billingIntervalText: "Monthly",
        billingIntervalValue: 1,
        PlanRateListing: [],
        editPlanRateListing: [],  
        isAddPeakHours:false,
        currentEditIndex: null,
        SortBy: "",
        SortExp: "",
        PageSize: "100000",
        isChecked: false,
        rateScheduleName:'',
        refCode:'',
        refCodeRetro:'',
        refCodeCharge:'',
        PageNumber: "1",
        isLoading: false,
        serviceTypes: [],
        serviceList:[],
        rateArray:[],
        planRateId:0,
        previousJsonPlanListing : null,
        isCheckedMinimiumCharge: false,
        ApplicableOnPlanRateDetails: {
            isCheckedApplicableForPlan: false,
            planRateType: "fixed",
            rateList: [{
                rateScheduleName: "Updated Plan",
                currencyId: "",
                rateDetails: [
                {
                    rateLimitId: null,
                    limitType: null,
                    limitFrom: null,
                    limitTo: null,
                    customerRateType: null,
                    startDate: null,
                    effective_start_date: null,
                    endDate: null,
                    effective_end_date: null,
                    billing_type: "FLAT",
                    rates: [
                    {
                        from: 0,
                        to: Infinity,
                        rate: "0",
                        invalidFrom: false
                    }
                    ],
                    isCurrent: true
                }
                ]
            }]
            },
        editApplicableOnPlanRateDetails: {},
        MonthApplicable:[],
        enableLimitData:[],
        serviceNameList:'',
        collapseStates: {},
        collapseStatesPlan: {},
        newRateForms: {},
        newRateFormsPlan: {},
        CurrencyCode: 'PKR',
        IsSave: true,
        IsUpdate: false,
        CurrentDate: new Date()
      };
    },
    mounted() {
        this.PlanRateListing.forEach(plan => {
            plan.serviceTypes.forEach(serviceType => {
                serviceType.serviceList.forEach(service => {
                    this.$set(this.collapseStates, service.planServiceId, false);
                    if (!this.newRateForms[service.planServiceId]) {
                        this.$set(this.newRateForms, service.planServiceId, {
                            billing_type: service.billing_type,
                            effective_start_date: null,
                            effective_end_date: null,
                            rates: service.rateDetails[0] ?.rates.map(r => ({
                                ...r
                            })) || [{
                                from: 0,
                                to: null,
                                rate: 0
                            }, ],
                        });
                    }
                });
            });
        });
    },
    created: async function () {
      var vm = this;
      await vm.FetchRecords();
      vm.PlanRateListing[0].serviceTypes.forEach(item => {
      if (item.serviceTypeName === 'Support') {
        if (item.serviceList[0].billingType === 'FLT') {
          item.serviceList[0].billingType = 'PERCENTAGE';
        } else {
        }
      }
    });
       vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    
    },
    methods: {
        
        // Clear end date when start date changes for plan-based rates
        clearPlanEndDate(index) {
            if (this.newRateFormsPlan[index]) {
                this.$set(this.newRateFormsPlan[index], 'effective_end_date', null);
            }
        },
        // Clear end date when start date changes for service-based rates
        clearServiceEndDate(planServiceId) {
            if (this.newRateForms[planServiceId]) {
                this.$set(this.newRateForms[planServiceId], 'effective_end_date', null);
            }
        },
       addNewSlabForSubscriptionPlanRate: function (rt) {
            const lastRate = rt.rateDetails[0].rates[rt.rateDetails[0].rates.length - 1];
            var newFrom = "";
            if (lastRate.to === Infinity) {
                newFrom = lastRate.from;
            } else if (lastRate.to > 1) {
                newFrom = lastRate.to + 1;
            } else if (lastRate.to >= 0 && lastRate.to < 1) {
                newFrom = lastRate.to + 0.01;
            }
            if (lastRate.to === Infinity) {
                lastRate.to = 0;
            }
            const newRate = {
                from: newFrom,
                to: Infinity,
                rate: 0,
                invalidFrom: false
            };

            rt.rateDetails[0].rates.push(newRate);
        },
       deleteSubscriptionPlanRate: function (rt, index) {
            rt.rateDetails[0].rates.splice(index, 1);
        },
      deleteRateDetailsWithLimit: function(RateDetails, index) {
        if (RateDetails.rateDetails.length > 1) {
          RateDetails.rateDetails.splice(index, 1);
        }
      },
      toDisplay(value) {
        ;
        return (value === Infinity) || (value === null) ? "" : value;
      },
      fromDisplay(value) {
        ;
        var res="";
        if(value === ""){
          res = "";
        }
        else if(value === ""){
          res = Infinity;
        }
        else{
          res = parseFloat(value);
        }
        return res;
      },
        FetchRecords: async function (isChangedManually) {
            var vueObj = this;
            vueObj.PlanRateListing = [];
            vueObj.serviceTypes = [];
            vueObj.serviceList = [];
            vueObj.isLoading = true;
            var ClientId= vueObj.Id>0 ? 0:vueObj.ClientId;
            var url = `id=${vueObj.Id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&PlanId=${vueObj.PlanId}&ClientId=${ClientId}`;

            await DataServices.GetRateSchedulePlanServices(url).then(function (response) {
              if (response.data != "" && response.data != null) {
                    let rsltData = response.data.result;

                    // Handling ApplicableOnPlanRateDetails
                    if (rsltData.is_rate_applicable_on_plan == "1") {
                        vueObj.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan = true;
                        vueObj.ApplicableOnPlanRateDetails.planRateType = rsltData.rate_type;
                        vueObj.rateId = rsltData.subscriptionBillingData.length > 0 ? rsltData.subscriptionBillingData[0].plan_rate_id : null;
                        vueObj.ApplicableOnPlanRateDetails.rateList = [];
                        let ratelist = [];

                        rsltData.subscriptionBillingData.forEach(function (element) {
                            let obj = {
                                rateScheduleName: element.rate_schedule_name,
                                currencyId: element.currency_id,
                                rateDetails: [{
                                    rates: []
                                }]
                            };
                            let checkIfExists = ratelist.find(x => x.currencyId == element.currency_id);
                            if (!checkIfExists) {
                                let rateItems = rsltData.subscriptionBillingData.filter(x => x.currency_id == element.currency_id && x.effective_start_date != null && x.effective_start_date !== '');
                                const isFixed = rsltData.rate_type === 'fixed';
                                if (rateItems.length > 0) {
                                    // Group by effective_start_date
                                    const groupedByDate = {};
                                    rateItems.forEach(item => {
                                        const dateKey = item.effective_start_date;
                                        if (!groupedByDate[dateKey]) {
                                            groupedByDate[dateKey] = {
                                                rates: [],
                                                endDate: item.effective_end_date
                                            };
                                        }
                                        groupedByDate[dateKey].rates.push({
                                            from: item.from,
                                            to: item.to,
                                            rate: item.rate,
                                            invalidFrom: false
                                        });
                                    });
                                    const rateDetails = Object.keys(groupedByDate).map(dateKey => ({
                                        rateLimitId: null,
                                        limitType: null,
                                        limitFrom: null,
                                        limitTo: null,
                                        customerRateType: null,
                                        startDate: null,
                                        effective_start_date: dateKey,
                                        endDate: groupedByDate[dateKey].endDate, // From item
                                        effective_end_date: groupedByDate[dateKey].endDate,
                                        billing_type: isFixed ? 'FLAT' : 'FLT',
                                        rates: groupedByDate[dateKey].rates,
                                        isCurrent: false // Set in groupedRates
                                    }));

                                    // Sort by date descending and set first as current
                                    rateDetails.sort((a, b) => new Date(b.effective_start_date) - new Date(a.effective_start_date));
                                    if (rateDetails.length > 0) {
                                        rateDetails[0].isCurrent = true;
                                    }

                                    obj.rateDetails = rateDetails;
                                } else {
                                    // Default single if no items
                                    const billingType = isFixed ? 'FLAT' : '';
                                    obj.rateDetails = [{
                                        rateLimitId: null,
                                        limitType: null,
                                        limitFrom: null,
                                        limitTo: null,
                                        customerRateType: null,
                                        startDate: null,
                                        effective_start_date: null,
                                        endDate: null,
                                        effective_end_date: null,
                                        billing_type: billingType,
                                        rates: [{
                                            from: 0,
                                            to: isFixed ? Infinity : null,
                                            rate: 0,
                                            invalidFrom: false
                                        }],
                                        isCurrent: true
                                    }];
                                }
                                ratelist.push(obj);
                            }
                        });

                        vueObj.ApplicableOnPlanRateDetails.rateList = ratelist;
                        vueObj.previousJsonApplicableOnPlanRateDetails = JSON.stringify(vueObj.ApplicableOnPlanRateDetails.rateList);
                    }

                    // Handling data records
                    if (rsltData.data && rsltData.data.length > 0) {
                        var row = rsltData.data;
                        vueObj.planRateId = row[0].plan_rate_id;

                        // Handle RETROACTIVE_DATA
                        let retroActive = rsltData.RETROACTIVE_DATA && rsltData.RETROACTIVE_DATA.length > 0 ?
                            rsltData.RETROACTIVE_DATA[0][0] :
                            null;
                        if (retroActive) {
                            vueObj.retroactiveData = retroActive;
                            vueObj.isRetroActive = true;
                        }

                        // Handle ENABLE_MINIMUM_CHARGE_DATA
                        let enableCharge = rsltData.ENABLE_MINIMUM_CHARGE_DATA && rsltData.ENABLE_MINIMUM_CHARGE_DATA.length > 0 ?
                            rsltData.ENABLE_MINIMUM_CHARGE_DATA[0] :
                            null;
                        if (enableCharge) {
                            vueObj.MonthApplicable = enableCharge.type_value.split(',');
                            vueObj.serviceNameList = enableCharge.map(item => item.service_names).join(", ");
                            vueObj.enableLimitData = enableCharge;
                            vueObj.isCheckedMinimiumCharge = true;
                        }

                        vueObj.serviceTypes = vueObj.serviceTypes || [];
                        vueObj.serviceList = vueObj.serviceList || [];

                        // Grouping logic
                        let servicesMap = new Map();

                        row.forEach(function (record) {
                            const serviceId = record.service_id;
                            if (!servicesMap.has(serviceId)) {
                                servicesMap.set(serviceId, {
                                    planRateId: record.plan_rate_id,
                                    planServiceId: record.plan_service_id,
                                    isPeakHoursEnabled: record.peak_rates != null && record.peak_rates.length > 0,
                                    serviceInterval: record.billing_interval || "monthly",
                                    billingIntervalValue: "1",
                                    billingType: record.billing_type,
                                    billableUnit: record.billing_unit_id || "",
                                    serviceId: record.service_id,
                                    serviceName: record.service_name,
                                    service_type_name: record.service_type_name,
                                    peakRates: record.peak_rates || [],
                                    rateDetails: []
                                });
                            }
                            let service = servicesMap.get(serviceId);

                            const effectiveDate = record.effective_start_date || '';  // Default to empty string if null
                            const billingType = record.billing_type || 'none';

                            // Only add rate details if effective_start_date is not null or empty string
                            if (effectiveDate !== null && effectiveDate !== '') {
                                let rateDetail = service.rateDetails.find(rd =>
                                    (rd.effective_start_date || 'no_date') === effectiveDate &&
                                    (rd.billing_type || 'none') === billingType
                                );

                                if (!rateDetail) {
                                    rateDetail = {
                                        rateLimitId: record.rate_limit_id,
                                        limitType: record.limit_value_type,
                                        limitFrom: record.limit_from,
                                        limitTo: record.limit_to,
                                        customerRateType: record.customer_rate_type,
                                        startDate: record.start_date,
                                        effective_start_date: record.effective_start_date,
                                        endDate: record.end_date,
                                        effective_end_date: record.effective_end_date,
                                        billing_type: record.billing_type,
                                        rates: [],
                                        isCurrent: false // We'll set this later
                                    };
                                    service.rateDetails.push(rateDetail);
                                }

                                rateDetail.rates.push({
                                    from: record.from != null ? record.from : 0,
                                    to: record.to != null ? record.to : null,
                                    rate: record.rate != null ? record.rate : 0
                                });
                            }
                        });

                        // After filtering, you can proceed with the rest of the logic...


                        // Assign isCurrent based on latest effective_start_date
                        servicesMap.forEach(service => {
                            service.rateDetails.forEach(rd => {
                                rd.isCurrent = false;
                            });
                            service.rateDetails.sort((a, b) => {
                                const dateA = a.effective_start_date ? new Date(a.effective_start_date) : new Date(0);
                                const dateB = b.effective_start_date ? new Date(b.effective_start_date) : new Date(0);
                                return dateB - dateA;
                            });
                            if (service.rateDetails.length > 0) {
                                service.rateDetails[0].isCurrent = true;
                            }
                        });

                        // Build PlanRateListing
                        vueObj.PlanRateListing = [{
                            currencyId: row.length > 0 ? row[0].currency_id : vm.currId,
                            rateScheduleName: row.length > 0 ? row[0].rate_schedule_name || vueObj.PlanName : vueObj.PlanName,
                            serviceTypes: [{
                                serviceTypeName: "Services",
                                serviceList: Array.from(servicesMap.values())
                            }]
                        }];

                        vueObj.previousJsonPlanListing = JSON.stringify(vueObj.PlanRateListing);
                        vueObj.isLoading = false;
                    } else {
                        vueObj.PlanRateListing = [];
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.PlanRateListing = [];
                    vueObj.isLoading = false;
                }
            });
        },
      addRow(row, index) {
            var vm= this;
            vm.IsUpdate= false;
            vm.IsSave= true;
        const lastRate = row.rates[row.rates.length - 1];
        // const newFrom =
        //   lastRate.to === Infinity ? lastRate.from : lastRate.to + 1;
       
        var newFrom ="";
        if(lastRate.to === Infinity){
          newFrom = lastRate.from;
        }
        else if(lastRate.to > 1){
          newFrom = lastRate.to + 1;
        }
        else if(lastRate.to > 0 && lastRate.to < 1){
          newFrom = lastRate.to + 0.01;
        }
        if (lastRate.to === Infinity) {
          lastRate.to = 0; 
        }
        const newRate = {
          from: newFrom,
          to: Infinity,
          rate: 0,
        };
        row.rates.push(newRate);
      },
      
     onChangePlanRateType: function () {
            const isFixed = this.ApplicableOnPlanRateDetails.planRateType === 'fixed';
            const toValue = isFixed ? Infinity : null;
            this.ApplicableOnPlanRateDetails.rateList = [{
                rateScheduleName: "",
                currencyId: this.currId,
                rateDetails: [{
                    rateLimitId: null,
                    limitType: null,
                    limitFrom: null,
                    limitTo: null,
                    customerRateType: null,
                    startDate: null,
                    effective_start_date: null,
                    endDate: null,
                    effective_end_date: null,
                    billing_type: isFixed ? 'FLAT' : '',
                    rates: [{
                        from: 0,
                        to: toValue,
                        rate: 0,
                        invalidFrom: false
                    }],
                    isCurrent: true
                }]
            }];
        },
      removeRow(row, index) {
        if (row.rates.length > 1) {
          row.rates.splice(index, 1);
        }
      },
      closepopup(){
        this.$parent.isServices=false
      },
      getJSONData() {
        var vm = this;
        const data = {
            PlanRateListing: vm.PlanRateListing.map(schedule => ({
              currencyId: 107,
              planId: vm.PlanId,
              clientId: vm.ClientId,
              rateScheduleName: schedule.rateScheduleName,
              clientPlanMappingId :vm.Id==0?'':vm.Id,
              serviceTypes: schedule.serviceTypes.map(serviceType => ({
                serviceTypeName: serviceType.serviceTypeName,
                serviceList: serviceType.serviceList.map(service => {
                  
                  return {
                    ...service,
                  };
                }),
              })),
            })),
          };
        return JSON.stringify(data);
    },
      async SaveServiceBased(){
        var vm=this;
        const jsonData = vm.getJSONData();
        const obj = JSON.stringify(jsonData);
        const requestPayload = { objData: obj};
        await DataServices.SaveRateSchedulePlanServices(requestPayload).then(function (response) {
          debugger
          if(response.data.Result==1){
            vm.ShowAlert("Rates updated successfully", "success", true, vm.$t('Alert'));
            vm.$parent.isServices=false;
            vm.$emit('refresh-plans');
          }
          else {
            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
           }
        })
      },
      async SavePlanBased(){
        var vm=this;
        var str = {
          planId: vm.PlanId,
          ClientId: vm.ClientId,
          clientPlanMappingId:vm.Id,
          isCheckedApplicableForPlan: vm.ApplicableOnPlanRateDetails.isCheckedApplicableForPlan, 
          planRateType: vm.ApplicableOnPlanRateDetails.planRateType,
          rateList: JSON.stringify(vm.ApplicableOnPlanRateDetails.rateList)
        }
        await DataServices.SaveRateScheduleOnBehalfOfPlan(str).then(function (response) {
          debugger
          if(response.data.result==1){
            vm.ShowAlert("Rates updated successfully", "success", true, vm.$t('Alert'));
            vm.$parent.isServices=false;
             vm.$emit('refresh-plans');
          }
          else {
            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
           }
        })
      },
       getServiceTypeClass(serviceTypeName) {
            const map = {
                'one time': 'service-one-time',
                'recurring': 'service-recurring',
                'usage based': 'service-usage',
                'activation': 'service-activation',
                'support': 'service-support',
                'Fixed Rate': 'service-fixed-rate'
            };
            return map[serviceTypeName.toLowerCase()] || 'service-fixed-rate';
        },

        formatDate(dateStr) {
            if (!dateStr) return '';
            const date = new Date(dateStr);
            return date.toLocaleDateString('en-GB', {
                day: '2-digit',
                month: 'short',
                year: 'numeric'
            });
        },

       groupedRates(rateDetails) {
   if (!rateDetails) return [];

   const today = new Date();
   today.setHours(0, 0, 0, 0); // Normalize to avoid time issues

   let groups = rateDetails
       .filter(detail => detail.effective_start_date != null && detail.effective_start_date !== '')
       .map(detail => {
           if (detail.rates) {
               return {
                   effective_start_date: detail.effective_start_date,
                   effective_end_date: detail.effective_end_date,
                   billing_type: detail.billing_type,
                   isCurrent: detail.isCurrent, // Use pre-set if available
                   slabs: detail.rates,
                   originalDetail: detail
               };
           } else if (detail.slabs) {
               return {
                   effective_start_date: detail.effective_start_date,
                   effective_end_date: detail.effective_end_date,
                   billing_type: detail.billing_type,
                   isCurrent: detail.isCurrent,
                   slabs: detail.slabs,
                   originalDetail: detail
               };
           }
           return {
               effective_start_date: null,
               effective_end_date: null,
               billing_type: null,
               isCurrent: false,
               slabs: [],
               originalDetail: detail
           };
       });

   // Mark groups as current if today's date is within their range
   groups.forEach(group => {
       const start = group.effective_start_date ? new Date(group.effective_start_date) : null;
       if (start) start.setHours(0, 0, 0, 0);
       const end = group.effective_end_date ? new Date(group.effective_end_date) : null;
       if (end) end.setHours(0, 0, 0, 0);

       let isCurrent = false; // Strictly compute based on date range

       if (start && !end) {
           isCurrent = today >= start;
       } else if (!start && end) {
           isCurrent = today <= end;
       } else if (start && end) {
           isCurrent = today >= start && today <= end;
       } else if (!start && !end) {
           isCurrent = true; // If no dates, consider current
       }
       group.isCurrent = isCurrent;
   });

   // Sort: first by isCurrent, then by latest start date
   groups.sort((a, b) => {
       if (a.isCurrent && !b.isCurrent) return -1;
       if (!a.isCurrent && b.isCurrent) return 1;

       const dateA = a.effective_start_date ? new Date(a.effective_start_date) : new Date(0);
       const dateB = b.effective_start_date ? new Date(b.effective_start_date) : new Date(0);

       return dateB - dateA;
   });

   return groups;
},

          filteredGroupedRates(rateDetails) {
            if (!rateDetails) return [];
            return this.groupedRates(rateDetails).filter(group => group.effective_start_date !== null);
        },
         filteredGroupedRatesPlan(rateDetails) {
           if (!rateDetails) return [];
           return this.groupedRates(rateDetails).filter(group => group.effective_start_date !== null);
       },

        // toggle collapse per service
        toggleCollapse(planServiceId) {
               var vm =this;
            vm.IsUpdate=false;
            vm.IsSave=true;
            if (!this.collapseStates[planServiceId]) {
                // Open collapse
                this.$set(this.collapseStates, planServiceId, true);

                // Reset the form for adding a new rate
                this.resetNewRateForm(planServiceId);
            } else {
                // Toggle collapse
                this.collapseStates[planServiceId] = !this.collapseStates[planServiceId];
                if (this.collapseStates[planServiceId]) {
                    // If opened again, reset the form
                    this.resetNewRateForm(planServiceId);
                }
            }
        },
        resetNewRateForm(planServiceId) {
            const service = this.findServiceByPlanServiceId(planServiceId);
            this.$set(this.newRateForms, planServiceId, {
                formSubmitted: false, // Per-form validation trigger
                billingType: service ? service.billingType || '' : '',
                effective_start_date: null,
                effective_end_date: null,
                rates: [{
                    from: 0,
                    to: null,
                    rate: 0,
                    invalidFrom: false
                }]
            });
        },
        initializeNewRateForm(planServiceId) {
            if (!this.newRateForms[planServiceId]) {
                const service = this.findServiceByPlanServiceId(planServiceId);
                if (service) {
                    this.$set(this.newRateForms, planServiceId, {
                        formSubmitted: false,
                        billingType: service.billingType || '',
                        effective_start_date: null,
                        effective_end_date: null,
                        rates: JSON.parse(JSON.stringify(service.rateDetails[0] ?.rates || [{
                            from: 0,
                            to: null,
                            rate: 0
                        }]))
                    });
                }
            }
        },
        findServiceByPlanServiceId(planServiceId) {
            for (let rate of this.PlanRateListing) {
                for (let serviceType of rate.serviceTypes) {
                    const service = serviceType.serviceList.find(s => s.planServiceId === planServiceId);
                    if (service) return service;
                }
            }
            return null;
        },
        addRowData(planServiceId) {
            const rates = this.newRateForms[planServiceId].rates;
            if (rates.length > 0) {
                const lastRate = rates[rates.length - 1];
                const form = this.newRateForms[planServiceId];
                form.rates.push({
                    from: lastRate.to + 1,
                    to: null,
                    rate: 0
                });
            }
        },
        removeRowData(planServiceId, index) {
            const form = this.newRateForms[planServiceId];
            if (form.rates.length > 1) {
                form.rates.splice(index, 1);
            }
        },
      saveRate(planServiceId) {
            const vm = this;
            if (!this.validateForm(planServiceId)) return;

            const service = this.findServiceByPlanServiceId(planServiceId);
            if (!service) return;

            const form = this.newRateForms[planServiceId];
            const newStart = new Date(form.effective_start_date);
            const newEnd = form.effective_end_date ? new Date(form.effective_end_date) : null;
            // Improved overlap check with better null handling (ongoing rates)
            const hasOverlap = service.rateDetails.some(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail && detail === form.originalRateDetail;
                if (isCurrentEditing) return false;

                const existingStart = detail.effective_start_date ? new Date(detail.effective_start_date) : null;
                const existingEnd = detail.effective_end_date ? new Date(detail.effective_end_date) : null;
                const newStart = form.effective_start_date ? new Date(form.effective_start_date) : null;
                const newEnd = form.effective_end_date ? new Date(form.effective_end_date) : null;

                if (!existingStart || !newStart) return false;

                // Normalize to date strings (UTC start of day)
                const existingStartStr = existingStart.toISOString().split('T')[0];
                const existingEndStr = existingEnd ? existingEnd.toISOString().split('T')[0] : 'ongoing';
                const newStartStr = newStart.toISOString().split('T')[0];
                const newEndStr = newEnd ? newEnd.toISOString().split('T')[0] : 'ongoing';

                // Overlap if new range intersects existing range
                // Handle 'ongoing' as infinite future
                const newStartBeforeExistingEnd = newEndStr === 'ongoing' || (!existingEndStr || newStartStr <= existingEndStr);
                const existingStartBeforeNewEnd = existingEndStr === 'ongoing' || (!newEndStr || existingStartStr <= newEndStr);
                const rangesOverlap = newStartStr <= existingEndStr && existingStartStr <= newEndStr;

                return rangesOverlap && newStartBeforeExistingEnd && existingStartBeforeNewEnd;
            });

            // Note: This aligns with backend expectations in SavePlanRate/SaveRateOnBehalfOfPlan, where overlapping dates are rejected server-side if not caught here.



            if (hasOverlap) {
                vm.ShowAlert(
                    `${vm.$t("The date range for")} ${service.serviceName} ${vm.$t("service overlaps for existing rate.")}`,
                    "warning",
                    vm.$t('Alert')
                );
                return;
            }

            // Update previous rates with null end dates
            service.rateDetails.forEach(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail && detail === form.originalRateDetail;
                if (!isCurrentEditing && (detail.effective_end_date === null || detail.effective_end_date === undefined)) {
                    detail.effective_end_date = form.effective_start_date;
                }
            });

            const rateDetail = {
                rateLimitId: null,
                limitType: null,
                limitFrom: null,
                limitTo: null,
                customerRateType: null,
                startDate: null,
                effective_start_date: form.effective_start_date,
                endDate: null,
                effective_end_date: form.effective_end_date,
                billing_type: form.billingType,
                rates: form.rates.map(r => ({
                    ...r
                })),
                isCurrent: true
            };

            service.rateDetails.forEach(d => d.isCurrent = false);

            if (form.isEdit && form.originalRateDetail) {
                const idx = service.rateDetails.findIndex(d => d === form.originalRateDetail);
                if (idx !== -1) service.rateDetails[idx] = rateDetail;
            } else {
                service.rateDetails.push(rateDetail);
            }

            this.resetNewRateForm(planServiceId);
            this.toggleCollapse(planServiceId);
        },

        deleteRule(planServiceId, group) {
            const service = this.findServiceByPlanServiceId(planServiceId);
            if (!service || !group) return;
            const index = service.rateDetails.findIndex(d => d === group || d.rates === group.slabs);
            if (index !== -1) {
                service.rateDetails.splice(index, 1);
            }
            if (this.collapseStates[planServiceId]) {
                this.resetNewRateForm(planServiceId);
                this.collapseStates[planServiceId] = false;
            }
        },
        openEditPlan(planServiceId, group) {
             var vm =this;
            vm.IsSave=false;
            vm.IsUpdate=true;
            const service = this.findServiceByPlanServiceId(planServiceId);
            if (!service || !group || !group.originalDetail) return;

            this.$set(this.collapseStates, planServiceId, true);

            this.$set(this.newRateForms, planServiceId, {
                formSubmitted: false,
                isEdit: true,
                originalRateDetail: group.originalDetail, // <-- store original object
                billingType: group.billing_type,
                effective_start_date: group.effective_start_date,
                effective_end_date: group.effective_end_date,
                rates: group.slabs.map(r => ({
                    ...r
                }))
            });
        },

        findServiceByPlanServiceId(planServiceId) {
            for (let rate of this.PlanRateListing) {
                for (let type of rate.serviceTypes) {
                    for (let service of type.serviceList) {
                        if (service.planServiceId === planServiceId) {
                            return service;
                        }
                    }
                }
            }
            return null;
        },
         billingTypeError(planServiceId) {
            const service = this.findServiceByPlanServiceId(planServiceId);
            if (!service) return false;

            // Only validate billing type for 'Usage Based' or 'Support'
            if (service.service_type_name !== 'Usage Based' && service.service_type_name !== 'Support') {
                return false;
            }

            const form = this.newRateForms[planServiceId];
            if (!form || !form.formSubmitted) return false;
            return !form.billingType; // true if empty  error
        },
        startDateError(planServiceId) {
            const form = this.newRateForms[planServiceId];
            if (!form || !form.formSubmitted) return false; // Skip unless triggered
            const date = form.effective_start_date;
            return !date; // True if date is null/empty
        },
        validateForm(planServiceId, triggerSubmitted = true) {
            const form = this.newRateForms[planServiceId];
            if (triggerSubmitted) {
                form.formSubmitted = true; // Per-form trigger
            }
            let isValid = true;

            // Check billing type
            if (this.billingTypeError(planServiceId)) {
                isValid = false;
            }

            // Check start date
            if (this.startDateError(planServiceId)) {
                isValid = false;
            }

            // Check rates: all >=0 and non-null
            if (form.rates && !form.rates.every(r => r.rate >= 0 && r.rate != null)) {
                isValid = false;
            }

            // For Usage Based: check from >=0 and sequential (optional enhancement)
            const service = this.findServiceByPlanServiceId(planServiceId);
             if (service && service.service_type_name === 'Usage Based' && form.rates) {
                for (let i = 0; i < form.rates.length; i++) {
                    if (this.serviceUnitToError(planServiceId, i)) {
                        isValid = false;
                        break;
                    }
                }
            }
            if (service && service.service_type_name === 'Usage Based' && form.rates) {
                for (let i = 1; i < form.rates.length; i++) {
                    if (form.rates[i].from <= form.rates[i-1].to || form.rates[i].from < 0) {
                        isValid = false;
                        break;
                    }
                }
            }
             // For PERCENTAGE: check each rate between 0 and 100, not null
            if (form.billingType === 'PERCENTAGE' && form.rates.some(r => r.rate === null || r.rate > 100 || r.rate < 0)) {
                isValid = false;
            }

            return isValid;
        },
        rateError(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            if (form.billingType !== 'PERCENTAGE') return false;
            const rate = form.rates[ri];
            return rate.rate === null || rate.rate > 100 || rate.rate < 0;
        },

        // Computed for dynamic button disable (runs validate without triggering submitted)
        isFormValid() {
            return (planServiceId) => this.validateForm(planServiceId, false);
        },

        // Proactive validation on field interaction
        validateField(planServiceId, field) {
            this.validateForm(planServiceId, true);
            // Specific field error handling can be added here if needed
        },
        fromDisplay(value) {
            return value !== '' ? parseFloat(value) : null;
        },
          toggleCollapsePlan(index) {
            if (!this.collapseStatesPlan[index]) {
                this.$set(this.collapseStatesPlan, index, true);
                this.resetNewRateFormPlan(index);
            } else {
                this.collapseStatesPlan[index] = !this.collapseStatesPlan[index];
                if (this.collapseStatesPlan[index]) {
                    this.resetNewRateFormPlan(index);
                }
            }
        },

        resetNewRateFormPlan(index) {
            const isFixed = this.ApplicableOnPlanRateDetails.planRateType === 'fixed';
            const toValue = isFixed ? Infinity : null;
            this.$set(this.newRateFormsPlan, index, {
                formSubmitted: false,
                isEdit: false,
                originalRateDetail: null,
                billingType: isFixed ? 'FLAT' : '',
                effective_start_date: null,
                effective_end_date: null,
                rates: [{
                    from: 0,
                    to: toValue,
                    rate: 0,
                    invalidFrom: false
                }]
            });
        },

        addRowPlan(index) {
            if (this.ApplicableOnPlanRateDetails.planRateType !== 'slab') return;
            const rates = this.newRateFormsPlan[index].rates;
            if (rates.length > 0) {
                const lastRate = rates[rates.length - 1];
                rates.push({
                    from: lastRate.to ? lastRate.to + 1 : 1,
                    to: null,
                    rate: 0,
                    invalidFrom: false
                });
            }
        },

        removeRowPlan(index, ri) {
            if (this.ApplicableOnPlanRateDetails.planRateType !== 'slab') return;
            const rates = this.newRateFormsPlan[index].rates;
            if (rates.length > 1) {
                rates.splice(ri, 1);
            }
        },

        savePlanRate(index) {
            if (!this.validatePlanForm(index)) return;

            const rt = this.ApplicableOnPlanRateDetails.rateList[index];
            const form = this.newRateFormsPlan[index];
            const newDetail = {
                rateLimitId: null,
                limitType: null,
                limitFrom: null,
                limitTo: null,
                customerRateType: null,
                startDate: null,
                effective_start_date: form.effective_start_date,
                endDate: null,
                effective_end_date: form.effective_end_date,
                billing_type: form.billingType,
                rates: form.rates.map(r => ({ ...r })),
                isCurrent: true
            };

            // Check overlap and update previous
            const hasOverlap = rt.rateDetails.some(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail === detail;
                if (isCurrentEditing) return false;

                const existingStart = detail.effective_start_date ? new Date(detail.effective_start_date) : null;
                const existingEnd = detail.effective_end_date ? new Date(detail.effective_end_date) : null;
                const newStart = form.effective_start_date ? new Date(form.effective_start_date) : null;
                const newEnd = form.effective_end_date ? new Date(form.effective_end_date) : null;

                if (!existingStart || !newStart) return false;

                const existingStartStr = existingStart.toISOString().split('T')[0];
                const existingEndStr = existingEnd ? existingEnd.toISOString().split('T')[0] : 'ongoing';
                const newStartStr = newStart.toISOString().split('T')[0];
                const newEndStr = newEnd ? newEnd.toISOString().split('T')[0] : 'ongoing';

                const rangesOverlap = newStartStr <= existingEndStr && existingStartStr <= newEndStr;
                const newStartBeforeExistingEnd = newEndStr === 'ongoing' || (!existingEndStr || newStartStr <= existingEndStr);
                const existingStartBeforeNewEnd = existingEndStr === 'ongoing' || (!newEndStr || existingStartStr <= newEndStr);

                return rangesOverlap && newStartBeforeExistingEnd && existingStartBeforeNewEnd;
            });

            if (hasOverlap) {
                this.ShowAlert(
                    this.$t("The date range overlaps with existing rate."),
                    "warning",
                    true,
                    this.$t('Alert')
                );
                return;
            }

            // Update previous ongoing
            rt.rateDetails.forEach(detail => {
                const isCurrentEditing = form.isEdit && form.originalRateDetail === detail;
                if (!isCurrentEditing && (detail.effective_end_date === null || detail.effective_end_date === undefined)) {
                    detail.effective_end_date = form.effective_start_date;
                }
                detail.isCurrent = false;
            });

            if (form.isEdit && form.originalRateDetail) {
                const gidx = rt.rateDetails.findIndex(d => d === form.originalRateDetail);
                if (gidx !== -1) {
                    rt.rateDetails[gidx] = newDetail;
                }
            } else {
                rt.rateDetails.push(newDetail);
            }

            this.resetNewRateFormPlan(index);
            this.collapseStatesPlan[index] = false;
            this.IsSave = true;
            this.IsUpdate = false;
        },

        deletePlanRateDetail(index, gidx) {
            const rt = this.ApplicableOnPlanRateDetails.rateList[index];
            rt.rateDetails.splice(gidx, 1);
            if (rt.rateDetails.length > 0) {
                // Set the latest as current
                rt.rateDetails.sort((a, b) => new Date(b.effective_start_date || 0) - new Date(a.effective_start_date || 0));
                rt.rateDetails[0].isCurrent = true;
            } else {
                // Add default
                const isFixed = this.planRateType === 'fixed';
                const toValue = isFixed ? Infinity : null;
                const billingType = isFixed ? 'FLAT' : 'FLT';
                rt.rateDetails.push({
                    rateLimitId: null,
                    limitType: null,
                    limitFrom: null,
                    limitTo: null,
                    customerRateType: null,
                    startDate: null,
                    effective_start_date: null,
                    endDate: null,
                    effective_end_date: null,
                    billing_type: billingType,
                    rates: [{
                        from: 0,
                        to: toValue,
                        rate: 0,
                        invalidFrom: false
                    }],
                    isCurrent: true
                });
            }
            if (this.collapseStatesPlan[index]) {
                this.collapseStatesPlan[index] = false;
            }
        },

        openEditPlanRate(index, group) {
            this.IsSave = false;
            this.IsUpdate = true;
            this.$set(this.collapseStatesPlan, index, true);
            this.$set(this.newRateFormsPlan, index, {
                formSubmitted: false,
                isEdit: true,
                originalRateDetail: group.originalDetail,
                billingType: group.billing_type,
                effective_start_date: group.effective_start_date,
                effective_end_date: group.effective_end_date,
                rates: group.slabs.map(r => ({ ...r }))
            });
        },

        planStartDateError(index) {
            const form = this.newRateFormsPlan[index];
            if (!form || !form.formSubmitted) return false;
            return !form.effective_start_date;
        },

        planBillingTypeError(index) {
            const form = this.newRateFormsPlan[index];
            if (!form || !form.formSubmitted) return false;
            if (this.ApplicableOnPlanRateDetails.planRateType !== 'slab') return false;
            return !form.billingType;
        },

        validatePlanForm(index, triggerSubmitted = true) {
            const form = this.newRateFormsPlan[index];
            if (triggerSubmitted) {
                form.formSubmitted = true;
            }
            let isValid = true;

            // Check billing type for slab
            if (this.ApplicableOnPlanRateDetails.planRateType === 'slab' && this.planBillingTypeError(index)) {
                isValid = false;
            }

            // Check start date
            if (this.planStartDateError(index)) {
                isValid = false;
            }

            // Check rates
            if (form.rates && !form.rates.every(r => r.rate >= 0 && r.rate != null)) {
                isValid = false;
            }

            if (this.ApplicableOnPlanRateDetails.planRateType === 'slab' && form.rates) {
                for (let i = 0; i < form.rates.length; i++) {
                   
                    // Check UnitTo validation
                    if (this.planUnitToError(index, i)) {
                        isValid = false;
                        break;
                    }
                }
            }

            // For slab: check sequential
            if (this.ApplicableOnPlanRateDetails.planRateType === 'slab' && form.rates) {
                for (let i = 1; i < form.rates.length; i++) {
                    if (form.rates[i].from <= form.rates[i - 1].to || form.rates[i].from < 0) {
                        isValid = false;
                        break;
                    }
                }
            }

            return isValid;
        },

        isPlanFormValid(index) {
            return this.validatePlanForm(index, false);
        },

        validatePlanField(index, field) {
            this.validatePlanForm(index, true);
        },
         enforceRateLimit(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            const currentRate = form.rates[ri];
            if (form.billingType === 'PERCENTAGE' && currentRate.rate > 100) {
                currentRate.rate = null;
            }
            if (currentRate.rate < 0) {
                currentRate.rate = 0;
            }
        },

        onBillingTypeChange(planServiceId) {
            const form = this.newRateForms[planServiceId];
            if (form.billingType === 'PERCENTAGE') {
                form.rates.forEach(rate => {
                    rate.rate = null;
                });
            }
        },
         planUnitToError(index, ri) {
            const form = this.newRateFormsPlan[index];
            if (!form || !form.formSubmitted) return false;
            const rate = form.rates[ri];
            if (rate.to === null || rate.to === undefined || rate.to === Infinity) return false;
            return rate.to < rate.from;
        },
        validatePlanUnitRange(index, ri) {
            const form = this.newRateFormsPlan[index];
            if (form) {
                form.formSubmitted = true;
            }
        },

        serviceUnitToError(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            if (!form || !form.formSubmitted) return false;
            const rate = form.rates[ri];
            // Allow null, undefined, infinity, or equal values - only prevent UnitTo < UnitFrom
            if (rate.to === null || rate.to === undefined || rate.to === Infinity) return false;
            return rate.to < rate.from;
        },

        validateServiceUnitRange(planServiceId, ri) {
            const form = this.newRateForms[planServiceId];
            if (form) {
                form.formSubmitted = true;
            }
        }
    },
    
  };
  </script>
  
<style>
body {
    background-color: #f8f9fa;
