<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Tax') }}</h4>
                        <button type="button" class="close" v-on:click="CloseTaxClient">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">                     
                        <tg-list ref="tgList" :listType="['List']" :IsShowAction="false" :showCheckBox="true" ModuleName="Tax"
                                SubModuleCode="TaxCategory" IdentityColumn="id" :HeaderText="$t('Tax Category')" :ListData="ListingData"
                                :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords"
                                :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchTaxCategoryList" :SortExp="SortExp"
                                :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
                                :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
                                v-on:ActionButtonClick="actionButtonClick">
                                <template v-slot:slotdata="{ data }">                
                                    <template v-if="data.column.COLUMN_NAME == 'name'">                      
                                           <span>{{ data.row.name }} </span>                                               
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'effective_from'">                      
                                        <span>
                                            {{$options.filters.formatDate(data.row.effective_from) }}
                                        </span>                                                
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'effective_to'">                      
                                        <span>
                                            {{$options.filters.formatDate(data.row.effective_to) }}
                                        </span>                                               
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'status_name'">                       
                                        <a class="inactive-disabled">
                                            <span class="text-center text-white d-block" :style="{backgroundColor: data.row.status_bg_color}"> {{ data.row.status_name }} </span>
                                        </a>                                           
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'rate_type'">                      
                                        <span v-if="data.row.rate_type=='P'">
                                            {{$t('Percentage')}}
                                        </span> 
                                        <span v-else>{{ $t('Flat') }}</span>                                              
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'is_slab_rate'">                      
                                        <span v-if="data.row.is_slab_rate">                        
                                            <a class="task-dec" @click="ShowSlabDeails(data.row.id)">{{$t('Slab')}}</a> 
                                        </span> 
                                        <span v-else>{{ data.row.rate }}%</span>                                              
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'rate'"> 
                                        <span>{{ data.row.rate.toFixed(2) }}</span>   
                                    </template>
                                </template>
                        </tg-list>
                         <div class="row flex-row-reverse mt-3 pt-2">
                        <div class="col-lg-6 text-right">                                    
                            <a href="javascript:;" v-on:click="SaveClientTax" title="Save" class="btn btn-success mr-2"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</a>
                            <a href="javascript:;" v-on:click="CloseTaxClient" title="Cancel" class="btn btn-danger"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                        </div> 
                    </div> 
                    </div>
                   
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
export default{  
     props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        }
    },
    data(){
        return{           
            isLoading:false,             
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CompanyId: null,
            TotalRecords: 0,     
            ListingData:[],
            Headers: [
                {
                    "COLUMN_NAME": "name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxName",
                    "DISPLAY_ORDER": 1,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "DESCRIPTION",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "effective_from",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Effective_From",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "effective_to",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "EffectiveTo",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },             
                {
                    "COLUMN_NAME": "rate_type",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "EffectiveTo",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }
                },
                {
                    "COLUMN_NAME": "is_slab_rate",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": false,
                    "DISPLAY_NAME": "TaxRate",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }                  
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "StatusName",
                    "DISPLAY_ORDER": 3,
                    settings : {
                            isInSlot: true
                        }                    
                }
            ]
        }
    },
    created:async function(){ 
        this.FetchTaxCategoryList();
    },
    methods:{ 
        leftCommonSearch(){},
        renderActions(){},
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTaxCategoryList();
        },
        actionButtonClick(){},
        commonHeaderButtonClick(){},
        sortdata(){},
        FetchTaxCategoryList(){ 
            var vm = this;
            vm.isLoading=true;
            var url=`Client=${vm.ClientId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.GetTaxCategoryList(url).then((response)=>{             
                vm.isLoading=false;
                if(response.data.result.DATA.length>0){                    
                    vm.ListingData=response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                }else{
                    vm.ListingData=[];
                    vm.TotalRecords = 0;
                }
            })
        }, 
        CloseTaxClient(){
            var vm = this;
            vm.$emit('CloseTaxClientMap');
        }, 
        SaveClientTax: function () {            
            var vm = this;
            var val = "";
            vm.isLoading = true;            
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });                    
            if (val != null) {                                
                var url=`Ids=${val}&ClientId=${vm.ClientId}`;                
                DataService.SaveClientTax(url).then(function (response) {                         
                    vm.isLoading=false;
                    if(response.data.result.DATA.length>0){
                        if(response.data.result.DATA[0].VALUE=='INSERT'){
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Tax')]), "Success", true, vm.$t('Alert'));  
                            vm.CloseTaxClient();                         
                        }                     
                    }else{
                       vm.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }               
                }) 
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }       
    }
}
</script>
