    methods: {

        async displayPlanDetails() {

            const vm = this;
            const url = `planId=${vm.PlanId}`;

            try {
                const response = await DataService.displayPlanDetails(url);
                if (response.data.status === 'Success') {
                    const result = JSON.parse(response.data.result).data;
                    if (result.length > 0) {
                        vm.planDetail = result;
                        vm.planDetail.forEach((element) => {
                            ['EFFECTIVE_DATE', 'EXPIRATION_DATE'].forEach((key) => {
                                if (element[key]) {
                                    element[key] = new Date(element[key]);
                                }
                            });
                        });
                        vm.paymentType = vm.planDetail[0].PAYMENT_TYPE;
                        vm.planCode = vm.planDetail[0].PLAN_CODE;
                        console.log("vm.planCode", vm.planCode);
                        if (vm.planDetail[0].CLIENT_COUNT > 0) {
                            vm.listActions[1].isDisabled = true;
                        }
                    }
                }
            } catch (error) {
                console.error("Error in displayPlanDetails:", error);
            }
        },

        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Published':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;


                default:
                    returnClass = '';
            }
            return returnClass;
        },
        formatCondition(conditionData) {
            debugger
    try {
        debugger;
        if (!conditionData) return '--';

        let conditions = [];

        // 1 If it's already an array
        if (Array.isArray(conditionData)) {
            // Example: [{condition: "A"}, {condition: "B"}]
            for (const item of conditionData) {
                if (typeof item === 'object' && item.condition) {
                    conditions.push(item.condition);
                } else if (typeof item === 'string' && item.trim() !== '') {
                    conditions.push(item.trim());
                }
            }
        } 
        // 2 If it's a string (maybe JSON or raw)
        else if (typeof conditionData === 'string') {
            if (conditionData.includes('"condition"')) {
                try {
                    const parsed = JSON.parse(conditionData);
                    if (Array.isArray(parsed)) {
                        for (const item of parsed) {
                            if (typeof item === 'object' && item.condition) {
                                conditions.push(item.condition);
                            } else if (typeof item === 'string' && item.trim() !== '') {
                                conditions.push(item.trim());
                            }
                        }
                    } else if (parsed.condition) {
                        conditions.push(parsed.condition);
                    }
                } catch (parseError) {
                    console.warn('JSON parse failed, using raw string');
                    conditions.push(conditionData);
                }
            } else {
                conditions.push(conditionData);
            }
        } 
        // 3 If it's an object
        else if (typeof conditionData === 'object') {
            if (conditionData.condition) {
                conditions.push(conditionData.condition);
            } else {
                conditions.push(String(conditionData));
            }
        }

        // 4 Clean and format
        conditions = conditions
            .map(c => c.replace(/\(\s*([^()]+?)\s*\)/g, '$1').trim())
            .filter(c => c && c !== '--');

        if (conditions.length === 0) return '--';

        // 5 Join multiple conditions with AND
        return conditions.join(' And ');
    } catch (e) {
        console.error('Error in formatCondition:', e);
        console.error('conditionData was:', conditionData);
        return '--';
    }
},

