<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('BillingStartDate') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
       
        MeterMappingId:{
            type: Number,
            required: true
        },
        statusId:{
            type: Number,
            required: true
        },
        StartBillingDate: {
            type: Date,
            required: true
        },
        InstallationDate: {
            type: Date,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
               
                {
                    layoutType: "Single",
                    Data: [
                    
                    {
                    astype: "DateField",                                                
                    label: this.$t('StartBillingDate'),
                    name: "StartBillingDate",
                    value: "",                                                         
                    placeholder: this.$t('Select Date'),
                    validationRules: { 
                        "required": true, 
                        max: 100 
                    },
                    config: {
                    }
                 }
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created() {
        var vm =this;
        const date=vm.StartBillingDate;
        const NewDate = new Date(vm.StartBillingDate);
        vm.StartBillingDate=NewDate;
        this.FormSchema[0].Data[0].value = date==null?new Date().toISOString().substr(0, 10): vm.StartBillingDate; // Set default date to today
        vm.InstallationDate= new Date(vm.InstallationDate);
        this.FormSchema[0].Data[0].config.minDate = vm.InstallationDate;
    },
    methods: {
        
        Close: function () {
            this.$parent.IsSaveDate = false;
        },
        onSubmit(formfields, val) {
            const date = new Date(formfields.StartBillingDate);

            const offsetMs = date.getTimezoneOffset() * 60 * 1000; 
            const localDate = new Date(date.getTime() - offsetMs); 

            const billingDate = localDate.toISOString().slice(0, 19); 

            this.$emit("billingSaved", {
                meterMappingId: this.MeterMappingId,
                billingStartDate: billingDate,
                statusId: this.statusId
            });

            this.Close();
        }

    }
}
</script>