<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Contracts') }}  </h4>
                        <button type="button" class="close" v-on:click="CloseAddContract">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <div id="divDialogViewCCoptionTicket" class="w-100">
                            <div class="table-responsive table-fix-header" style="max-height: 450px; overflow-y: auto;">
                                <table class="table table-bordered dt-responsive nowrap mt-0 mb-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th class="text-center font-weight-bold" width="10%">S.No</th>
                                            <th class="font-weight-bold" width="80%">{{ $t('Contract') }}</th>
                                            <th class="font-weight-bold" width="10%">{{ $t('Status') }}</th>                                         
                                        </tr>
                                    </thead> 
                                    <tbody>
                                        <tr v-for="(item,index) in ListData" :key="index">
                                            <td class="text-center">{{ index+1 }}</td>
                                            <td>{{ item.CHILD_NAME }}</td>
                                            <td class="text-center">
                                                <span class="px-2 d-block text-center text-white" :class="index.STATUS_NAME == Active ? 'bg-success' : 'bg-danger'">{{ item.STATUS_NAME }}</span>
                                            </td>
                                        </tr>
                                    </tbody>                                   
                                </table>
                            </div>
                        </div>
                    </div>   
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    props:{
        ContractHolidayId:{
            type:Number
        }
    },
    data(){
        return{
            isLoading:false,
            ListData:[]
        }
    },
    created:function(){
        var ths =this;
        ths.FetchData();
    },
    methods:{
        FetchData(){            
            var ths=this;
            ths.isLoading=true;
            var url=`holidayId=${ths.ContractHolidayId}`;
            DataService.GetContractsListByHolidayId(url).then(response=>{                
                ths.isLoading=false;
                if(response.data.result.Data.length>0){
                    ths.ListData=response.data.result.Data;
                }
            })
        },
        CloseAddContract(){
            var ths=this;
            ths.$parent.IsContractHolidayModal=false;
        }
    }
}
</script>