<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ProcessDetail') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div v-if="MeterData.length > 0" class="mb-3">
                          <div class="row">
                            <div class="col-4"><strong>Reading From Date:</strong> {{ new Date(MeterData[0].start_date).toLocaleDateString() }}</div>
                            <div class="col-4"><strong>Reading To Date:</strong> {{ new Date(MeterData[0].end_date).toLocaleDateString() }}</div>
                            <div class="col-4"><strong>Total Consumption:</strong> {{ (MeterData[0].consumption).toFixed(2) }}</div>
                          </div>
                          <div class="row mt-3">
                            <div class="col-4"><strong>Total Amount:</strong> <span style="font-size: 18px;">
                              {{ $n(totalAmount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                              <!-- {{ totalAmount.toFixed(2) }} -->
                            </span></div>
                            <div class="col-4"><strong>Subsidy Amount:</strong> <span class="text-danger" style="font-size: 18px;">
                              <!-- {{ subsidyAmount.toFixed(2) }} -->
                               {{ $n(subsidyAmount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                               </span>
                            </div>
                            <div class="col-4"><strong>Net Total without Tax:</strong> <span class="text-success" style="font-size: 18px;">
                              <!-- {{ (totalAmount + subsidyAmount).toFixed(2) }} -->
                              {{ $n(totalAmount + subsidyAmount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                            </span></div>
                          </div>
                        </div>
                        <div>
                          <div class="table-responsive table-fix-header">
                            <table id="tablelistingdata_186498" class="table table-bordered mt-0" style="width: 100%;">
                              <thead class="thead-bg">
                                <tr>
                                  <th id="service" style="text-transform: capitalize;">Service/Subsidy Name</th>
                                  <th id="work_code" style="text-transform: capitalize;">Work Code</th>
                                  <th id="condition" style="text-transform: capitalize;">Condition</th>
                                  <th id="condition" style="text-transform: capitalize;">Subsidy</th>
                                  <th id="reading_value" class="text-right" style="text-transform: capitalize;">Units</th>
                                  <th id="rate" class="text-right" style="text-transform: capitalize;">Rate ({{ CurrencyCode }})</th>
                                  <th id="total_amount" class="text-right" style="text-transform: capitalize;">Amount ({{ CurrencyCode }})</th>
                                  <th id="created_at" style="text-transform: capitalize;">Created At</th>
                                </tr>
                              </thead>
                              <tbody style="font-size: 13px;">
                                <tr v-if="MeterData && MeterData.length === 0">
                                  <td colspan="8" class="text-center text-danger">
                                    No record found
                                  </td>
                                </tr>
                                <tr v-else v-for="(item, index) in MeterData" :key="index">
                                  <td>{{ item.service_name }}</td>
                                  <td>{{ item.work_code_name }}</td>
                                  <td>{{ item.condition_name }}</td>
                                  <td>{{ item.is_for_subsidy }}</td>
                                  <td class="text-right">{{ (item.reading_value).toFixed(2) }}</td>
                                  <td class="text-right">  {{ $n(item.rate, 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                                     <!-- {{ (item.rate).toFixed(2) }} -->
                                  </td>
                                  <td class="text-right" :class="{ 'text-danger': item.is_for_subsidy === 'Yes' }">
                                    <!-- {{ item.is_for_subsidy === 'Yes' ? '-' + Math.abs(item.total_amount).toFixed(2) : item.total_amount.toFixed(2) }} -->
                                    {{ item.is_for_subsidy === 'Yes' ? '-' +  $n(item.total_amount, 'currency', $getLocaleByCurrency(CurrencyCode)) : $n(item.total_amount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}
                                  </td>
                                  <td>{{ $options.filters.formatDateTime(item.created_at) }} </td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    props: {
        MeterReadingId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            MeterData: [],
            isLoading: false,
            errorMessage: '' ,
            CurrencyCode: 'PKR'
        };
    },
    computed: {
        totalAmount() {
            return this.MeterData.filter(item => item.is_for_subsidy === 'No').reduce((sum, item) => sum + Math.abs(item.total_amount), 0);
        },
        subsidyAmount() {
            return -this.MeterData.filter(item => item.is_for_subsidy === 'Yes').reduce((sum, item) => sum + item.total_amount, 0);
        }
    },
    created: async function () {
    var vm = this;
    vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
  },
    methods: {
        Close() {
            this.$parent.IsMeterReadingDetail = false;  
        },

        async FetchDataByMeterReading() {
            this.MeterData = [];
            this.isLoading = true;
            const params = `meterreadingid=${this.MeterReadingId}`;

            try {
                const response = await DataService.GetMeterReadingBYId(params);
                if (response && response.data) {
                    this.MeterData = response.data;  
                } else {
                }
            } catch (error) {
                this.errorMessage = `Error fetching data: ${error.message}`;
                console.error("Error fetching meter reading data:", error);
            } finally {
                this.isLoading = false;  
            }
        }
    },
    mounted() {
        this.FetchDataByMeterReading(); 
    }
};
</script>
