<template>
     <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content" style="max-width:800px">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Update') }} {{ HeaderTitle }}</h4>
                        <button type="button" class="close" v-on:click="CloseConstructionType">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
               
                 <div class="modal-body">
                    <dynamic-form lang="en" :schema="FormSchema" ref="AddTaxCategoryForm"
                            :mandatorytext='MandatoryString' :buttons="buttons"  v-on:OnSubmit="OnSubmit">
                    </dynamic-form>
                 </div> 
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        },
        AddAddProperty: {
            type: Object,
            required: true
        },
        Type: {
            type: String,
            required: true,
        }
    },
    data(){
        return{
            isLoading: false,
            PropertyId: 0, 
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [
                {
                    layoutType: "single",
                    group_name: 'Property Details',
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Property Type'),
                            name: "Property Type",
                            value: "",
                            disabled: false,
                            visibility: false,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Property Type'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Construction Status'),
                            name: "ConstructionStatus",
                            value: "",
                            disabled: false,
                            visibility: false,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Construction Status'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseConstructionType,
                },
            ],
            HeaderTitle:''
        }
    },
    created:function(){
        var ths=this;
        if(ths.Type=='T'){
            ths.FormSchema[0].Data[0].visibility=true;
            ths.HeaderTitle='Property Type';
            ths.GetPropertyType();
        }
        if(ths.Type=='C'){
            ths.FormSchema[0].Data[1].visibility=true;
            ths.HeaderTitle='Construction Status';
            ths.GetPropertConstructioStauts();
        }
        ths.BindPropertyDetails();
    },
    methods:{
        CloseConstructionType(){
            this.$emit('CloseConstructionType');
        },
        GetPropertyType() {
            var vm = this;
            var url = `id=&moduleName=&type=PROPERTY_TYPE&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[0].Data[0].config.options.push(obj);
                                vm.$refs.AddTaxCategoryForm.UpdateKeyValue();
                            })

                        }
                    }
                }
            })
        },
        GetPropertConstructioStauts() {
            var vm = this;
            var url = `id=&moduleName=&type=PROPERTY_CONSTRUCTION_STATUS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item => {
                                let obj = {
                                    value: item.value,
                                    name: item.name
                                };
                                vm.FormSchema[0].Data[1].config.options.push(obj);
                                vm.$refs.AddTaxCategoryForm.UpdateKeyValue();
                            })

                        }
                    }
                }
            })
        },
        BindPropertyDetails() {
            var vm = this;
            vm.PropertyId = vm.AddAddProperty.id 
            vm.FormSchema[0].Data[0].value = vm.AddAddProperty.property_type_id; 
            vm.FormSchema[0].Data[1].value = vm.AddAddProperty.construction_status_id;
        },
        OnSubmit() {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddTaxCategoryForm.validateForm.validate().then(async valid => {
                if (valid) {
                    var Obj = {
                        id: vm.PropertyId,                       
                        property_type_id: vm.FormSchema[0].Data[0].value, 
                        construction_status_id: vm.FormSchema[0].Data[1].value,                                              
                    } 
                    var JsonObj = JSON.stringify(Obj);
                    DataService.SaveClientProperty(JsonObj).then(function (response) {
                        vm.isLoading = false;
                        if (response.data.result.DATA.length > 0) {
                            if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Property')]), "success", true, vm.$t("Alert"));
                                vm.CloseConstructionType();
                            }
                            else if (response.data.result.DATA[0].VALUE == 'EXISTS') {
                                vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Property')]), "warning", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Property')]), "success", true, vm.$t("Alert"));
                                vm.CloseConstructionType();
                            } 
                        }                        
                    });
                }
            })
        }
    }
}
</script>