<template>
    <div>
        <div class="row" v-if="IsShow">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddClient') }}: </span>
                    <span class="float-right">
                        <RouterLink to="/Client/Index" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <Form ref="LeadForm">
                <dynamic-form ref="ManageClientForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="saveForm" />
            </Form>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        ClientData: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            IsShow:true,
            FormSchema: [],
            fileMulitpleData: [],
            buttons: [{
                type: "submit",
                class: "btn btn-success AddLeadDetail_a",
                text: "<i class='fa fa-save pr-2'></i>Save"
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.onCancel,
                isVisible: true,
            }]
        }
    },
    created() {  
        debugger
        var ths =this;
        ths.IsShow=ths.$route.params.id>0?false:true;
        ths.getForm();
    },
    methods: {        
        onCancel: function () {
            var vm = this;
            vm.$router.push({ name: 'ClientListing' });
        },
        getForm() {
            var ths = this;
            ths.isLoading = true;
            var url = `recordId=${ths.$route.params.id}&moduleName=Company&subModuleCode=Client&langCode=en`;
            DataService.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {  
                    ths.FormSchema = ths.$refs.ManageClientForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach(async (item, fi) => {
                            if (item.name == 'is_existing_account') {                                
                                if(item.value==''){
                                    item.value="1"
                                }
                            } 
                            if (item.name == 'user_type_id') {                                
                                if(item.value==''){
                                    item.value=4;
                                }
                            } 
                            if (item.name == 'email_id') {
                                item.disabled= true;
                                item.value=item.value==''?ths.$route.params.email:item.value;
                            } 
                            if (item.name == 'status_id') {                               
                                item.value=1001;
                            }  
                            if(item.name=='client_type'){
                                if(item.value==''){
                                    item.value="individual"
                                }
                            }          
                            if (item.name == 'avatar') {
                                item.showLabel = false;
                                item.config = {
                                    maxSize: 5,
                                    accept: [
                                        {
                                            fileType: 'jpg', 
                                            iconClass: 'fa-file-image-o' 
                                        },
                                        {
                                            fileType: 'jpeg', 
                                            iconClass: 'fa-file-image-o' 
                                        },
                                        {
                                            fileType: 'png', 
                                            iconClass: 'fa-file-image-o' 
                                        },
                                        {
                                            fileType: 'gif', 
                                            iconClass: 'fa-file-image-o' 
                                        },
                                        {
                                            fileType: 'bmp',
                                            iconClass: 'fa-file-image-o'
                                        }
                                    ],
                                    multiple: false,
                                    showSelectedFiles: [],
                                    onDeleteSelectedFiles: ths.onDeleteSelectedFiles,
                                    onClickSelectedFiles: ths.onClickSelectedFiles                                                                    
                                }
                                var attachmentsArray=[];
                                if(item.value != ''){
                                    var attachmentData = {
                                        attachmentId: 0,
                                        attachmentName: JSON.parse(item.value)[0].AttchmentName,
                                        attachmentPath: JSON.parse(item.value)[0].AttachmentPath,
                                        attachmentDesc: JSON.parse(item.value)[0].AttachmentDesc
                                    };
                                    attachmentsArray.push(attachmentData);
                                    if (attachmentsArray.length > 0) {
                                        item.config.showSelectedFiles.push(...attachmentsArray);
                                    }
                                }
                            }
                            item.config.onChange = ths.onChangeEventForFields;
                        });
                    });
                    ths.isLoading = false;
                }
            });
        },      
        onDeleteSelectedFiles:function(){  
            var vm =this;
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach(async (field, fi) => {
                    if (field.name == "avatar") { 
                        vm.$delete(field.config, 'showSelectedFiles');
                        vm.fileMulitpleData=[];
                    }   
                })
            })
        },
        onChangeEventForFields(x, y, z) {
            if (y.name == "avatar") {
                this.GetBase64String(y.value);
            }
            if (y.name == 'is_existing_account') {
                if (y.value == '0') {
                    this.$refs.ManageClientForm.updateProperty("parent_id", "visibility", false);
                    this.$refs.ManageClientForm.updateProperty("account_name", "visibility", true);
                }
                else {
                    this.$refs.ManageClientForm.updateProperty("parent_id", "visibility", true);
                    this.$refs.ManageClientForm.updateProperty("account_name", "visibility", false);
                }
            }
        },
        GetBase64String(evt) {
            try {
                var vm = this;
                var f = evt[0]; 
                var reader = new FileReader();
                let fileObj = {};
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        fileObj.attachement_mime = attachemnetMime,
                        fileObj.fileName = fileName,
                        fileObj.base64String = base64StringEncoded,
                        fileObj.fileSize = f.size,
                        fileObj.contentType = f.type,
                        fileObj.ModuleName = 'Company',
                        fileObj.SubModuleCode = 'Client'
                        vm.fileMulitpleData.push(fileObj);
                    };
                })(f);                
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        saveForm() {
            var ths = this;
            ths.isLoading=true;
            ths.$refs.ManageClientForm.validateForm.validate().then(result => {
                if (result) {
                    ths.isLoading = true;
                    var CustomFieldArray = ths.$refs.ManageClientForm.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);  
   
                    CustomFieldArray.forEach(field => {
        if (field.field_name === "last_name") {
            if (!field.field_value || field.field_value.trim() === "") {
                field.field_value = ".";
            }
        }
    });           
                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Company",
                        subModuleCode: "Client",
                        attachmentData: ths.fileMulitpleData.length > 0 ? ths.fileMulitpleData : null
                    }
                    DataService.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        if (response != null && response.data != null && response.data[0].Status == "Success") {
                            
                            ths.isLoading=false;
                            if (ths.$route.params.id > 0) {                                
                                ths.ShowAlert(ths.$t('UpdatedSuccess', [ths.$t('Client')]), "success", true, ths.$t("Alert")); 
                                 ths.onCancel(); 
                            }
                            else {
                                if(response.data[0].Value.MSG=='DUPLICATE'){
                                    ths.ShowAlert(ths.$t('AlreadyExists', [ths.$t('Email')]), "warning", true, ths.$t("Alert"));
                                }else{
                                    ths.ShowAlert(ths.$t('AddedSuccess', [ths.$t('Client')]), "success", true, ths.$t("Alert"));
                                    var url=`User_Id=${response.data[0].Value.MAIN_ID}`
                                    DataService.SendEmailClient(url).then(response=>{});
                                     ths.onCancel(); 
                                }                                
                            }
                           
                        }
                    })
                    ths.isLoading = false;
                }
            })
        },
    },

}
</script>
