<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t("History") }}</h5>
                            <button type="button" class="close" @click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <div v-if="InvoiceHistory.length">
                                <div class="row">
                                    <!-- {{ InvoiceHistory }} -->
                                    <div class="col-md-6 col-lg-3">
                                        <label><b>{{ $t("InvoiceNumber") }}:</b> {{ InvoiceHistory[0].MainInvoiceNumber
                                            }}</label>
                                    </div>
                                    <div class="col-md-6 col-lg-3">
                                        <label><b>{{ $t("IssueDate") }}:</b> {{ InvoiceHistory[0].MainInvoiceIssueDate
                                            }}</label>
                                    </div>
                                    <div class="col-md-6 col-lg-3">
                                        <label><b>{{ $t("DueDate") }}:</b> {{ InvoiceHistory[0].MainInvoiceDueDate
                                            }}</label>
                                    </div>
                                    <div class="col-md-6 col-lg-3">
                                        <label><b>{{ $t("Amount") }}:</b> {{ $n(InvoiceHistory[0].MainInvocieNetAmount , 'currency', $getLocaleByCurrency(CurrencyCode)) }} </label>
                                    </div>
                                </div>

                                <div class="listing mt-2" v-if="InvoiceHistory[0].InvoiceListingGrid.Count > 0">
                                    <div id="divmemb" class="table-responsive">
                                        <table class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th class="td1">
                                                        <span>InvoiceNumber</span>
                                                    </th>
                                                    <th class="td2 tdcm">
                                                        <span>Amount {{GetUserInfo.Currency}}</span>
                                                    </th>
                                                    <th class="td3 tdcm">
                                                        <span>DueDate</span>
                                                    </th>
                                                    <th class="td4 tdcm">
                                                        <span>PaymentDate</span>
                                                    </th>
                                                    <th class="td4 tdcm">
                                                    <span> Status</span>
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody v-if="InvoiceHistory[0].InvoiceListingGrid.Count > 0">
                                                <!-- @if (Model.InvoiceListingGrid.Count > 0)
                                                { -->
                                                    <!-- foreach (var childInvoice in Model.InvoiceListingGrid)
                                                    { -->
                                                    <tr v-for="(childInvoice,index) in InvoiceHistory[0].InvoiceListingGrid" :key="index"> 
                                                        <td>
                                                            {{ childInvoice.BillNumber }}
                                                        </td>
                                                        <td class="tdcm">{{ childInvoice.BillNetTotal.tofixed(2) }}
                                                        </td>
                                                        <td class="tdcm">
                                                            {{childInvoice.IssueDate}}
                                                        </td>
                                                        <td class="tdcm">
                                                        {{childInvoice.BillDueDate}}
                                                        </td>
                                                        <!-- <td class="tdcm">
                                                            @switch (@childInvoice.StatusCode)
                                                            {
                                                                case "CLBSND":
                                                                <span class="bg-success px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                                case "CLBDSPT":
                                                                <span class="bg-info px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                                case "CLBPAD":
                                                                <span class="bg-primary px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                                case "CLBRSTP":
                                                                <span class="bg-danger px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                                case "CLBBDBT":
                                                                <span class="bg-dark px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                                case "CLBDFT":
                                                                <span class="bg-warning px-2 py-1 text-white text-center">@childInvoice.StatusName</span>break;
                                                            }
                                                        </td> -->
                                                        <td class="tdcm">
                                                            <span :class="getStatusClass(childInvoice.StatusCode)" class="px-2 py-1 text-white text-center">
                                                                {{ childInvoice.StatusName }}
                                                                </span>
                                                        </td>
                                                    </tr>
                                            </tbody>
                                            <tbody v-else>
                                                <tr>
                                                    <td colspan="5" class="text-center text-danger py-3">
                                                        {{$t('NoRecordfound') }}
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>

                                <div id="divPostComment" class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <ValidationProvider rules="required" v-slot="{ errors }"
                                                class="tt_textarea_span position-relative" name="title" ref="commentValidator">
                                                <label>Comment:<span class="mandatory">*</span></label>
                                                <textarea name="title" class="form-control " v-model="comment" v-on:keydown="textCounter" v-on:keyup="textCounter" v-on:mouseout="textCounter"    
                                                    :class="errors[0] ? 'is-invalid' : ''" />
                                                <span class="invalid-feedback">{{ errors[0] ? $t('Comment') : ''
                                                    }}</span>
                                            </ValidationProvider>
                                            <br />
                                             <small>
                                                 <strong> <span class="alert-warning px-2 mr-2" id="txtCount">{{ textCountertlength }}</span> </strong>
                                                    {{ $t('CharactersLeft') }}
                                             </small>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <div class="search-btm-btn">
                                                <a href="javascript:;" class="btn btn-success" id="saveComment"
                                                    name="aSave" title="PostNewComment" @click="SaveComment"><i
                                                        class="fa fa-save pr-2"></i>{{ $t("PostNewComment") }}</a>
                                                <a class="btn btn-danger ml-2" href="javascript:;"
                                                    @click="Close" title="Cancel"><i
                                                        class="fa fa-close pr-2"></i>{{ $t("Cancel") }}</a>
                                            </div>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2 text-danger">
                                                {{ $t('MandatoryString') }}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="clearfix"></div>
                                <div id="accordion" class="w-100 activity-tab mt-3">
                                    <div class="card" v-for="(item, index) in InvoiceHistory[0].CommentList"
                                                                :key="index">
                                                                <div class="card-header">
                                                                    <a class="card-link" data-toggle="collapse"
                                                                        :href="'#CommentAccordion' + index">
                                                                        <div class="conversation-date heading-border">
                                                                            <span>{{
                                                                               item.DateGroup
                                                                                }}</span>
                                                                        </div>
                                                                    </a>
                                                                </div>
                                                                <div :id="'CommentAccordion' + index" class="collapse show"
                                                                    data-parent="#accordion">
                                                                    <div class="card-body" v-for="(data,i) in item.CommentList">
                                                                        <div class="conversation-main">
                                                                            
                                                                            <div class="conversation-time"><span>
                                                {{FormatDateToTime(data.CommnetAt ) }}
                                                                                    </span></div>
                                                                            <div class="conversation-box-con">
                                                                                <div class="conversation-box">
                                                                                    <div class="name-circle light-yellow">
                                                                                        <img class="img-circle avatar" v-if="item.AVATAR!=null"
                                                                                            :src="'https://cogniterblob.blob.core.windows.net/' +GetUserInfo.ContainerName + '/Upload/UserProfileImage/' + item.AVATAR"
                                                                                            width="51" height="51"
                                                                                            alt="Image">
                                                                                            <img v-else class="img-circle avatar" src="/Content/images/DefaultImages/avatar-icon.jpg" width="51" height="51" alt="Image" />
                                                                                    </div>
                                                                                    <div
                                                                                        class="conversation-header addednote-ticket">
                                                                                        <span>
                                                                                            <span
                                                                                                class="sender-name"></span>
                                                                                            <span class="sender-department"
                                                                                                :title="data.Comment">
                                                                                                {{ data.Comment }}</span>
                                                                                        </span>
                                                                                    </div>
                                                                                    <div class="conversation-content">
                                                                                        <div class="col-12 p-0 notesdata">

                                                                                            <h6 class="text-muted time">
                                                                                                <i><b>{{ data.CommentByName
                                                                                                        }}</b>,
                                                                                                    Posted on: <span
                                                                                                        class="text-danger">
                                                                                                       {{data.CommnetAt}}  
                                                                                                         </span> </i></h6>
                                                                                        </div>
                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
import moment from 'moment';
export default {
    props: {
        BillId: { type: Number }
    },
    data() {
        return {
            isLoading: false,
            InvoiceHistory: [],
            textCountertlength: 2000,
            comment:'',
            CurrencyCode: 'PKR'
        };
    },
    created() {
        var vm=this;
        this.fetchData();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
        async fetchData() {
            debugger
            this.isLoading = true;
            try {
                debugger
                const response = await DataService.InvoiceHistory(`invoiceId=${this.BillId}`);
                const groupedData = this.groupInvoiceData(response.data);
                this.InvoiceHistory = groupedData;

            } catch (error) {
                console.error("Error fetching invoice data", error);
            } finally {
                this.isLoading = false;
            }
        },
        groupInvoiceData(data) {
            const grouped = [];
            debugger;
            data.forEach((info) => {
                let invoice = grouped.find((g) => g.MainInvoiceId === info.BILL_ID);
                if (!invoice) {
                    invoice = {
                        MainInvoiceId: info.BILL_ID,
                        MainClientId: info.BILL_TO,
                        MainInvoiceNumber: info.BILL_NUMBER,
                        MainInvocieNetAmount: info.BILL_NET_TOTAL || 0,
                        MainInvoiceDueDate: new Date(info.DUE_DATE).toLocaleDateString(),
                        MainInvoiceIssueDate: new Date(info.ISSUE_DATE).toLocaleDateString(),
                        MainInvocieAmount: info.AMOUNT || 0,
                        CommentList: [],
                        InvoiceListingGrid: []
                    };
                    grouped.push(invoice);
                }
                this.addInvoiceDetails(invoice, info);
            });
            return grouped;
        },
        addInvoiceDetails(invoice, info) {
            if (!invoice.CommentList) {
                invoice.CommentList = [];
            }

            if (info.ISPARENT === 1 && info.COMMENT) {
                let createdDate = new Date(info.CREATED_AT);
                let dateKey = `${String(createdDate.getDate()).padStart(2, '0')}-${createdDate.toLocaleString('en-US', { month: 'short' })}-${createdDate.getFullYear()}`;

                // Find the existing group for this dateKey
                let existingGroup = invoice.CommentList.find(group => group.DateGroup === dateKey);

                if (!existingGroup) {
                    // Create a new group if it doesn't exist
                    existingGroup = {
                        DateGroup: dateKey,
                        CommentList: []
                    };
                    invoice.CommentList.push(existingGroup);
                }

                // Add the comment to the group
                existingGroup.CommentList.push({
                    CommnetAt: info.CREATED_AT ? new Date(info.CREATED_AT).toLocaleString() : '',
                    Comment: info.COMMENT,
                    CommentByName: info.CLIENT_NAME || 'Unknown Client',
                    UserTypeName: info.USER_TYPE_NAME || 'Unknown Type',
                    AVATAR: info.AVATAR
                });
            }

            if (info.ISPARENT === 0) {
                if (!invoice.InvoiceListingGrid) {
                    invoice.InvoiceListingGrid = [];
                }

                invoice.InvoiceListingGrid.push({
                    BillId: info.BILL_ID,
                    BillNumber: info.BILL_NUMBER,
                    ClientName: info.CLIENT_NAME,
                    ProjectName: info.PROJECT_NAME,
                    BillDueDate: new Date(info.DUE_DATE).toLocaleDateString(),
                    IssueDate: new Date(info.ISSUE_DATE).toLocaleDateString(),
                    BillNetTotal: info.BILL_NET_TOTAL || 0,
                    StatusName: info.STATUS_NAME
                });
            }
        },
        Close() {
            this.$parent.isInvoiceHistory = false;
        },
        textCounter() {
            this.textCountertlength = 2000 - this.comment.length
        },
        FormatDateToTime(Getdate) {
            
            const date = new Date(Getdate.replace(/(\d{2})\/(\d{2})\/(\d{4}), (\d{2}):(\d{2}):(\d{2})/, '$2/$1/$3 $4:$5:$6'));

            let hours = date.getHours();
            let minutes = date.getMinutes();
            let ampm = hours >= 12 ? 'PM' : 'AM';

            // Convert to 12-hour format
            hours = hours % 12;
            hours = hours ? hours : 12; // the hour '0' should be '12'
            minutes = minutes < 10 ? '0' + minutes : minutes;

            const timeString = `${hours}:${minutes} ${ampm}`;
            console.log(timeString);
            return timeString;

        },
        async SaveComment() {
            var params = {
                invoiceId: this.BillId,
                comment: this.comment,
                invoiceNo: this.InvoiceHistory[0].MainInvoiceNumber,
                clientId: this.InvoiceHistory[0].MainClientId
            };
            debugger;
            let param =JSON.stringify(params);
            const response = await DataService.CommentOnInvoice(param);
            if (response.data.Status == "Success") {
                var notificationData={
                    message:response.data.Comment,
                    notificationFor:response.data.NotificationFor,
                    typeCode:'CB_INV_COMMENT',
                    referenceId:response.data.InvoiceId,
                    referenceId2:0
                }
                let data =JSON.stringify(notificationData);
                const res = await DataService.InsertNotification(data);
                this.comment = '';
                this.$refs.commentValidator.reset();
                this.fetchData();
            }
             else {
                  this.ShowAlert(this.$t("ErrorWhileProcessingRequest"), "failure", true, this.$t("Alert"));
             }
        },
        getStatusClass(statusCode) {
            switch (statusCode) {
                case "CLBSND":
                return "bg-success";
                case "CLBDSPT":
                return "bg-info";
                case "CLBPAD":
                return "bg-primary";
                case "CLBRSTP":
                return "bg-danger";
                case "CLBBDBT":
                return "bg-dark";
                case "CLBDFT":
                return "bg-warning";
                default:
                return "bg-secondary";
        }
    }

    },
};
</script>