<template lang="en">
<section>
    <loader :is-visible="isLoading"></loader>
    <div class="theme-primary partition">
        <span class="p-name text-white">  
        {{ $t('ConfirmSubscriptionChanges') }}  
        <strong v-if="selectedBilling != PreselectedBilling">
            ({{ PreselectedBilling }}  {{ selectedBilling }})
        </strong>
        </span>
        <span class="p-name text-white float-right">  
        {{ $t('Plan') }}   {{ PlanName }}
        </span>
    </div>

    <div class="listing">
        <div class="table-responsive table-fix-header mt-2 px-0" v-if="RateApplicableOnPlan">
            <table id="example" class="table table-bordered mb-0" style="width:100%;">
                <thead class="thead-bg">
                    <tr>
                        <th style="min-width:250px;"><span class="font-weight-500">{{ $t('Plan') }} </span></th>
                        <th style="min-width:200px;"><span class="font-weight-500">{{ $t('Previous') }} </span></th>
                        <th style="min-width:200px;"><span class="font-weight-500">{{ $t('New') }} </span></th>
                        <th style="min-width:150px;"><span class="font-weight-500">{{ $t('Change') }} </span></th>
                        <th style="min-width:150px;" class="text-right"><span class="font-weight-500">{{ $t('PriceImpact') }} </span></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{{PlanName}}</td>
                        <td>{{PlanExistingUnit}}</td>
                        <td>{{PlanExistingUnit + Plan_changed_unit}}</td>
                        <td>
                            <span v-if="Plan_changed_unit === 0">{{ $t('NoChange') }} </span>
                            <span v-else-if="Plan_changed_unit > 0" class="text-success">
                                + {{ Plan_changed_unit}} <em class="fa fa-arrow-up"></em>
                            </span>
                            <span v-else class=" text-danger">
                                {{ Plan_changed_unit }} <em class="fa fa-arrow-down"></em>
                            </span>
                        </td>
                        <td class="text-right">
                            <span v-if="((PlanExistingUnit+ Plan_changed_unit) * PlanRate)  - (PlanExistingUnit * FrequencyRate) === 0">
                                {{ $n(0.00, "currency", $getLocaleByCurrency(CurrencyCode))}}
                            </span>
                            <span v-else-if="(PlanRate* PlanUnit) > (FrequencyRate * PlanExistingUnit)" class="text-success">
                                + {{
                                                    PlanRate > 0
                                                        ? $n(((PlanExistingUnit + Plan_changed_unit) * PlanRate)  - (PlanExistingUnit* FrequencyRate), "currency", $getLocaleByCurrency(CurrencyCode))
                                                        : $n(0, "currency", $getLocaleByCurrency(CurrencyCode))
                                                    }}
                                <em class="fa fa-arrow-up"></em>
                            </span>
                            <span v-else class="text-danger">
                                {{
                                                    PlanRate > 0
                                                        ? $n(((PlanExistingUnit+ Plan_changed_unit) * PlanRate)  - (PlanExistingUnit * FrequencyRate), "currency", $getLocaleByCurrency(CurrencyCode))
                                                        : $n(0, "currency", $getLocaleByCurrency(CurrencyCode))
                                                    }}
                                <em class="fa fa-arrow-down"></em>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div class="table-responsive col-lg-12 mt-2 px-0" v-else>
            <table id="example" class="table table-bordered mb-0" style="width:100%;">
                <thead class="thead-bg">
                    <tr>
                        <th style="min-width:250px;"><span class="font-weight-500">{{ $t('Service') }} </span></th>
                        <th style="min-width:200px;"><span class="font-weight-500">{{ $t('Previous') }} </span></th>
                        <th style="min-width:200px;"><span class="font-weight-500">{{ $t('New') }} </span></th>
                        <th style="min-width:150px;"><span class="font-weight-500">{{ $t('Change') }} </span></th>
                        <th style="min-width:150px;" class="text-right"><span class="font-weight-500">{{ $t('PriceImpact') }} </span></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in servicesData">
                        <td>{{item.service_name}}</td>
                        <td>{{item.Existing_unit}}</td>
                        <td>{{item.Existing_unit + item.Changed_unit}}</td>
                        <td>
                            <span v-if="item.Changed_unit === 0">{{ $t('NoChange') }} </span>
                            <span v-else-if="item.Changed_unit > 0" class="text-success">
                                + {{ item.Changed_unit }} <em class="fa fa-arrow-up"></em>
                            </span>
                            <span v-else class=" text-danger">
                                {{ item.Changed_unit }} <em class="fa fa-arrow-down"></em>
                            </span>
                        </td>
                        <td class="text-right" v-if="item.ServiceRate.length>1">
                             <span v-if="item.service_type_code === 'Support' || item.PriceImpact === 0">
                                 {{ $n(0.00, "currency", $getLocaleByCurrency(CurrencyCode))}}
                             </span>
                             <span v-else-if="item.PriceImpact>0" class="text-success">
                                 +   {{ $n(item.PriceImpact, "currency", $getLocaleByCurrency(CurrencyCode))}}
                                 <em class="fa fa-arrow-up"></em>
                             </span>
                             <span v-else class="text-danger">
                                {{ $n(item.PriceImpact, "currency", $getLocaleByCurrency(CurrencyCode))}}
                                 <em class="fa fa-arrow-down"></em>
                             </span>
                         </td>
                       <td class="text-right" v-else>
                             <span v-if="item.service_type_code === 'Support'">
                                {{ $n(0.00, "currency", $getLocaleByCurrency(CurrencyCode))}}
                            </span>
                            <span v-else-if="((item.Existing_unit + item.Changed_unit) * item.ServiceRate[0].rate)  - (item.Existing_unit*item.ServiceRate[0].Freqrate) === 0">
                                {{ $n(0.00, "currency", $getLocaleByCurrency(CurrencyCode))}}
                            </span>
                            <span v-else-if="(item.ServiceRate[0].rate* item.Unit) > (item.ServiceRate[0].Freqrate * item.Existing_unit)" class="text-success">
                                + {{
                                                    item.ServiceRate && item.ServiceRate.length > 0
                                                        ? $n(((item.Existing_unit + item.Changed_unit) * item.ServiceRate[0].rate)  - (item.Existing_unit*item.ServiceRate[0].Freqrate), "currency", $getLocaleByCurrency(CurrencyCode))
                                                        : $n(0, "currency", $getLocaleByCurrency(CurrencyCode))
                                                    }}
                                <em class="fa fa-arrow-up"></em>
                            </span>
                            <span v-else class="text-danger">
                                {{
                                                    item.ServiceRate && item.ServiceRate.length > 0
                                                        ? $n(((item.Existing_unit + item.Changed_unit) * item.ServiceRate[0].rate)  - (item.Existing_unit*item.ServiceRate[0].Freqrate), "currency", $getLocaleByCurrency(CurrencyCode))
                                                        : $n(0, "currency", $getLocaleByCurrency(CurrencyCode))
                                                    }}
                                <em class="fa fa-arrow-down"></em>
                            </span>
                        </td>
                         
                    </tr>
                </tbody>
            </table>
        </div>

    </div>

    <div class="col-12 float-left mt-4 p-0">

        <div class="heading-border">
            <h5 class="h5 text-capitalize"><span>{{ $t('PricingSummary') }}</span></h5>
        </div>

        <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">

            <div class="col-sm-6 col-md-6 col-xl-6 p-2"> {{ $t('PreviousAmount') }} :</div>
            <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2"> {{ $n(BillingAmount, "currency", $getLocaleByCurrency(CurrencyCode))}} </div>
        </div>
        <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">
            <div class="col-sm-6 col-md-6 col-xl-6 p-2"> {{ $t('AdditionalAmount') }}  :</div>
            <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2"> {{ $n(IncreasedAmount, "currency", $getLocaleByCurrency(CurrencyCode))}} </div>
        </div>
        <div v-if="DiscountData && DiscountData.length > 0" style="margin-top: 12px;">
            <!-- Header -->

            <div class="heading-border">
                <h5 class="h5 text-capitalize"><span>{{ $t('Discounts') }}</span></h5>
            </div>

            <!-- List of Discounts -->
            <div v-for="discount in DiscountData" :key="discount.discountName" class="d-flex justify-content-between font-16 my-2 rowhighlighter">
                <div class="col-sm-6 col-md-6 col-xl-6 p-2">{{ discount.discountName || 'Discount' }}:</div>
                <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2">-{{ $n(Number(discount.calculatedDiscount), "currency", $getLocaleByCurrency(CurrencyCode)) }}</div>
            </div>
        </div>

        <!-- Tax -->
        <div v-if="TaxData && TaxData.length > 0" style="margin-top: 12px;">

            <div class="heading-border">
                <h5 class="h5 text-capitalize"><span>{{ $t('Taxes') }}</span></h5>
            </div>

            <div v-for="tax in TaxData" :key="tax.taxName" class="d-flex justify-content-between font-16 my-2 rowhighlighter">
                <div class="col-sm-6 col-md-6 col-xl-6 p-2">{{ tax.taxName || 'Tax' }}:</div>
                <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2">+{{ $n(Number(tax.CalculatedTax), "currency", $getLocaleByCurrency(CurrencyCode)) }}</div>
            </div>
        </div>

        <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">
            <div class="col-sm-6 col-md-6 col-xl-6 p-2 font-weight-medium">New {{ selectedBilling }} Cost:</div>
            <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2 font-weight-medium">
                {{
      $n(
        Number(BillingAmount || 0) +
        Number(IncreasedAmount || 0) -
        getTotalDiscount() +
        getTotalTax(),
        "currency", $getLocaleByCurrency(CurrencyCode)
      )
    }}
            </div>
        </div>

        <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">
            <div class="col-sm-6 col-md-6 col-xl-6 p-2">{{ $t('ProrataCalculatedAmount') }} <a href="javascript:;" title="View All" v-if="EstimatedAmount !=0" @click="ViewProrataList()" ><em class="fa fa-eye text-info action-icon"></em></a>:</div>
            <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2 ">
                {{
      $n(
        EstimatedAmount,
        "currency", $getLocaleByCurrency(CurrencyCode)
      )
    }}
   
            </div>
        </div>

      <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">
  <div class="col-sm-6 col-md-6 col-xl-6 p-2">
    Previous Credit Balance:
  </div>
  <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2">
    <span v-if="PreviousCreditAmount === 0">
      {{ $n(0, "currency", $getLocaleByCurrency(CurrencyCode)) }}
    </span>
    <span v-else-if="PreviousCreditAmount > 0" class="text-success">
      +{{ $n(Math.abs(PreviousCreditAmount), "currency", $getLocaleByCurrency(CurrencyCode)) }}
    </span>
    <span v-else class="text-danger">
      {{ $n(Math.abs(PreviousCreditAmount), "currency", $getLocaleByCurrency(CurrencyCode)) }}
    </span>
  </div>
</div>

    <div class="d-flex justify-content-between font-16 my-2 rowhighlighter">
  <div class="col-sm-6 col-md-6 col-xl-6 p-2 font-weight-medium">
    <div v-if="EstimatePayableAmount > 0 ">
       <span v-if="PlanType=='Postpaid'|| IsProrate==false">{{ $t('NextBillingCyclePayableAmount') }}</span><span v-else>{{ $t('PayableAmount') }}</span>:
    </div>
    <div v-else-if="EstimatePayableAmount < 0">
      <span v-if="PlanType=='Postpaid'|| IsProrate==false">{{ $t('NextBillingCycleAvailableCreditBalance') }}</span><span v-else>{{ $t('AvailableCreditBalance') }}</span>:
    </div>
    <div v-else>
       Payable {{ selectedBilling }} Cost:
    </div>
  </div>
  <div class="col-sm-6 col-md-6 col-xl-6 text-right p-2 font-weight-medium">
    <span v-if="EstimatePayableAmount === 0">
      {{ $n(0, "currency", $getLocaleByCurrency(CurrencyCode)) }}
    </span>
    <span v-else-if="EstimatePayableAmount > 0" class="text-success">
      {{ $n(EstimatePayableAmount, "currency", $getLocaleByCurrency(CurrencyCode)) }}
    </span>
  <span v-else class="text-danger">
  {{ $n(Math.abs(EstimatePayableAmount), "currency", $getLocaleByCurrency(CurrencyCode)) }}

</span>
  </div>
</div>
       <p style="font-size: 13px; color: #555; margin-top: 10px;">
  <strong>Note:</strong>
  {{ $t('AdjustmentAccountsCurrentBillingPeriod') }}
  <strong>
    {{
      $n(Math.abs(EstimatePayableAmount), "currency", $getLocaleByCurrency(CurrencyCode))
    }}
  </strong>
  {{
    EstimatePayableAmount >= 0
      ? $t('WillBeCharged')
      : $t('Available Credited Balance')
  }}.
</p>
 <p style="font-size: 14px; color: #555; margin-top: 10px;" v-if="selectedBilling != PreselectedBilling">
        <strong>{{$t("BillingCycleNotes")}}</strong>
      </p>
        <!-- <div class="mt-6" style="margin-top: 16px; display: flex; gap: 12px; justify-content: flex-end;">
            <button @click="generateInvoice">{{ $t('ConfirmChanges') }}</button>
            <button @click="ClosePopUp">{{ $t('Cancel') }}</button>
        </div> -->


<div class="row flex-row-reverse mb-2">
                        <div class="col-lg-12 text-right">
                            <a title="Click here to save record" href="javascript:;" class="submitbtn btn btn-primary formbtn widthhalf mr-2" id="hlkSave" @click="ClosePopUp">
                       <em class="fa fa-angle-double-left pr-2"></em>{{ $t('Back') }}
                            </a>
                            <a title="Click here to save record" href="javascript:;" class="submitbtn btn btn-success formbtn widthhalf mr-2" id="hlkSave" @click="generateInvoice">
                                <em class="fa fa-save pr-2 text-white"></em>{{ $t('ConfirmChanges') }}
                            </a>
                            <a title="Cancel" href="javascript:;" class="submitbtn btn btn-danger formbtn widthhalf" id="hlkCancel" @click="$emit('backtoPlanList')">
                                <em class="fa fa-close pr-2 text-white"></em>{{ $t('Cancel') }}
                            </a>
                        </div>
                    </div>
                </div>
    <transition name="modal">
                <div class="my-popups">
                    <div class="modal d-block dialog" v-if="isShowProrataData">
                        <div class="modal-dialog modal-lg">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h4 class="modal-title">{{ $t('Details') }}</h4>

                                    <button type="button" class="close" v-on:click="Closedetails">
                                        <em class="fa fa-times" aria-hidden="true"></em>
                                    </button>

                                </div>

                                <div class="modal-body">
                                   <div class="listing" v-if="ProrataDescription && ProrataDescription.length">
                                        <div class="table-responsive table-fix-header mt-2 px-0">
                                            <table id="example" class="table table-bordered mb-0" style="width:100%;">
                                            <thead class="thead-bg">
                                                <tr>
                                                <th style="min-width:190px;"><span class="font-weight-500">{{ $t('ServiceAndPlan') }} </span></th>
                                                <th style="min-width:110px;"><span class="font-weight-500">{{ $t('From') }} </span></th>
                                                <th style="min-width:110px;"><span class="font-weight-500">{{ $t('To') }} </span></th>
                                                <th style="min-width:90px;"><span class="font-weight-500">{{ $t('BillingDays') }} </span></th>
                                                <th style="min-width:90px;"><span class="font-weight-500">{{ $t('PlanDays') }} </span></th>
                                                <th style="min-width:50px;"><span class="font-weight-500">{{ $t('P.F') }} </span></th>
                                                <th style="min-width:70px;"><span class="font-weight-500">{{ $t('Units') }} </span></th>
                                                <th style="min-width:70px;"><span class="font-weight-500">{{ $t('Rate') }} </span></th>
                                                <th><span class="font-weight-500">{{ $t('Amount') }} </span></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in ProrataDescription" :key="index">
                                                <td>{{ item["service/plan"] }}</td>
                                                <td>{{$options.filters.formatDate( item.from) }}</td>
                                                <td>{{$options.filters.formatDate(item.to) }}</td>
                                                <td>{{ item.billing_days }}</td>
                                                <td>{{ item.plan_days }}</td>
                                                <td>{{ Number(item.multiply_factor).toFixed(4) }}</td>
                                                <td>{{ item.units }}</td>
                                                <td>{{ $n(item.rate, "currency", $getLocaleByCurrency(CurrencyCode)) }}</td>
                                                <td>{{ $n(item.amount, "currency", $getLocaleByCurrency(CurrencyCode))}}</td>
                                                </tr>
                                            </tbody>
                                          
                                            </table>
                                        </div>
                                        </div>
                                         <p style="font-size: 14px; color: #555; margin-top: 30px;">
                                            <strong> {{$t("ProrationFactorDays")}}</strong>
                                        </p>
                                    </div>
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
</section>
</template>

<script>
import DataService from '../../../services/DataService';
export default {

    props: {
        invoiceData: {
            type: Object
        },
        plandetails: {
            type: Object
        },
        UserId: {
            type: Number
        },
        EstimatedAmount: {
            type: Number
        },
        EstimatePayableAmount: {
            type: Number
        },
        EstimatedAdjustmentAmount: {
            type: Number
        },
        PreviousCreditAmount:{
            type: Number
        },
        SelectedDate:{
         type: Date
        },
        selectedBilling:{
            type: String
        },
        PreselectedBilling:{
            type: String
        },
        descriptionData:{
            type: String
        },
        MultiplyFrequencyFactor:{
                 type: Number
        },
        IsProrate:{
             type: String
        },
        PlanType:{
             type: String
        }
    },

    data() {
        return {
            isLoading: false,
            servicesData: [],
            PlanDetailsData: [],
            RateApplicableOnPlan: false,
            PlanName: '',
            PlanUnit: '',
            PlanExistingUnit: '',
            PlanRate: '',
            Plan_changed_unit: '',
            BillingAmount: '',
            IncreasedAmount: '',
            TaxData: [],
            CurrencyCode: 'PKR',
            FrequencyRate: '',
            isShowProrataData: false,
            ProrataDescription:[]
        }
    },
    async created() {
        var vm = this;
        vm.servicesData = vm.invoiceData.plans[0].services;
        vm.servicesData.forEach(function (itm) {
            if(itm.ServiceRate && itm.ServiceRate.length>1){
                itm.PriceImpact=  itm.newCaluclateRate-itm.calculatedRate;
            }
            if (itm.ServiceRate && itm.ServiceRate.length > 1) {
                itm.ServiceRate.forEach(function (rateSlab) {
                    rateSlab.Freqrate= rateSlab.Pre_rate* vm.MultiplyFrequencyFactor;
                    rateSlab.rate=rateSlab.Freqrate;
                });
            }
        });
        vm.RateApplicableOnPlan = vm.invoiceData.plans[0].is_rate_applicable_on_plan;
        vm.PlanDetailsData = vm.invoiceData.plans[0];
        vm.PlanName = vm.PlanDetailsData.Plan_name;
        vm.PlanUnit = vm.PlanDetailsData.PlanUnits;
        vm.PlanExistingUnit = vm.PlanDetailsData.plan_Existing_unit;
        vm.PlanRate = vm.PlanDetailsData.PlanRate;
        vm.Plan_changed_unit = vm.PlanDetailsData.Plan_changed_unit;
        vm.BillingAmount = vm.plandetails[0].billing_amount;
        vm.TaxData = vm.invoiceData.plans[0].tax;
        vm.DiscountData = vm.invoiceData.plans[0].discount;
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
        vm.FrequencyRate= vm.PlanDetailsData.Freqrate;
        try {
            if (typeof vm.descriptionData === 'string') {
            vm.ProrataDescription = JSON.parse(vm.descriptionData);
            }
        } catch (e) {
            console.error("Invalid descriptionData JSON", e, vm.descriptionData);
            vm.ProrataDescription = [];
        }
        if (vm.RateApplicableOnPlan == true) {
            //vm.IncreasedAmount = vm.Plan_changed_unit * vm.PlanRate
             const existingAmount= (vm.PlanDetailsData.plan_Existing_unit + vm.PlanDetailsData.Plan_changed_unit)* vm.PlanDetailsData.PlanRate;
             const newAmount= vm.PlanDetailsData.plan_Existing_unit * vm.PlanDetailsData.Freqrate;
             const difference = existingAmount - newAmount;
             vm.IncreasedAmount += difference;
        } else {
            let NewAmount = 0;
            vm.servicesData.forEach(function (itm) {
                if (itm.ServiceRate.length > 1) {
                    if (itm.service_type_code === 'Support') {
                        NewAmount += 0;
                    } else {
                        NewAmount += parseFloat(itm.PriceImpact || 0);
                    }
                } else {
                    if (itm.service_type_code === 'Support') {
                        NewAmount += 0;
                    } else {
                        const freqRate = parseFloat(itm.ServiceRate[0].Freqrate || 0);
                        const newRate = parseFloat(itm.ServiceRate[0].rate || 0);
                        const existingUnit = parseFloat(itm.Existing_unit || 0);
                        const changedUnit = parseFloat(itm.Changed_unit || 0);

                        const existingAmount = (existingUnit + changedUnit) * newRate;
                        const adjustedAmount = existingUnit * freqRate;
                        const difference = existingAmount - adjustedAmount;

                        NewAmount += difference;
                    }
                }
            });

            vm.IncreasedAmount = NewAmount;
        }
    },
    methods: {
        getTotalDiscount() {
            return this.DiscountData ?.reduce((total, discount) => {
                return total + Number(discount.calculatedDiscount || 0);
            }, 0) || 0;
        },
        getTotalTax() {
            return this.TaxData ?.reduce((total, tax) => {
                return total + Number(tax.CalculatedTax || 0);
            }, 0) || 0;
        },
        ViewProrataList(){
            var vm=this;
            vm.isShowProrataData = true;
        },
        Closedetails(){
               var vm=this;
            vm.isShowProrataData = false;
        },
        ClosePopUp() {
            var vm = this;
            this.$parent.isPlanSubscription = false;
            this.$parent.isTemplate = false;
            this.$parent.isInvoice = false;
            this.parent.isPlanDetails = true;
        },
        async generateInvoice() {
            var vm = this;
            vm.isLoading=true;
            // Prepare invoice data from plans
            const invoiceData = vm.invoiceData
            // Check if any plan has changed_unit > 0
            const hasAnyChangedUnit = invoiceData.plans.some(plan => plan.hasAnyChangedUnit);

            if (hasAnyChangedUnit || vm.changed_unit != 0 || vm.PlanMappingId == '') {
                //this.selectedBilling = this.plandetails[0].payment_type;
                //const jsonString = JSON.stringify(invoiceData);
                const params = {
                    json: JSON.stringify(invoiceData),
                    clientId: this.UserId,
                    type: 'Generate',
                    billingCycle: vm.selectedBilling,
                    isForAddOrEdit: 'yes',
                    SelectedDate: vm.SelectedDate
                    };
                // const jsonString = encodeURIComponent(JSON.stringify(invoiceData));
                // const params = `json=${jsonString}&clientId=${this.UserId}&type=Generate&billingCycle=${this.selectedBilling}&isForAddOrEdit=${this.flag == 'isEditingFromPlanListing' ? 'yes' : 'no'}`;

                try {
                    const response = await DataService.GenrateInvoice(params);
                    if (response.status == 200) {
                        const result = response.data.result.data;
                        if (response.data.status == 'Success') {
                            if (result && (result[0].status_id == 1 ||result[0].status_id == 11)) {
                                const billParams = `billId=${result[0].bill_id}`;
                                const billResponse = await DataService.getInvoiceDetails(billParams);
                                const invoicedata = billResponse.data.result.data;
                                if (billResponse.status == 200 && invoicedata && invoicedata[0].status_id == 1) {
                                    if (this.flag != 'isEditingFromPlanListing') {
                                        this.$parent.isAddInvoice = false;
                                    }
                                    this.$parent.isPlanSubscription = false;
                                    this.$parent.isInvoice = true;
                                    this.$parent.isTemplate = true;

                                    this.$parent.templateDetails = invoicedata;
                                     vm.isLoading=false;
                                    vm.ShowAlert(result[0].status_message, 'success', false, this.$t('Alert'));
                                } else {
                                    this.$parent.isPlanSubscription = true;
                                    this.$parent.isInvoice = false;
                                    this.$parent.isTemplate = false;
                                     vm.isLoading=false;
                                    this.ShowAlert(invoicedata ?. [0] ?.status_message || this.$t('SomethingWentWrong'), 'failure', true, this.$t('Alert'));
                                }
                            } else if (result && (result[0].status_id == 6 || result[0].status_id == 61)) {
                                const planParams = `clientPlanMappingId=${result[0].client_plan_mapping_id}`;
                                const planResponse = await DataService.getSubscribePlanDetails(planParams);

                                const invoicedata = planResponse.data.result.data;
                                if (invoicedata && invoicedata[0].status_id == 1) {
                                    if (this.flag != 'isEditingFromPlanListing') {
                                        this.$parent.isAddInvoice = false;
                                    }
                                    this.$parent.isPlanSubscription = false;
                                    this.$parent.isInvoice = true;
                                    this.$parent.isTemplate = true;
                                    this.$parent.templateDetails = invoicedata;
                                     vm.isLoading=false;
                                } else {
                                    this.$parent.isPlanSubscription = true;
                                    this.$parent.isInvoice = false;
                                    this.$parent.isTemplate = false;
                                     vm.isLoading=false;
                                    this.ShowAlert(invoicedata ?. [0] ?.status_message || this.$t('SomethingWentWrong'), 'failure', true, this.$t('Alert'));
                                }
                            } 
                            else if(result && result[0].status_id == 21){
                                vm.ShowAlert(result[0].status_message, 'success', false, this.$t('Alert'));
                                vm.isLoading=false;
                                vm.$parent.backtoPlanList();
                            }
                            else if (result && result[0].status_id == 22) {
                                vm.ShowAlert(result[0].status_message, 'success', false, this.$t('Alert'));
                                vm.isLoading=false;
                                vm.$parent.backtoPlanList();
                            } else if (result && result[0].status_id == 12) {
                                this.ShowAlert(this.$t('QuantityUpdatedSuccessfully'), 'success', true, this.$t('Alert'));
                                vm.isLoading=false;
                                vm.$parent.backtoPlanList();
                            } else {
                                this.ShowAlert(result[0].status_message, 'failure', true, this.$t('Alert'));
                                vm.isLoading=false;
                            }
                        } else {
                            this.ShowAlert(this.$t('SomethingWentWrong'), 'failure', true, this.$t('Alert'));
                            vm.isLoading=false;
                        }
                    }
                } catch (error) {
                    console.error('Error fetching invoice template:', error);
                }
            } else {
                this.ShowAlert(this.$t('NoChangedQuantity'), 'warning', true, this.$t('Alert'));
                 vm.isLoading=false;
            }
        },

    },

}
</script>

