<template>
    <section>
        <loader :is-visible="isLoading"></loader>

        <div>
            <tg-list :IsShowAction="true" ModuleName="Company" SubModuleCode="" :LegendArray="[]"
                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                IdentityColumn="meter_mapping_id" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                IsSearchFieldsOnTop="false" SearchPosition="top" :ListDataCallBackFunction="FetchData"
                :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'SNo'">
                        <span>
                            {{ data.row.SNo }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'date_Of_installation'">
                        <span class="text-center single-action tourguide_a">
                            {{ $options.filters.formatDate(data.row.date_Of_installation) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <span class="px-2 text-center text-white d-block bg-success"  :title="$t('Active')"
                            v-if="data.row.status_id == 1001">{{
                                $t("Active") }} </span>
                        <span class="px-2 bg-danger text-center text-white d-block"  :title="$t('InActive')" v-else>{{ $t("InActive") }} </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'property'">
                        <span :title="getFullAddress(data.row)">
                            {{ data.row.property }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'contract_name'">
                                <span v-if="data.row.contract_name != null">{{ data.row.contract_name }}</span>
                                <span v-else>--</span>
                     </template>
                     <template v-if="data.column.COLUMN_NAME == 'invoice_grouping_name'">
                                <span v-if="data.row.invoice_grouping_name != null">{{ data.row.invoice_grouping_name }}</span>
                                <span v-else>--</span>
                     </template>
                        <template v-if="data.column.COLUMN_NAME == 'plan_name'">
                        <a href="javascript:;" v-on:click="OpenPlanDetail(data.row)"><i aria-hidden="true"></i>{{
                            data.row.plan_name }}
                        </a>
                    </template>
                      <template v-if="data.column.COLUMN_NAME == 'serial_number'">
                        <a href="javascript:;" v-on:click="OpenMeterDetail(data.row)"><i aria-hidden="true"></i>{{
                            data.row.serial_number }}
                        </a>
                    </template>
                     <template v-if="data.column.COLUMN_NAME == 'billing_status_id'">
                       <span class="px-2 text-center text-white d-block bg-info" :title="$t('Pending')"
                            v-if="data.row.billing_status_code == 'BILLING_PENDING'">{{
                                $t("Pending") }} </span>
                                <span class="px-2 text-center text-white d-block bg-success" :title="$t('Started')"
                            v-if="data.row.billing_status_code == 'BILLING_START'">{{
                                $t("Started") }} </span>
                                <span class="px-2 text-center text-white d-block bg-danger"  :title="$t('Stopped')"
                            v-if="data.row.billing_status_code == 'BILLING_STOP'">{{
                                $t("Stopped") }} </span>
                    </template>
                </template>
            </tg-list>
        </div>
        <ReplaceMeter v-if="IsReplace" :SerialNo="SerialNo" :MeterId="MeterId" :MeterMappingId="MeterMappingId">
        </ReplaceMeter>
        <ViewHistory v-if="IsHistory" :MeterMappingId="MeterMappingId"></ViewHistory>
        <SaveBillingDate v-if="IsSaveDate" :MeterMappingId="MeterMappingId" :statusId="SelectedStatusId" :StartBillingDate="StartBillingDate" :InstallationDate="InstallationDate"
            @billingSaved="onBillingSaved">
        </SaveBillingDate>
        <ModalAddMeter v-if="isAddMeter" :reqFrom="reqFrom"  :utilityTypeId="utilityTypeId" :ClientAdditionalId="ClientAdditionalId" :editMeterDetail="editMeterDetail" :meterMappingId="meterMappingId"></ModalAddMeter>
        <ModalViewPlanDetails v-if="isViewPlanDetailsUtilityCase" :PlanId="PlanId" :ClientId="ClientId"
            :PlanMappingId="PlanMappingId" @close="isViewPlanDetailsUtilityCase = false"></ModalViewPlanDetails>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="IsShowMeterDetail">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h4 class="modal-title">{{ $t('MeterDetails') }}</h4>
                                <button type="button" class="close" v-on:click="CloseTemplate">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">

                          <div class="heading-border"><h5 class="h5 mb-4"><span> {{ $t('MeterName') }}: {{ editMeterDetail.meter_name || '--' }}</span> 
                              <small class="float-right">
                                 <span v-if="editMeterDetail.status_id == 1001" class="bg-success rounded answer rightlabeltext ml-1 px-3 text-white font-14 py-1" style="border-bottom: none;">{{ $t('Active') }}</span>
                                 <span v-else class="bg-danger rounded answer rightlabeltext ml-1 px-3 text-white font-14 py-1" style="border-bottom: none;">{{ $t('Inactive') }} </span>
                              </small>
                              </h5></div>


                                <div class="row">
                                    <!-- <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('MeterName') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.meter_name || '--' }}</span></label></div>
                                    </div> -->
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('SerialNumber') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.serial_number || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('MeterType') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.meter_service || '--' }}</span></label></div>
                                    </div>
                           

                                    <!-- <div class="col-md-4">
                                  <div class="form-group"><label><strong> {{ $t('Status') }}:</strong>
                                     <span v-if="editMeterDetail.status_id == 1001" class="badge badge-success answer rightlabeltext ml-1">{{ $t('Active') }}</span>
                                     <span v-else class="badge badge-danger answer rightlabeltext ml-1">{{ $t('Inactive') }} </span>
                                  </label></div>                                      
                                    </div> -->

                                    <div class="col-md-4">
                                         <div class="form-group"><label><strong> {{ $t('Plan') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.plan_name || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                          <div class="form-group"><label><strong> {{ $t('InstallationDate') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.date_Of_installation ? $options.filters.formatDate(editMeterDetail.date_Of_installation) : '--' }}</span></label></div>
                                    </div>
                                
                                    <div class="col-md-4">
                                         <div class="form-group"><label><strong> {{ $t('BillingStartDate') }}:</strong><span class="answer rightlabeltext ml-1"> {{ $options.filters.formatDate(editMeterDetail.billing_start_date) || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4" >
                                         <div class="form-group"><label><strong> {{ $t('ModelName') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.model_name || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                         <div class="form-group"><label><strong> {{ $t('MeterService') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.meter_service || '--' }}</span></label></div>
                                    </div>
                              
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('SanctionedLoad') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.sanctioned_load || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4" >
                                        <div class="form-group"><label><strong> {{ $t('MultiplyFactor') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.multiply_factor || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('PowerFactor') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.power_factor || '--' }}</span></label></div>
                                    </div>
                                
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('VoltageRating') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.volate_rating || '--' }}</span></label></div>
                                     </div>
                                    <div class="col-md-4" >
                                        <div class="form-group"><label><strong> {{ $t('CurrentRating') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.current_rating || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('BillingStatus') }}:</strong><span class="answer rightlabeltext ml-1"> {{editMeterDetail.billing_status_code === 'BILLING_STOP' ? 'Stopped' : editMeterDetail.billing_status_code === 'BILLING_START' ? 'Started' : editMeterDetail.billing_status_code === 'BILLING_PENDING' ? 'Pending' : 'Pending' }}</span></label></div>
                                    </div>
                               
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('ConnectionType') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.connection_type || '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4" v-if="editMeterDetail.connection_type == 'Temporary'">
                                        <div class="form-group"><label><strong> {{ $t('TentativeEndDate') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.tentative_end_date ? $options.filters.formatDate(editMeterDetail.tentative_end_date) : '--' }}</span></label></div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group"><label><strong> {{ $t('Property') }}:</strong><span class="answer rightlabeltext ml-1"> {{ editMeterDetail.property || '--' }}</span></label></div>
                                    </div>
                                </div>
                            </div>

                            <div class="modal-footer">
                                <button @click="CloseTemplate" class="btn btn-danger">
                                    <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>
<script>
import Schema from './MeterListingSchema'
import DataService from "../../../services/DataService";
import ReplaceMeter from "../../Client/ClientDetails/ReplaceMeter.vue"
import ViewHistory from "../../Client/ClientDetails/ViewHistory.vue"
import SaveBillingDate from "../../Client/ClientDetails/AddBillingStartDate.vue"
import moment from 'moment'
import ModalAddMeter from '../../../components/Modals/modalAddMeter.vue';
import ModalViewPlanDetails from '../../../components/Modals/ModalViewPlanDetails.vue';
export default {
    components: { ReplaceMeter, ViewHistory,SaveBillingDate,ModalAddMeter,ModalViewPlanDetails },
    props: {
        ClientId: {
            type: Number
        }
    },
    data() {
        return {
            TotalRecords: 0,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            ProjectName: '',
            taskId: '',
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            ListingData: [],
            searchCondition: '',
            Buttons: Schema.buttons,
            QuotesListingSchema: false,
            IsReplace: false,
            SerialNo: null,
            MeterId: null,
            IsHistory: false,
            IsSaveDate:false,
            SelectedStatusId:null,
            reqFrom:'',
            utilityTypeId:0,
            ClientAdditionalId:0,
            editMeterDetail:[],
            isAddMeter:false,
            isViewPlanDetailsUtilityCase:false,
            PlanId:'',
            PlanMappingId:'',
            IsShowMeterDetail: false,
            StartBillingDate: '',
            InstallationDate: '',
        };
    },
    created: async function () {
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.user_Id = this.GetUserInfo.ID;
        this.FetchData();

    },
    methods: {
        EditMeter: function(item){
            var vm = this;
            vm.utilityTypeId= item.meter_type_id;
            vm.meterMappingId = item.meter_mapping_id;
            vm.ClientAdditionalId= item.client_additional_id;
            vm.editMeterDetail= item;
            vm.reqFrom='Edit';
            vm.isAddMeter = true;
        },
        getFullAddress(row) {
            return `${row.user_address_1}, ${row.user_city}, ${row.user_state}, ${row.user_zipcode}, India`;
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            var params = `clientId=${vm.ClientId}`;
            if (!vm.isBlank(this.searchCondition)) {
                params += `&search=${this.searchCondition}`;
            }
            await DataService.GetMapMeterById(params).then(function (response) {
                if (response?.data) {
                    if (response.data.length > 0) {
                        vm.isLoading = false;
                        vm.ListingData = response.data;
                        vm.noRecord = false;
                        vm.TotalRecords = response.data[0].TOTAL_RECORDS;

                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.isLoading = false;

                    }
                } else {
                    vm.noRecord = true;
                    vm.isLoading = false;
                    vm.ListingData = [];
                    vm.TotalRecords = 0
                }
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchCondition != '') {
                    let str = value.searchConditionJson;
                    let result = str.split('=')[1];
                    this.searchCondition = result;
                }
                else {
                    this.searchCondition = "";
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        OpenMeterDetail(item) {
            var vm = this;
            vm.editMeterDetail = item;
            vm.IsShowMeterDetail = true;
        },
        CloseTemplate: function () {
            var vueObj = this;
            vueObj.IsShowMeterDetail = false;
        },
        OpenPlanDetail(item) {
            var vm = this;
            vm.viewDetails(item);
        },
        async viewDetails(item) {
            var vm = this;
            vm.PlanId = item.plan_id;
            vm.PlanMappingId = item.plan_mapping_id;
            vm.isViewPlanDetailsUtilityCase = true;

        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {

                    case 'Delete':
                    if(row.billing_status_code == 'BILLING_START'){
                        action.isVisible = false;
                    }else{
                        action.isVisible = true;
                    }
                        break;
                    case 'Replace':
                        if (row.status_id == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'View History':
                        if (row.status_id == 1002) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Start Billing':
                            //action.isVisible = true;
                        if ((row.billing_status_code == 'BILLING_PENDING'|| row.billing_status_code == 'BILLING_STOP') && row.status_id==1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                         break;
                    case 'Stop Billing':
                        if (row.billing_status_code == 'BILLING_START' && row.status_id==1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        // action.isVisible = true;
                        break;

                    case 'Edit Meter':
                         if (row.status_id == 1001) {
                            action.isVisible = true;
                            action.isDisabled = false;
                        } else {
                            action.isVisible = true;
                            action.isDisabled = true;
                        }
                        break;	
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'Delete':
                    this.DeleteMultiple(item);
                    break;
                case 'Replace':
                    this.ReplaceMeter(item);
                    break;
                case 'ViewHistory':
                    this.ViewHistory(item);
                    break;
                case 'StartBilling':
                    this.StartBilling(item);
                    break;
                case 'StopBilling':
                    this.StopBilling(item);
                    break;
                case 'EditMeter':
                     this.EditMeter(item);
                    break;
            }
        },
        ReplaceMeter: function (item) {
            var vm = this;
            vm.SerialNo = item.serial_number;
            vm.MeterId = item.asset_catalogue_id;
            vm.MeterMappingId = item.meter_mapping_id;
            vm.IsReplace = !vm.IsReplace;
        },
        ViewHistory: function (item) {
            var vm = this;
            vm.MeterMappingId = item.meter_mapping_id;
            vm.IsHistory = !vm.IsHistory;
        },
        DeleteMultiple: function (item) {
            var vm = this;
            var val = "";
            val += item.meter_mapping_id;
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Meter")), function () {
                    let url = `ids=${val}&tableName=talygen_meter_mapping&primaryKey=meter_mapping_id`;
                    DataService.Delete(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
         UpdateBillingStatus(payload) {
            this.isLoading = true;
            DataService.UpdateMeterBillingStatus(payload)
                .then((response) => {
                    if (response.data?.success) {
                        this.ShowAlert(this.$t('UpdatedSuccessfully'), 'Success', true, this.$t('Alert'));
                        this.FetchData();
                    } else {
                        this.ShowAlert(this.$t('UpdateFailed'), 'Failure', true, this.$t('Alert'));
                    }
                })
                .catch((error) => {
                    console.error(error);
                    this.ShowAlert(this.$t('UpdateFailed'), 'Failure', true, this.$t('Alert'));
                })
                .finally(() => {
                    this.isLoading = false;
                    this.IsSaveDate = false;
                });
        },
        StartBilling(item) {
            var vm = this;
            vm.MeterMappingId = item.meter_mapping_id;
            vm.SelectedStatusId = item.billing_changed_status_id;
            vm.StartBillingDate= item.billing_start_date;
            vm.InstallationDate= item.date_Of_installation;
            vm.IsSaveDate = !vm.IsSaveDate;
        },
        StopBilling(item) {
            debugger
            const payload = {
                meterMappingId: item.meter_mapping_id,
                statusId: item.billing_changed_status_id,
                billingStartDate: null 
            };

            this.UpdateBillingStatus(payload);
        },
        onBillingSaved({ meterMappingId, billingStartDate, statusId }) {
            debugger
            const payload = {
                meterMappingId,
                statusId,        
                billingStartDate
            };

            this.UpdateBillingStatus(payload);
        }

    }
};
</script>