<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Filter Meter Reading') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">

                        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="OnSubmit">

                            <!-- <template v-slot:tgslot-Meter="{ data }">

                                <div class="input-group">
                                    <MutliSelectDropDown :options="filteredOptions" :value="selectedProjects"
                                        placeholder="Select meter" :searchable="true" searchPlaceholder="Search meter"
                                        :maxItems="3" :selectAllEnabled="true" allText="Select All"
                                        @input="updateSelectedProjects" :Formtype="FormType" />
                                </div>

                            </template> -->

                            <!-- <template v-slot:tgslot-Meter="{ data }">
                                <div class="input-group">
                                    <MutliSelectDropDown :options="filteredOptions" :value="selectedProjects"
                                        placeholder="Select meter" :searchable="true" searchPlaceholder="Search meter"
                                        :maxItems="3" :selectAllEnabled="true" allText="Select All"
                                        @input="updateSelectedProjects" :Formtype="FormType" />
                                </div>
                            </template> -->

                        </dynamic-form>

                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService';
// import MutliSelectDropDown from '../../ClientBill/AddInvoice/MutliSelectDropDown.vue';


export default {

    components: {
        // MutliSelectDropDown,
    },
    props: {
        ClientId: {
            type: Number
        }
    },
    data() {
        return {
            isLoading: false,
            FormType: 'Create',
            selectedProjects: [],
            filteredOptions: [],

            FormSchema: [{
                layoutType: "four",
                Data: [
                    {
                        astype: "SelectField",
                        label: this.$t('MeterType'),
                        name: "Meter_type",
                        value: "",
                        config: {
                            options: [],
                            onChange: this.getMeters
                        },
                        placeholder: this.$t('Meter'),
                        validationRules: { "required": true, max: 100 },
                        disabled: false,
                        visibility: true
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('Meter'),
                        name: "Meter",
                        value: "",
                        config: {
                            options: [],

                        },
                        placeholder: this.$t('Meter'),
                        validationRules: { "required": false, max: 100 },
                        disabled: false,
                    },

                    {
                        astype: "SelectField",
                        label: this.$t('Plans'),
                        name: "Plans",
                        value: "",
                        config: {
                            options: [],
                            // onChange: this.InvoiceComponentFieldsValues,
                        },
                        placeholder: this.$t('Plans'),
                        validationRules: { "required": false, max: 100 },
                    },
                    {
                        astype: "DateField",
                        label: this.$t('ReadingDateFrom'),
                        name: "Reading_Date_From",
                        value: '',
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            // onChange: this.InvoiceComponentFieldsValues,
                            onChange: this.setDateForm,
                            minDate: ''
                            // maxDate: false
                        },
                        validationRules: { "required": false },
                        disabled: false,
                    },
                    {
                        astype: "DateField",
                        label: this.$t('ReadingDateTo'),
                        name: "Reading_Date_To",
                        value: '',
                        placeholder: "",
                        config: {
                            format: "MM/DD/YYYY",
                            // onChange: this.InvoiceComponentFieldsValues,
                            // maxDate: false
                            minDate: ''
                        },
                        validationRules: { "required": false },
                        disabled: false,
                    },


                ]
            },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search'></i>",
                    // onClick: this.OnSubmit,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh'></i>",
                    onClick: this.resetForm,


                },
            ],
        };
    },
    created() {

        this.getMeterTypes();
        this.GetPlanDDlData();

    },
    methods: {

        Close() {
            this.$parent.IsFilterMeterReading = false;

        },

        async GetPlanDDlData() {
            var vm = this;
            let Url = `clientId=${vm.ClientId}`;

            try {
                const response = await DataService.GetCompanyPlans(Url);
                let PlanData = response.data;
                if (Array.isArray(PlanData) && PlanData.length > 0) {

                    PlanData.forEach(item => {
                        vm.FormSchema[0].Data[2].config.options.push({
                            value: item.value,
                            name: item.name
                        });
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();

                } else {
                    vm.FormSchema[0].Data[1].config.options = [];

                }
            } catch (error) {
                console.error('Error fetching plan data:', error);
            }
        },

        // async getMeters() {
        //     const vm = this;
        //     let url = `clientId=${this.ClientId}`
        //     try {
        //         // let Url = `clientId=${vm.ClientId}&meterTypeId=${data.value}`
        //         const response = await DataService.GetMeters(url);
        //         if (response?.data) {
        //             const responseData = JSON.parse(response.data.result).data;
        //             if (Array.isArray(responseData) && responseData.length > 0) {
        //                 responseData.map(item => {

        //                     let obj = {

        //                         label: item.name,
        //                         value: item.value
        //                     }
        //                     vm.filteredOptions.push(obj);
        //                 });
        //             } else {
        //                 console.warn('No valid data found in the response');
        //             }
        //         } else {
        //             console.error('Invalid response structure:', response);
        //         }
        //     } catch (error) {
        //         console.error('Error fetching Customer type:', error);
        //     } finally {
        //         //
        //     }
        // },


        async getMeterTypes() {
            debugger;
            const vm = this;
            try {
                let url = `is_mapped=1`
                const response = await DataService.GetMeterTypes(url);
                debugger;
                if (response?.data) {
                    const responseData = JSON.parse(response.data.result).data;

                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {

                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.meter_id,
                                name: item.meter_name
                            });

                        });
                        vm.$refs.dynamicform.UpdateKeyValue();

                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Meter types:', error);
            } finally {
                //
            }
        },

        async getMeters(event, data) {
            debugger
            var vm = this;
            vm.FormSchema[0].Data[1].value = '';
            vm.FormSchema[0].Data[1].config.options = [];
            try {

                let Url = `clientId=${vm.ClientId}&meterTypeId=${data.value}`
                const response = await DataService.GetMeters(Url);

                const responseData = JSON.parse(response.data.result).data;
                debugger
                if (Array.isArray(responseData) && responseData.length > 0) {

                    responseData.forEach(item => {

                        vm.FormSchema[0].Data[1].config.options.push({
                            value: item.value,
                            name: item.name
                        });
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                } else {
                    vm.FormSchema[0].Data[1].config.options = [];
                    console.warn('No data found or data is not an array.');
                }
            } catch (error) {
                console.error('Error fetching meter name data:', error);
            }
        },


        updateSelectedProjects(newSelection, singleSelectDetails) {
            this.selectedProjects = newSelection;
        },
        resetForm() {
            this.selectedProjects = [];
            this.FormSchema[0].Data[2].value = '';
            this.FormSchema[0].Data[3].value = '';
            this.$refs.dynamicform.validateForm.reset();

        },
        OnSubmit(data) {
            debugger;
            let fields = {
                //meterIds: this.selectedProjects.map(item => item.value).join(","),
                meterTypeId: data.Meter_type,
                meterIds: data.Meter || '',
                planIds: data.planIds || '',
                dateFrom: this.$options.filters.formatDateTime(data.Reading_Date_From) || '',
                dateTo: this.$options.filters.formatDateTime(data.Reading_Date_To) || ''
            }
            this.$emit("filterSearch", fields);
        }


    },
};
</script>

