<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h4 class="modal-title">{{ $t('AssociateContact') }}</h4>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <div class="heading-border">
                                <h5 class="h5">
                                    <span>{{ $t('Manage') }}</span> <small class="float-right">
                                        <a href="javascript:;" v-on:click="aAddContact" id="aAddContact"><em class="fa fa-plus" aria-hidden="true"></em> {{ $t('AddNewContact') }}</a>
                                    </small>
                                </h5>
                            </div>
                            <section>
                                <tg-list :IsShowAction="false" ModuleName="Company" SubModuleCode="" :LegendArray="[]"
                                    :isShowHeaderText="false"
                                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                                    :widgets="[]" IdentityColumn="CONTACT_ID" :listType="['List']"
                                    :ListData="ListingData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                                    :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                                    IsSearchFieldsOnTop="true" SearchPosition="top"
                                    :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                                    v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:SortdataButtonClick="sortdata">
                                </tg-list>
                        </section>
                        </div>
                        <div class="d-flex justify-content-end mr-3 mb-3">
                            <a title="Save" v-on:click="SaveAssociateContact()" class="btn btn-success mr-2 mt-3"><i class="fa fa-floppy-o pr-2" aria-hidden="true"></i>{{ $t("Save") }}</a>
                            <a title="Close" v-on:click="Close()" class="btn btn-primary delete-btn mt-3"><i class="fa fa-times pr-2" aria-hidden="true"></i>{{ $t("Cancel") }} </a>
                        </div>
                    </div>
                </div>
            </div>
            <AddNewContact v-if="OpenAddContact" :CountryId="CountryId"></AddNewContact>
        </div>
    </section>
</template>
<script>
import Schema from './AssociateContactListingSchema'
import DataService from "../../../services/DataService";
import AddNewContact from "../../Client/ClientDetails/AddNewContact.vue"
import moment from 'moment'
export default {
    components: { AddNewContact },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        }
    },
    data() {
        return {
            TotalRecords: 0,
            noRecord: false,
            CountryId:'',
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            ProjectName: '',
            taskId: '',
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            ListingData: [],
            searchCondition: '',
            Buttons: Schema.buttons,
            QuotesListingSchema: false,
            leftSearchFields: [{
                fieldName: this.$t('ContactName'),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            OpenAddContact: false
        };
    },
    created: async function () {
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.user_Id = this.GetUserInfo.ID;
        this.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var params = `clientId=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            if (!vm.isBlank(this.searchCondition)) {
                params += `&search=${this.searchCondition}`;
            }
            await DataService.GetAdditionalNewContactsNotWithClient(params).then(function (response) {
                if (response.data) {
                    if (response.data.length > 0) {
                        vm.ListingData = response.data;
                        vm.TotalRecords = response.data[0].TOTALRECORDS;
                        vm.noRecord = false;

                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ListingData = [];
                    vm.TotalRecords = 0
                }
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        Close: function () {
            var ths=this;
            ths.$emit('CloseAssociatePopup'); 
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {

                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {
        },
        commonHeaderButtonClick: async function (event) {
        },
        aAddContact: function () {
            var vm = this;
            vm.OpenAddContact = !vm.OpenAddContact;
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        SaveAssociateContact() {
            var vm = this;
            var data = [];
            vm.ListingData.forEach(item => {
                if (item.isSelected == true) {
                    var statusCodeId = item.CONTACT_ID;
                    var dataObject = {
                        id: statusCodeId,
                        userId: vm.UserId,
                        isfor: "Associate"
                    };
                    data.push(dataObject);
                }
            });
            if (data.length > 0) {
                var params = JSON.stringify(data);
                DataService.SubmitAdditionalNewContacts(params).then((response) => {
                    if (response.data.Status == 'Success') {
                        vm.ShowAlert(vm.$t("ContactsAddedSuccessfully"), "success", true, vm.$t("Alert"));
                        vm.Close();                        
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("Contacts")]), "failure", true, vm.$t("Alert"));
                        vm.Close();
                    }
                });
            } else {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
            }
        },
    }
};
</script>