<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('ManageCommunicationType') }}
                            </h5>
                            <button class="close" v-on:click="onCancel"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <Form ref="LeadForm">
                                <dynamic-form  lang="en" ref="refCommunication" :mandatorytext="MandatoryString" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                            </Form>
                            <div class="listing mt-3">
                                <div class="table-responsive">
                                    <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <!-- <th style="width:3% !important;" class="text-center">
                                                    <div class="custom-control custom-checkbox"><input type="checkbox" id="chkAll_5" name="checkbox" class="custom-control-input" style="display: none;">
                                                        <label class="custom-control-label universal-custom-control-label pt-1" for="chkAll_5"></label>
                                                    </div>
                                                </th> -->
                                                <th class="text-center"><span>#</span></th>
                                                <th class="text-left"><span>{{$t('CommunicationType')}}</span></th>
                                                <th class="text-left"><span>{{$t('AddressOrScreenName')}}</span></th>
                                                <th class="text-left"><span>{{$t('NotesDescription')}}</span></th>
                                                <th class="text-center"><span>{{$t('IsinCompanyDirectory')}}</span></th>
                                                <th class="text-center"><span>{{$t('Edit')}}</span></th>
                                                <th class="text-center"><span>{{$t('Delete')}}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="EmailData.length === 0">
                                             <td colspan="5" class="text-center text-danger no_records no-record"> {{ $t("NoRecordfound") }}</td>
                                            </tr>                                          
                                            <tr v-for="(email, index) in EmailData" :key="email.ROWNUMBER">
                                                <!-- <td class="text-center">
                                                    <div>
                                                        <div class="custom-control custom-checkbox">
                                                            <input class="chkItems custom-control-input" type="checkbox" value="80720" style="display: none" name="membercheckbox" id="chk_6">
                                                            <label class="custom-control-label universal-custom-control-label pt-1" for="chk_6"></label>
                                                        </div>
                                                    </div>
                                                </td> -->
                                                <td class="text-center">{{ index + 1 }}</td>
                                                <td class="text-left">{{ email.CARRIER_TYPE_NAME }}</td>
                                                <td class="text-left">{{ email.CARRIER_VALUE }}</td>
                                                <td class="text-left">{{ email.USER_EMAIL_DESC }}</td>
                                                <td class="text-center">{{ email.ADD_TO_DIRECTORY ? 'Yes' : 'No' }}</td>
                                                <td class="text-center single-action"><a class="enable" data-placement="left" v-on:click="Edit(email)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Edit">
                                                    <em class="fa fa-pencil text-success action-icon"></em></a>
                                                </td>
                                                <td class="text-center single-action"><a :class="{'enable':true,'disabled':isDiabled}" data-placement="left" v-on:click="Delete(email)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete" aria-describedby="tooltip845041">
                                                    <em class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        ClientId: {
            type: Number
        },
        IsClient: {
            type: String
        }
    }, 
    data(){
        return {
            EmailData: [],
            MandatoryString: this.$t('MandatoryString'),
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "SelectField",
                    label: this.$t('CommunicationType'),
                    name: "CommunicationType",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        required: true,
                        max: 250,
                    },
                    config: {
                        options: [],
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t('AddressOrScreenName'),
                    name: "AddressScreenName",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        required: true,
                    },
                },

                {
                    astype: "TextField",
                    label: this.$t('NotesDescription'),
                    name: "NoteDescription",
                    value: "",
                    placeholder: "",
                    disabled: false,
                    validationRules: {
                        required: false,
                    },

                },

                ],
            },
            {
                layoutType: "triple",
                Data: [{
                    astype: "toggleField",
                    label: this.$t('IsinCompanyDirectory'),
                    name: "CompanyDirectory",
                    value: "",
                    placeholder: "",
                },
                {
                    astype: "toggleField",
                    label: this.$t('VisibleToClient'),
                    name: "VisibleToClient",
                    value: "",
                    placeholder: "",
                    config: {
                        onChange: this.visibleToClient
                    }
                },

                ],
            },

            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.onCancel,
            },
            ],
            CommunicationTypes: [],
            pageNo: 1,
            pageSize: 10,
            EmailId: 0,
            isDiabled: false,
        }
    },
    created() {
        var vm = this;
        vm.getCommunicationTypes();
        vm.FetchData();
        if(vm.IsClient=='Client'){
            vm.FormSchema[1].Data[1].visibility = false;
        }else{
            vm.FormSchema[1].Data[1].visibility = true;
        }  
    },
    methods:{
        async getCommunicationTypes() {
            try {
                var vm = this;
                await DataService.GetCommunicationTypes().then(response => {
                    vm.CommunicationTypes = response.data;
                    if (vm.CommunicationTypes.length > 0) {
                        vm.CommunicationTypes.forEach((item, index) => {
                            (vm.FormSchema)[0].Data[0].config.options.push({
                                name: item.text,
                                value: item.value
                            })
                        })
                        vm.$refs.refCommunication.UpdateKeyValue();
                    }
                })
            } catch (error) {
                console.error('Error in getCommunicationTypes:', error);
            }
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            var params = `pageNum=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userId=${vm.ClientId}`;
            DataService.GetUserEmailList(params).then(function (response) {
                var data = JSON.parse(response.data);
                vm.EmailData = data.data;
            })  
        },
        Edit: function (Item) {
            var vm = this;
            vm.EmailId = Item.USER_EMAIL_ID;
            (vm.FormSchema)[0].Data[0].value = Item.CARRIER_TYPE_ID;
            (vm.FormSchema)[0].Data[1].value = Item.CARRIER_VALUE;
            (vm.FormSchema)[0].Data[2].value = Item.USER_EMAIL_DESC;
            (vm.FormSchema)[1].Data[0].value = Item.ADD_TO_DIRECTORY;
            (vm.FormSchema)[1].Data[1].value = Item.IS_VISIBLE;
            vm.isDiabled = true;
        },
        Delete: function (Item) {
            var vm = this;
            vm.EmailId = Item.USER_EMAIL_ID;
            if (vm.EmailId > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("AddtionalEmail")), function () {
                    var params = `EmailId=${vm.EmailId}&userId=${vm.ClientId}`;
                    DataService.DeleteAddtionalEmail(params).then(function (response) {

                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t("Alert"));
                        }
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        visibleToClient() {
            var vm = this;
            vm.confirmR(vm.$t('Are you sure you want to make this Visible to Client?'), true, false, function (result) {

            }, function () {
                vm.cancelAction();
            });
        },
        cancelAction() {
            (this.FormSchema)[1].Data[1].value = ((this.FormSchema)[1].Data[1].value==true) ? false: true;
        },
        async onSubmit(formfields) {
            var vm = this;
            vm.isLoading = true;
            let myObj = {
                EmailId: vm.EmailId,
                CommunicationType: formfields.CommunicationType,
                AddressScreenName: formfields.AddressScreenName,
                NoteDescription: formfields.NoteDescription,
                AddToDirectory: formfields.CompanyDirectory,
                VisibleTo: formfields.VisibleToClient==''?false:formfields.VisibleToClient,
                configUserId: vm.ClientId
            }
            var Jsondata = JSON.stringify(myObj);
            await DataService.AddUpdateAdditionalEmail(Jsondata).then(function (response) {

                if (response != null && response.data != null && response.data > 0) {
                    vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                    vm.Clear();
                    vm.FetchData();
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                    vm.FetchData();
                }
            })
            vm.isLoading = false;
        },
        onCancel: function () {
            this.Clear();
            this.$emit('CloseAddCommuniction');
        },
        Clear: function () {
            var vm = this;
            vm.EmailId = 0;
            vm.isDiabled = false;
            (vm.FormSchema)[0].Data[0].value = '';
            (vm.FormSchema)[0].Data[1].value = '';
            (vm.FormSchema)[0].Data[2].value = '';
            (vm.FormSchema)[1].Data[0].value = '';
            (vm.FormSchema)[1].Data[1].value = '';
            vm.$refs.refCommunication.validateForm.reset();
        }

    }
}
</script>