<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
            <template v-slot:tgslot-Avatar="{ data }">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left p-0 d-flex flex-wrap">
                    <div class="user-detail bg-light p-2 w-100 border position-relative float-left">
                        <div title="Profile Image" class="user-image">
                            <img id="maleProfile" src="https://talygenv.github.io/Vue-UI-Content/images/avatar-icon.jpg"
                                alt="Profile Image"
                                v-if="(base64String == '' || base64String == null) && (profilePicture == '' || profilePicture == null) && (gender == 'M' || gender == '' || gender == null)"
                                loading="lazy">
                            <img id="femaleProfile"
                                src="https://talygenv.github.io/Vue-UI-Content/images/avatar-icon-girl.jpg"
                                alt="Profile Image"
                                v-if="(base64String == '' || base64String == null) && (profilePicture == '' || profilePicture == null) && gender == 'F'"
                                loading="lazy">
                            <img :src="'https://cogniterblob.blob.core.windows.net/' + containerName + '/Upload/UserProfileImage/' + profilePicture"
                                alt="Profile Image"
                                v-if="(base64String == '' || base64String == null) && (profilePicture != '' && profilePicture != null)"
                                loading="lazy">
                            <img :src="'data:image/gif;base64,' + base64String" loading="lazy"
                                v-if="base64String != ''">
                        </div>
                        <div class="user-name ml-4 mt-3">
                            <!-- <label class="float-left pb-2"> 
                                        {{ $t("Avatar") }}
                                    </label> -->
                        </div>
                        <div class="user-name ml-4 mt-0">
                            <div class="col-10 col-sm-10 col-lg-10 col-xl-10 float-left p-0">
                                <input type="file" :key="fileInputKey" id="flAvatar"
                                    v-on:change="GetBase64String($event)" size="19"
                                    accept=".jpeg,.jpg,.jpe,.gif,.bmp,.dib,.ico,.png,.tif" name="flAvatar" tabindex="-1"
                                    style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                <div class="bootstrap-filestyle input-group">
                                    <input type="text" v-bind:value="attachmentName" id="aaaa" disabled="disabled"
                                        class="form-control disabled">
                                    <div class="input-group-append" style="cursor: pointer;">
                                        <span tabindex="0"
                                            class="group-span-filestyle input-group-btn input-group-text bg-white">
                                            <label title="Click here to select a file." for="flAvatar"
                                                class="mb-0 font-weight-normal ">
                                                {{ $t('FileControlText') }}
                                            </label>
                                        </span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-2 col-sm-2 col-lg-2 col-xl-2 float-left pl-0">
                                <a id="clearUserfile" v-on:click="ResetProfileImage()" href="javascript:;"
                                    title="Click here to clear attachment"
                                    class="actionicons float-left round-icon-small btn-danger-light ml-2">
                                    <i class="fa fa-undo"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>

            </template>
        </dynamic-form>
    </section>
</template>
<script>
import DataServices from "../../../services/DataService";
export default {
    props: {
        ClientData: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            avatar: "avatar-icon.jpg",
            userId: 0,
            ClientId: 0,
            fileError: null,
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            companyId: null,
            userId: null,
            AccountName: '',
            AccountProfileData: [],
            fileData: [],
            fileInputKey: 0,
            profilePicture: '',
            gender: '',
            containerName: '',
            attachmentName: '',
            base64String: '',
            FormSchema: [
                {
                layoutType: "four",
                Data: [{
                    astype: "TextField",
                    label: this.$t('FirstName'),
                    name: "FirstName",
                    value: "",
                    validationRules: {
                        "required": true
                    },
                    disabled: false,
                },
                {
                    astype: "TextField",
                    label: this.$t('LastName'),
                    name: "LastName",
                    value: "",
                    validationRules: {
                        "required": true
                    },
                    disabled: false,
                },
                {
                    astype: "TextField",
                    label: this.$t('Email'),
                    name: "Email",
                    value: "",
                    validationRules: [{ "email": true }, { "required": true }],
                    disabled: true,
                },
                {
                    astype: "SelectField",
                    label: this.$t('Gender'),
                    name: "Gender",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": false
                    },
                    config: {
                        options: [
                            { value: "M", name: 'Male' },
                            { value: "F", name: 'Female' }
                        ],
                        onChange: this.onChangeGender,
                    },
                    visibility: true,
                    disabled: false
                }]
            },
            {
                layoutType: "four",
                Data: [{
                    astype: "RadioField",
                    label: this.$t('ACCOUNT'),
                    name: "Account",
                    value: 1,
                    validationRules: { "required": false, max: 200 },
                    config: {
                        options: [{ name: this.$t('ExistingAccount'), value: 1 }, { name: this.$t('CreateNewAccount'), value: 2 }],
                        showAddIcon: false,
                        onChange: this.onChangeRadio
                    },
                },
                {
                    astype: "SelectField",
                    label: this.$t('ParentAccounts'),
                    name: "ParentAccounts",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [],
                    },
                    visibility: true
                },
                {
                    astype: "TextField",
                    label: this.$t('AccountName'),
                    name: "AccountName",
                    value: "",
                    validationRules: {
                        "required": true
                    },
                    disabled: false,
                    visibility: false
                },
                {
                    astype: "SelectField",
                    label: this.$t('ClientOwner'),
                    name: "ClientOwner",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [],
                    },
                    visibility: true
                },
                {
                    astype: "SelectField",
                    label: this.$t('CustomerType'),
                    name: "CustomerType",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": false
                    },
                    config: {
                        options: [],
                    },
                    visibility: true
                }
                ]
            },
            {
                group_name: this.$t('AdvancedSettings'),
                layoutType: "four",
                Data: [
                    {
                        astype: "SelectField",
                        label: this.$t('TimeZone'),
                        name: "TimeZone",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('DateFormat'),
                        name: "DateFormat",
                        value: "M/D/YYYY",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [{ name: 'M/d/yyyy', value: 'M/D/YYYY' }, { name: 'dd/MM/yyyy', value: 'DD/MM/YYYY' }],
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Role'),
                        name: "Role",
                        value: "",
                        placeholder: this.$t('Role'),
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Jurisdiction'),
                        name: "Jurisdiction",
                        value: "",
                        config: {
                            options: []
                        },
                        placeholder: this.$t('Select Jurisdiction'),
                        validationRules: { "required": false, max: 100 },
                    }
                ]
            },

            {
                layoutType: "single",
                Data: [
                    {
                        astype: "SlotField",
                        label: this.$t('Avatar'),
                        name: "Avatar",
                        value: "",
                        visibility: true,
                        placeholder: "",
                        config: {
                            options: [],
                            showAddIcon: false,
                        },
                        validationRules: "",
                    },
                ]
            },
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                isVisible: true,

            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.Close,
            },
            ],
        }
    },
    created: async function () {        
        var vm = this;
            vm.isLoading = true;
        vm.userId = vm.GetUserInfo.ID;
        vm.user_type = vm.GetUserInfo.UserType;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.CompanyName = vm.GetUserInfo.CompanyName;
        vm.GetTaxJurisdiction();
        await this.GetClientParentByCompanyId();
        await this.GetClientOwner();
        await this.GetTimeZone();
        await this.GetAllRolesDDL();
        await this.getCustomertype();
        await this.BindDetailData();
            vm.isLoading = false;
    },
    methods: {
        BindDetailData: async function () {            
            var vm = this;
        
            var clientDetailData = vm.ClientData.clientdetail;
            vm.ClientId = clientDetailData.user_id;
            vm.$refs.dynamicform.updateProperty("FirstName", "value", clientDetailData.first_name);
            vm.$refs.dynamicform.updateProperty("LastName", "value", clientDetailData.last_name);
            vm.$refs.dynamicform.updateProperty("Email", "value", clientDetailData.email_id);
            vm.$refs.dynamicform.updateProperty("Gender", "value", clientDetailData.gender);
            vm.$refs.dynamicform.updateProperty("Account", "value", clientDetailData.account_name == null ? 1 : 2);
            vm.$refs.dynamicform.updateProperty("ParentAccounts", "value", clientDetailData.parent_id);
            vm.$refs.dynamicform.updateProperty("AccountName", "value", clientDetailData.account_name);
            vm.$refs.dynamicform.updateProperty("ClientOwner", "value", clientDetailData.clientOwnerId);
            vm.$refs.dynamicform.updateProperty("TimeZone", "value", clientDetailData.timezone_id);
            vm.$refs.dynamicform.updateProperty("Jurisdiction", "value", vm.ClientData.jurisdictionId);
            vm.$refs.dynamicform.updateProperty("DateFormat", "value", clientDetailData.date_format == 'M/d/yyyy' ? 'M/D/YYYY' : clientDetailData.date_format);
            if (clientDetailData.role_id != null)
                vm.$refs.dynamicform.updateProperty("Role", "value", clientDetailData.role_id);
            if (clientDetailData.customerTypeId != null)
                vm.$refs.dynamicform.updateProperty("CustomerType", "value", clientDetailData.customerTypeId);
            vm.profilePicture = clientDetailData.avatar;
            vm.containerName = clientDetailData.containername;
            vm.attachmentName = clientDetailData.attachemenT_NAME;
        

        },
          GetTaxJurisdiction(){
            var vm=this;
            var url = `id=&moduleName=&type=TAX_JURISDICTION&search=&refId=`;
            vm.FormSchema[2].Data[3].config.options=[];
            DataServices.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            response.data.DATA.forEach(item=>{
                                let obj={
                                    value:item.value,
                                    name:item.name
                                };
                                vm.FormSchema[2].Data[3].config.options.push(obj);
                            })  
                            vm.$refs.dynamicform.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        GetClientOwner: async function () {
            var vm = this;
            let Url = `leadId=0&type=p&moduleName=Client`
            await DataServices.GetClientOwner(Url).then(function (response) {
                vm.ClientOwnerddl = response.data;
                vm.ClientOwnerddl.forEach((item, index) => {
                    (vm.FormSchema)[1].Data[3].config.options.push({
                        value: item.USERID,
                        name: item.USERNAME
                    });
                    vm.$refs.dynamicform.UpdateKeyValue();
                })
            });
        },
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.Type = e.target.dataset.value;
            if (vm.Type == '2') {
                vm.FormSchema[1].Data[1].visibility = false;
                vm.FormSchema[1].Data[2].visibility = true;
            } else {
                vm.FormSchema[1].Data[2].visibility = false;
                vm.FormSchema[1].Data[1].visibility = true;
            }
        },
        GetAllRolesDDL: async function () {
            var vm = this;
            let Url = `userType=4`
            await DataServices.GetAllRoles(Url).then(function (response) {
                if (response.data.length > 0) {
                    vm.Rolesddl = response.data;
                    vm.Rolesddl.forEach((item, index) => {
                        (vm.FormSchema)[2].Data[2].config.options.push({
                            value: item.Value,
                            name: item.Name
                        });
                        vm.$refs.dynamicform.UpdateKeyValue();
                    })
                }
            });
        },
        GetTimeZone: async function () {
            var vm = this;
            await DataServices.GetTimeZoneList().then(function (response) {
                vm.TimeZoneddl = response.data;
                vm.TimeZoneddl.forEach((item, index) => {
                    (vm.FormSchema)[2].Data[0].config.options.push({
                        value: item.code,
                        name: item.value
                    });
                    vm.$refs.dynamicform.UpdateKeyValue();
                })
            });
        },
        GetClientParentByCompanyId: async function () {
            var vm = this;
            await DataServices.GetClientParentByCompanyId().then(function (response) {
                vm.ParentAccountddl = response.data;
                vm.ParentAccountddl.forEach((item, index) => {
                    (vm.FormSchema)[1].Data[1].config.options.push({
                        value: item.Value,
                        name: item.Name
                    });
                    vm.$refs.dynamicform.UpdateKeyValue();
                })
            });
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                vm.attachmentName = files[0].name;
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            //vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            //vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            //vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "",
                                subModuleCode: "",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        ResetProfileImage() {
            var vm = this;
            vm.fileData = [];
            vm.attachmentName = '';
            vm.base64String = '';
            vm.profilePicture = '';
            vm.fileInputKey += 1;
        },
        async UploadProfilePicture() {
            var vm = this;
            var formData = JSON.stringify(vm.fileData);
            await DataServices.UploadFile(formData).then(function (response) {
                if (response.data && response.data.length > 0) {
                    if (response.data[0].Name.length > 0) {
                        var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                        vm.profilePicture = img;
                    }
                }
            })
        },
        async OnSubmit(formfields, val) {
            var vm = this;
            if (vm.fileData.length > 0 || vm.profilePicture) {
                await vm.UploadProfilePicture();
            }

            var jsonobj = {
                ClientId: vm.ClientId,
                Account_id: formfields.ParentAccounts == "" ? 0 : formfields.ParentAccounts,
                first_name: formfields.FirstName,
                last_name: formfields.LastName,
                Email: formfields.Email,
                department_id: null,
                clientType: "",
                password: "",
                UserType: vm.user_type,
                timezone_id: formfields.TimeZone,
                date_of_birth: "",
                date_format: formfields.DateFormat,
                gender: formfields.Gender == '' ? 'M' : formfields.Gender,
                clientOwnerId: formfields.ClientOwner,
                role_id: formfields.Role ? formfields.Role : 0,
                profile_path: vm.profilePicture,
                associatefg: false,
                account_name: formfields.AccountName,
                user_type_id: 4,
                shift_id: null,
                hourly_rate: 0,
                designation_id: null,
                AliasName: '',
                IsLeadOwner: '',
                user_emp_id: 0,
                user_title_id: 0,
                supervisor_id: 0,
                is_virtual: '',
                IsProjectrequestOwner: '',
                is_enable_Login: 1,
                policy_id: '',
                company_name: vm.CompanyName,
                CustomerTypeId: formfields.CustomerType,
                pay_class: '',
                home_business_unit_id: 0,
                minitial: '',
                job_type: '',
                start_date: '',
                email_id: formfields.Email,
                JurisdictionId:formfields.Jurisdiction
            };
            var params = JSON.stringify(jsonobj);
            DataServices.AddClient(params).then((response) => {
                if (response.data == "success") {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Client")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            var vm = this;
            vm.$router.push({ name: 'ClientListing' });
        },
        async getCustomertype() {
            const vm = this;
            try {
                const response = await DataServices.GetMeterCategories();
                if (response?.data) {
                    const responseData = response.data;
                    if (Array.isArray(responseData) && responseData.length > 0) {
                        responseData.map(item => {
                            (vm.FormSchema)[1].Data[4].config.options.push({
                                value: item.value,
                                name: item.key
                            });
                            vm.$refs.dynamicform.UpdateKeyValue();
                        });
                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Customer type:', error);
            } finally {
                //
            }
        },

    }
}
</script>