<template>
    <section v-if="IsPrivilege">
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Tax"
            SubModuleCode="TaxCategory" IdentityColumn="id" :HeaderText="$t('Property')"
            :ListData="ListingData" :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons"
            :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="GetPropertyList" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
            :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">   
            <template v-slot:slotdata="{ data }">                
                <template v-if="data.column.COLUMN_NAME == 'status_name'">
                    <a class="inactive-disabled">
                        <span class="text-center text-white d-block"
                            :style="{ backgroundColor: data.row.status_bg_color }"> {{ data.row.status_name }} </span>
                    </a>
                </template>         
                <template v-if="data.column.COLUMN_NAME == 'meter_count'">
                    <span>
                        <a id="ancProjectDetail" :class="{'text-dark text-center': true, 'disabled': (data.row.meter_count == 0)}" href="javascript:;"  v-on:click="OpenMeterListPopup(data.row.id)">
                            <i class="fa fa-user" aria-hidden="true"></i>
                            <span class="noti-circle noti-blue">{{ data.row.meter_count }}</span>
                        </a>
                    </span>
                </template>      
                <template v-if="data.column.COLUMN_NAME == 'property_size'">
                    <span>
                        <span v-if="data.row.property_size==0"></span>
                        <span v-else>{{ data.row.property_size }}</span>
                    </span>
                </template> 
                <template v-if="data.column.COLUMN_NAME == 'user_zipcode'">
                    <span>
                        <span v-if="data.row.user_zipcode==0"></span>
                        <span v-else>{{ data.row.user_zipcode }}</span>
                    </span>
                </template>      
            </template>         
        </tg-list>
        <AddPropertymodal v-if="IsAddProperty" @CloseAddProperty="CloseAddProperty" :ClientData="ClientData" :ClientId="ClientId" :AddAddProperty="AddAddProperty"></AddPropertymodal>
        <PropertyHistory v-if="IsPropertyHistory" @ClosePropertyHistory="ClosePropertyHistory" :PropertyId="PropertyId" :PropertyName="PropertyName"></PropertyHistory>
        <ConstructionType v-if="IsConstructionType" @CloseConstructionType="CloseConstructionType" :ClientData="ClientData" :ClientId="ClientId" :Type="IsType" :AddAddProperty="AddAddProperty"></ConstructionType>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isMeterMapped">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('MappedMeter') }}</h4>
                                <button type="button" class="close"  v-on:click="popupClose">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="col-md-12">
                                    <div class="listing">
                                        <div class="table-responsive table-fix-header">
                                            <table class="table table-bordered dt-responsive nowrap mt-0" id="tbl_formList">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th><span>{{ $t('MeterType') }}</span></th>
                                                        <th><span>{{ $t('MeterName') }}</span></th>
                                                        <th><span>{{ $t('Plan') }}</span></th>
                                                        <th><span>{{ $t('SerialNumber') }}</span></th>
                                                        <th><span>{{ $t('ModelName') }}</span></th>
                                                        <th><span>{{ $t('MeterService') }}</span></th>
                                                        <th><span>{{ $t('MeterUnit') }}</span></th>
                                                        <th><span>{{ $t('ConnectionType') }}</span></th>
                                                        <th><span>{{ $t('Property') }}</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(row, index) in meterList">
                                                        <td>{{ row.meter_type }}</td>
                                                        <td>{{ row.meter_name }}</td>
                                                        <td>{{ row.plan_name }}</td> 
                                                        <td>{{ row.serial_number }}</td>
                                                        <td>{{ row.model_name }}</td>
                                                        <td>{{ row.meter_service }}</td>
                                                        <td>{{ row.meter_unit }}</td>
                                                        <td>{{ row.connection_type }}</td>
                                                        <td>{{ row.property }}</td>
                                                    </tr>
                                                    <tr v-if="(meterList == null || meterList.length == 0)">
                                                        <td colspan="9" class="text-danger text-center">
                                                            {{ $t('NoRecordfound') }} </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
    <section v-else>
        <div class="container-fluid mt-2 p-0">
            <div class="theme-primary">
                <span class="p-name text-white">{{ $t('Property') }}</span>
            </div>
           <div class="border p-2">
                <div class="col-lg-12 col-sm-12 alert alert-warning text-center mb-1" role="alert">
                    {{ $t('NotAuthorizedSection')}}
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import PropertySchma from './PropertySchma';
import AddPropertymodal from '../../../components/Modals/AddPropertymodal.vue';
import PropertyHistory from '../../../components/Modals/PropertyHistory.vue';
import ConstructionType from '../../../components/Modals/ConstructionType.vue';
import Vue from 'vue'
export default {
    components: {AddPropertymodal,PropertyHistory,ConstructionType},
    props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        },
        ChecKPrivilege:{
            type: Boolean,
            required: true,
        }
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,           
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            ListingData: [],  
            TotalRecords: 0,          
            SortBy: '',
            moduleCode: 'Billing',
            SortExp: '',
            Buttons: PropertySchma.buttons, 
            listActions: PropertySchma.listActions,         
            leftSearchFields: PropertySchma.leftsearchSchema,
            Headers: [
                {
                    "COLUMN_NAME": "property_title",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Title",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "property_type",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Type",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "property_construction_status",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Construction",
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "block_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "BlockName",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "floor_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "FloorName",
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "user_address_1",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "AddressLine1",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "user_address_2",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "AddressLine2",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "user_city",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "City",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "user_state",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "STATE",
                    "DISPLAY_ORDER": 3
                },
                  {
                    "COLUMN_NAME": "user_zipcode",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "ZIPCODE",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                  {
                    "COLUMN_NAME": "country_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "COUNTRY",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "user_address_desc",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "DESCRIPTION",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "property_size_unit",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Unit",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "property_size",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Size",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "meter_count",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "MeterMappedCount",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                }
            ],        
            userId: null,
            companyId: null,
            IsAddProperty:false,
            AddAddProperty:null,
            PropertyId:null, 
            PropertyName:'', 
            IsPropertyHistory:false ,
            isMeterMapped:false,
            meterList:[],
            IsType:'',
            IsConstructionType:false,           
            IsPrivilege:true    
        }
    },
    created: function () {
        var ths = this;
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId; 
        if(ths.ChecKPrivilege){
             ths.IsPrivilege=true; 
             ths.GetPropertyList();                  
        }else{
            ths.IsPrivilege=false;
        }  
    },
    methods: {          
        popupClose:function(){
            var vm = this;
            vm.isMeterMapped = false;
        },   
        OpenMeterListPopup: async function (propertyId) {
            var vm = this;
            vm.isLoading = true;
            vm.isMeterMapped= true;
            var params = `clientId=${vm.ClientId}&propertyId=${propertyId}`;
            if (!vm.isBlank(this.searchCondition)) {
                params += `&search=${this.searchCondition}`;
            }
            await DataService.GetMapMeterById(params).then(function (response) {
                if (response.data) {
                    if (response.data.length > 0) {
                        vm.isLoading = false;
                        vm.meterList = response.data;

                    } else {
                        vm.meterList = [];
                        vm.isLoading = false;

                    }
                } else {
                    vm.isLoading = false;
                }
            });
        },
        GetPropertyList() {
            var vm = this;
            vm.isLoading = true;            
            var url = `ClientId=${vm.ClientId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.GetPropertyList(url).then((response) => {                
                vm.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.PageNumber = vm.PageNumber; 
                    vm.ListingData.forEach(property => {
                            Vue.set(property, "isSelected", false); 
                            if(property.meter_count > 0) {
                                Vue.set(property, "isCheckBoxDisabled", true); ;
                            }
                            else{
                                Vue.set(property, "isCheckBoxDisabled", false);
                            }
                        });   
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            })
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.GetPropertyList();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Update Construction':
                        action.isVisible = true;
                        break;
                    case 'Update Type':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible =  row.meter_count > 0 ? false : true;
                        break;
                    case 'History':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetPropertyList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditUpdateConstruction':
                    this.EditConstructionType(item,'C');
                    break;
                case 'EditUpdateType':
                    this.EditConstructionType(item,'T');
                    break;
                case 'DeleteTaxClient':
                    this.commonDeleteData(item.id);
                    break;
                case 'PropertyHistory':
                    this.PropertyHistory(item);
                    break;
            }
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
            switch (event.callbackfunction) {
                case "AddProperty":
                    vm.AddProperty(null);
                    break;
                case "BacktoList":
                    vm.BacktoList();
                    break;
                case "commonDeleteData":
                    vm.commonDeleteData(null);
                    break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTaxList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },   
        BacktoList() {
            this.$router.push({ name: 'ClientListing' });
        }, 
        AddProperty(val){
            var ths =this;
            ths.AddAddProperty=val;
            ths.IsAddProperty=true;
        }, 
        EditConstructionType(val,type){
            var ths =this; 
            ths.IsType=type;          
            ths.AddAddProperty=val;
            ths.IsConstructionType=true;
        },
        CloseConstructionType(){
            var ths =this; 
            ths.IsConstructionType=false;
            ths.GetPropertyList();
        },
        CloseAddProperty(){
            var ths =this;
            ths.IsAddProperty=false;
            ths.GetPropertyList();
        },      
        commonDeleteData(id) { 
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Property")), function () {
                    var objectPostString = `ids=${val}`;
                    DataService.DeletePropertyList(objectPostString).then(response => {                        
                        vm.isLoading=false;
                        if(response.data.result.DATA.length>0){ 
                            if(response.data.result.DATA[0].VALUE=='REMOVE_MAPPING') {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Property')]), "Success", true, vm.$t('Alert'));
                                vm.CloseAddProperty();                                         
                            }                            
                            else{
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Property')]), "failure", true, vm.$t('Alert'));
                                vm.CloseAddProperty();                                         
                            }                                         
                        }
                        else{
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }                        
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        PropertyHistory(val){
            var ths=this;
            ths.PropertyId=val.id;
            ths.PropertyName=val.property_title;
            ths.IsPropertyHistory=true;
        },
        ClosePropertyHistory(){
            var ths=this;         
            ths.IsPropertyHistory=false;
        }      
    }
}
</script>
<style>
.ticketsmallfix.theme-primary .p-name {
    float: left;
}
</style>