                        <!-- <template v-if="data.column.COLUMN_NAME === 'CurrentReading'">
                          <a href="javascript:;" class="task-dec"  @click="onReadingClick(data.row)">
                                    {{ data.row.CurrentReading }}
                          </a>
                         
                    </template> -->
