        async handleGenerateBill() {
            var vm = this;
            let val = "";
            document.querySelectorAll(".chkItems:checked")
                .forEach(function (checkbox) {
                    if (!checkbox.id.startsWith('chkAll')) {
                        if (val.length > 0) val += ",";
                        val += checkbox.value;
                    }
                });

            if (val == "") {
                vm.ShowAlert(
                    vm.$t("Please select at least one item"),
                    "warning",
                    vm.$t("Alert")
                );

            }
            var MeterMappingId=vm.ListingData[0].MeterMappingId;
            let url = `ids=${val}&clientId=${this.UserId}&plan_mapping_id=${MeterMappingId}&type=Generate&billing_cycle=monthly&current_date=&is_foredit=no`
            try {
                const response = await DataService.GenerateMeterInvoice(url);
                if (response?.data) {
                    debugger
                    const result = response.data.result;
                    if (result && (result.result.data[0].bill_id!=null)) {
                        this.ShowAlert(this.$t("Invoice generated successfully"), "success", this.$t('Alert'));
                        this.$emit("refreshList");
                        this.FetchData();
                    } else if (result && result.result.data[0].bill_id === null) {
                        this.ShowAlert(this.$t(result.result.data[0].status_message), "warning", this.$t('Alert'));
                        this.$emit("refreshList");
                        this.FetchData();
                    } else {
                        this.ShowAlert(this.$t("Error while save meter data"), "failure", this.$t('Alert'));
                    }
                }
            } catch (error) {
                //
            } finally {
                //
            }


        },
        handlefilterSearch(newValue) {
            this.meterTypeId = newValue.meterTypeId;
            this.meterIds = newValue.meterIds;
            this.planIds = newValue.planIds;
            this.dateFrom = newValue.dateFrom;
            this.dateTo = newValue.dateTo;
            this.IsFilterMeterReading = false;
            this.isFilterApplied = true;
            this.FetchData();

        },
        closeshowLightbox() {
            var vm = this;
            vm.images = [];
            vm.showLightbox = false;
        },
        handleResetFilter() {
            this.meterTypeId = ''
            this.meterIds = ''
            this.planIds = ''
            this.dateFrom = ''
            this.dateTo = ''
            this.isFilterApplied = false;
            this.FetchData();

        },

