            vm.IsMeterDetails = false;
        },
        async FetchData() {
            const vm = this;
            vm.ListingData = [];
            vm.isLoading = true;

            const params = `ClientId=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&meter_type_id=${vm.meterTypeId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&customerTypeId=1&meterids=${vm.meterIds}&plans=${vm.planIds}&datefrom=${vm.dateFrom}&dateto=${vm.dateTo}&isPartial=0`;

            try {
                const response = await DataService.GetMeterReadingList(params);
                if (response?.data) {
                    let data = JSON.parse(response.data.result).data;

                    if (data.length > 0) {
                        vm.isLoading = false;

                        vm.isCheckbox = (vm.meterTypeId != '');

                        vm.ListingData = data.map(item => {
                            item.processStatus = (item.is_processed === 1) ? "Processed" : "UnProcessed";
                            
                            item.isCheckBoxDisabled = false;
                            if(item.is_Billed === 'No' && item.is_processed==1) {
                                item.isCheckBoxDisabled = false;
                            }else{
                                item.isCheckBoxDisabled = true;
                            }

                            return item;
                        });

                        vm.Headers.forEach((column) => {
                            column.settings = null;
                            if (column.COLUMN_NAME === "MeterMappingName") {
                                column.settings = {
                                    clickEvent: vm.clickEventOfColumn
                                };
                            }
                            if (column.COLUMN_NAME == 'ReadingDate') {
                                column.settings = {
                                    isInSlot: true
                            }
                            }
                            if (column.COLUMN_NAME == 'processStatus') {
                                column.settings = {
                                    isInSlot: true
                            }
                            }
                        });

                        vm.noRecord = false;
                        vm.TotalRecords = vm.ListingData[0]?.TotalRecords || 0;
                    } else {
                        // No data case
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }
                } else {
                    // Response has no data
                    vm.ListingData = [];
                    vm.noRecord = true;
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }
            } catch (error) {
                console.error("Error fetching meter reading data:", error);
                vm.ListingData = [];
                vm.noRecord = true;
                vm.TotalRecords = 0;
                vm.isLoading = false;
            }
