        async getMeterTypes() {
            const vm = this;
            try {
                let url = `is_mapped=2`

                const response = await DataService.GetMeterTypes(url);
                if (response?.data) {
                    const responseData = JSON.parse(response.data.result).data;

                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {

                            this.meterTypes.push({
                                value: item.meter_id,
                                name: item.meter_name
                            });

                        });
                        vm.$refs.dynamicform.UpdateKeyValue();

                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Meter types:', error);
            } finally {
                //
            }
        },
        leftCommonSearch(value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchCondition != '') {
                    let str = value.searchConditionJson;
                    let result = str.split('=')[1];
                    this.searchCondition = result;
                }
                else {
                    this.searchCondition = "";
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        if (row.is_Billed == 'No' && row.ROWNUMBER == 1) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Edit':
                        if (row.is_Billed == 'No' && row.processStatus != 'Processed'  && row.ROWNUMBER == 1) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Replace':
                        if (row.status_id == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'View History':
                        if (row.status_id == 1002) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                        case 'View Process':
                        if (row.is_processed == 0) {
                            action.isVisible = false;
                        } else {
                            action.isVisible = true;
                        }
                        break;
                        case 'Processed':
                        if (row.is_processed == 0) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        formatDate(value) {
            debugger
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
