            switch (callbackfunction) {
                case 'DeleteMeterReading':
                    this.DeleteMeterReading(item);
                    break;
                case 'EditMeterReading':
                    this.EditMeterReading(item);
                    break;

                    case 'ViewMeterReading':
                    this.ViewMeterReading(item);
                    break;
                    case 'Delete':
                        this.DeleteMultiple(item);
                        break;
                    case 'Replace':
                        this.ReplaceMeter(item);
                        break;
                        case 'Processed':
                        this.IsProcessed(item);
                        break;
                    // case 'ViewHistory':
