            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        DeleteMeterReading(itm) {
            var vm = this;
            var val = "";
            val = itm.MeterReadingId.toString();
            if (val != null && val != undefined) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Plan")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "CompanySetup",
                        refCode: "Delete_Meter_Reading"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchData();
                            vm.ShowAlert('Deleted Data Successfully!', "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
            }
        },

        EditMeterReading (itm) {
            var vm = this;
            vm.EditDetails = itm;
            vm.IsAddMeterReading = true;

        },
         ViewMeterReading(itm) {
            var vm = this;
            vm.MeterReadingId = itm.MeterReadingId; 
            vm.IsMeterReadingDetail = true;
        },
        IsProcessed(itm) {
            var vm = this;
            var url = `clientId=${vm.UserId}&clientAdditionalId=${itm.ClientAdditionalId}&locationId=${vm.locationId}&fromDate=&toDate=&meterReadingId=${itm.MeterReadingId}&batchDate=&isLog=true&processType=AUTO&processId=`;
            //var url = `clientId=${vm.UserId}&clientAdditionalId=${itm.ClientAdditionalId}&locationId=${vm.locationId}&fromDate=&toDate=&meterReadingId=${itm.MeterReadingId}&batchDate=&isLog=true&processType=AUTO&processId=`;
            DataService.ProcessMeterReading(url).then(function (response) {
                if (response.data && response.data[0].status_id==1) {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Status')]), "Success", true, vm.$t('Alert'));
                    vm.FetchData();
                }else if(response.data && response.data[0].status_id==2){
                   vm.ShowAlert("Billing stoped", "failure", true, vm.$t("Alert"));
                    vm.FetchData();
                }else{
                     vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                }

            });
        },
       getStatusClass: function (is_processed) {
        var returnClass = '';
        if (is_processed === 1) {
            returnClass = 'px-2 text-center bg-success text-white d-block';  
        } else if (is_processed === 0) {
            returnClass = 'px-2 text-center bg-danger text-white d-block';  
        } else {
            returnClass = '';
        }
        return returnClass;
    },


    },
}
</script>
