<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ReplaceMeter') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <div id="app" class="row listing mt-2">
                            <div class="col-lg-12">
                                <h5>Meter Replacement History</h5>
                                <div class="table-responsive table-fix-header">
                                    <table id="meterReadingsTable" class="table table-bordered dt-responsive nowrap">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>Old Meter ID</th>
                                                <th>Final Reading (Old Meter)</th>
                                                <th>Replacement Date</th>
                                                <th>Reason</th>
                                                <th>Old Meter Status</th>
                                                <th>New Meter ID</th>
                                                <th>New Meter Initial Reading</th>
                                                <th>New Meter Installation Date</th>
                                                <th>Reading Carry Forwarded?</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="noRecord">
                                                <td colspan="9" class="text-danger text-center no-record">
                                                    {{ $t("NoRecordfound") }}
                                                </td>
                                            </tr>
                                            <tr v-for="(list, index) in ListingData" :key="index">
                                                <td>{{ list.Old_Metername }}</td>
                                                <td>{{ list.old_meter_reading }}</td>
                                                <td>{{ $options.filters.formatDate(list.date_Of_replacement) }} </td>
                                                <td>{{ list.meter_status }}</td>
                                                <td>{{ list.replacement_reason }}</td>
                                                <td>{{ list.New_metername }}</td>
                                                <td>{{ list.new_meter_reading }}</td>
                                                <td>{{ $options.filters.formatDate(list.date_Of_installation) }} </td>
                                                <td>Yes</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        MeterMappingId: {
            type: Number,
            required: true,
        },
    },
    data() {
        return {
            isLoading: false,
            ListingData: [],
            noRecord: false,
        };
    },
    created: async function () {
        await this.FetchData();
    },
    methods: {
        async FetchData() {
            try {
                this.isLoading = true;
                const response = await DataServices.ViewHistory(`meterId=${this.MeterMappingId}`);
                if (response.data) {
                    this.ListingData = response.data;
                    this.noRecord = this.ListingData.length === 0;
                }
            } catch (error) {
                console.error("Error fetching meter replacement history:", error);
                this.noRecord = true;
            } finally {
                this.isLoading = false;
            }
        },
        formatDateTime: function (value) {
            debugger
            if (value) {
                return new Date(value).toLocaleString();
            }
        },
        Close() {
            this.$parent.IsHistory = false;
        },
    },
};
</script>

<style scoped></style>