<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AssociateContact') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="AddContact" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import DataService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        TagArr: {
            type: Object,
            required: true
        },
        CountryId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "FirstName",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "LastName",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('JobTitle'),
                            name: "JobTitle",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                    astype: "TextField",
                    label: this.$t('Email'),
                    name: "Email",
                    value: "",
                    validationRules: [{ "email": true }],
                    disabled: false,
                },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('BusinessPhone'),
                            name: "BusinessPhone",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "PhoneField",
                            label: this.$t('Mobilenumber'),
                            name: "MobileNumber",
                            value: "",
                            config: {
                                dependentOn: null,
                                fieldCode: null,
                                mode: "date",
                                options: [],
                                showWhen: null,
                                valueMapping: null,
                            },
                            validationRules: { "required": false },
                            visibility: true,
                            mode: ""
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('FAX'),
                            name: "Fax",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PreferredContact'),
                            name: "PreferredContactMethod",
                            value: "",
                            config: {
                                options: [{ value: "Any", name: "Any" }, { value: "Email", name: "Email" }, { value: "Phone", name: "Phone" }, { value: "Fax", name: "Fax" }, { value: "Mail", name: "Mail" }],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": false },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('AddressStreet1'),
                            name: "AddressStreet1",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AddressStreet2'),
                            name: "AddressStreet2",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('AddressStreet3'),
                            name: "AddressStreet3",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('STATE'),
                            name: "State",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('ZIPCODE'),
                            name: "ZipCode",
                            value: "",
                            validationRules: { "required": false, max: 15 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "SelectField",
                            label:  this.$t("COUNTRY"),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                required: false,
                                max: 250,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Gender'),
                            name: "Gender",
                            value: "",
                            config: {
                                options: [{ value: "M", name: "Male" }, { value: "F", name: "Female" }],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "SelectField",
                            label: this.$t('MaritalStatus'),
                            name: "MaritalStatus",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: {
                                required: false,
                                max: 250,
                            },
                            config: {
                                options: [{ value: "Single", name: "Single" }, { value: "Married", name: "Married" }, { value: "Divorced", name: "Divorced" }, { value: "Widowed", name: "Widowed" }],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('SPOUSENAME'),
                            name: "SpouseName",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('PersonalNotes'),
                            name: "PersonalNotes",
                            value: "",
                            validationRules: { "required": false, max: 100 },
                        },
                    ],
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.GetCountryData();
    },
    methods: {
        GetCountryData: function () {
      var vm = this;
      vm.FormSchema[7].Data[0].config.options=[];
      DataService.GetCountryData().then(function (response) {
        var data = response.data;
        data.forEach((element) => {
          vm.FormSchema[7].Data[0].config.options.push({
            name: element.countrY_FULL_NAME,
            value: element.id,
          });
        });
        vm.$refs.AddContact.UpdateKeyValue();
      });
    },
        Close: function () {
            this.$parent.OpenAddContact = false;
        },
        onSubmit(formfields, val) {            
            var vm = this;
            debugger
            var jsonobj = {
                CountryId: formfields.Country,
                first_name: formfields.FirstName,
                last_name: formfields.LastName,
                job_title:formfields.JobTitle,
                email_id:formfields.Email,
                business_phone:formfields.BusinessPhone,
                mobile_phone: formfields.MobileNumber,
                fax: formfields.Fax,
                preferred_contact:formfields.PreferredContactMethod,
                address_street_1:formfields.AddressStreet1,
                address_street_2:formfields.AddressStreet2,
                address_street_3: formfields.AddressStreet3,
                city: formfields.City,
                state:formfields.State,
                zipcode:formfields.ZipCode,
                gender:formfields.Gender,
                marital_status: formfields.MaritalStatus,
                spouse_name: formfields.SpouseName,
                date_of_birth: "",
                anniversary: "",
                personal_notes:formfields.PersonalNotes,
                status_id:1001
            };
            debugger

            var params = JSON.stringify(jsonobj);
            DataServices.SaveNewContacts(params).then((response) => {
                if (response.data[0].Status == 'Success') {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Contacts")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Contacts")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>