<template>
    <div class="my-popups">
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle d-flex">
                        <h4 class="modal-title">{{ $t('Time') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('UserGuide') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UserGuideProcessingTimeClients')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="TimeForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-Description="{ data }">
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        clientId: {
            type: Number,
            required: false
        },
        ClientAdditionalId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            FormSchema: [
            {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('ProcessingBasedOn'),
                            name: "Processingbasedon",
                            visibility: true,
                            value: "",
                            config: {
                                options: [{ value: "TimeBased", name: this.$t('TimeBased') }, { value:  "EventBased", name: this.$t('EventBased') }],
                                onChange: this.onChangeProcessingBased
                            },
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ProcessingInterval'),
                            name: "ProcessInterval",
                            value: "",
                            config: {
                                options: [
                                       { value: "month", name: "Monthly" },
                                       { value: "biweek", name: "Bi-Weekly" },
                                       { value: "week", name: "Weekly" },
                                       { value: "daily", name: "Daily" }],
                                onChange: this.onChangeProcessInterval
                            },
                            placeholder: "Select",
                            visibility: false,
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('MonthDate'),
                            name: "ProcessDate",
                            value: "",
                            visibility:false,
                            config: {
                                options: []
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ProcessDay'),
                            name: "ProcessDay",
                            value: "",
                            visibility:false,
                            config: {
                                options: [ { name: 'Monday', value: 'Monday' },
                                            { name: 'Tuesday', value: 'Tuesday' },
                                            { name: 'Wednesday', value: 'Wednesday' },
                                            { name: 'Thursday', value: 'Thursday' },
                                            { name: 'Friday', value: 'Friday' },
                                            { name: 'Saturday', value: 'Saturday' },
                                            { name: 'Sunday', value: 'Sunday' }
                                        ]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('EventFor'),
                            name: "EventFor",
                            value: "",
                            config: {
                                options: [
                                       { value: "Meter Reading", name: "OnMeterReading" }]
                            },
                            placeholder: "Select",
                            visibility: false,
                            validationRules: { "required": false,},
                        }
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            invoiceDurationList: [],
            processingTimeId:0
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetProcessingTimeNew();
        setTimeout(() => {
            vm.monthDateList();
         }, 100);
       
    },
    methods: {
        GetProcessingTimeNew: async function () {
            var vm = this;
            var url = `additionalId=${vm.ClientAdditionalId}`
            await DataServices.GetProcessingTimeNew(url).then((response) => {
                if (response.data.length > 0) {
                    debugger;
                    vm.$refs.TimeForm.updateProperty("Processingbasedon", "value", response.data[0].processingBased);
                    vm.processingTimeId = response.data[0].processingTimeId;
                    if(response.data[0].processingBased == 'TimeBased'){
                        if(response.data[0].processInterval == 'month'){
                            vm.$refs.TimeForm.updateProperty("ProcessDate", "value", response.data[0].processDate);
                            vm.$refs.TimeForm.updateProperty("ProcessDate", "visibility", true);
                            vm.$refs.TimeForm.updateProperty("ProcessDay", "visibility", false);
                        }
                        else if(response.data[0].processInterval == 'week'){
                            vm.$refs.TimeForm.updateProperty("ProcessDay", "value", response.data[0].processDay);
                            vm.$refs.TimeForm.updateProperty("ProcessDay", "visibility", true);
                            vm.$refs.TimeForm.updateProperty("ProcessDate", "visibility", false);
                        }
                        vm.$refs.TimeForm.updateProperty("ProcessInterval", "visibility", true);
                        
                        vm.$refs.TimeForm.updateProperty("ProcessInterval", "value",  response.data[0].processInterval);

                    }
                    else if(response.data[0].processingBased == 'EventBased')
                    {
                        vm.$refs.TimeForm.updateProperty("EventFor", "value", response.data[0].eventFor);
                        vm.$refs.TimeForm.updateProperty("EventFor", "visibility", true);
                        vm.$refs.TimeForm.updateProperty("ProcessInterval", "visibility", false);
                        vm.$refs.TimeForm.updateProperty("ProcessDay", "visibility", false);
                        vm.$refs.TimeForm.updateProperty("ProcessDate", "visibility", false);
                    }
                   
                    vm.$refs.TimeForm.UpdateKeyValue();
                }
            });
        },
        onChangeProcessingBased(x,y,z){
            const vm =this;
            debugger;
            const value = x.target.dataset.value;
            if(value == 'TimeBased'){
                vm.$refs.TimeForm.updateProperty("ProcessInterval", "visibility", true);
                vm.$refs.TimeForm.updateProperty("EventFor", "visibility", false);
                vm.$refs.TimeForm.UpdateKeyValue();
            }
            else if(value == 'EventBased'){
                vm.$refs.TimeForm.updateProperty("EventFor", "visibility", true);
                vm.$refs.TimeForm.updateProperty("ProcessInterval", "visibility", false);
                vm.$refs.TimeForm.updateProperty("ProcessDay", "visibility", false);
                vm.$refs.TimeForm.updateProperty("ProcessDate", "visibility", false);
                vm.$refs.TimeForm.updateProperty("ProcessInterval", "value", "");
                vm.$refs.TimeForm.updateProperty("ProcessDay", "value", "");
                vm.$refs.TimeForm.updateProperty("ProcessDate", "value", "");
                vm.$refs.TimeForm.UpdateKeyValue();
            }
        },
        onChangeProcessInterval(event) {
            const vm = this;
            const value = event.target.value;
            const isMonth = value === 'month';
            const isWeek = value === 'week';

            vm.$refs.TimeForm.updateProperty("ProcessDate", "visibility", isMonth);
            vm.$refs.TimeForm.updateProperty("ProcessDay", "visibility", isWeek);
            vm.$refs.TimeForm.UpdateKeyValue();
        },
        monthDateList: function () {
            var vm = this;
            vm.invoiceDurationList = Array.from({ length: 31 }, (_, i) => {
                const day = (i + 1).toString();
                return { name: day, value: day };
            });
            if (vm.invoiceDurationList.length > 0) {
                if (vm.$refs.TimeForm) {
                    vm.$refs.TimeForm.updateProperty("ProcessDate", "config.options", vm.invoiceDurationList);
                    vm.$refs.TimeForm.UpdateKeyValue();
                }
            }
        },
        onSubmit: function (formfields, val) {            
            var vm = this;
            var day = null;
            var date = null;
                if (formfields.ProcessInterval == "week")
                {
                    day = formfields.ProcessDay;
                }
                if (formfields.ProcessInterval == "biweek")
                {
                    day = formfields.ProcessDay;
                }
                if (formfields.ProcessInterval == "month")
                {
                    date = formfields.ProcessDate;
                    day = formfields.ProcessDay;
                }
                if (formfields.ProcessInterval == "daily")
                {
                    day = formfields.ProcessDay;
                }
            var postJSON = {
                additionalId: vm.ClientAdditionalId,
                processingTimeId: vm.processingTimeId,
                processDay: day,
                processDayDate : date,
                processInterval : formfields.ProcessInterval,
                processingBasedOn : formfields.Processingbasedon,
                eventFor : formfields.EventFor
            };
            var objectPostString = JSON.stringify(postJSON);
            DataServices.SaveProcessingTime(objectPostString).then((response) => {
                    if (response.data != null && response.data != "" && response.data.status == "Success") {
                        if(response.data.result == 'INSERT'){
                            vm.ShowAlert(vm.$t('AddedTime'), "success");
                            vm.Close();
                        }
                        else if(response.data.result == 'UPDATE'){
                            vm.ShowAlert(vm.$t('UpdatedTime'), "success");
                            vm.Close();
                        }
                    }
                    else{
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                    
                });
          
        },
        Close: function () {
            this.$parent.isShowAddUtilityTime = false;
            this.$parent.$parent.LoadAdditionalInfo();
        }
    }
}

</script>