              itemTemplate += "<tr><td>" + item.productname + "</td><td>" + item.consumption + "</td><td>" + item.slabrate + "</td></tr>";
            })
            const value = itemTemplate;

            return value !== undefined ? value : match;
          }
          else {
            return "";
          }
        }
        else if (key.trim() == "discounttaxadjustments") {

          //debugger
          let itemsAdjustments = this.Details[0]["adjustments"];


          let adjustmentsDetails = "";
          let type = "";
          if (itemsAdjustments != null) {
            itemsAdjustments.forEach(function (item) {
              if (item.adjustment_operation == 1) {
                type = "tax"
              }
              else {
                type = "discount"
              }
              adjustmentsDetails += "<table width='100%'><tr><td style='padding:0px;font-size: 14px;border:none; '>" + item.adjustment_name + "</td><td style='width:80px; text-align:right;font-size: 14px;font-weight:400;padding:0px;border:none;'>" + item.adjustment_amount + "</td></tr></table>";
            })

            const value = adjustmentsDetails;
            return value !== undefined ? value : match;
          }
          else {
            return "";
          }
        }
        else {
          const value = this.Details[0][key.trim()];
          return value !== undefined ? value : match;
        }
        vm.isLoading = false;

      });

      vm.isLoading = false;
    }
  },

