    async ConvertLogoBase64(logoUrl) {
      try {
        let params = `imageUrl=${encodeURIComponent(logoUrl)}`;
        const response = await DataServices.getConvertBase64(params);
        //debugger;
        if (response.status === 200 && response.data && response.data.base64Image) {
          // Return with proper prefix for direct <img> usage
          const base64WithPrefix = response.data.base64Image;
          //this.logoBase64 = base64WithPrefix;

          return base64WithPrefix;
        } else {
          throw new Error("Invalid Base64 response");
        }
      } catch (error) {
        console.error("Error converting logo:", error);
        this.ShowAlert(this.$t("UnknownErrorOccur"), "failure", true, this.$t("Alert"));
        return null;
      }
    },

    async downloadInvoice() {
      //debugger;
      var vueObj = this;
      vueObj.isLoading = true;

      const doc = new jsPDF({
        unit: 'mm',
        format: 'a4',
      });

      const pageWidth = 210;
      const pageHeight = 297;
      const margin = 5;
      const contentWidth = pageWidth - 2 * margin;
      const contentHeight = pageHeight - 2 * margin;

      // Create a temporary div to hold the dynamic HTML content
      const element = document.createElement('div');
      element.innerHTML = this.parsedHtml;
      element.style.width = `${contentWidth}mm`;
      element.style.height = 'auto';
      element.style.boxSizing = 'border-box';
      element.style.padding = '10mm';

      document.body.appendChild(element);

      const elementWidth = element.offsetWidth;
      const elementHeight = element.offsetHeight;

      const scaleX = contentWidth / elementWidth;
      const scaleY = contentHeight / elementHeight;
      const scaleFactor = Math.min(scaleX, scaleY);
      //debugger;
      // Ensure images are loaded and convert them to Base64
      const images = element.querySelectorAll('img');
      document.body.removeChild(element);
      for (let i = 0; i < images.length; i++) {
        const img = images[i];

        const base64Image = await this.ConvertLogoBase64(img.src);
        //img.src = "https://flowhcm.com/wp-content/uploads/elementor/thumbs/logo-qbr1xyrgrwh5tzxp8yebwu9spwdca2sgibytxozfay.webp";

        img.src = base64Image;
      }


      // Remove the temporary element from the document


      console.log("pdf element : ", element.innerHTML)

      // Render the HTML to the PDF using jsPDF
      doc.html(element, {
        x: margin,
        y: margin,
        html2canvas: {
          scale: scaleFactor,
          logging: true,
          useCORS: true,
        },
        callback: function (doc) {
          vueObj.isLoading = false;
          doc.save('invoice.pdf');
        },
      });
    },


    downloadInvoice111111() {
      //debugger;
      const vueObj = this;
      vueObj.isLoading = true;

      const doc = new jsPDF({
        unit: 'mm',
        format: 'a4',
      });

      const pageWidth = 210;
      const pageHeight = 297;
      const margin = 5;
      const contentWidth = pageWidth - 2 * margin;
      const contentHeight = pageHeight - 2 * margin;

      // Create a new DOM element from the parsed HTML
      const element = document.createElement('div');
      element.innerHTML = this.parsedHtml;
      element.style.width = `${contentWidth}mm`;
      element.style.boxSizing = 'border-box';
      element.style.padding = '10mm';

      // Set crossorigin="anonymous" on all images inside the content
      const tempDiv = document.createElement('div');
      tempDiv.innerHTML = this.parsedHtml;
      tempDiv.querySelectorAll('img').forEach((img) => {
        img.setAttribute('crossorigin', 'anonymous');
      });
      element.innerHTML = tempDiv.innerHTML;

      // Append to DOM temporarily
      document.body.appendChild(element);

      const elementWidth = element.offsetWidth;
      const elementHeight = element.offsetHeight;

      const scaleX = contentWidth / elementWidth;
      const scaleY = contentHeight / elementHeight;
      const scaleFactor = Math.min(scaleX, scaleY);

      // Wait for images to load before rendering
      const images = element.querySelectorAll('img');
      const promises = Array.from(images).map((img) => {
        return new Promise((resolve) => {
          if (img.complete) {
            resolve();
          } else {
            img.onload = () => resolve();
            img.onerror = () => resolve(); // Resolve even if image fails
          }
        });
      });

      Promise.all(promises).then(() => {
        // Add small delay before rendering to ensure styles and images are ready
        setTimeout(() => {
          doc.html(element, {
            x: margin,
            y: margin,
            html2canvas: {
              scale: scaleFactor,
              useCORS: true,
              logging: false,
            },
            callback: function (doc) {
              vueObj.isLoading = false;
              doc.save('invoice.pdf');
              document.body.removeChild(element);
            },
          });
        }, 200); // Adjust delay if needed
      });
