<template>
    <section>
        <div class="listing mt-2" id="divAttachmentsList">
            <div class="heading-border mb-2">
                <h5 class="h5"><span>{{ $t("AttachmentList") }}</span></h5>
            </div>
            <div class="table-responsive">
                <table id="tbllistingdata" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                    <thead class="thead-bg">
                        <tr>
                            <th id="th-FILENAME" class="ui-resizable">
                                <span>{{ $t("FileName") }}</span>
                            </th>
                            <th id="th-CREATEDAT" class="ui-resizable">
                                <span>{{ $t("CreatedAt") }}</span>
                            </th>
                            <th id="th-CREATEDBY" class="ui-resizable">
                                <span>{{ $t("CreatedBy") }}</span>
                            </th>
                            <th id="th-FILETYPE" class="ui-resizable">
                                <span>{{ $t("FileType") }}</span>
                            </th>
                            <th id="th-DESCRIPTION" class="ui-resizable">
                                <span>{{ $t("Description") }}</span>
                            </th>
                            <th class="text-center nonresize">
                                <span>{{ $t("Download") }}</span>
                            </th>
                            <th class="text-center nonresize">
                                <span>{{ $t("Delete") }}</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="filteredAttachmentList.length === 0">
                            <td colspan="7" class="text-center text-danger no-record">
                                {{ $t("NoRecordfound") }}
                            </td>
                        </tr>
                        <tr v-for="(attachment, index) in filteredAttachmentList" :key="index">
                            <td>{{ attachment.file_name }}</td>
                            <td>{{ $options.filters.formatDate(attachment.created_at) }}</td>
                            <td>{{ attachment.created_by }}</td>
                            <td>
                                <i
                                    :class="['file-icon', getIconClass(attachment.Extension), getColorClass(attachment.Extension)]"></i>
                                {{ attachment.Extension }}
                            </td>
                            <td>{{ truncate(attachment.file_description || 'No Description', 30) }}</td>
                            <td class="text-center">
                                <a @click="downloadAttachment(attachment)" class="download" title="Download File">
                                    <i class="fa fa-download text-success"></i>
                                </a>
                            </td>
                            <td class="text-center">
                                <a @click="deleteAttachment(attachment.attachment_id)" class="delete" title="Delete">
                                    <i class="fa fa-trash text-danger"></i>
                                </a>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    props: {
        BillId: {
            type: Number,
        },

    },
    data() {
        return {
            attachmentList: [],
        };
    },
    computed: {
        filteredAttachmentList() {
            return this.attachmentList;
        }
    },
    async created() {
        console.log("billId", this.BillId);
        await this.getBillAttachMentList();
    },
    methods: {
        truncate(text, length) {
            return text.length > length ? text.substring(0, length) + '...' : text;
        },
        getIconClass(extension) {
            switch (extension.toLowerCase()) {
                case 'doc':
                case 'docx':
                    return 'fa fa-file-word';
                case 'pdf':
                    return 'fa fa-file-pdf';
                case 'txt':
                    return 'fa fa-file-text';
                case 'xls':
                case 'xlsx':
                    return 'fa fa-file-excel';
                default:
                    return 'fa fa-file';
            }
        },
        getColorClass(extension) {
            switch (extension.toLowerCase()) {
                case 'doc':
                case 'docx':
                    return 'word-color';
                case 'pdf':
                    return 'pdf-color';
                case 'txt':
                    return 'text-color';
                case 'xls':
                case 'xlsx':
                    return 'excel-color';
                default:
                    return 'default-color';
            }
        },
        // formatDate(dateString) {
        //     // Format the date (you can use libraries like moment.js or day.js, or use vanilla JS)
        //     const date = new Date(dateString);
        //     return date.toLocaleDateString('en-US', {
        //         weekday: 'short',
        //         year: 'numeric',
        //         month: 'short',
        //         day: 'numeric',
        //         hour: 'numeric',
        //         minute: 'numeric',
        //         second: 'numeric',
        //     });
        // },
        async getBillAttachMentList() {
            this.$parent.isLoading = true;
            this.attachmentList = [];
            try {
                let params = `billId=${this.BillId}`
                const response = await DataService.GetBillAttachMentList(params);
                if (response.status === 200) {
                    this.$parent.isLoading = false;
                    if (response.data.result != '' || response.data.result != null) {
                        this.attachmentList = JSON.parse(response.data.result).data;

                    }
                } else {
                    this.$parent.isLoading = false;
                }
            } catch (error) {
                console.error('Error fetching attachment list:', error);
            }
        },

        downloadAttachment(attachmentDetails) {

            try {
                if (!attachmentDetails || !attachmentDetails.file_path || !attachmentDetails.file_name) {
                    throw new Error('Invalid attachment details.');
                }
                var fileLink = document.createElement('a');
                fileLink.href = attachmentDetails.file_path;
                fileLink.setAttribute('download', attachmentDetails.file_name);
                document.body.appendChild(fileLink);
                fileLink.click();
                document.body.removeChild(fileLink);
            } catch (error) {
                console.error('Error downloading attachment:', error);
            }
        },


        async deleteAttachment(attachmentId) {
            try {

                const confirmDelete = await new Promise((resolve, reject) => {
                    this.confirmR(this.$t('Are you sure you want to delete this attachment?'), true, false, function (result) {
                        resolve(result);
                    });
                });
                this.$parent.isLoading = true;
                if (confirmDelete == undefined) {
                    const url = `documentId=${attachmentId}`;
                    const response = await DataService.DeleteAttachment(url);
                    if (response.status === 200) {
                        this.$parent.isLoading = false;
                        if (response.data.result.Result == '1') {
                            this.ShowAlert(this.$t('deleteBillAttachment'), "success");
                            this.getBillAttachMentList();
                        }

                    } else {
                        this.ShowAlert(this.$t("UnknownError"), "failure", this.$t('Alert'));
                    }
                }
            } catch (error) {
                console.error('Error deleting attachment:', error);
            }
        }


    },
};
</script>

<style scoped>
/* Icon color classes */
.word-color {
    color: #007bff;
    /* Blue for Word documents */
}

.pdf-color {
    color: #e2211b;
    /* Red for PDF files */
}

.text-color {
    color: #f0ad4e;
    /* Orange for text files */
}

.excel-color {
    color: #28a745;
    /* Green for Excel files */
}


.default-color {
    color: #6c757d;
    /* Gray for unknown file types */
}

/* Optional: Customize icon size */
.file-icon {
    font-size: 20px;
    /* Adjust icon size */
}
</style>
