<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block" v-show="PlanData.length > 0">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('associateplandetails') }}</h4>

                            <button type="button" class="close" @click="$emit('close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">

                            <div class="fluid-container p-0 m-0" v-if="PlanData.length > 0">
                                <div class="plan-info row">
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Plan Name:</b>
                                                {{ PlanData[0].plan_name }}</label></div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Plan Type:</b>
                                                {{ PlanData[0].plan_type }}</label></div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Effective Date:</b> {{
                                            $options.filters.formatDate(PlanData[0].effective_date) }}</label></div>
                                    </div>
                                    <!-- <div class="col-md-4 col-lg-4"><div class="form-group"><label><b>Expiration Date:</b> {{ $options.filters.formatDate(PlanData[0].expiration_date) }}</label></div></div> -->
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Status:</b> <span
                                                    class="px-2" :class="{'bg-success text-center text-white':PlanData[0].status_name=='Active','bg-danger text-center text-white':PlanData[0].status_name=='Inactive', 'bg-danger text-center text-white':PlanData[0].status_name=='Cancelled' }">{{
                                                        PlanData[0].status_name }}</span></label>
                                        </div>
                                    </div>
                                     <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Next Bill date:</b> {{
                                            $options.filters.formatDate(PlanData[0].next_bill_date) }}</label></div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Last Update date:</b> {{
                                            $options.filters.formatDate(PlanData[0].last_update_date) }}</label></div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Last Bill date:</b> {{
                                            $options.filters.formatDate(PlanData[0].Last_bill_date) }}</label></div>
                                    </div>
                                    <div class="col-md-4 col-lg-4">
                                        <div class="form-group"><label><b>Billing Frequency:</b> {{PlanData[0].frequency }}</label></div>
                                    </div>
                                    <div class="col-md-12 col-lg-12">
                                        <div class="form-group"><label><b>Description:</b>
                                                {{ PlanData[0].plan_description }}</label></div>
                                        </div>                                        
                                </div>
                               
                                <div class="plan-details">

                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-2 p-0">
                                        <div class="heading-border">
                                            <h5 class="h5"><span id="SProjectDetailTitleSpan">Plan Detail</span></h5>
                                        </div>
                                    </div>

                                    <div class="lisitng">
                                        <div class="table-responsive table-fix-header">
                                            <table class="table table-bordered mt-0"
                                                v-if="PlanData[0].is_rate_applicable_on_plan != null">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th>Plan</th>
                                                        <th class="text-center">Quantity</th>
                                                        <th class="text-center">Unit Price</th>
                                                        <th class="text-right pr-4"><strong>Total Price</strong></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>{{ PlanData[0].plan_name }}</td>
                                                        <td class="text-center">{{ PlanData[0].total_units }}</td>
                                                        <td class="text-center">{{ PlanData[0].symbol }}
                                                            {{ PlanData[0].rate }}</td>
                                                        <td class="text-right pr-4">{{ PlanData[0].symbol }}
                                                            {{ (PlanData[0].total_units * PlanData[0].rate).toFixed(2) }}</td>
                                                    </tr>
                                                </tbody>
                                            </table>

                                            <table class="table table-bordered mt-0" v-else>
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th>Services</th>
                                                        <th class="text-center">Quantity</th>
                                                        <th class="text-center">Unit Price</th>
                                                        <th class="text-right pr-4">Total Price</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(item, i) in PlanData" :key="i">
                                                        <td>{{ item.service_name }}</td>
                                                        <td class="text-center">{{ item.total_units }}</td>
                                                        <td class="text-center">{{ $n(item.rate, "currency", $getLocaleByCurrency(CurrencyCode))}}</td>
                                                        <td class="text-right pr-4 font-weight-medium">{{ $n((item.total_units *
                                                            item.rate ).toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}} </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                            <table class="table table-bordered mt-0 float-right w-50 border">
                                                <tbody>
                                                    <tr>
                                                        <td class="font-weight-medium bg-light">Sub Total</td>
                                                        <td class="font-weight-medium bg-light text-right pr-4">{{ $n(SubTolal.toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}} 
                                                        </td>
                                                    </tr>
                                                    <tr v-for="(item, y) in DiscountData" :key="y">
                                                        <td class="font-14">{{ item.name }}<span
                                                                v-if="item.discount_type.toLowerCase() == 'percentage'">({{$n(item.discount_amount, "currency", $getLocaleByCurrency(CurrencyCode))}}%)</span>
                                                        </td>
                                                        <td class="text-right pr-4 font-14">- {{ $n(item.total_discount_amount, "currency", $getLocaleByCurrency(CurrencyCode))}}</td>
                                                    </tr>
                                                    <tr>
                                                        <td class="font-weight-medium bg-light">Total Discount</td>
                                                        <td class="font-weight-medium bg-light text-right pr-4">- {{ $n(TotalDiscount, "currency", $getLocaleByCurrency(CurrencyCode))}} </td>
                                                    </tr>
                                                     <tr>
                                                        <td class="font-weight-medium bg-light-custom">After Discount Total</td>
                                                        <td class="font-weight-medium bg-light-custom text-right pr-4"> {{ $n(AfterDiscountAmount.toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}}
                                                        </td>
                                                    </tr>
                                                    <tr v-for="(item, y) in TaxData" :key="y">
                                                        <td class="font-14">{{ item.name }}<span
                                                                v-if="item.discount_type.toLowerCase() == 'percentage'">({{ $n(item.discount_amount, "currency", $getLocaleByCurrency(CurrencyCode))}}%)</span>
                                                        </td>
                                                        <td class="text-right pr-4 font-14">+ {{ $n(item.total_discount_amount, "currency", $getLocaleByCurrency(CurrencyCode))}}</td>
                                                    </tr>
                                                    <tr>
                                                        <td class="font-weight-medium bg-light">Total Tax</td>
                                                        <td class="font-weight-medium bg-light text-right pr-4"> +{{ $n(TotalTax, "currency", $getLocaleByCurrency(CurrencyCode))}} 
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <td class="font-weight-medium bg-light-custom">Grand Total</td>
                                                        <td class="font-weight-medium bg-light-custom text-right pr-4"> {{ $n(Total.toFixed(2), "currency", $getLocaleByCurrency(CurrencyCode))}} 
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>

                                </div>
                            </div>
                            <div v-else>
                                <p class="text-danger text-center">No record(s) found</p>
                            </div>
                            <div class="row flex-row-reverse mt-2">
                                <div class="col-lg-6 text-right">
                                    <div class="search-btm-btn">
                                        <a type="button" class="btn btn-danger" @click="$emit('close')"><em
                                                class="fa fa-close pr-2"></em>{{ $t('Close') }}</a>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        PlanId: {
            type: Number,
            required: true
        },
        ClientId: {
            type: Number,
            required: true
        },
        PlanMappingId:{
            type:Number,
            required:true
        }
    },
    data() {
        return {
            isLoading: false,
            PlanData: [],
            TaxData: [],
            DiscountData: [],
            SubTolal: 0,
            AfterDiscountAmount:0,
            TotalDiscount: 0,
            TotalTax: 0,
            Total: 0,
            CurrencyCode: 'PKR'
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
        async FetchData() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var url = `planId=${vm.PlanId}&clientId=${vm.ClientId}&planMappingId=${vm.PlanMappingId}`
            await DataService.GetSubscribedPlanDetails(url)
                .then(response => {
                    if (response.data.status === "Success" && response.data.code === "1") {
                        if (response.data.result.plan_data.length > 0) {
                            vm.PlanData = response.data.result.plan_data;
                            vm.PlanData.forEach(item => {
                                if (item) {
                                    vm.SubTolal = vm.SubTolal + item.total_units * item.rate;
                                }
                            })
                        }
                        else {
                            vm.PlanData = [];
                        }
                        if (response.data.result.discount_data.length > 0) {
                            vm.DiscountData = response.data.result.discount_data;
                            var Flatdiscount = 0;
                            var PDiscount = 0;
                            vm.DiscountData.forEach(item => {
                                if (item.discount_type.toLowerCase() == 'percentage') {
                                    item.total_discount_amount = (vm.SubTolal * item.discount_amount) / 100;
                                     PDiscount = PDiscount + (vm.SubTolal * item.discount_amount) / 100;
                                     vm.AfterDiscountAmount= vm.SubTolal -  item.total_discount_amount;
                                } else {
                                    Flatdiscount = Flatdiscount + item.discount_amount
                                    vm.AfterDiscountAmount = vm.AfterDiscountAmount - Flatdiscount;
                                }
                            })
                            vm.TotalDiscount = PDiscount + Flatdiscount;
                        } else {
                            vm.DiscountData = [];
                            vm.AfterDiscountAmount = vm.SubTolal 
                        }
                        if (response.data.result.tax_data.length > 0) {
                            vm.TaxData = response.data.result.tax_data;
                            debugger
                            var Flatdiscount = 0;
                            var PDiscount = 0;
                            vm.TaxData.forEach(item => {
                                if (item.discount_type.toLowerCase() == 'percentage') {
                                    item.total_discount_amount = (vm.AfterDiscountAmount * item.discount_amount) / 100;
                                    PDiscount = PDiscount + (vm.AfterDiscountAmount * item.discount_amount) / 100;
                                } else {
                                    Flatdiscount = Flatdiscount + item.discount_amount
                                }
                                vm.TotalTax=PDiscount + Flatdiscount;
                               // vm.TotalTax = vm.TotalTax + item.discount_amount;
                            })
                        }
                        else {
                            vm.TaxData = [];
                        }
                        vm.Total = vm.SubTolal + vm.TotalTax - vm.TotalDiscount;
                    }
                    vm.isLoading = false;
                })
                .catch(error => {
                    console.log(error);
                    vm.isLoading = false;
                });
        }
    },
}
</script>
<style>
.plan-info,
.plan-details {
    margin-bottom: 20px;
}

.plan-info p {
    margin: 5px 0;
}

.plan-details table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.plan-details th,
.plan-details td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.plan-details th {
    background-color: #f2f2f2;
}

.total {
    font-weight: bold;
}

.bg-light-custom{background: #efefef;}
</style>