<template>
    <section v-if="IsPrivilege">     
        <section v-if="IsTaxableOrNot">
            <loader :is-visible="isLoading"></loader>
            <tg-list ref="tgList" :listType="['List']" :IsShowAction="IsShowAction" :showCheckBox="true" ModuleName="Tax"
                SubModuleCode="TaxCategory" IdentityColumn="jurisdiction_id" :HeaderText="$t('TaxOverride')"
                :ListData="ListingData" :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons"
                :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="GetModuleConfigurationKeys" :SortExp="SortExp" :SortBy="SortBy"
                :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
                :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
                v-on:ActionButtonClick="actionButtonClick">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'effective_from'">
                        <span>
                            {{ $options.filters.formatDate(data.row.effective_from) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'effective_to'">
                        <span>
                            {{ $options.filters.formatDate(data.row.effective_to) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'status_name'">
                        <a class="inactive-disabled">
                            <span class="text-center text-white d-block"
                                :style="{ backgroundColor: data.row.status_bg_color }"> {{ data.row.status_name }} </span>
                        </a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'rate_type'">
                        <span v-if="data.row.rate_type == 'P'">
                            {{ $t('Percentage') }}
                        </span>
                        <span v-else>{{ $t('Flat') }}</span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'is_slab_rate'">                      
                        <span v-if="data.row.is_slab_rate">                        
                            <a class="task-dec" @click="ShowSlabDeails(data.row.tax_category_id,data.row.rate_type)">{{ $t('Slab') }}</a> 
                        </span> 
                        <span v-else-if ="data.row.rate_type=='P'">{{ data.row.rate }}%</span>    
                        <span v-else-if ="data.row.rate_type=='F'">{{ data.row.rate }}</span>                                            
                    </template>
                    <!-- <template v-if="data.column.COLUMN_NAME == 'is_slab_rate'">
                        <span v-if="data.row.is_slab_rate">
                            <a class="task-dec" @click="ShowSlabDeails(data.row.tax_category_id)">{{ $t('Slab') }}</a>
                        </span>
                        <span v-else>{{ data.row.rate }}%</span>
                    </template> -->
                    <template v-if="data.column.COLUMN_NAME == 'rate'">
                        <span>{{ data.row.rate.toFixed(2) }}</span>
                    </template>
                </template>
                <template v-slot:headerleftbuttons="{ data }">
                    <div class="col-sm-12 col-md-2 col-lg-2 float-left mt-2 pt-1 pl-0">
                        <div class="dynamic-form-toggle">
                            <span class="text-white">:</span>                         
                            <div tabindex="0" role="switch" class="switch dynamic-form">
                                <label class="switch w70">
                                    <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                        v-model="IsTaxOverride" name="isTaxOverride" v-on:change="ShowButton($event)" />
                                    <span class="slider round" style="cursor:default;">
                                        <span class="slider-yes">{{ $t('Yes') }}</span>
                                        <span class="slider-no">{{ $t('No') }}</span>
                                    </span>
                                </label>
                            </div>
                        </div>
                    </div>
                </template>
            </tg-list>
            <SlabDetalis v-if="IsSlabDetalis" @CloseSlabDeails="CloseSlabDeails" :TaxCategoryData="TaxCategoryData" :Type="Type" :rateType="rateType">
            </SlabDetalis>
            <TaxClientModal v-if="IsTaxClient" @CloseTaxClient="CloseTaxClient" :ClientId="ClientId" @IsOverrideTaxClient="IsOverrideTaxClient">
            </TaxClientModal>
            <AddClientTax v-if="isAddClientTax" :ClientData="ClientData" :ClientId="ClientId"
                @CloseTaxClientMap="CloseTaxClientMap"></AddClientTax>
            <AddTaxCategory v-if="isAddTaxCategory" @CloseTaxCategory="CloseTaxCategory" :TaxCategoryData="TaxCategoryId" :ClientId="ClientId"  LayoutType="Tax"></AddTaxCategory>
        </section>
        <section v-else>
            <div class="container-fluid mt-2 p-0">
                <div class="theme-primary">
                    <span class="p-name text-white">{{ $t('Tax') }}</span>
                </div>
            <div class="border p-4">
                    <div class="col-lg-12 col-sm-12 alert alert-primary text-center" role="alert">
                        {{$t('TaxExemptionis')}}
                    </div>
                </div>
            </div>
        </section>
    </section>
    <section v-else>
        <div class="container-fluid mt-2 p-0">
            <div class="theme-primary">
                <span class="p-name text-white">{{ $t('Tax') }}</span>
            </div>
           <div class="border p-2">
                <div class="col-lg-12 col-sm-12 alert alert-warning text-center mb-1" role="alert">
                    {{ $t('NotAuthorizedSection')}}
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import TaxListingSchema from './TaxListingSchema';
import DataService from '../../../services/DataService';
import SlabDetalis from '../../TaxManagement/SlabDetalis.vue';
import TaxClientModal from '../../../components/Modals/TaxClientModal.vue';
import AddClientTax from './AddClientTax.vue';
import AddTaxCategory from '../../TaxManagement/AddTaxCategory.vue'
export default {
    components: { SlabDetalis, TaxClientModal, AddClientTax,AddTaxCategory },
    props: {
        ClientId: {
            type: Number,
            required: true,
        },
        ClientData: {
            type: Object
        },
        ChecKPrivilege:{
            type: Boolean,
            required: true,
        }
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            TaxCategoryId:null,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            ListingData: [],
            Buttons: null,
            IsShowAction: false,
            TotalRecords: 0,
            Type: 'Tax_Category',
            SortBy: '',
            moduleCode: 'Billing',
            SortExp: '',
            Buttons: TaxListingSchema.buttons,
            IsSlabDetalis: false,
            TaxCategoryData: null,
            listActions: TaxListingSchema.listActions,
            Headers: TaxListingSchema.TableHeader,
            leftSearchFields: TaxListingSchema.leftsearchSchema,
            Headers: [
                {
                    "COLUMN_NAME": "tax_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxName",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "jurisdiction_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxJurisdictionName",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "effective_from",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Effective_From",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "effective_to",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "EffectiveTo",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "rate_type",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "RateType",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "is_slab_rate",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "TaxRate",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "StatusName",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                }
            ],
            IsTaxableOrNot: true,
            IsTaxOverride: false,
            IsTaxClient: false,
            isAddClientTax: false,
            userId: null,
            companyId: null,
            IsTaxOverrideId: 0,
            isAddTaxCategory:false,
            IsPrivilege:true
        }
    },
    created: async function () {  
        var ths = this;
        ths.userId = ths.GetUserInfo.ID;
        ths.companyId = ths.GetUserInfo.CompanyId;
        if(ths.ChecKPrivilege){
             ths.IsPrivilege=true; 
             await ths.GetModuleConfigurationKeys();
             ths.GetModuleConfigurationClientKeys();                  
        }else{
            ths.IsPrivilege=false;
        }         
    },
    methods: {      
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchTaxList();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTaxList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditTaxCategory':
                    this.EditTaxCategory(item.id);
                    break;
                case 'DeleteTaxClient':
                    this.commonDeleteData(item.id);
                    break;
            }
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
            switch (event.callbackfunction) {
                case "AddTaxCategory":
                    vm.AddTaxCategory(null);
                    break;
                case "BacktoList":
                    vm.BacktoList();
                    break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTaxList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        GetModuleConfigurationKeys: async function (item) {
            var ths = this;
            await ths.GetModuleIdByModuleName();
            var params = 'moduleId=' + ths.ModuleId;
            await DataService.GetConfigurationkeysData(params).then(function (response) {                 
                if (response != null && response.data != null) {
                    const filtered = response.data.StorageKeys.filter(key => key.CORE_CONFIG_KEY === "IsTaxable");                    
                    if (filtered[0].CORE_CONFIG_VALUE == 'Exempt') {
                        ths.IsTaxableOrNot = false;
                    } 
                    else {
                        ths.IsTaxableOrNot = true; 
                        // const filteredd = response.data.StorageKeys.filter(key => key.CORE_CONFIG_KEY === "TaxOverride");  
                        // ths.IsTaxOverrideId=  filteredd[0].CORE_CONFIG_ID;                                        
                        // if (filteredd[0].CORE_CONFIG_VALUE == "false") {
                        //     ths.IsTaxOverride = false;
                        //     ths.IsShowAction = false;
                        //     ths.Buttons[0].isvisible = false;                            
                        // } else {
                        //     ths.IsTaxOverride = true;    
                        //     ths.IsShowAction = true;
                        //     ths.Buttons[0].isvisible = true;                       
                        // }  
                        // ths.FetchTaxList();                     
                    }                                    
                }
            });
        },

        GetModuleConfigurationClientKeys: async function (item) {
            var ths = this;
            ths.isLoading=true;
            var params = `ClientId=${ths.ClientData.clientdetail.user_id}&Key=TaxOverride`;
            await DataService.GetClientConfigurationkeysData(params).then(function (response) {                 
                if (response != null && response.data != null) { 
                        ths.IsTaxOverrideId=  response.data.result.core_config_id;                                        
                        if (response.data.result.config_value == "false" || response.data.result.config_value== 'no') {
                            ths.IsTaxOverride = false;
                            ths.IsShowAction = false;
                            ths.Buttons[0].isvisible = false;                            
                        } else {
                            ths.IsTaxOverride = true;    
                            ths.IsShowAction = true;
                            ths.Buttons[0].isvisible = true;                       
                        }  
                        ths.FetchTaxList();                                  
                }
            });
        },
        async GetModuleIdByModuleName() {
            var vm = this;
            var params = `moduleName=${vm.moduleCode}`;
            await DataService.GetModuleIdByModuleName(params).then(function (response) {
                if (response != null && response.data != null) {
                    vm.ModuleId = response.data;
                }
            });
        },
        FetchTaxList() {
            var vm = this;
            vm.isLoading = true;            
            var url = `Clientid=${vm.ClientData.clientdetail.user_id}&JurisdictionId=${vm.ClientData.jurisdictionId==undefined?0:vm.ClientData.jurisdictionId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&isOverride=${vm.IsTaxOverride==true?1:0}`;                      
            DataService.GetClientTaxList(url).then((response) => {               
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.PageNumber = vm.PageNumber;                                        

                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
                vm.isLoading = false;
            })
        },
        ShowSlabDeails(val,type){
            this.TaxCategoryData=val;
            this.rateType = type;
            this.IsSlabDetalis=true;
        },
        // ShowSlabDeails(val) {
        //     this.TaxCategoryData = val;
        //     this.IsSlabDetalis = true;
        // },
        CloseSlabDeails() {
            this.IsSlabDetalis = false;
        },
        ShowButton(val) {
            var vm = this;
            if (val.target.checked) {
                var url = `ClientId=${vm.ClientData.clientdetail.user_id}&Id=${vm.IsTaxOverrideId}&IsOverride=${val.target.checked == true ? 1 : 0}`
                DataService.UpdateIsTaxOverrideTax(url).then(response => {
                    if (response.data.result.DATA.length > 0) {
                        if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                            vm.IsTaxClient = true;
                        }
                    }
                });
            }
            else {
                  vm.confirmR(vm.$t("ExistingJurisdictionTax"), true,null, function () {
                    var url = `ClientId=${vm.ClientData.clientdetail.user_id}&Id=${vm.IsTaxOverrideId}&IsOverride=${val.target.checked == true ? 1 : 0}`
                    DataService.UpdateIsTaxOverrideTax(url).then(response => {
                        if (response.data.result.DATA.length > 0) {
                            if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                                vm.IsTaxClient = false;
                                vm.GetModuleConfigurationKeys();
                            }
                        }
                    });
                },function() {
                    vm.IsTaxOverride = !val.target.checked
                }
            );
            }
        },
        CloseTaxClient() {
            var vm =this;
            vm.IsTaxClient = false;
            var url = `ClientId=${vm.ClientData.clientdetail.user_id}&Id=${vm.IsTaxOverrideId}&IsOverride=0`
            DataService.UpdateIsTaxOverrideTax(url).then(response => {
                if (response.data.result.DATA.length > 0) {
                    if (response.data.result.DATA[0].VALUE == 'UPDATE') {
                        vm.IsTaxClient = false;
                        vm.GetModuleConfigurationKeys();
                    }
                }
            });
        },
        BacktoList() {
            this.$router.push({ name: 'ClientListing' });
        },
        IsOverrideTaxClient(val) {            
            var vm = this;
            if (val == "Yes") {
                var url = `IsOverride=Yes&ClientId=${vm.ClientData.clientdetail.user_id}&JurisdictionId=${vm.ClientData.jurisdictionId}`;
                DataService.MappingClientTax(url).then((response) => {                    
                    if (response.data.result.DATA.length > 0) {
                        if (response.data.result.DATA[0].VALUE == 'CLIENT_MAP') {
                            vm.IsShowAction = true;
                            vm.Buttons[0].isvisible = true;
                            vm.IsTaxClient = false;
                            vm.FetchTaxList();
                        }
                    }
                })
            }
            if (val == 'No') {
                vm.IsTaxClient = false;
                vm.IsShowAction = true;
                vm.Buttons[0].isvisible = true;
                vm.ListingData = [];
                vm.TotalRecords = 0;
                vm.TotalPages = 10;
                vm.PageNumber = 1;
            }
        },
        AddTaxCategory() {
            this.isAddClientTax = true;
        },
        CloseTaxClientMap() {
            this.isAddClientTax = false;
            this.FetchTaxList();
        },
        commonDeleteData(val) {   
            var vm = this;
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tax Client")), function () {
                    var url = `ids=${val}&Type=TAX_CLIENT`;
                    DataService.RemoveMapping(url).then(function (response) {
                        vm.isLoading = false;
                        if (response.data.result.DATA.length > 0) {
                            if (response.data.result.DATA[0].VALUE == 'DELETE_TAX_CLIENT') {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Tax Client')]), "Success", true, vm.$t('Alert'));
                                vm.FetchTaxList();
                            } else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Tax Client')]), "failure", true, vm.$t('Alert'));
                            }
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Tax Client')]), "failure", true, vm.$t('Alert'));
                        }
                    })
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        EditTaxCategory(val){            
            var ths=this;
            ths.TaxCategoryId=val;
            ths.isAddTaxCategory=true;
        },
        CloseTaxCategory(){
            var ths=this;
            ths.isAddTaxCategory=false;
            ths.GetModuleConfigurationKeys();
        }
    }
}
</script>
<style>
.ticketsmallfix.theme-primary .p-name {
    float: left;
}
</style>