<template>
    <section>
        <div class="tab-content">
           <div id="step1" class="tab-pane call-detail fade p-0 border-0 active show ">
                <dynamic-form ref="ConfigurationsForm" :schema="formSchema" :buttons="buttons" v-on:OnSubmit="FinalSaveConfigurations"></dynamic-form>
            </div>
       </div>
    </section>
    </template>
<script>
    import dateService from "@/services/DataService";
    export default {
        components: {
        },
        data: function(){
            return {
                modueKeys: [],
                currentSelectedModule: "",
                formSchema: [],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-success",
                        text: "<i class='fa fa-save pr-2'></i> Save Settings",
                    },
                ],
                showTicketNumber:false,
                ticketCoreConfigId: 0,
                noRecordfound: false,
                Message:'',
                AttachmentFileDetail:[],
                uploadUrl: process.env.VUE_APP_DOCSTORAGE_API + '/UploadStreamNew',
                token:null,
                CheckFileSave:false,
                CompanyLogo:null,
                UserId:null,
                moduleCode: 'Billing',
                ModuleId: 0
            }
        },
        created:async function() {      
            var vm=this;
            vm.UserId = this.GetUserInfo.ID
            await this.GetModuleConfigurationKeys(); 
            if(vm.formSchema[0].Data[0].value>0) {
                await this.GetTemplateListBySetupId(vm.formSchema[0].Data[0].value);  
                await this.LoadTemplateBySetupId(vm.formSchema[0].Data[0].value); 
            }          
        },
        methods: {
            Close: function() {                
              var vm=this;
              vm.$parent.ConfigurationPopup=false;
            },
            GetModuleConfigurationKeys:async function(item) {
                var ths = this; 
                await ths.GetModuleIdByModuleName();
                var params = 'moduleId=' + ths.ModuleId;                
                await dateService.GetConfigurationkeysData(params).then(function (response) {
                    if(response != null && response.data != null){ 
                        ths.ConvertConfigurationDataIntoDynamicFormobj(response.data.StorageKeys);                        
                    }
                });
            },
            GetTemplateListBySetupId: async function(SetupId){
                var vm=this;
                var params = `SetupId=${SetupId}`;  
                vm.formSchema[0].Data[2].config.options = [];              
                await dateService.GetTemplateListBySetupId(params).then(function (response) {
                    if (response.data.length > 0) {
                        response.data.forEach((itm) => {
                        vm.formSchema[0].Data[2].config.options.push({
                            name: itm.NAME,
                            value: `${itm.INVOICE_TYPE_ID}`
                        })
                        });
                    }else{
                        vm.formSchema[0].Data[2].config.options = [];
                    }
                    vm.$refs.ConfigurationsForm.UpdateKeyValue();
                });
            },
            LoadTemplateBySetupId: async function(SetupId){
                var vm=this;
                var params = `SetupId=${SetupId}`;   
                vm.formSchema[0].Data[1].config.options = [];          
                await dateService.LoadTemplateBySetupId(params).then(function (response) {
                    if (response.data.length > 0) {
                        response.data.forEach((itm) => {
                        vm.formSchema[0].Data[1].config.options.push({
                            name: itm.Name,
                            value: `${itm.Value}`
                        })
                        });
                    }else{
                        vm.formSchema[0].Data[1].config.options = [];
                    }
                    vm.$refs.ConfigurationsForm.UpdateKeyValue();
                });
            },
            async GetModuleIdByModuleName(){
                var vm=this;
                var params = `moduleName=${vm.moduleCode}`;             
                await dateService.GetModuleIdByModuleName(params).then(function (response) {
                    if(response != null && response.data != null){ 
                        vm.ModuleId=response.data;                        
                    }
                });
            },
            Getoption: function (params) {
                params = params == null ? '' : params;
                var options = [];
                $.each(params.split('^'), function (i, v) {
                    options.push(v);
                });
                return options;
            },
            ConvertConfigurationDataIntoDynamicFormobj: function(configurations){
                var ths = this;
                ths.formSchema = [];
                configurations.sort((a, b) => {
                const orderA = ths.$t(a.DISPLAY_ORDER);
                const orderB = ths.$t(b.DISPLAY_ORDER);
                const numA = Number(orderA);
                const numB = Number(orderB);
                if (!isNaN(numA) && !isNaN(numB)) {
                    return numA - numB; 
                }
                if (orderA > orderB) return 1;
                if (orderB > orderA) return -1;
                return 0;
                });
                if(configurations != null && configurations.length > 0){
                    let obj = {
                        layoutType: 'double',
                        groupId: 0,
                        group_name: "",
                        Data: []
                    }
                    configurations.forEach(function(item, index){                                                
                        let validationRules = {}
                        validationRules["required"] = true;
                        let astype = item.ASTYPE
                        let options = []
                        var val = item.CORE_CONFIG_VALUE??""
                        if(item.CORE_CONFIG_OPTIONS != null){
                            options = ths.MakeArray(ths.$t(item.CORE_CONFIG_OPTIONS), 'radio', '^');
                        }
                        if(item.SELECT_OPTIONS != null && item.SELECT_OPTIONS != ""){
                            options = ths.MakeNormalArray(item.SELECT_OPTIONS);
                        }
                        let comfigMode = "date";
                        let placeholder = "Enter " + ths.$t(item.CORE_CONFIG_KEY);
                        if(astype == "SelectField" || astype == "CustomSelectField" || astype == "TreeSelectField"){
                            placeholder = "Select " + ths.$t(item.CORE_CONFIG_KEY);
                        }
                        else if(astype == "MultiSelectField"){
                            val = [];
                            let valArray = item.CORE_CONFIG_VALUE.split('^')
                            valArray.forEach(function(x){
                                val.push({
                                    label: x,
                                    value: x
                                })
                            });

                        }else if(astype == "CheckBoxField"){
                            val = item.CORE_CONFIG_VALUE.split('^')
                        }
                        if(astype == "DateField"){
                            comfigMode = "date";
                        }else if (astype == "MultiSelectField"){
                            comfigMode = 'tag'
                        }
                        else if (astype == "FileField"){                            
                            var filePath=item.CORE_CONFIG_VALUE;                           
                            var filename = filePath.split('/').pop();
                        }
                        else if(item.CORE_CONFIG_KEY == "JPJobCode"){                           
                            validationRules["alpha_num"] = true;
                            validationRules["min"] = 4;
                            validationRules["max"] = 6;
                        }
                        else if(item.CORE_CONFIG_KEY == "TaxOverride"){                                                        
                            astype='toggleField';
                            validationRules["required"] = false;
                            val = item.CORE_CONFIG_VALUE=="true"?true:false;
                        }
                        let fieldObj = {
                            customFieldId: item.CORE_CONFIG_ID,
                            astype: astype,
                            label: ths.$t(item.CORE_CONFIG_KEY),
                            name: item.CORE_CONFIG_KEY,
                            displayOrder: item.DISPLAY_ORDER,
                            value: val,
                            placeholder: placeholder,
                            mode: comfigMode,
                            config: {
                                maxSize: 5,
                                showSelectedFiles: [{                          
                                    attachmentName: filePath,
                                    attachmentPath: filename
                                }],
                                onChange:ths.onChangeField,
                                onDeleteSelectedFiles:ths.onDeleteSelectedFiles,
                                onClickSelectedFiles:ths.showCompanyLogo,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpeg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                ],
                                mode: comfigMode,
                                options: options,
                                dependentOn: null,
                                showWhen: null,
                                valueMapping: null,
                                fieldCode: null,
                                showCrossOnSelectedFiles:false,
                            },
                            visibility: true,
                            validationRules: validationRules,
                            dtCode: item.CORE_CONFIG_TYPE
                        }
                        obj.Data.push(fieldObj);
                    })                    
                    ths.formSchema.push(obj);
                    const filtered = ths.formSchema[0].Data.filter(key => key.name === "IsTaxable");
                    const IsTaxableValue=filtered[0].value;
                    if(IsTaxableValue=='Exempt'){
                        ths.formSchema.forEach(function(group, index){
                            group.Data.forEach(function(item, index){ 
                                if(item.name=='TaxOverride'){
                                    item.visibility=false;
                                }
                            })
                        });
                    }                    
                }                    
            },
            async showCompanyLogo(item){
                var vm = this;
                let a= document.createElement('a');
                a.target= '_blank';
                await vm.GetCompanyLogo(item.attachmentPath);
                a.href = vm.CompanyLogo;
                a.click();
            },
            async GetCompanyLogo(imageName) {
                var vm = this;
                var param = 'imagename=' + imageName;
                await dateService.GetCompanyLogo(param).then((response) => {
                    vm.CompanyLogo = response.data;
                });
            },
            MakeArray: function (value, type, splitter) {
                var array = [];
                var splitWith = ","
                if (typeof (splitter) != 'undefined' && splitter != null) {
                    splitWith = splitter;
                }
                var arr = String(value).split(splitWith);
                if (type == "radio" || type == "checkbox") {
                    if (arr.length > 0) {
                        for (let item of arr) {
                            if (item.split("|").length > 1) {
                                var person = { name: item.split("|")[0].trim(), value: item.split("|")[1].trim() }
                                array.push(person);
                            }
                            else {
                                var person = { name: this.$t(item.trim()), value: item.trim() };
                                array.push(person);
                            }
                        }
                    }
                }
                else {
                    if (arr.length > 0) {
                        for (let item of arr) {
                            var person = { name: item.trim(), value: item.trim() };
                            array.push(person);
                        }
                    }
                }
                return array;
            },
            onChangeField: async function (evt, x) {
                var vm = this;
                if (x.astype == 'FileField'){
                    try {
                    vm.fileData = [];
                    var files = evt.target.files; 
                    for (var i = 0; i < files.length; i++) {
                        var f = files[i];
                        var reader = new FileReader();
                        reader.onload = (function (theFile) {
                            return function (e) {
                                var binaryData = e.target.result;
                                var base64StringEncoded = window.btoa(binaryData);
                                
                                var fileName = f.name;
                                vm.fileName = fileName;
                                var n = fileName.lastIndexOf(".");
                                var extensionName = fileName.substring(n + 1, fileName.length);

                                vm.fileExt += "." + extensionName;
                                var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                                var fileSize = f.size;
                                vm.fileSize = fileSize;
                                var fileObj = {
                                    attachement_mime: attachemnetMime,
                                    fileName: fileName,
                                    userId: vm.GetUserInfo.ID,
                                    companyId: vm.GetUserInfo.CompanyId,
                                    id: 0,
                                    moduleName: vm.currentSelectedModule.moduleName.toLowerCase(), //vm.moduleCode
                                    subModuleCode: "",
                                    containerName: vm.GetUserInfo.ContainerName,
                                    base64String: base64StringEncoded,
                                    attachement_path: '',
                                    fieldName: x.name
                                };

                                vm.AttachmentFileDetail.push(fileObj);
                            };
                        })(f);
                        reader.readAsBinaryString(f);
                    }
                }
                catch (ex) {
                }
                } 
                if(x.name == 'SelectSetup'){
                    await vm.LoadTemplateBySetupId(x.value);
                    await vm.GetTemplateListBySetupId(x.value);
                }
                if(x.name == 'IsTaxable'){
                    vm.formSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if(x.value=='Exempt'){
                                if (field.name == "TaxOverride") {
                                    field.visibility = false;
                                    field.value=false;
                                }else{
                                    field.visibility = true;
                                }
                            }else{
                                if (field.name == "TaxOverride") {
                                    field.visibility = true;
                                }
                            }
                            
                        });
                    });
                }
            },
            onDeleteSelectedFiles:function(e, x){
                var vm=this;
               vm.formSchema.forEach(function(group, index){
                    group.Data.forEach(function(item, index){ 
                        if(item.astype == 'FileField'){       
                            vm.$delete(item.config, 'showSelectedFiles');
                            item.value = '';
                        }
                    })
                });
            },
            SendAttactment: async function () {                 
                this.token = this.GetUserInfo ? this.GetUserInfo.Token : '';
                var vm = this;
                var params = JSON.stringify(vm.AttachmentFileDetail);                
               await dateService.UploadFile(params).then(function (response) {
                    if(response.data && response.data.length > 0){ 
                        vm.CheckFileSave= false;
                        vm.formSchema.forEach(function(group, index){
                            group.Data.forEach(function(item, index){ 
                                if(item.astype == 'FileField'){
                                    if(item.name.toLowerCase()=='companylogo'){
                                        var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                                        item.value =  img;
                                    }
                                    else{
                                        item.value = response.data[0].Name;
                                    }
                                    
                                }
                            })
                        });
                        vm.SaveConfigurations();     
                    }
                });
            },
            FinalSaveConfigurations:function(){                                
                var vm=this;
                vm.formSchema[0].Data.forEach(field=>{
                    if(field.dtCode && field.dtCode.toLowerCase()=='image' && vm.AttachmentFileDetail.length > 0){
                        vm.CheckFileSave=true;
                    }
                })
                if(vm.CheckFileSave){
                    vm.SendAttactment();
                }else{
                    vm.SaveConfigurations();
                }
            },               
            SaveConfigurations: function(){
                var ths = this;
                var configObj = []
                ths.formSchema.forEach(function(group, index){
                    group.Data.forEach(function(item, index){
                        var value = item.value;
                        if(item.astype == "CheckBoxField"){
                            value = (item.value.filter((x) => x != "")).join("^");
                        }
                        if(item.astype == 'MultiSelectField'){
                            let val = "";
                            if(typeof(item.value) == 'object'){
                                try{
                                    if(typeof(item.value.length) != 'undefined' && item.value.length > 0 ){
                                        item.value.forEach(function(item){
                                            if(val == ""){
                                                val = item.value;
                                            } else {
                                                val = val + '^' + item.value;
                                            }
                                        })
                                    } else {
                                        val = item.value.value
                                    }
                                }catch(e){
                                    val = item.value.value
                                }
                            } else {
                                val = item.value
                            }
                            value = val;
                        }                       
                        var obj = {
                            DataId: item.customFieldId,
                            key: item.name,
                            DataValue: value
                        }
                        configObj.push(obj)
                    });
                });
                var paramObj = {
                    configData: configObj,
                    conditionData: {
                        refCode: 'CompanySetting',
                        moduleName: ths.moduleCode
                    }
                }
                var data = JSON.stringify(paramObj);
                dateService.ManageDocUserStorageSettingData(data).then(function (response) {
                    if(response != null && response.data != null && response.data == 1){
                        ths.ShowAlert(ths.$t('RecordsSavedSuccessfully'), 'success');
                        ths.Close();
                    } else {
                        ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t("Alert"));
                    }
                });
            }
        },
    }
</script>
<style>
    .custom-control-label{
        text-transform: capitalize;
    }
</style>