<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle d-flex">
                        <h4 class="modal-title">{{ $t('Notes') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddNotes')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <div class="heading-border">
                            <h5 class="h5"><span>{{ $t('Notes') }}</span><small class="float-right"><a href="javascript:;" id="showattachment"  v-on:click="ShowNotePopUp"
                                        title="Add Note"><i class="fa fa-file-text-o pr-2"></i>{{ $t('Add') }} {{$t('NoteKey') }}</a></small></h5>
                        </div>
                        <div class="modal-data" style="padding-top: 10px;">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="UploadFileWithNote" v-if="ShowNotePopUpData">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                    </ckeditor>
                                    <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                </template>
                            </dynamic-form>

                            <div class="w-100" id="tab-notes-content-quickview" v-if="NoteList != null">
                                <NoteUtilityNotes :NoteData="NoteList"></NoteUtilityNotes>
                            </div>

                            <div class="text-danger text-center no-record float-left w-100" v-if="noRecord">
                                {{ $t('NoRecordfound') }}
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
 import NoteUtilityNotes from "./NoteUtilityNotes.vue";

export default {
    components: {
        NoteUtilityNotes,
       
    },
   
    props: {
        divisionId: {
            type: Number,
            required: false
        },
        clientId: {
            type: Number,
            required: false
        },
        utilityId:{
            type: Number,
            required: false
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        },
        viewType:{
            type: String,
            required: true
        }
    },
    data() {
        return {
            showError: false,
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            privacyType: '',
            isLoading: false,
            TicketBindList: [],
            NoteList: [],
            Description: null,
            ShowNotePopUpData: false,
            FileName: null,
            checkFileValidSize: false,
            checkFileValidExt: false,
            allowedPerFileSizeInMb: 0,
            noRecord:false,
            disallowFileTypes: '',
            file: null,
            noteId:0,
            fileTypeList: [],
            noteModal: {
                NoteDescription: '',
                privacy: '',
                NoteId: 0,
                ModuleName: '',
                SubModuleCode: '',
                ClientId: 0,
                DivisionId: 0,
                UtilityId:0,
                fileData: [],
                type:''
            },
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('NotesDescription'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 5000 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: "",
                            name: "Type",
                            value: 2,
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                            config: {
                                options: [{ name: this.$t('Private'), value: 1 }, { name: this.$t('Public'), value: 2 }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },

                        }
                    ],
                },
                {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "ListNoteAttachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: true
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        vm.LoadUtilityDivisionNotes();
    },
    methods: {
        LoadUtilityDivisionNotes: async function () {
            var vm = this;
            var url = ''
            if(vm.viewType == 'Utility')
            {
                url = `divisionId=&utilityId=${vm.utilityId}&clientId=${vm.clientId}`;
            }
            else if(vm.viewType == 'InvoiceGrouping')
            {
                url = `divisionId=${vm.divisionId}&utilityId=0&clientId=${vm.clientId}`;    
            }
            await DataServices.GetNoteUtilityDivisionList(url).then(function (response) {
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                }
                else {
                    vm.NoteList = null;
                    vm.noRecord = true;
                }
            });
        },
        ShowNotePopUp: function () {
            var vm = this;
                    if (vm.ShowNotePopUpData == false) {
                        vm.ShowNotePopUpData = true;
                        vm.noRecord = false;
                        vm.fileTypeList = this.AttachmentDisallowedFileType(this.disallowFileTypes);
                        vm.FormSchema[2].Data[0].config.reject = this.fileTypeList;
                    }
                    else {
                        vm.ShowNotePopUpData = false;
                    }                   
         
        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.privacyType = e.target.dataset.value;
        },
        LoadNotesForQuickView: async function (moduleName) {
            var vm = this;
            if (moduleName == null || moduleName == 'undefined') {
                moduleName = vm.moduleName;
            }
            var url = `ticketId=${vm.id}&isSuptTckt=false&moduleName=${moduleName}&isView=1`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                    $("#tab-notes-content-quickview").removeClass("d-none");
                }
                else {
                    $("#div_NotesLabel").removeClass("d-none");
                    vm.NoteList = null;
                    vm.noRecord = true;
                }
            });
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },

        UploadFileWithNote: function (formfields, val) {
            var vm = this;
            vm.buttons[0].class += " disabled";
            if (vm._data.FormSchema[0].Data[0].value == '' || vm._data.FormSchema[0].Data[0].value == null) {
                return;
            }
            vm.noteModal.privacy = vm.privacyType == 1 ? 'Private' : 'Public';
            vm.noteModal.NoteDescription = vm._data.FormSchema[0].Data[0].value;
            vm.noteModal.NoteId = vm.noteId == 0 ? 0 : vm.noteId;
            vm.noteModal.ClientId = vm.clientId;
            vm.noteModal.DivisionId = vm.divisionId;
            vm.noteModal.UtilityId = vm.utilityId;
            vm.noteModal.ModuleName = vm.modulename;
            vm.noteModal.SubModuleCode = vm.submodulecode;
            vm.noteModal.type = 'Add';
            vm.noteModal.fileData = vm.fileData;
            var formData = JSON.stringify(vm.noteModal);
            DataServices.AddNoteUtilityDivision(formData).then((response) => {
                    try {
                        if (response.data != null && response.data != "" && response.data.status == "Success") {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Notes")]), "success", true, vm.$t("Alert"));
                            $("input[type=text]").val('');
                            vm.Close();
                            if(vm.viewType == 'Utility'){
                                vm.$parent.$parent.LoadAdditionalInfo();
                            }
                            else if(vm.viewType == 'InvoiceGrouping'){
                                vm.$parent.$parent.GetInvoicedata();
                            }
                        }
                        else {
                            vm.FileSrc = '';
                             $("input[type=text]").val('');

                        }
                    } catch (exinner) {
                            return HandleErrorOccured(exinner, 'saveNote');
                    }
            });
               
        },
        Close: function () {
            this._data.FormSchema[0].Data[0].value = null;
            this.FileName = null;
            this.file = null;
            this.FormSchema[2].Data[0].config.onChange = this.GetBase64String;
            this.$parent.isShowAddNoteDivision = false;
            this.$parent.noRecord = false;
        }
    }
}

</script>