<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Comment') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="defaultStatus" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        MappingId: {
            type: Object,
            required: true
        },
         type: {
            type: String,
            required: true
        },
        planNamesStr: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            isLoading:false,
            FormSchema:[
            {
                    layoutType: "single",
                    Data: [
                         {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "Status",
                            config: {
                                options: [],
                              
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: this.$t('Enter Comment'),
                            validationRules: {
                                "required": true
                            },                         
                            disabled: false,
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,                    
                   
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId:null,
            userId:null
        }
    },
    created:function(){        
        var vm=this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetStatusList();
    },
    methods:{
          GetStatusList() {
            var vm = this;
            let Url = `id=&moduleName=Billing&type=Subscription_Status&search=&refId=`
            DataService.GetDDLData(Url).then(response => {
                debugger
                if (response.data != '' && response.data != null) {
                    vm.getprojectlist = response.data.DATA;

                    vm.getprojectlist.forEach((item) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })

                    vm.$refs.defaultStatus.UpdateKeyValue();
                }

            })
        },
        onSubmit(formfields, val){            
            var vm = this;
            var status_name = '';
            vm.getprojectlist.forEach(function (itm) {
                  if(itm.value==formfields.Status){
                    status_name = itm.name
                  }
            });
            const payload = {
                    ID: vm.MappingId,
                    StatusId: formfields.Status,
                    IsFor: "TALYGEN_CLIENT_PLAN_Mapping",
                    Module: "Billing",
                    Submodule: "Subscription",
                    Comment: formfields.Comment,
                    StatusName: status_name,
                    PlanName: vm.planNamesStr
                    };
                    DataService.CommonUpdateStatus(JSON.stringify(payload)).then(response => {
                    if (response?.data?.status === 'Success') {
                        vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success",vm.$t('Alert'));
                        this.Close();
                        if(vm.type=='Subscription'){
                          this.$parent.fetchSubscriptions();
                        }else{
                          this.$parent.FetchData();  
                        }
                    } else {
                        this.ShowAlert(this.$t("UnknownErrorOccur"), "failure", true, this.$t("Alert"));
                    }
                    });
        },
        Close:function(){
            this.$parent.IsComment=false;
        }
        
    }
}
</script>