<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddCreditHistory') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ClientData: {
            type: Object,
            required: true
        },
        UserId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CreditHistoryId:0,
            MandatoryString: this.$t('MandatoryString'),
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "NumericField",
                            label: this.$t('Amount'),
                            name: "Amount",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
    },
    methods: {
        Close: function () {
            this.$parent.AddHistory = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                CreditHistoryId: vm.CreditHistoryId == null ? 0 : vm.CreditHistoryId,
                Amount: formfields.Amount,
                InvoiceId: null,
                UserId: vm.UserId,
                Remarks:formfields.Remarks,
                StatusCode:'INV_CREDIT_ADDED'
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveUpdateCreditInfo(params).then((response) => {
                if (response.data.Status == 'Success') {
                        //vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("CreditInfo")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.refreshKey += 1;
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("CreditInfo")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>