<template>
    <section class="col-md-12 p-0">
        <div class="container-fluid p-0">
            <div class="col-md-12 p-0">
                <div class="listing">
                    <div class="table-responsive table-fix-header float-left" style="max-height:630px;">
                        <table id="mainTable" class="table table-bordered mt-0" style="width: 2500px;">
                            <thead class="thead-bg">
                                <tr>
                                    <th>
                                        <span v-if="isCheckExtraFeatureUSICBILLING">{{ $t("DivisionId") }}</span>
                                        <span v-if="isCheckExtraFeatureUtilityBilling">{{ $t("InvoiceGroupingID") }}</span>
                                    </th>
                                    <th><span>{{ $t("DESCRIPTION") }}</span><span class="float-left text-danger"></span></th>
                                    <th class="text-center"><span>{{ $t("AutoGenerateInvoice") }}</span></th>
                                    <th class="text-center">
                                        <span v-if="isCheckExtraFeatureUSICBILLING">{{ $t("AutomateInvoiceWorkday") }}</span>
                                        <span v-if="isCheckExtraFeatureUtilityBilling">{{ $t("AutomateSendToCustomer") }}</span>
                                    </th>
                                    <th><span>{{ $t("InvoiceFrequency") }}</span></th>
                                    <th><span>{{ $t("InvoiceMemo") }}</span></th>
                                    <th><span>{{ $t("DivisionPONumber") }}</span><span class="float-left text-danger"></span>
                                    </th>
                                   
                                    <th width="10%" v-if="isShowWorkdayInvoiceType">
                                        <span class="d-inline-block">{{ $t("WorkdayInvoiceType") }}<span class="text-danger d-inline-block">*</span></span>
                                    </th>
                                    <th style="display:none;"><span>{{ $t("InvoiceDetailTemplate") }}</span></th>
                                    <th  style="display:none;"><span>{{ $t("DetailFileFormat") }}</span><span class="float-left text-danger"></span>
                                    </th>
                                    <th v-if="isAssociateProjectWithDivision"><span>{{ $t("Project") }}</span></th>
                                    
                                    <th class="text-center d-none"><span>{{ $t("SummarizeLineItems") }}</span></th>
                                    <th v-if="configNotes == 'yes'"><span>{{ $t("Notes") }}</span></th>
                                    <th class="text-center single-action"><span>{{ $t("Delete") }}</span></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in InvoiceData" :key="item.DIVISIONS_ID">
                                    <td v-if="item.DIVISIONS_ID == 0">
                                        <input class="form-control" type="text" v-model="item.NAME"/>
                                    </td>
                                    <td v-else-if="item.DIVISIONS_ID != 0">
                                        {{ item.NAME }}
                                    </td>
                                    <td v-if="item.DIVISIONS_ID == 0">
                                        <input class="form-control" type="text" v-model="item.DESCRIPTION"/>
                                    </td>
                                    <td v-else-if="item.DIVISIONS_ID != 0">
                                         {{ item.DESCRIPTION }}
                                    </td>
                                    <td class="text-center"><label class="switch w70">
                                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                v-model="item.AUTO_INVOICE" v-bind:name="'IsLogin'" v-disabled="disabled" />
                                            <span class="slider round" style="cursor:default;">
                                                <span class="slider-yes">{{ $t('Yes') }}</span>
                                                <span class="slider-no">{{ $t('No') }}</span>
                                            </span>
                                        </label>
                                    </td>
                                    <td class="text-center"><label class="switch w70">
                                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                v-model="item.AUTOMATE_INVOICE_WORKDAY" v-bind:name="'IsLogin'"
                                                v-disabled="disabled" />
                                            <span class="slider round" style="cursor:default;">
                                                <span class="slider-yes">{{ $t('Yes') }}</span>
                                                <span class="slider-no">{{ $t('No') }}</span>
                                            </span>
                                        </label>
                                    </td>
                                    <td><select name="INVOICE_FREQUENCY" v-model="item.INVOICE_FREQUENCY"
                                            :class="{ 'form-control': true }">
                                            <option value="" disabled selected>{{ $t("Select") }}</option>
                                            <option value="week">{{ $t("Weekly") }}</option>
                                            <option value="biweek">{{ $t("BiWeekly") }}</option>
                                            <option value="month">{{ $t("Monthly") }}</option>
                                            <option value="quarterly">{{ $t("Quarterly") }}</option>
                                            <option value="halfyear">{{ $t("HalfYear") }}</option>
                                            <option value="annual">{{ $t("Annually") }}</option>
                                            <option value="custom">{{ $t("CustomDate") }}</option>
                                            <option value="daily">{{ $t("Daily") }}</option>
                                        </select>
                                    </td>


                                    <td>
                                        <input type="text" :class="{ 'form-control': true }" v-model="item.INVOICE_MEMO" />
                                    </td>
                                    <td>
                                        <input type="text" :class="{ 'form-control': true }" v-model="item.PO_NUMBER" />
                                    </td>
                                    
                                    <td  v-if="isShowWorkdayInvoiceType">

                                        <select name="DetailTemplate" v-model="item.INVOICE_TYPE_ID"
                                            :class="{ 'form-control': true }">

                                            <option value="" disabled selected>{{ $t("Select") }}</option>
                                            <option v-for="itm in invoiceTypeList" :key="itm.INVOICE_TYPE_ID"
                                                :value="itm.INVOICE_TYPE_ID">
                                                {{ itm.NAME }}
                                            </option>
                                        </select>
                                    </td>

                                    <td style="display:none;">

                                        <select name="DetailTemplate" v-model="item.INVOICE_DETAIL_TEMPLATE"
                                            :class="{ 'form-control': true }">

                                            <option value="" disabled selected>{{ $t("Select") }}</option>
                                            <option v-for="itm in invoiceDetailTemplateList" :key="itm.invoice_template_id"
                                                :value="itm.invoice_template_id">
                                                {{ itm.invoice_template_name }}
                                            </option>
                                        </select>
                                    </td>

                                    <td style="display:none;"><select name="DETAIL_FORMAT_TYPE" v-model="item.DETAIL_FORMAT_TYPE" :class="{ 'form-control': true }">
                                           <option value="" disabled selected>{{ $t("Select") }}</option>
                                            <option v-for="itm in fileFormatList" :key="itm.Value"
                                                :value="itm.Value">
                                                {{ itm.Name }}
                                            </option>
                                        </select></td>

                                    <td v-if="isAssociateProjectWithDivision"><select name="PROJECT_ID" v-model="item.PROJECT_ID" :class="{ 'form-control': true }">
                                           <option value="" disabled selected>{{ $t("Select") }}</option>
                                            <option v-for="itm in projectList" :key="itm.PROJECT_ID"
                                                :value="itm.PROJECT_ID">
                                                {{ itm.PROJECT_NAME }}
                                            </option>
                                        </select></td>

                                   

                                    <td class="text-center d-none"><label class="switch w70">
                                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                v-model="item.SUMMARIZED_LINE_ITEMS" v-bind:name="'IsLogin'"
                                                v-disabled="disabled" />
                                            <span class="slider round" style="cursor:default;">
                                                <span class="slider-yes">{{ $t('Yes') }}</span>
                                                <span class="slider-no">{{ $t('No') }}</span>
                                            </span>
                                        </label>
                                    </td>
                                 
                                    <td class="text-center single-action" v-if="configNotes == 'yes'">    
                                        <a href="javascript:;" class="text-dark single-action" @click="ViewNotes(item)">
                                            <em aria-hidden="true" class="fa fa-file-text-o"></em><span class="noti-circle noti-orange">{{ item.NO_OF_NOTES }}</span></a>
                                    </td>
                                    <td class="text-center">
                                        <a class="blue recyclebin enable ancdeleteadditionalinfo" @click="RemoveInvoice(index,item.DIVISIONS_ID)">
                                            <i class="fa fa-trash text-danger action-icon"></i>
                                    </a>
                                    </td>
                                </tr>

                                <tr v-if="IsInvoiceGroupingDataShow">
                                    <td colspan="10" class="text-center text-danger no_records no-record">
                                        {{ $t('NoRecordfound') }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                       
                    </div>
                


                    <div class="col-lg-12 p-0 float-left text-right mt-3">
                        <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveInvoiceData()"
                                title="Save">
                                <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                            </a>
                            <a href="javascript:;" class="btn btn-danger" v-on:click="Cancel()" title="Cancel">
                                <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                            </a>
            <input type="hidden" id="hdnPageNum" value="1">
        </div>
                    
                </div>
               
            </div>
        </div>
        <AddNoteUtilityOrDivision v-if="isShowAddNoteDivision" :divisionId="divisionId"  viewType="InvoiceGrouping" :utilityId="utilityId" :clientId="clientId" modulename="Billing" submodulecode="Billing_InvoiceGrouping" :callbackfunction="GetDivisionMappingDetail"></AddNoteUtilityOrDivision>
    </section>
</template>
<script>

import AddNoteUtilityOrDivision from "../../../components/Modals/AddNoteUtilityOrDivision.vue";
import DataService from "../../../services/DataService";

export default {
    components: {
        AddNoteUtilityOrDivision
    },
    props: {
        UserId: {
            type: Number
        },
        ClientData: {
            type: Object
        },
        InvoiceData: {
            type: Array
        },
        IsInvoiceGroupingDataShow:{
            type: Boolean            
        }
    },
    data() {
        return {

            isLoading: false,
            CompanyId: null,
            UserId: null,
           
            Result: [],
            invoiceFrequencyCustomDate:[],
            invoiceTypeList:[],
            projectList:[],
            invoiceDetailTemplateList:[],
            fileFormatList:[],
            configNotes:'',
            isAssociateProjectWithDivision:false,
            isShowWorkdayInvoiceType : false,
            isShowAddNoteDivision:false,
            divisionId:0,
            clientId:0,
            utilityId:0,
            isCheckExtraFeatureUtilityBilling:false,
            isCheckExtraFeatureUSICBILLING:false
        };
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
        await vm.CheckFeaturesForCompany('USICBILLING'),
        await vm.CheckFeaturesForCompany('UTILITY_BILLING')
        await vm.CheckExtraFeatures('AssociateProjectWithDivision');
        await vm.CheckExtraFeatures('EnableWorkdayInvoiceType');
        await vm.GetDivisionMappingDetail();
    },
    methods: {
        CheckFeaturesForCompany: async function (type) {
            var vm = this;
            var appendingUrl = `featureCode=${type}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if(type == 'USICBILLING'){
                    if (response.data == true) {
                         vm.isCheckExtraFeatureUSICBILLING = true;
                    } else {
                         vm.isCheckExtraFeatureUSICBILLING = false;
                    }
                }
                else if(type == 'UTILITY_BILLING'){
                    if (response.data == true) {
                         vm.isCheckExtraFeatureUtilityBilling = true;
                    } else {
                         vm.isCheckExtraFeatureUtilityBilling = false;
                    }
                }
               
            });
        },
        ViewNotes: function(item){
             var vm = this;
             vm.divisionId = item.DIVISIONS_ID;
             vm.clientId = this.UserId;
             vm.isShowAddNoteDivision =true;
        },
        GetDivisionMappingDetail: async function () {
            var vm = this;
            await DataService.GetDivisionMappingDetail().then((response) => {
                if(response.data != null){
                    this.invoiceFrequencyCustomDate = response.data.invoiceFrequencyCustomDateDropdDownList ? JSON.parse(response.data.invoiceFrequencyCustomDateDropdDownList) : [];
                    this.invoiceTypeList = response.data.listInvType ? JSON.parse(response.data.listInvType) : [];
                    this.projectList = response.data.projectDetailDropdDownList ? JSON.parse(response.data.projectDetailDropdDownList) : [];
                    this.invoiceDetailTemplateList = response.data.invoiceDetailTemplateDropdDownList ? JSON.parse(response.data.invoiceDetailTemplateDropdDownList) : [];
                    this.fileFormatList = response.data.invoiceDetailFileFormatDropdDownList ? JSON.parse(response.data.invoiceDetailFileFormatDropdDownList) : [];
                    this.configNotes = response.data.getNotesByCompanyId;
                }
            });
        },
        async CheckExtraFeatures(featureCode) {
            const params = `featureCode=${featureCode}`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    if(featureCode == 'AssociateProjectWithDivision'){
                        this.isAssociateProjectWithDivision = response.data === true;
                    }
                    else
                    {
                        this.isShowWorkdayInvoiceType = response.data === true;
                    }
                    
                }
            } catch (error) {
                console.error("Error checking extra features:", error);
            }
        },

        RemoveInvoice(index,divisionId) {
            var vm = this;
            var msg = vm.$t("ConfirmDelete");
            vm.confirmR(msg, true, null,function () {
                vm.InvoiceData.splice(index, 1); 
                if(divisionId != 0){
                    var postJSON = {
                        ids: divisionId,
                        companyId: vm.CompanyId,
                        userId: vm.user_id,
                        moduleName: "Billing",
                        refCode: "TALYGEN_BE_BILLING_DIVISION"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {   
                            vm.$parent.GetInvoicedata();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Division')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Division')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                }
            });
        },
        SaveInvoiceData: function () {
            var vm = this;

            var params = `ClientId=${vm.UserId}`;
            var obj = {
                MainData: vm.InvoiceData
            }

            DataService.SaveInvoiceData(params, obj).then(function (response) {
                if (response.data.status == "Success") {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Division")]), "success", true, vm.$t("Alert"));
                    vm.$parent.GetInvoicedata();
                    vm.$parent.IsInvoiceGroupingDataShow = false;

                }
                else {
                    vm.ShowAlert(vm.$t('UnknownErrorOccur'), "failure", true, vm.$t("Alert"));
                }
            });
        },
        Cancel: function () {
            var vm =this;
            vm.$parent.GetInvoicedata();
        },
    }
};
</script>