<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Holiday') }} {{ $t('Bank') }} </h4>
                        <button type="button" class="close" v-on:click="CloseHolidayBank">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en"  :schema="FormSchema" ref="AssignTagForm" 
                            v-on:OnSubmit="OnSubmit"></dynamic-form>  
                        <div id="divHolidayBankListing" class="w-100 float-left" v-for="(country, countryIndex) in countries" :key="countryIndex">
                            <div v-for="(location, locationIndex) in country.locations" :key="locationIndex">
                                <div class="designcontroller boxsizing mb-3">
                                    <div class="col-lg-12 float-left px-0 mb-2 designcontroller boxsizing">
                                        <div class="heading-border">
                                            <h5 class="h5">
                                                <span><b>{{ $t('CountryLevel') }}:</b> {{ country.CountryName }}</span>
                                            </h5>
                                        </div>
                                    </div>
                                    <div class="listing countrylisting">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 search Search-panel float-left py-2 p-0" id="divHolidayBankDataForSearch">
                                            <div class="col-12 float-left">
                                                <div class="row mt-0">
                                                    <div class="col-6 float-left pr-1 pl-0">
                                                        <input v-model="searchQuery[countryIndex][locationIndex]" class="form-control txtHolidayBankDataSearch" type="text" placeholder="Search by Holiday Name" />
                                                    </div>
                                                    <div class="col-3 float-left pl-0">
                                                        <div class="search-btm-btn background-0 text-left form-check-inline">
                                                            <a title="Search" id="aHolidayBankDataSearch_1" class="btn btn-success mr-1 px-4 text-white" @click="searchHoliday(countryIndex, locationIndex)"><i class="fa fa-search"></i></a>
                                                            <a title="Clear all " id="clearHolidayBank_1" class="btn btn-danger px-4 text-white" @click="clearSearch(countryIndex, locationIndex)"><i class="fa fa-refresh"></i></a>
                                                        </div>
                                                    </div>
                                                    <div class="col-lg-3 col-sm-12 mt-3 p-0" id="divShowlistingData">
                                                        <div class="row">
                                                            <div class="col-12">
                                                                <div class="mr-2 d-inline-block">
                                                                    <span class="legends_medium colorboxsquare"></span>{{ $t('HolidayBank') }}
                                                                </div>
                                                                <div class="mr-2 d-inline-block">
                                                                    <span class="legends_high colorboxsquare"></span>{{ $t('ManualHoliday') }}
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="table-responsive table-fix-header">
                                            <table :id="'tblHBList_' + countryIndex + '_' + locationIndex" class="table table-bordered mt-0" style="width:100%;">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th class="text-center" width="5%">{{ $t('Select') }}</th>
                                                        <th style="width:174px"><span style="min-width:174px" class="ui-resizable" id="HolidayName">{{ $t('HolidayName') }}</span></th>
                                                        <th style="width:258px"><span style="min-width:258px" class="ui-resizable" id="HolidayDescription">{{ $t('HolidayDescription') }}</span></th>
                                                        <th style="width:138px"><span style="min-width:138px" class="ui-resizable" id="Recurring">{{$t('Recurring')}}</span></th>
                                                        <th style="width:174px"><span style="min-width:174px" class="ui-resizable" id="HolidayType">{{ $t('HolidayType') }}</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody v-for="(holiday, holidayIndex) in filteredHolidays(country.CountryId, location.LocationId)" :key="holidayIndex">                                                  
                                                        <tr :class="{'high-bar':holiday.isManual==1,'medium-bar':holiday.isManual==0}" >                                                                                                                    
                                                                <td class="text-center" v-if="holiday.old_Holiday_id > 0 && UserId > 0">
                                                                    <div class="custom-control custom-checkbox">
                                                                        <input class="chkItems popchk custom-control-input" type="checkbox" :name="'Checkbox_'+holiday.bank_Holiday_Id" :id="'Checkbox_'+holiday.bank_Holiday_Id" :value="holiday.bank_Holiday_Id" style="display: none;" @change="CheckCheckBox(holiday)">
                                                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'Checkbox_'+holiday.bank_Holiday_Id"></label>
                                                                    </div>
                                                                </td> 
                                                                <td class="text-center" v-else>
                                                                    <div class="custom-control custom-checkbox">
                                                                        <input class="chkItems popchk custom-control-input" disabled="disabled" type="checkbox" :name="'Checkbox_'+holiday.bank_Holiday_Id" :id="'Checkbox_'+holiday.bank_Holiday_Id" :value="holiday.bank_Holiday_Id" style="display: none;">
                                                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'Checkbox_'+holiday.bank_Holiday_Id"></label>
                                                                    </div>
                                                                </td>                                                                
                                                                <td>{{ holiday.bank_Holiday_Name }}</td>
                                                                <td>{{ holiday.bank_Holiday_Desc }}</td>
                                                                <td class="text-center">
                                                                    <label class="switch w70">
                                                                        <input :checked="holiday.isRecurring==1" :name="'Checkbox_'+holiday.isRecurring" class="form-check-input custom-control-input dynamic" type="checkbox" />
                                                                        <span class="slider round" style="cursor:default;">
                                                                            <span class="slider-yes">{{ $t('Yes') }}</span>
                                                                            <span class="slider-no">{{ $t('No') }}</span>
                                                                        </span>
                                                                    </label>
                                                                </td>
                                                                <td>
                                                                    <div class="pl-0 border-0">
                                                                        <div class="custom-control custom-radio d-inline-block">
                                                                            <input :id="'IsFixed_'+holiday.isFixedDate" class="form-check-input radio-checkbox" type="radio" :disabled="holiday.old_Holiday_id > 0" :name="'IsFixed_'+holiday.isFixedDate" :checked="holiday.isFixedDate> 0" value="1">
                                                                            <label class="form-check-label" :for="'IsFixed_'+holiday.isFixedDate">{{ $t('Fixed') }}</label>
                                                                        </div>
                                                                        <div class="custom-control custom-radio d-inline-block">
                                                                            <input :id="'IsFixed_'+holiday.isFixedDate" class="form-check-input radio-checkbox" type="radio" :name="'IsFixed_'+holiday.isFixedDate" value="0" :disabled="holiday.old_Holiday_id > 0" :checked="holiday.isFixedDate < 1">
                                                                            <label class="form-check-label" :for="'IsFixed_'+holiday.isFixedDate">{{ $t('Observed') }}</label>
                                                                        </div>
                                                                    </div>
                                                                </td>

                                                        </tr>                                                        
                                                </tbody>
                                            </table>                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="row flex-row-reverse">
                            <div class="col-lg-12 text-right mt-3">
                                <a href="javascript:;" @click="btnSaveHB" class="btn btn-success mr-2" title="Save" value="Save" name="btnSaveHB"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</a>
                                <a href="javascript:;" @click="CloseHolidayBank" title="Cancel" class="btn btn-danger"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                            </div>
                        </div>
                    </div>   
                </div>
            </div>
        </div>        
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import {  TreeSelectField } from 'tg-controls_cli/dist/myLib.common'
export default {
    components: {
        'tree-view': TreeSelectField
    },
    data(){
        return{
            isLoading:false,
            RequestForm:'Client',
            FormSchema:[
            {
                layoutType: "single",
                Data: [
                    {
                        astype: "RadioField",
                        name: "rdMapExisting",
                        value: "1",
                        visibility: true,
                        config: {
                            options: [{ name: this.$t('MapwithExistingHoliday'), value: 1 }, { name: this.$t('MapwithNewHoliday'), value: 0 }],                                
                            onChange: this.onChangeRadio
                        }
                    },
                ]
            },
            {
                layoutType: "triple",
                Data: [
                         {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            visibility: false,
                            placeholder: "Select Location",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",                                                
                            label: this.$t('Status'),
                            name: "Status",
                            visibility: false,
                            value: "",  
                            config: {
                                options: []
                            },                           
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('SelectYear'),
                            name: "SelectYear",
                            visibility: false,
                            value: "1",                       
                            config: {
                                options: []  
                            }
                        }  
                    ]
            }
            ],
            UserId:0,
            HolidayListData:[],
            countries:[],
            searchQuery: {},
            saveHolidayBankObj:[],
            LocationId:0,
            currentYear:0
        }
    },
    created:function(){
        var ths =this;
        ths.UserId=ths.$route.params.id;
        ths.currentYear = new Date().getFullYear();
        ths.GetStatusList();
        ths.HolidayBankList();
        ths.CompanyHolidayMultipleLocation();
        setTimeout(() => {
            ths.$refs.AssignTagForm.UpdateKeyValue();
        }, 200); 
    },
    methods:{
        CompanyHolidayMultipleLocation(){
                var vm = this;    
                var LocationData=[];
                var url=`RequestFrom=${'COMPANY_LOCATIONS'}`;
                DataService.CompanyHolidayMultipleLocation(url).then(function (response) {
                var json  = response.data.result;
                json.forEach((value) => {                                       
                    var CheckIfExist = LocationData.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.result)                            
                            LocationData.push(rslt)
                        }
                    }                      
                });  
                vm.FormSchema[1].Data[0].config.options=LocationData;                 
            });
            
        },      
        getChildLocation (locationId, currentItem, data) { 
            var vm =this;
            let treeArray={};
            treeArray={
                id:currentItem.COMPANY_LOCATION_ID,
                label:currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = vm.getChildLocation(ChildfolderId, value, data);                                              
                        finalChildArray.push(rslt);
                    } else {                        
                        treeArray={
                            id:`${value.COMPANY_LOCATION_ID}`,
                            label:`${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        GetStatusList:function(){            
            var ths=this;
            var url=`statusType=Category&hasglobal=true&langCode=en`
            DataService.GetStatusList(url).then((response)=>{                
                if(response.data.length>0){
                    var statusData=JSON.parse(response.data);
                    statusData.forEach(element => {
                        var Obj={
                            name:element.STATUS,
                            value:element.STATUS_ID
                        }
                        ths.FormSchema[1].Data[1].config.options.push(Obj);
                    });
                    ths.$refs.AssignTagForm.UpdateKeyValue();
                }
            });
        },
        HolidayBankList(){
            var ths =this;
            var url=`countryId=&searchText=&reqFrom=Client&clientId=${ths.UserId}&existingMap=true`;
            DataService.HolidayBankList(url).then((response)=>{
                if(response.data.length>1){                    
                    var HolidayData=response.data;
                    ths.HolidayListData=HolidayData;
                    ths.prepareCountries(HolidayData);
                }
            })
        },
        OnSubmit(){},
        prepareCountries(HolidayData){
            var ths =this;
            const countriesMap = {};
            HolidayData.forEach(holiday => {
                if (!countriesMap[holiday.bank_Holiday_Country_Id]) {
                    countriesMap[holiday.bank_Holiday_Country_Id] = {
                        CountryId: holiday.bank_Holiday_Country_Id,
                        CountryName: holiday.bank_Holiday_Country_Name,
                        locations: []
                    };
                }                
                const country = countriesMap[holiday.bank_Holiday_Country_Id];
                ths.LocationId = countriesMap[holiday.company_Location_Id];
                if (!country.locations.some(loc => loc.LocationId === holiday.company_Location_Id)) {
                    country.locations.push({
                        LocationId: holiday.company_Location_Id,
                        LocationName: holiday.location_Name
                    });
                }                
            })
            
            ths.countries = Object.values(countriesMap);
            ths.countries.forEach((country, countryIndex) => {
                ths.searchQuery[countryIndex] = [];
                country.locations.forEach((location, locationIndex) => {
                    ths.searchQuery[countryIndex][locationIndex] = "";
                });
            });                
            ths.LocationId=ths.countries[0].locations[0].LocationId        
        },
        filteredHolidays(countryId, locationId) {
            return this.HolidayListData.filter(holiday => holiday.bank_Holiday_Country_Id === countryId && holiday.company_Location_Id === locationId);
        },
        searchHoliday(countryIndex, locationIndex) {
            console.log(`Search for country: ${countryIndex}, location: ${locationIndex}`);        
        },        
        clearSearch(countryIndex, locationIndex) {
            this.searchQuery[countryIndex][locationIndex] = "";
        },
        CloseHolidayBank(){
            var ths =this;
            ths.$emit('CloseHolidayBank');
        },
        onChangeRadio(){
            debugger
            var ths =this;
            var YearData=[];
            var chkval=ths.FormSchema[0].Data[0].value;
            ths.currentYear = new Date().getFullYear();
            const previousYear = ths.currentYear - 1;
            const nextYear = ths.currentYear + 1;
            YearData.push({name:`Previous Year (${previousYear})`,value:previousYear},{name:`Current Year (${ths.currentYear})`,value:ths.currentYear},{name:`Next Year (${nextYear})`,value:nextYear});
            if(chkval==0){
                ths.FormSchema[1].Data[0].visibility=true;                
                ths.FormSchema[1].Data[1].visibility=true;
                ths.FormSchema[1].Data[2].visibility=true;
                ths.FormSchema[1].Data[2].config.options=YearData;
            }else{
                ths.FormSchema[1].Data[0].visibility=false;                
                ths.FormSchema[1].Data[1].visibility=false;
                ths.FormSchema[1].Data[2].visibility=false;
            }  
        },
        CheckCheckBox(val){
            var ths =this;
            if(val !=null){
                var Obj={
                    country_id:val.bank_Holiday_Country_Id,
                    holidaybank_id:val.bank_Holiday_Id,
                    selectedlocations:val.company_Location_Id,
                    IsRecurring:val.isRecurring==0?false:true,
                    IsFixed:val.isFixedDate==1?true:false
                }
            }else{
                var Obj={}
            }
           ths.saveHolidayBankObj.push(Obj);            
        },
        btnSaveHB(){
            debugger
            var ths = this;
            var retVal = '';  
            let CheckVal=ths.FormSchema[0].Data[0].value;          
            if(CheckVal != 1){
                if(ths.LocationId != 0){
                    retVal += "";
                }else{
                    retVal += "no";
                }
            }
            if (ths.saveHolidayBankObj.length == 0) {                
                ths.ShowAlert(ths.$t('SelectHolidayBank'),"warning", true, ths.$t("Alert"));
                return false;
            }
            if(retVal.length == 0){
                var arrids = $('#visiblecolumn input:checkbox').map(function () {
                    return this.value;
                }).get();               
                var isForCompany = 0;
                if ($(".chkAddHolidayIn:eq(0):checked").length > 0) {
                    isForCompany = 1;
                }
                var isForContract = 0;
                if ($(".contractclick:checked").length > 0) {
                    isForContract = 1;
                }
                if (ths.RequestForm == "Client") {
                    isForContract = 1;
                }
                else {
                    //$("#hdnMainClientId").val(arrids.join(","));
                }
                var _status = 1001;
                if (ths.FormSchema[1].Data[1].value != "") {
                    _status = ths.FormSchema[1].Data[1].value;
                }
                var existingMapping = true;
                if (CheckVal == 0) {
                    existingMapping = false;
                }
                if (ths.RequestForm != "Client") {
                    existingMapping = false;
                }
                var data = { 
                    client_id: ths.UserId, 
                    selectedBankHolidays: ths.saveHolidayBankObj, 
                    isforcompany: isForCompany, 
                    isforcontract: isForContract, 
                    year: ths.currentYear, 
                    status: _status, 
                    existingMapping: existingMapping 
                };
                var JsonObj=JSON.stringify(data);
                DataService.SaveBankHoliday(JsonObj).then((response)=>{
                if(response.status==200){                    
                    ths.CloseHolidayBank();                    
                }
            })
            }
        }
    }
}
</script>
<style scoped>
.switch {width: 71px;}
</style>